import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class Progress extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressPromiseTimeSure",e.target.value );
	}

	handleBaseTime=(date, datestring)=>{
		this.props.updateData("progressPromiseBaseTime",date.valueOf())
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	render() {
		const { sureState } = this.state;
		const { buildTeamTwoMainObj } = this.store;
		let fields = mobx.toJS(buildTeamTwoMainObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};

		return (
			<div className="mt15">
				<Form {...formItemLayout} >
					<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
							<Form.Item label="进场时间" labelAlign="left" className="w build-two-form-label" style={{ marginBottom:"0"}}>
								{getFieldDecorator('progressPromiseTimeSure', {
									initialValue: fields.progressPromiseTimeSure,
									rules: [{required: true, message: '此项是必填'}],
								})(
									<Radio.Group onChange={this.handleTimeSure}>
										<Radio value={0}>未确定</Radio>
										<Radio value={1}>已确定</Radio>
									</Radio.Group>
								)}
								{getFieldDecorator('progressTargetBaseTime', {
									initialValue: fields.progressPromiseTimeSure == null || fields.progressPromiseBaseTime == 0 ? "" : moment(moment(fields.progressPromiseBaseTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
									rules: [{required: false, message: '此项是必填'}],
								})(
									fields.progressPromiseTimeSure===1?
										<DatePicker
											className="w"
											style={{width:'160px'}}
											placeholder="请选择"
											format="YYYY-MM-DD"
											onChange={this.handleBaseTime}
										/>:<span></span>
								)}
							</Form.Item>
							<Form.Item label="非标箱进场时间" labelAlign="left" className="w build-two-form-label" style={{ marginBottom:"0"}}>
								 T+ {getFieldDecorator('progressPromiseNostandardDay', {
									initialValue: fields.progressPromiseNostandardDay,
									rules: [{required: false, message: '此项是必填'}],
								})(
									<InputNumber className="build-two-input" onBlur={(e)=>{this.handleDaysChange(e, 'progressPromiseNostandardDay')}} placeholder="请输入数字" />
								)} 日历天
							</Form.Item>
							<Form.Item label="装配完成时间" labelAlign="left" className="w build-two-form-label" style={{ marginBottom:"0"}}>
								T+ {getFieldDecorator('progressPromiseOverDay', {
								initialValue: fields.progressPromiseOverDay,
								rules: [{required: true, message: '此项是必填'}],
							})(
								<InputNumber className="build-two-input" onBlur={(e)=>{this.handleDaysChange(e, 'progressPromiseOverDay')}} placeholder="请输入数字" />
							)} 日历天
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</div>
		)
	}
}






