import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import {Button, Table, Spin} from 'antd';
import TimelineTable from './TimelineTable';

@observer
class TwoItemView extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {

		}
	}

	render() {
		const {dataSource, dataSourceTwo, type, disabled} = this.props;
		const columns = [
			{
				title: '施工任务',
				dataIndex: 'name',
				key: 'name',
				align: 'left',
				width: "100px",
				render: (text, record) => {
					return (<div style={{
						paddingLeft: record.level * 10 + "px"
					}}>
						{(record.code==null?"":record.code) + " " + record.name}
					</div>)
				}
			},
			{
				title: <div style={{width: "56px"}}>
					<p>{type=="three"?"剩余用时":"标准用时"}</p>
					<p>（天）</p>
				</div>,
				dataIndex: 'needWorkTime',
				key: 'needWorkTime',
				align: 'center',
				width: "90px"
			},
			{
				title: '计划开始时间',
				dataIndex: 'planBeginTime',
				key: 'planBeginTime',
				align: 'center',
				width: "90px",
				render: (text, record) => {
					return (<div>
						{text !== 0 && text !== null && text !== undefined ? moment(text).format('MM月DD日') : ''}
					</div>)
				}
			},
			{
				title: '计划完成时间',
				dataIndex: 'planEndTime',
				key: 'planEndTime',
				align: 'center',
				width: "90px",
				render: (text, record) => {
					return (<div>
						{text !== 0 && text !== null && text !== undefined ? moment(text).format('MM月DD日') : ''}
					</div>)
				}
			}
		];

		return (
			<div>
				<Button
					disabled={disabled}
					className={this.props.currentStep==3?"hidden":"mb10"}
					ghost
					type="primary"
					icon="poweroff"
					onClick={this.props.reStartHandover}
				>
					重新启动
				</Button>
				<div className="clearfix">
					<div className={"fl"}
					     style={{width: '480px'}}>
						<Table
							dataSource={dataSource.filter((item)=>{
								return item.name!=="材料运输"
							})}
							columns={columns}
							pagination={false}
							bordered
							className="agenda_nohover construction_table"
							rowClassName={(record) => {
								if (record.currentProgress == 100||record.needWorkTime==0) {
									return "construct_trfive";
								} else {
									if (record.level == 0) {
										return "construct_trone";
									} else if (record.level == 1) {
										return "construct_trtwo";
									} else if (record.level == 2) {
										return "construct_trthree";
									} else {
										return "";
									}
								}
							}}
						/>
					</div>
					<div className={"fl "}
					     style={{
						     width: 'calc(100% - 480px)',
						     paddingLeft: "15px"
					     }}>
						<Spin
							tip="正在加载，请稍等......"
							spinning={disabled}
						>
						<TimelineTable
							dataSource={dataSourceTwo}
						/>
						</Spin>
					</div>
				</div>
			</div>
		)
	}
}


export default TwoItemView;



