/*
* 确认供应商
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Button} from 'antd';
import * as mobx from "mobx";
import OneItem from "./OneItem";
import TwoItem from "./TwoItem";
import ThreeItem from "./ThreeItem";

@inject('DeliveryStore')
@observer
class ConfirmSupplier extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
			currentItem:0,
			projectId:null,
			currentStep:1,
			taskId:null,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if(projectDetail.id!==this.state.projectId){
			this.setState({projectId:projectDetail.id})
			this.getConfirmSupplierState();
		}
	}

	componentDidMount() {
		const { projectDetail } = this.store;
		this.setState({projectId:mobx.toJS(projectDetail).id})
		this.getConfirmSupplierState();
		this.store.getSts();
	}

	getConfirmSupplierState=()=>{
		const { taskDetail } = this.store;
		this.store.getConfirmSupplierState({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			if(res){
				this.store.supplierState = res;
			}
		})
	}

	getBuildTeamStateInfo=(state)=>{
		if(state===0){
			return "待启动"
		}
		if(state===1){
			return "进行中"
		}
		if(state===2){
			return "已完成"
		}
	}

	getBuildTeamStateIcon=(state)=>{
		if(state===0){
			return "./images/delivery/u1055.png"
		}
		if(state===1){
			return "./images/delivery/u1053.png"
		}
		if(state===2){
			return "./images/delivery/u1232.png"
		}
	}

	overCurrentStageTask=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.overConfirmSupplierStep({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.supplierState = res;
				this.getProjectDetail();
			}
		})
	}

	reStartHandover=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.reStartConfirmSupplier({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.supplierState = res;
				this.getProjectDetail();
			}
		})
	}

	getProjectDetail=()=>{
		const projectId =this.store.projectId;
		this.store.getProjectDetail({'id':projectId}).then((res)=>{
			this.store.projectDetail = res
		})
	}

	switchSteps=(step)=>{
		this.setState({currentStep: step})
	}

	render() {
		const { currentStep } = this.state;
		const { supplierState } = this.store;

		return (
			<div className="dailyModal">
					<div className="mt20 f14" style={{borderBottom: "1px dashed #e6e6e6", paddingBottom: "30px"}}>
						<div style={{display:"flex",justifyContent:'center', alignItems:'center'}}>
							<div style={{display:"flex", flexDirection:"column",alignItems:'center'}}>
								<div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(supplierState).state1)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(supplierState).state1)}</div>
								<div style={{display:"flex", alignItems:'center'}}>
									<div className="build-team-line-white"> </div>
									<Button onClick={()=>{this.switchSteps(1)}} type={currentStep===1?'primary':'link'} className={currentStep===1?'active-button':'no-active-button'}>选择供应商</Button>
									<div className={currentStep===1?'build-team-line-green':'build-team-line-gray'}> </div>
								</div>
							</div>
							<div style={{display:"flex", flexDirection:"column", alignItems:'center'}}>
								<div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(supplierState).state2)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(supplierState).state2)}</div>
								<div style={{display:"flex", alignItems:'center'}}>
									<div className={currentStep===2?'build-team-line-green':'build-team-line-gray'}> </div>
									<Button onClick={()=>{this.switchSteps(2)}} type={currentStep===2?'primary':'link'} className={currentStep===2?'active-button':'no-active-button'}>签订合同</Button>
									<div className={currentStep===2?'build-team-line-green':'build-team-line-gray'}> </div>
								</div>
							</div>
							<div style={{display:"flex", flexDirection:"column", alignItems:'center'}}>
								<div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(supplierState).state3)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(supplierState).state3)}</div>
								<div style={{display:"flex", alignItems:'center'}}>
									<div className={currentStep===3?'build-team-line-green':'build-team-line-gray'}> </div>
									<Button onClick={()=>{this.switchSteps(3)}} type={currentStep===3?'primary':'link'} className={currentStep===3?'active-button':'no-active-button'}>填写用工信息</Button>
									<div className="build-team-line-white"> </div>
								</div>
							</div>
						</div>
					</div>
					<div className="mt30">
						{
							currentStep === 1 ?
									<OneItem
										overCurrentStageTask={this.overCurrentStageTask}
										reStartHandover={this.reStartHandover}
										switchSteps={this.switchSteps}
										currentStep={currentStep}
										store={this.store}/>: currentStep === 2 ?
								<TwoItem
									overCurrentStageTask={this.overCurrentStageTask}
									reStartHandover={this.reStartHandover}
									switchSteps={this.switchSteps}
									currentStep={currentStep}
									store={this.store}/>: currentStep === 3 ?
									<ThreeItem
										switchSteps={this.switchSteps}
										currentStep={currentStep}
										overCurrentStageTask={this.overCurrentStageTask}
										reStartHandover={this.reStartHandover}
										store={this.store}/>:null
						}
					</div>
			</div>
		)
	}
}


export default ConfirmSupplier;



