/*
* Table组件
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	Table,
	Input,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	Select,
	message,
	InputNumber,
	notification,
	Popover,
	Switch
} from 'antd';
import moment from 'moment';
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';

const { Option } = Select;
const { TextArea } = Input;


const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/*自定义编辑组件*/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};

	componentDidMount() {

	}

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			//this.toggleEdit();
			handleSave({...record, ...values});
		});
	};



	handleChangeLevel=(e)=>{
		const {record, handleSave} = this.props;
		handleSave({...record, ...{"level":e}});
	}
	//日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			handleSave({...record, ...values});
		});
	}
	updatePerson = (users) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (users.length) {
			userIds = users
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		// if(users.length>0){
		// 	data["masterName"] = userIds[0]['userName'];
		// 	data["masterId"] = userIds[0]['userId'];
		// }
		handleSave({...record, ...data});
	}

	handleRemoveItem=(id)=>{
        const { handleDelete } = this.props;
		handleDelete(id)

	}

	onBlur = () => {
		//this.toggleEdit();
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,  handleDelete, pDataList} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'keyIndex') {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
						<div className="w clearfix">
							<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
								<Icon
									style={{
										width: '35px',
										color: '#F17E7E',
										fontSize: '20px',
										float: 'left'
									}}
									type="minus-circle"/>
							</Popconfirm>
							<Input
								style={{
									float: 'right',
									width: 'calc(100% - 35px)',
								}}
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								//onChange={this.save}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div className="w clearfix">
					<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
								width: '35px',
								color: '#F17E7E',
								fontSize: '22px',
								float: 'left'
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							float: 'right',
							width: 'calc(100% - 35px)',
							minHeight: '31px'
						}}
						onClick={this.toggleEdit}
					>
						{children}
					</div>
				</div>
			);
		}else if (dataIndex == 'level'){
			return  (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? undefined : record[dataIndex],
					})(

						<Select  placeholder={'请选择决策等级'} style={{ width: 120 }} onChange={this.handleChangeLevel}>
							{pDataList.map((item,index)=>{
								return (
									<Option key={'level'+index} value={item.keyIndex}>{item.level}</Option>
								)
							})}
						</Select>
					)}
				</Form.Item>
			);
		}else if (dataIndex == 'caozuo'){
			return  (
				<Popconfirm title="确定要删除吗?" onConfirm={()=>{this.handleRemoveItem(`${record.id}`)}}>
					<img  style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" />
				</Popconfirm>
			);
		}else{
			return editing&&record['orders']>6 ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea
							rows={5}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
					//onClick={this.toggleEdit}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* 编辑table
* */
@observer
class EditableTableThreeItemMatter extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 任务数据源
			count: 0,        // table的key
			primaryData: {
				"id": null,
				"buildTeamThree": null,
				"orders": 0,
				"item": "",
				"level": 0,
			}, // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
			activeObj: {}, // 记录拆解前主任务数据
			activeSub: [], // 记录拆解前子任务数据
			subTaskDis: false, // 控制子任务的编辑状态
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {key: index})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"id": null,
					"buildTeamThree": null,
					"orders": 0,
					"item": "",
					"level": 0,
				}) // 新增任务数据
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource ===undefined || this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource ===undefined ||  this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"id": null,
				"buildTeamThree": null,
				"orders": 0,
				"item": "",
				"level": 0,
			}), // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
		});
		this.props.onRef && this.props.onRef(this);
	}



	// 删除
	handleDelete = (id) => {
        const {taskDetail} = this.store;
		this.store.delBuildTeamThreeItem({"id":id}).then((res)=>{
			if(res){
				message.success("删除成功");
				this.store.getBuildTeamThreeItemList({"taskId":taskDetail.id}).then((res)=>{
					this.props.saveData(res)
				})
			}else{
				message.error("删除失败")
			}
		})
	};

	// 新增
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		const newData = Object.assign(primaryData, {key: count, id:"!"+uuidv4()});
		// const newData = Object.assign(primaryData, {key: count});
		this.setState({
			dataSource: [...dataSource, ...newData],
			count: count + 1,
		}, () => {
			this.props.saveData(dataSource.concat([newData]));
		});
		const {id, ...newItem} = newData;
		this.props.saveItem(newItem);

	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 更新保存数据
			this.props.saveData(this.state.dataSource);
		});
		this.props.saveItem(newData[index])
	};

	// 折叠效果
	saveExpandedRows = (key) => {
		let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
		_this.setState({
			expandedKeys: keys
		}, () => {
			let obj = {[_this.props.id]: keys};
			let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
			_this.store.expandedKeys = Object.assign(expandedKeys, obj);
		});
	}

	// 查看
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 不可编辑
		}, () => {
			this.saveExpandedRows(record);
		});
	}


	// 取消保存
	onCalac = (key) => {
		this.saveExpandedRows(key);
		this.store.isEdit = false;
		// 更新tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
		this.subtask.clearState();
		this.setState({
			activeObj: Object.assign({})
		});
	}


	// 点击
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children == null ? [] : record.children.filter((item) => {
			return item.treeType === 'subtask'
		})
		const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
		if (disabled && subNumber > 0) {  // 详情态
			this.handleView(record.id)
		}
	}

	showDecisionLevelInfo=(level)=>{
		if(level===1){
			return "一级决策"
		}
		if(level===2){
			return "二级决策"
		}
		if(level===3){
			return "三级决策"
		}
		if(level===4){
			return "四级决策"
		}
		if(level===0){
			return ""
		}

	}

	checkForm=(form)=>{
		console.log('form=>',form)
	}

	checkFormValidate(){
		console.log("EditableFormRow=>",EditableFormRow)
		// this.props.form.validateFields((err, values) => {
		// 	if (!err) {
		// 		console.log('Received values of form: ', values);
		// 	}else{
		//
		// 	}
		// });
	}

	render() {
		const {dataSource, expandedKeys, subTaskDis} = this.state;
		const {disabled, tableType, selectedRows, pDataList} = this.props;
		const isChaijie = expandedKeys.length > 0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		let columnEle = [
			{
				title: '序号', dataIndex: 'keyIndex', align: 'left', width:'50px'
			},
			{
				title: '决策事项',
				dataIndex: 'item',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
						className="pr">
						{text}
					</div>);
				}
			},
			{
				title: '决策等级',
				dataIndex: 'level',
				width: '25%',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%',  height: '46px', lineHeight: '46px',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
						className="pr">
						{this.showDecisionLevelInfo(text)}
					</div>);
				}
			}

		];
		if(!disabled){
			columnEle = [{
				title: '操作', dataIndex: 'caozuo', align: 'left', editable: !disabled, width:'50px',
				render: (text, record) => { //rendcell 实现操作业务
					return (<img style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" />);
				}
			}].concat(columnEle)
		}
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					pDataList,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
					checkForm:this.checkForm,
				}),
			};
		});

		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					rowKey={row => row.id}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTableThreeItemMatter;




