import React from 'react';
import {inject, observer} from 'mobx-react';
import {Col, Row, Tooltip, message, Button, Popconfirm, Divider , Icon} from 'antd';
import * as mobx from "mobx";
import EditableTableUpload from "@/components/handover/EditableTableUpload";


//@inject('DeliveryStore')
@observer
class StakeholdeFile extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: []
		}
	}

	componentDidMount() {
		this.getEnclosureList();
	}

	saveContents=()=>{
		this.getEnclosureList();
	}

	saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:1,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:1}
		this.store.saveEnclosureMain(parmas).then((res)=>{
			this.getEnclosureList()
		})
	}

	getEnclosureList=()=>{
		const { taskDetail } = this.store;
		this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, "indexs":0}).then((res)=>{
			this.store.enclosureList = res
			this.setState({dataSource: res, enclosureList: res})
		})
	}

	handleBack = () => {
		this.props.changeVis("2");
	}

	render() {
		const {dataSource} = this.state;

		return (
			<div>
				<div className="daily_list_op mb30">
					<span onClick={this.handleBack} className="pointer">
						<Icon type="left-circle" style={{color: "#57ACFF", fontSize: "18px", marginRight: "5px"}}/>返回干系人列表
					    <Button className="title" type="link"><img src="./images/delivery/u3771.png" style={{width: '23px', height: '23px'}}/>附件</Button>
					</span>
					<Divider type="vertical" />
					<span style={{color: "#333", fontSize: '12px'}}>
						请按照要求上传附件内容，标星为必须上传项，可自由添加附件项目中不含的相关内容
					</span>
				</div>
				<EditableTableUpload
					id={'id'}
					store={this.store}
					dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
					saveData={this.saveContents}
					saveItem={this.saveItem}
					disabled={false}
					pid={''}
				/>
			</div>
		)
	}
}


export default StakeholdeFile;



