import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class Profit extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
		this.props.updateData("profitTarget", e.target.value)
	}

	handleNumChange=(e, type)=>{
		// console.log(e.target.value)
		this.props.updateData(type, e.target.value)
	}

	render() {
		const { sureState } = this.state;
		const { buildTeamTwoMainObj } = this.store;
		let fields = mobx.toJS(buildTeamTwoMainObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};

		return (
			<div className="mt15">
				<Form {...formItemLayout} labelAlign="left" >
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
							<Form.Item label="" className="w" style={{ marginBottom:"0"}}>
								{getFieldDecorator('profitTarget', {
									initialValue: fields.profitTarget,
									rules: [{required: true, message: '此项是必填'}],
								})(
									<Radio.Group onChange={this.handleTimeSure}>
										<Radio value={0}>不亏损</Radio>
										<Radio value={1}>盈利</Radio>
									</Radio.Group>
								)}
								{getFieldDecorator('profitTargetRate', {
									initialValue: fields.profitTargetRate,
									rules: [{required: false, message: '此项是必填'}],
								})(
									<InputNumber className="build-two-input"   onBlur={(e)=>{this.handleNumChange(e, 'profitTargetRate')}} placeholder="请输入数字" />
								)} %
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</div>
		)
	}
}






