/*
* Table组件 合同清单组件
* */
import React from 'react';
import {Table, Input, Popconfirm, Form, Icon, Button, InputNumber, message, Modal, Row, Col, Checkbox} from 'antd';
import numeral from 'numeral';
import {observer} from "mobx-react";

const EditableContext = React.createContext();
const EditableRow = ({ form, index, ...props }) => (
    <EditableContext.Provider value={form}>
        <tr {...props} />
    </EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
    state = {
        editing: false,
    };

    toggleEdit = () => {
        const editing = !this.state.editing;
        this.setState({ editing }, () => {
            if (editing) {
                this.input.focus();
            }
        });
    };

    save = e => {
        const { record, handleSave } = this.props;
        this.form.validateFields((error, values) => {
            if (error && error[e.currentTarget.id]) {
                return;
            }
            this.toggleEdit();
            const dataIndex = e.currentTarget.id;
            let obj = {};
            if (dataIndex == 'count') {
              let count = values["count"] == null || values["count"] == undefined?0: values["count"];
              let prize = record["prize"] == null || record["prize"] == undefined?0: record["prize"];
              obj.total = count*prize;
            }
            if (dataIndex == 'prize') {
                let count = record["count"] == null || record["count"] == undefined?0: record["count"];
                let prize = values["prize"] == null || values["prize"] == undefined?0: values["prize"];
                obj.total = count*prize;
            }
            handleSave({ ...record, ...values, ...obj });
        });
    };

    renderCell = form => {
        this.form = form;
        const { children, dataIndex, record, title } = this.props;
        const { editing } = this.state;

        if (dataIndex == 'count'||dataIndex == 'prize'||dataIndex == 'total') {  // 数字金额
            return editing ? (
                <Form.Item style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<InputNumber ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save} />)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{ minHeight: '31px' }}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        } else {
            return editing ? (
                <Form.Item style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save} />)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{ minHeight: '31px' }}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        }
    };

    render() {
        const {
            editable,
            dataIndex,
            title,
            record,
            index,
            handleSave,
            children,
            ...restProps
        } = this.props;
        return (
            <td {...restProps}>
                {editable ? (
                    <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
                ) : (
                    children
                )}
            </td>
        );
    }
}

@observer
class EditableTableInventory extends React.Component {
    constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            dataSource: [],
            count: 0,
            selectedRowKeys: [],
            selectedRowKeys2: [],
            visible: false,
            dataSource2: []
        };
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (JSON.stringify(nextProps.dataSource) != JSON.stringify(this.props.dataSource)) {
            let _list = nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((v, vindex)=>Object.assign(v, {rate: "13%", key: vindex+1}));
            this.setState({
                dataSource: _list,
                count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
            }, ()=>{
                let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
                this.setState({
                    selectedRowKeys2: dataSource.map(item=>item.parentId)
                });
            });
        }
    }

    componentDidMount() {
        if (this.props.dataSource) {
            let _list = this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((v, vindex)=>Object.assign(v, {rate: "13%", key: vindex+1}));
            this.setState({
                dataSource: _list,
                count: this.props.dataSource == null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
            }, ()=>{
                let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
                this.setState({
                    selectedRowKeys2: dataSource.map(item=>item.parentId)
                });
            });
        }
    }

    handleDelete = () => {
        if (this.state.selectedRowKeys.length==0) {
            message.warn("请先选中一项")
            return false;
        }
        const dataSource = [...this.state.dataSource];
        let data = dataSource.filter(item => !this.state.selectedRowKeys.includes(item.key)).map((v, vindex)=>Object.assign(v, {key: vindex+1}));
        this.setState({
            dataSource: data,
            count: data.length
        });
        this.props.updateState(data);
        this.setState({
            selectedRowKeys: []
        });
        message.success("删除成功");
    };

    handleAdd = () => {
        const { count, dataSource } = this.state;
        const {supplierTwoItem} = this.store;
        const obj = JSON.parse(JSON.stringify(supplierTwoItem));
        const newData = {
            key: count+1,
            id: "",
            type: 2,
            taskId: "",
            supplierId: obj.id,
            supplierTwoId: "",
            position: "",
            positionId: "",
            categoryType: "",
            categoryTypeId: "",
            productDetail: "",
            productDetailId: "",
            units: "",
            count: null,
            prize: null,
            total: null,
            remarks: "",
            specification: "",
            rate: "13%"
        };
        this.setState({
            dataSource: [...dataSource, newData],
            count: count + 1,
        });
        this.props.updateState([...dataSource, newData]);
    };

    handleSave = row => {
        const newData = [...this.state.dataSource];
        const index = newData.findIndex(item => row.key === item.key);
        const item = newData[index];
        newData.splice(index, 1, {
            ...item,
            ...row,
        });
        this.setState({ dataSource: newData });
        this.props.updateState(newData);
    };

    onSelectChange = selectedRowKeys => {
        this.setState({ selectedRowKeys });
    };

    onSelectChange2 = selectedRowKeys => {
        this.setState({ selectedRowKeys2: selectedRowKeys });
    };

    showModal = () => {
        const params = {
          id: this.store.projectId
        }
        this.store.getSupplierContractListByProjectId(params).then((res)=>{
            this.setState({
                dataSource2: res==null?[]:res.map(item=>{
                    return Object.assign(item, {
                        supplierTwoId: this.props.fields.id,
                        taskId: this.props.fields.taskId,
                    })
                }),
                visible: true,
            });
        });
    };

    handleOk = e => {
        let data = [...this.state.dataSource2];
        let newData = data.filter(item=>this.state.selectedRowKeys2.includes(item.parentId));
        this.props.updateState(newData);
        this.setState({
            visible: false
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false
        });
    };

    render() {
        const { dataSource, selectedRowKeys, visible, selectedRowKeys2, dataSource2 } = this.state;
        const columnsOld = [
            {
                title: '序号',
                dataIndex: 'key',
                width: '70px',
                align: "center",
                render: (text, record, index) => `${index + 1}`,
            },
            {
                title: '部位',
                dataIndex: 'position',
                align: "center",
                width: "10%",
            },
            {
                title: '产品类别',
                dataIndex: 'categoryType',
                align: "center",
                width: "10%",
            },
            {
                title: '产品明细',
                dataIndex: 'proudctDetail',
                align: "center",
                width: "10%",
            },
            {
                title: '规格尺寸',
                dataIndex: 'specification',
                align: "center",
                width: "10%",
            },
            {
                title: '单位',
                dataIndex: 'units',
                align: "center",
                width: "10%",
            },
            {
                title: '数量',
                dataIndex: 'count',
                align: "center",
                width: "10%",
            },
            {
                title: '采购单价',
                dataIndex: 'prize',
                editable: !this.props.disabled?true:false,
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text ==null||text ==0?0:numeral(text).format('0,0')
            },
            {
                title: '合计金额',
                dataIndex: 'total',
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text ==null||text ==0?0:numeral(text).format('0,0')
            },
            {
                title: '税率',
                dataIndex: 'rate',
                align: "center",
                width: '50px',
            },
            {
                title: '备注',
                dataIndex: 'remarks',
                align: "center",
                width: "10%",
            }
        ];
        const columnsOld2 =  [
            {
                title: '序号',
                dataIndex: 'key',
                width: '70px',
                align: "center",
                render: (text, record, index) => `${index + 1}`,
            },
            {
                title: '部位',
                dataIndex: 'position',
                align: "center",
                width: "10%",
            },
            {
                title: '产品类别',
                dataIndex: 'categoryType',
                align: "center",
                width: "10%",
            },
            {
                title: '产品明细',
                dataIndex: 'productDetail',
                align: "center",
                width: "10%",
            },
            {
                title: '规格尺寸',
                dataIndex: 'specification',
                align: "center",
                width: "10%",
            },
            {
                title: '单位',
                dataIndex: 'units',
                align: "center",
                width: "10%",
            },
            {
                title: '数量',
                dataIndex: 'count',
                align: "center",
                width: "10%",
            },
            {
                title: '销售单价',
                dataIndex: 'prize',
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text ==null||text ==0?0:numeral(text).format('0,0')
            },
            {
                title: '合计金额',
                dataIndex: 'total',
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text ==null||text ==0?0:numeral(text).format('0,0')
            },
            {
                title: '税率',
                dataIndex: 'rate',
                align: "center",
                width: '50px',
            },
            {
                title: '备注',
                dataIndex: 'remarks',
                align: "center",
                width: "10%",
            }
        ];
        const components = {
            body: {
                row: EditableFormRow,
                cell: EditableCell,
            },
        };
        const columns = columnsOld.map(col => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.handleSave,
                }),
            };
        });
        const {disabled} = this.props;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };
        let arrquantitynew = dataSource.map(itemnew => {
            return itemnew.total==null?0:itemnew.total; //单个
        });
        let total = dataSource.length>0?arrquantitynew.reduce((n,m) => n + m):0;
        const rowSelection2 = {
            selectedRowKeys: selectedRowKeys2,
            onChange: this.onSelectChange2,
        };

        return (
            <div>
                {disabled ? null :
                    <div>
                        <Button
                            onClick={this.showModal}
                            ghost
                            type="primary"
                            icon="plus-circle"
                            style={{paddingLeft: "8px", paddingRight: "8px", marginTop: "10px"}}
                        >
                            选择销售合同清单
                        </Button>
                        <Popconfirm title="确定要删除吗?" onConfirm={() => this.handleDelete()}>
                            <Button
                                ghost
                                type="primary"
                                icon="delete"
                                style={{marginLeft: "15px", paddingLeft: "8px", paddingRight: "8px", marginTop: "10px"}}
                            >
                                删除
                            </Button>
                        </Popconfirm>

                    </div>
                    }
                <Table
                    rowSelection={disabled?null:rowSelection}
                    components={components}
                    rowClassName={() => 'editable-row'}
                    className="expand_table bpm_table mt10"
                    bordered
                    dataSource={dataSource}
                    columns={columns}
                    pagination={false}
                    footer={() => '合计: '+numeral(total).format('0,0')}
                />
                {/*{disabled ? null :
                    <Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '18px'}}
                          type="plus-circle"/>}*/}
                <Modal
                    style={{width: "60%", minWidth: "1000px"}}
                    title="选择销售合同清单"
                    visible={visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Table
                        rowSelection={rowSelection2}
                        bordered
                        dataSource={dataSource2}
                        columns={columnsOld2}
                        pagination={false}
                        rowKey={record => record.parentId}
                    />
                </Modal>
            </div>
        );
    }
}

export default EditableTableInventory;




