import React from 'react';
import {inject, observer} from 'mobx-react';
import * as mobx from "mobx";
import Add from './Add';
import View from './View';

@inject('DeliveryStore')
@observer
class MakePlot extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
			projectId:null,
            currentStep: 1
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if(projectDetail.id!==this.state.projectId){
			this.setState({projectId:projectDetail.id})
			this.getConfirmSupplierState();
		}
	}

	componentDidMount() {
		const { projectDetail } = this.store;
		this.setState({projectId:mobx.toJS(projectDetail).id})
		this.getConfirmSupplierState();
	}

	getConfirmSupplierState=()=>{
		const { taskDetail } = this.store;
		this.store.getTaskStepState({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			if(res){
				this.store.plotState = res;
			}
		})
	}

	overCurrentStageTask=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.overTaskStep({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.plotState = res;
				this.getProjectDetail();
			}
		})
	}

	reStartHandover=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.reStartTaskStep({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.plotState = res;
				this.getProjectDetail();
			}
		})
	}

	getProjectDetail=()=>{
		const projectId =this.store.projectId;
		this.store.getProjectDetail({'id':projectId}).then((res)=>{
			this.store.projectDetail = res
		})
	}

	render() {
		const { plotState } = this.store;

		return (
			<div className="dailyModal" style={{
                paddingBottom: "50px",
                overflowX: "hidden",
				}}>
                {plotState.state1==2?<View
                    reStartHandover={this.reStartHandover}
                />:null}
                {plotState.state1==0||plotState.state1==1?<Add overCurrentStageTask={this.overCurrentStageTask}/>:null}
			</div>
		)
	}
}


export default MakePlot;



