import React from 'react';
import {observer, inject} from 'mobx-react';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {
    Form,
    Button,
    Input,
    message,
    Row,
    Col,
    DatePicker,
    Checkbox,
    Radio,
    Popconfirm,
    InputNumber,
    Modal,
    Select,
    Icon,
    Table
} from 'antd';
import moment from "moment";
import _ from "lodash";
const { confirm } = Modal;
const {TextArea} = Input;
const { Option } = Select;
import PageSelect2 from "@/components/common/PageSelect2";
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
    <EditableContext.Provider value={form}>
        <tr {...props} />
    </EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
class EditableCell extends React.Component {
    state = {
        editing: true,
    };

    toggleEdit = () => {
        const editing = !this.state.editing;
        this.setState({editing}, () => {
            if (editing) {
                this.input.focus();
            }
        });
    };

    save = e => {
        const {record, handleSave} = this.props;
        this.form.validateFields((error, values) => {
            if (error && error[e.currentTarget.id]) {
                return;
            }
            handleSave({...record, ...values});
        });
    };

    onChange = (type, e) => {
        const {record, handleSave} = this.props;
        if (record["resType"]!=e) {   // 不相等时清空
            handleSave({...record, ...{
                   [type]: e,
                    "name": "",
                    "omId": ""
            }});
        }
        else  {
            handleSave({...record, ...{[type]: e}});
        }
    }

    updateStore = (param) => {
        const {record, handleSave} = this.props;
        this.form.validateFields((error, values) => {
            values["name"] = param.name;
            values["omId"] = param.id;
            handleSave({...record, ...values});
        });
    }

    renderCell = form => {
        this.form = form;
        const {children, dataIndex, record, title} = this.props;
        const {editing} = this.state;
        if (dataIndex == "resType") {   // 资源类型
            return editing ? (
                <Form.Item style={{margin: 0}}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(
                        <Select style={{width: 200}}
                                ref={node => (this.input = node)}
                                value={record[dataIndex]}
                                onChange={this.onChange.bind(this, "resType")}
                        >
                            <Option value="人工">人工</Option>
                            <Option value="机械">机械</Option>
                        </Select>
                    )}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{paddingRight: 24, minHeight: "34px"}}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        } else if (dataIndex == "name") {   // 名称
            return editing ? (
                <Form.Item style={{margin: 0}} className="w">
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex] == null || record[dataIndex] == "" ? [] : record[dataIndex],
                    })(
                       <div className="w">
                           <PageSelect2
                               disabled={record["resType"]==null||record["resType"]==""?true:false}
                               type={record["resType"]=="机械"?"getMachinePageList":"getWorkerPageList"}
                               name={record["name"]}
                               value={record["omId"]|| ""}
                               updateStore={this.updateStore}
                           />
                       </div>)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{paddingRight: 24, minHeight: "34px"}}
                    onClick={this.toggleEdit}
                >
                    {record[dataIndex] == null || record[dataIndex] == "" ? null : record[dataIndex].join("、")}
                </div>
            );
        } else {
            return editing ? (
                <Form.Item style={{margin: 0}}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<InputNumber style={{width: "100%"}} ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{paddingRight: 24, minHeight: "34px"}}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        }
    };

    render() {
        const {
            editable,
            dataIndex,
            title,
            record,
            index,
            handleSave,
            children,
            ...restProps
        } = this.props;
        return (
            <td {...restProps}>
                {editable ? (
                    <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
                ) : (
                    children
                )}
            </td>
        );
    }
}

@Form.create()
@inject('DeliveryStore')
@withRouter
@observer
class OneItem extends React.Component {
    constructor(props) {
        super(props)
        this.store = this.props.DeliveryStore;
        this.state = {
            projectId: null,
            dictWork: [],
            dataSource: [
                /*{
                "id": "",
                "taskId": "",
                "name": "",
                "planBeginTime": 0,
                "planEndTime": 0,
                "constructionKey": [],
                "layers": 1,
                "remark": "",
                "orders": 1,
                "createTime": 0,
                "multiple": 1,
                "bocdesignId": ""
            }*/
            ],
            tableDatasource: [],  // 表格数据源
            main: {
                "efficiency": 100,
                "currentEfficiency": 100
            },
            projectTypes: [
                {
                    id: "1",
                    name: "标准箱"
                },
                {
                    id: "3",
                    name: "展厅"
                },
                {
                    id: "2",
                    name: "钢结构"
                }
            ]
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
        if (projectDetail.id !== this.state.projectId) {
            this.setState({projectId: projectDetail.id})
            this.getConstructionScheduleList();
        }
    }

    componentDidMount() {
        this.getDictWorkContentSelectList();
        this.getConstructionScheduleList();
        const {taskDetail} = this.store;
        this.store.getScheduleResourcesListByTaskId({
                "type": 0,
                "taskId": mobx.toJS(taskDetail).id
            }).then((res) => {
            this.setState({
                tableDatasource: res==null?[]:res
            });
        });
        this.store.getConstructionMainByProjectId().then((res)=>{
            if (res) {
                this.setState({
                    main: res
                });
            }
        });
    }

    getConstructionScheduleList = () => {
        const {taskDetail} = this.store;
        this.store.getConstructionScheduleList({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
            this.setState({
                dataSource: Array.isArray(res) && res.length > 0 ? res : [
                    /*{
                    "id": "",
                    "taskId": mobx.toJS(taskDetail).id,
                    "name": "",
                    "planBeginTime": 0,
                    "constructionKey": [],
                    "layers": 1,
                    "remark": "",
                    "multiple": 1,
                    "bocdesignId": ""
                }*/
                ]
            })
        });
    }

    getDictWorkContentSelectList = () => {
        this.store.getDictWorkContentSelectList({}).then((res) => {
            this.setState({
                dictWork: Array.isArray(res) ? res : []
            });
        });
    }

    // 新增
    handleAdd = () => {
        let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));

        let len = dataSource.length;
        if (len >= 1) {
            // 校验
            let param = dataSource[len - 1];
            if (param.name == null || param.name == "") {
                message.warn("楼栋名称是必填项");
                return false;
            }
            if (param.planBeginTime == null || param.planBeginTime == 0 || param.planBeginTime == "") {
                message.warn("计划开工时间是必填项");
                return false;
            }

            if ((param.bocdesignId==undefined||param.bocdesignId==""||param.bocdesignId==null)&&param.type!=undefined&&param.type!=1&&(param.planEndTime == null || param.planEndTime == 0 || param.planEndTime == "")) {
                message.warn("计划完成时间是必填项");
                return false;
            }

            if (param.type!=undefined&&param.type==1&&(param.layers == null || param.layers == "")) {
                message.warn("选择层数是必填项");
                return false;
            }
        }

        const {taskDetail} = this.store;
        const newData = {
            "id": "",
            "taskId": mobx.toJS(taskDetail).id,
            "name": "",
            "planBeginTime": 0,
            "planEndTime": 0,
            "constructionKey": [this.state.dictWork[0]],
            "layers": null,
            "remark": "",
            "bocdesignId": "",
            "standardCount": 1,
             "type": 1,
            "toiletCount": 0,
            "haveGallery": 0,
            "curtainType": null
        };

        dataSource = [...dataSource, newData];
        this.save(dataSource).then((res) => {
            this.setState({
                dataSource: res
            });
        });
    };

    // 删除
    handleDelete = record => {
        if (record.id != "" && record.id != null) {
            this.store.delConstructionSchedule({id: record.id}).then((res) => {
                if (res) {
                    message.success("删除成功");
                    const dataSource = [...this.state.dataSource];
                    let newData = dataSource.filter(item => item.id !== record.id)
                    this.setState({
                        dataSource: newData
                    });
                }
            });
        } else {
            message.warn("楼栋信息未保存");
            return false;
        }
    };

    // 完成该阶段
    overCurrentStage = () => {
        let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
        let len = dataSource.length;
        if (len == 0) {
            message.warn("请完善必填信息");
            return false;
        }

        if (len >= 1) {
            // 校验
            let param = dataSource[len - 1];
            if (param.name == null || param.name == "") {
                message.warn("楼栋名称是必填项");
                return false;
            }
            if (param.planBeginTime == null || param.planBeginTime == 0 || param.planBeginTime == "") {
                message.warn("计划开工时间是必填项");
                return false;
            }
            if ((param.bocdesignId==undefined||param.bocdesignId==""||param.bocdesignId==null)&&param.type!=undefined&&param.type!=1&&(param.planEndTime == null || param.planEndTime == 0 || param.planEndTime == "")) {
                message.warn("计划完成时间是必填项");
                return false;
            }
            /*if (param.constructionKey == null || (Array.isArray(param.constructionKey) && param.constructionKey.length == 0)) {
                message.warn("选择施工要素是必填项");
                return false;
            }*/
            if (param.type!=undefined&&param.type==1&&(param.layers == null || param.layers == "")) {
                message.warn("选择层数是必填项");
                return false;
            }
        }
        this.props.overCurrentStageTask();
    }

    // 完成并进入下一步
    doneAndNext = () => {
        let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
        let len = dataSource.length;
        if (len == 0) {
            message.warn("请完善必填信息");
            return false;
        }

        if (len >= 1) {
            // 校验
            let param = dataSource[len - 1];
            if (param.name == null || param.name == "") {
                message.warn("楼栋名称是必填项");
                return false;
            }
            if (param.planBeginTime == null || param.planBeginTime == 0 || param.planBeginTime == "") {
                message.warn("计划开工时间是必填项");
                return false;
            }
            if ((param.bocdesignId==undefined||param.bocdesignId==""||param.bocdesignId==null)&&param.type!=undefined&&param.type!=1&&(param.planEndTime == null || param.planEndTime == 0 || param.planEndTime == "")) {
                message.warn("计划完成时间是必填项");
                return false;
            }
            /*if (param.constructionKey == null || (Array.isArray(param.constructionKey) && param.constructionKey.length == 0)) {
                message.warn("选择施工要素是必填项");
                return false;
            }*/
            if (param.type!=undefined&&param.type==1&&(param.layers == null || param.layers == "")) {
                message.warn("选择层数是必填项");
                return false;
            }
        }
        const {currentStep} = this.props;
        this.props.overCurrentStageTask();
        this.props.switchSteps(currentStep + 1)
    }

    // 自动保存
    save = (css) => {
        const {taskDetail} = this.store;
        const params = {
            css: css,
            taskId: mobx.toJS(taskDetail).id
        }
        return new Promise((resolve => {
            this.store.saveConstructionScheduleList(params).then((res) => {
                resolve(res);
            });
        }));
    }

    handChange = (type, index, e) => {
        let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
        if (type == "constructionKey") {
            let constructionKey = dataSource[index][type] == null ? [] : JSON.parse(JSON.stringify(dataSource[index][type]));
            const {checked, value} = e.target;
            if (checked) { // true
                if (constructionKey.findIndex(i => i.id == value.id) == "-1") {
                    constructionKey.push(value);
                }
            } else {
                let mindex = constructionKey.findIndex(i => i.id == value.id);
                constructionKey.splice(mindex, 1);
            }
            dataSource[index][type] = constructionKey;
        } else if (type == "standardCount"||type == "toiletCount") {
            let val = _.trim(e)==""?1:e;
            dataSource[index][type] = val;
        } else if(type=="haveGallery") {
            let val = Number(e.target.value);
            dataSource[index][type] = val;
            if (val==0) {
                dataSource[index]["curtainType"] = 0;
            }
        } else if(type=="curtainType") {
            let val = Number(e);
            dataSource[index][type] = val;
        } else if (type=="type"&&e.target.value==1) {   // 切换类型
            let val = e.target.value;
            dataSource[index][type] = val;

            dataSource[index]["layers"] = null;
            dataSource[index]["standardCount"] = 1;
            dataSource[index]["toiletCount"] = 0;
            dataSource[index]["haveGallery"] = 0;
            dataSource[index]["curtainType"] = 0;
            dataSource[index]["planEndTime"] = 0;
        } else {
            let val = e.target.value;
            dataSource[index][type] = val;
        }

        if (type == "standardCount"||type == "toiletCount") {
            this.setState({
                dataSource: dataSource
            });
        } else {
            this.save(dataSource).then((res) => {
                this.setState({
                    dataSource: res
                });
            });
        }
    }

    handBlur = () => {
       this.save(this.state.dataSource).then((res) => {
            this.setState({
                dataSource: res
            });
       });
    }

    handleChange2 = (type, value) => {
        let obj = JSON.parse(JSON.stringify(this.state.main));
        obj[type] = value;
        obj["currentEfficiency"] = value;
        this.setState({
           main: Object.assign({}, obj)
        });
    }

    handBlur2 = () => {
        this.store.saveConstructionMain(this.state.main).then((res)=>{
            if (res) {
                message.success("操作成功");
                this.setState({
                    main: Object.assign({}, res)
                });
            }
        });
    }

    handDate = (type, index, date, dateString) => {
        let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
        dataSource[index][type] = dateString == null || dateString == "" ? 0 : moment(dateString, 'YYYY-MM-DD').valueOf();
        this.save(dataSource).then((res) => {
            this.setState({
                dataSource: res
            });
        });
    }

    onChangeSchedule = (type, value) => {
        if (value==null||value==""||typeof value=="string") {
            return false;
        }
        let scheduleResources = JSON.parse(JSON.stringify(this.state.scheduleResources));
        scheduleResources[type] = value;
        this.store.saveScheduleResources({"csr": scheduleResources})
            .then((res) => {
                this.setState({
                    scheduleResources: res==null?{
                        id: "",
                        projectId: this.store.projectId,
                        planUsePeople: null,
                        planUseMachine: null
                    }:res
                });
            });
    }

    // 同步
    handleAsync = () => {
        let _this = this;
        confirm({
            title: '您确定要同步合同方案吗?',
            content: '原有数据将要被覆盖',
            onOk() {
                _this.store.syncConstructionElements()
                    .then((res) => {
                        _this.save(res).then((mres) => {
                            _this.setState({
                                dataSource: mres
                            });
                        });
                    });
                _this.store.syncConstructionResources()
                    .then((res) => {
                        _this.setState({
                            tableDatasource: res==null?[]:res
                        });
                    });
            },
            onCancel() {},
        });
    }

    // 表格新增
    tableAdd = () => {
        const {tableDatasource} = this.state;
        const {taskDetail} = this.store;
        const newData = {
            id: "",
            projectId: this.store.projectId,
            taskId: mobx.toJS(taskDetail).id,
            name: "",
            code: "",
            counts: null,
            omId: "",
            resType: ""
        };
        let data = [...tableDatasource, newData];
        this.saveScheduleResourcesList(data);
    }

    // 表格删除
    tableDel = (index) => {
        const tableDatasource = [...this.state.tableDatasource];
        tableDatasource.splice(index, 1);
        this.saveScheduleResourcesList(tableDatasource);
        message.success("删除成功");
    }

    // 表格报存
    tableSave = (row) => {
        const newData = [...this.state.tableDatasource];
        const index = newData.findIndex(item => row.id === item.id);
        const item = newData[index];
        newData.splice(index, 1, {
            ...item,
            ...row
        });
        this.saveScheduleResourcesList(newData);
    }

    saveScheduleResourcesList = (data) => {
        const params = {
            list: data,
            type: 0
        }
        this.store.saveScheduleResourcesList(params)
            .then((res)=>{
                this.setState({
                    tableDatasource: res
                });
            });
    }

    render() {
        const {currentStep, switchSteps} = this.props;
        const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol: {span: 8},
            wrapperCol: {span: 12},
        };
        const formItemLayout2 = {
            labelCol: {span: 4},
            wrapperCol: {span: 18},
        };
        const {dataSource, dictWork, tableDatasource, main, projectTypes} = this.state;
        const {constructionState} = this.store;
        const obj = {1: "一层", 2: "二层", 3: "三层"};
        const obj3 = {1: "标准箱", 2: "钢结构", 3: "展厅"};
        const components = {
            body: {
                row: EditableFormRow,
                cell: EditableCell,
            },
        };
        const columns = [
            {
                title: '操作',
                dataIndex: 'opater',
                width: '80px',
                align: 'center',
                className: constructionState.state1 === 2 ? "hidden" : "",
                render: (text, record, index) => <Popconfirm title="确定要删除吗?" onConfirm={() => this.tableDel(index)}>
                        <Icon type="minus-circle" style={{fontSize: "18px", color: "#E9796B", cursor: "pointer"}}/>
                </Popconfirm>
            },
            {
                title: '序号',
                dataIndex: 'key',
                width: '80px',
                align: 'center',
                render:(text,record,index)=>`${index+1}`,
            },
            {
                title: '资源类型',
                dataIndex: 'resType',
                align: 'center',
                width: '200px',
                editable: constructionState.state1 === 2?false:true,
            },
            {
                title: '名称',
                dataIndex: 'name',
                align: 'center',
                editable: constructionState.state1 === 2?false:true,
            },
            {
                title: '数量',
                dataIndex: 'counts',
                align: 'center',
                editable: constructionState.state1 === 2?false:true,
            }
        ];
        const column = columns.map(col => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.tableSave
                }),
            };
        });

        const obj2 = {0: "不含", 1: "全隐", 2: "半隐"}

        return (
            <div>
                {/*编辑态*/}
                <div className={constructionState.state1 === 2 ? "hidden" : ""}>

                    <Form layout="inline" {...formItemLayout}>
                        <Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}
                             style={{
                                 margin: "0"
                             }}>
                            <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                <Form.Item label="工时系数" className="w">
                                    {getFieldDecorator('efficiency')(
                                        <div className="w">
                                            <InputNumber
                                                className="w"
                                                min={0}
                                                max={500}
                                                formatter={value => `${value}%`}
                                                parser={value => value.replace('%', '')}
                                                onChange={this.handleChange2.bind(this, "efficiency")}
                                                onBlur={this.handBlur2}
                                                value={main.efficiency}
                                            />
                                        </div>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>

                    <Row className="mt20">
                        <Col span={20} offset={3}>
                            <Button type="primary" onClick={this.handleAsync}>同步合同方案</Button>
                            <Button className="ml20" type="primary" onClick={this.handleAdd}>添加楼栋</Button>
                        </Col>
                        <Col span={20} offset={3}
                             style={{
                                paddingBottom: "20px",
                                borderBottom: "1px solid #E6E6E6"
                        }}>
                            <Table
                                components={components}
                                className="digital_table mt15 mb15"
                                rowClassName={() => 'editable-row'}
                                dataSource={tableDatasource}
                                columns={column}
                                pagination={false}
                                bordered
                                rowKey={record => record.id}
                            />
                            <Icon onClick={this.tableAdd}
                                  type="plus-circle"
                                  style={{
                                      position: "relative",
                                      left: "28px",
                                      fontSize: "18px",
                                      color: "#609AF1",
                                      cursor: "pointer"
                                  }}/>
                        </Col>
                    </Row>
                    <Form layout="inline" {...formItemLayout}>
                        {dataSource.map((item, index) => {
                            return <Row key={"list-" + index} gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}
                                        style={{
                                            margin: "0",
                                            padding: "20px 0",
                                           // borderBottom: "1px solid #E6E6E6"
                                        }}>
                                <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                    <Form.Item label="楼栋名称" className="w">
                                        {getFieldDecorator('name' + index, {
                                            initialValue: item.name || "",
                                            rules: [{required: true, message: '此项是必填'}]
                                        })(
                                            <Input
                                                placeholder="请输入"
                                                onBlur={this.handChange.bind(this, "name", index)}
                                                onPressEnter={this.handChange.bind(this, "name", index)}
                                                onChange={this.handChange.bind(this, "name", index)}
                                                value={item.name || ""}/>
                                        )}
                                    </Form.Item>
                                </Col>
                                {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null?null:
                                <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                    <Form.Item label="类型" className="w">
                                        {getFieldDecorator('type' + index, {
                                        })(
                                            <div>
                                                <Radio.Group
                                                    value={item.type}
                                                    onChange={this.handChange.bind(this, "type", index)}
                                                >
                                                    {
                                                        projectTypes.map((v)=>{
                                                            return <Radio value={Number(v.id)} key={v.id}>{v.name}</Radio>
                                                        })
                                                    }
                                                </Radio.Group>
                                            </div>
                                        )}
                                    </Form.Item>
                                </Col>}
                                <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                    <Form.Item label="计划开工时间" className="w">
                                        {getFieldDecorator('planBeginTime' + index, {
                                            rules: [{required: true, message: '此项是必填'}]
                                        })(
                                            <div>
                                                <DatePicker format="YYYY-MM-DD"
                                                            placeholder="请选择"
                                                            className="w"
                                                            value={item.planBeginTime == null || item.planBeginTime == 0 ? "" : moment(moment(item.planBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                                            onChange={this.handDate.bind(this, "planBeginTime", index)}
                                                />
                                            </div>
                                        )}
                                    </Form.Item>
                                </Col>
                                {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null?null:
                                    (item.type!=1?<Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                        <Form.Item label="计划完成时间" className="w">
                                            {getFieldDecorator('planEndTime' + index, {
                                                rules: [{required: true, message: '此项是必填'}]
                                            })(
                                                <div>
                                                    <DatePicker format="YYYY-MM-DD"
                                                                placeholder="请选择"
                                                                className="w"
                                                                value={item.planEndTime == null || item.planEndTime == 0 ? "" : moment(moment(item.planEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                                                onChange={this.handDate.bind(this, "planEndTime", index)}
                                                    />
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Col>:null)
                                }
                                {/*<Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <Form.Item label="选择施工要素" className="w" {...formItemLayout2}>
                                        {getFieldDecorator('constructionKey' + index, {
                                            rules: [{required: true, message: '此项是必填'}]
                                        })(
                                            <div>
                                                {dictWork.map((mitem) => {
                                                    return <Checkbox
                                                        disabled={mitem.isShowMust == 1 ? true : false}
                                                        checked={Array.isArray(item.constructionKey) && item.constructionKey.findIndex(i => i.id == mitem.id) != "-1" ? true : false}
                                                        value={mitem}
                                                        key={mitem.id}
                                                        onChange={this.handChange.bind(this, "constructionKey", index)}>{mitem.showName}</Checkbox>
                                                })}
                                            </div>
                                        )}
                                    </Form.Item>
                                </Col>*/}
                                {
                                    item.type!=undefined&&item.type!=1?null:<Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                        <Form.Item label="选择楼层" className="w">
                                            {getFieldDecorator('layers' + index, {
                                                rules: [{required: true, message: '此项是必填'}]
                                            })(
                                                <div>
                                                    <Radio.Group
                                                        value={item.layers}
                                                        onChange={this.handChange.bind(this, "layers", index)}
                                                    >
                                                        <Radio value={1}>一层</Radio>
                                                        <Radio value={2}>二层</Radio>
                                                        <Radio value={3}>三层</Radio>
                                                    </Radio.Group>
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Col>}
                               {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                                <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                    <Form.Item label="标准箱数量" className="w">
                                        {getFieldDecorator('standardCount' + index, {
                                            rules: [
                                                {
                                                required: false,
                                                message: '此项是必填'},
                                                {
                                                    pattern: /^([0-9]\d+)|[1-9]$/,
                                                    message: "输入大于等于1的正整数"
                                                }
                                            ]
                                        })(
                                            <div>
                                                <InputNumber
                                                    className="w"
                                                    min={1}
                                                    placeholder="请输入"
                                                    onBlur={this.handBlur.bind(this, "standardCount", index)}
                                                    onChange={this.handChange.bind(this, "standardCount", index)}
                                                    value={item.standardCount}/>
                                            </div>
                                        )}
                                    </Form.Item>
                                </Col>}
                                {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                                    <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                        <Form.Item label="卫生间数量" className="w">
                                            {getFieldDecorator('toiletCount' + index, {
                                                rules: [
                                                    {
                                                        required: false,
                                                        message: '此项是必填'},
                                                    {
                                                        pattern: /^([0-9]\d+)|[0-9]$/,
                                                        message: "输入大于等于0的整数"
                                                    }
                                                ]
                                            })(
                                                <div>
                                                    <InputNumber
                                                        className="w"
                                                        min={0}
                                                        placeholder="请输入"
                                                        onBlur={this.handBlur.bind(this, "toiletCount", index)}
                                                        onChange={this.handChange.bind(this, "toiletCount", index)}
                                                        value={item.toiletCount}/>
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Col>}
                                {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                                    <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                        <Form.Item label="是否包含廊道" className="w">
                                            {getFieldDecorator('haveGallery' + index, {
                                                rules: [
                                                    {
                                                        required: false,
                                                        message: '此项是必填'}
                                                ]
                                            })(
                                                <div>
                                                    <Radio.Group onChange={this.handChange.bind(this, "haveGallery", index)}
                                                                 value={item.haveGallery!=undefined&&typeof item.haveGallery == "number"?String(item.haveGallery):"0"}>
                                                        <Radio value={"1"}>是</Radio>
                                                        <Radio value={"0"}>否</Radio>
                                                    </Radio.Group>
                                                </div>
                                            )}
                                        </Form.Item>
                                    </Col>}
                               {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.haveGallery!="1"||item.type!=1?null:
                                    <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                                        <Form.Item label="幕墙类型" className="w">
                                            {getFieldDecorator('curtainType' + index, {
                                                rules: [
                                                    {
                                                        required: false,
                                                        message: '此项是必填'}
                                                ]
                                            })(
                                            <div>
                                                <Select
                                                    value={item.curtainType!=undefined&&typeof item.curtainType == "number"?String(item.curtainType):"0"}
                                                    onChange={this.handChange.bind(this, "curtainType", index)}
                                                >
                                                    <Option value="0">不含</Option>
                                                    <Option value="1">全隐</Option>
                                                    <Option value="2">半隐</Option>
                                                </Select>
                                            </div>
                                            )}
                                        </Form.Item>
                                    </Col>}
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <Form.Item label="备注" className="w" {...formItemLayout2}>
                                        {getFieldDecorator('remark' + index, {
                                            initialValue: item.remark || "",
                                            rules: [{required: false, message: '此项是必填'}]
                                        })(
                                            <TextArea
                                                value={item.remark}
                                                onBlur={this.handChange.bind(this, "remark", index)}
                                                onChange={this.handChange.bind(this, "remark", index)}
                                                rows={4}
                                                placeholder="请输入"
                                            />
                                        )}
                                    </Form.Item>
                                </Col>
                                {
                                    item.id != "" && item.id != null&&
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <Form.Item label=" " {...formItemLayout2} className="w" colon={false}>
                                        <Popconfirm title="确定要删除吗?" onConfirm={() => this.handleDelete(item)}>
                                            <Button type="danger">删除楼栋</Button>
                                        </Popconfirm>
                                    </Form.Item>
                                </Col>}
                            </Row>
                        })}
                    </Form>
                    {/*<Row className="mt20 mb20">
                        <Col span={20} offset={3}>
                            <Button type="primary" onClick={this.handleAdd}>添加新楼栋</Button>
                        </Col>
                    </Row>*/}
                </div>

                {/*详情态*/}
                <div style={{width: "80%", margin: "0 auto", padding: "0 25px"}}>
                    <Button
                        ghost
                        type="primary"
                        icon="poweroff"
                        onClick={this.props.reStartHandover}
                        style={constructionState.state1 === 2 ? {display: ''} : {display: 'none'}}
                    >
                        重新启动
                    </Button>
                    {constructionState.state1 === 2 &&<div>
                        <h1 style={{padding: "15px 0 0 ", fontSize: "16px", fontWeight: "700"}}>工时系数: {main&&main.efficiency} %</h1>
                    </div>}
                    {constructionState.state1 === 2 && <Table
                            components={components}
                            className="digital_table mt15"
                            rowClassName={() => 'editable-row'}
                            dataSource={tableDatasource}
                            columns={column}
                            pagination={false}
                            bordered
                            rowKey={record => record.id}
                        />}
                </div>
                {constructionState.state1 === 2 && dataSource.map((item) => {
                    const {name, planBeginTime,planEndTime, layers, constructionKey, remark, standardCount, toiletCount, haveGallery, curtainType, type} = item;
                    return <div key={"dataSource-" + item.id}
                                style={{width: "80%", margin: "0 auto", marginBottom: "30px", padding: "0 25px"}}>
                        <h1 style={{padding: "15px 0", fontSize: "16px", fontWeight: "700"}}>{name}</h1>
                        <table className="suppliertable" style={{width: "100%"}}>
                            <tr>
                                <td>计划开工时间</td>
                                <td>
                                    {planBeginTime !== 0 && planBeginTime !== null ? moment(planBeginTime).format('YYYY年MM月DD日') : ''}
                                </td>
                            </tr>
                            {
                                item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||(item.type!=undefined&&item.type==1)?null:<tr>
                                    <td>计划完成时间</td>
                                    <td>
                                        {planEndTime !== 0 && planEndTime !== null ? moment(planEndTime).format('YYYY年MM月DD日') : ''}
                                    </td>
                                </tr>}
                            {/*<tr>
                                <td>施工要素</td>
                                <td>
                                    {constructionKey == null ? "" : constructionKey.map(item => item.showName).join("、")}
                                </td>
                            </tr>*/}
                            {item.bocdesignId != undefined && item.bocdesignId != "" && item.bocdesignId != null ? null :
                                <tr>
                                    <td>类型</td>
                                    <td>
                                        {obj3[type]}
                                    </td>
                                </tr>
                            }
                            {
                                item.type!=undefined&&item.type!=1?null:<tr>
                                    <td>层数</td>
                                    <td>
                                        {obj[layers]}
                                    </td>
                                </tr>}
                            {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                            <tr>
                                <td>标准箱数量</td>
                                <td>
                                    {standardCount}
                                </td>
                            </tr>}
                            {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                                <tr>
                                    <td>卫生间数量</td>
                                    <td>
                                        {toiletCount}
                                    </td>
                                </tr>}
                            {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.type!=1?null:
                                <tr>
                                    <td>是否包含廊道</td>
                                    <td>
                                        {haveGallery=="1"?"含有":"不含"}
                                    </td>
                                </tr>}
                            {item.bocdesignId!=undefined&&item.bocdesignId!=""&&item.bocdesignId!=null||item.haveGallery!="1"||item.type!=1?null:
                                <tr>
                                    <td>幕墙类型</td>
                                    <td>
                                        {obj2[item.curtainType]||""}
                                    </td>
                                </tr>}
                            <tr>
                                <td>备注</td>
                                <td>
                                    {remark}
                                </td>
                            </tr>
                        </table>
                    </div>
                })
                }

                {/*底部*/}
                <div className="mt30 mb30" style={constructionState.state1 !== 2 ? {
                    display: 'flex',
                    justifyContent: 'center'
                } : {display: 'none'}}>
                    <Button onClick={this.overCurrentStage}
                            ghost
                            type="primary"
                    > 完成该阶段任务</Button><Button
                    onClick={this.doneAndNext} style={{marginLeft: '20px'}} type='primary'> 完成并进入下一步</Button>
                </div>
                <div className="mt30 mb30" style={constructionState.state1 === 2 ? {
                    display: 'flex',
                    justifyContent: 'center'
                } : {display: 'none'}}>
                    <Button onClick={() => {
                        switchSteps(currentStep + 1)
                    }} style={{marginLeft: '20px'}} type='primary'>下一步</Button>
                </div>
            </div>
        )
    }
}


export default OneItem;



