import React from 'react';
import {observer} from 'mobx-react';
import ReactEcharts from 'echarts-for-react';
import oss from 'ali-oss';
import bpmSchemas from "@/utils/Schemas/bpm";
import Config from "@/config";
import {requestPost} from "@/utils/api";
import moment from 'moment';

const client = (self) => {
    const {token} = self.state
    // ali-oss v6.x版本的写法
    return new oss({
        accessKeyId: token.access_key_id,
        accessKeySecret: token.access_key_secret,
        stsToken: token.stsToken,
        region: token.region,
        bucket: token.bucket,
        endpoint: token.OSS_ENDPOINT
    });
};

const uploadPath = (path, file) => {
    // 上传文件的路径，使用日期命名文件目录
    return `${moment().format('YYYYMMDDHHmmss')}/${file.name}`
}

const UploadToOss = (self, path, file) => {
    const url = uploadPath(path, file)
    return new Promise((resolve, reject) => {
        client(self).multipartUpload(url, file).then(data => {
            resolve(data); //上传成功后返回的上传结果，?后面的都去掉就能正常访问了
        }).catch(error => {
            reject(error)
        })
    })
}

@observer
class OrgChart extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
            token: {
                access_key_id: '', // oss的key_id
                access_key_secret: '', // oss的secret
                stsToken: '',
                region: 'oss-cn-beijing', // oss的region
                bucket: 'xinlj', // oss的bucket
                OSS_ENDPOINT: 'oss-cn-beijing.aliyuncs.com',  // 自己oss服务器的配置信息
            },
            host: 'https://xinlj.oss-cn-beijing.aliyuncs.com/', // 阿里云host
		}
	}

	componentDidMount() {
        // 访问后端，获取请求上传凭证
        this.init();
        this.props.onRef(this);
    }

    init = () => {
        // 阿里云上传参数
        requestPost('getSts', Config.bpm.getSts, bpmSchemas.bpm.getSts, {})
            .then((res) => {
                let str = typeof res == "string" ? JSON.parse(res) : res;
                if (str) {
                    let token = JSON.parse(JSON.stringify(this.state.token));
                    this.setState({
                        token: Object.assign(token, {
                            access_key_id: str.accessKeyId,
                            access_key_secret: str.accessKeySecret,
                            stsToken: str.securityToken
                        })
                    });
                }
            });
    }


    // 上传图片
    uploadImg = () => {
	    let _this = this;
        let echarts_instance = this.echarts_react.getEchartsInstance();
        let base64 = echarts_instance.getDataURL();

        //将base64转换为文件对象
        const dataURLtoFile = (dataurl, filename) => {
            var arr = dataurl.split(',');
            var mime = arr[0].match(/:(.*?);/)[1];
            var bstr = atob(arr[1]);
            var n = bstr.length;
            var u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n);
            }
            //转换成file对象
            return new File([u8arr], filename, {type: mime});
        };

        return new Promise((resove)=>{
        var myfile2 = dataURLtoFile(base64, '组织架构图.png');
        var formData2 = new FormData();
        formData2.append('newFile', myfile2);
        let reader2 = new FileReader();
        reader2.readAsDataURL(myfile2);
        reader2.onloadend = () => {
                // 使用ossupload覆盖默认的上传方法
                UploadToOss(this, '上传路径oss配置信息', myfile2)
                    .then(data => {
                        const {host} = _this.state;
                        let url = host + data.name;
                        console.log(url, 'url');
                        resove(url);
                    });
        }
        })
    }

	render() {

        var option = {
            tooltip : {
                show: false,
                trigger: 'item',
                formatter: "{b}: {c}"
            },
            toolbox: {
                show : false,
                feature : {
                    mark : {show: true},
                    dataView : {show: true, readOnly: false},
                    restore : {show: true},
                    saveAsImage : {show: true}
                }
            },
            calculable : false,
            series : [
                {
                    name:'树图',
                    type:'tree',
                    orient: 'vertical',
                    rootLocation: {x: '50%', y: '15%'},
                    nodePadding: 20,
                    layerPadding:40,
                    symbol: 'rectangle',
                    borderColor:'black',
                    itemStyle: {
                        normal: {
                            color: '#fff',
                            label: {
                                show: true,
                                position: 'inside',
                                textStyle: {
                                    color: 'black',
                                    fontSize: 14,
                                }
                            },
                            lineStyle: {
                                color: '#000',
                                width: 1,
                                type: 'solid' // 'curve'|'broken'|'solid'|'dotted'|'dashed'
                            }
                        },
                        emphasis: {
                            label: {
                                show: false
                            }
                        }
                    },
                    data: this.props.orgData
                    /*[
                        {
                            name: '董事会',
                            value: 6,
                            symbolSize: [70, 30],
                            symbol: 'rectangle',
                            itemStyle: {
                                normal: {
                                    borderWidth: 2,
                                    borderColor: 'black'
                                }
                            },
                            children: [
                                {
                                    name: '总经理',
                                    value: 6,
                                    symbolSize: [70, 30],
                                    symbol: 'rectangle',
                                    itemStyle: {
                                        normal: {
                                            borderWidth: 2,
                                            borderColor: 'black'
                                        }
                                    },
                                    children: [
                                        {
                                            name: '营销中心',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                        {
                                            name: '项目中心',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                        {
                                            name: '技术中心',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                        {
                                            name: '行政部',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                        {
                                            name: '财务部',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                        {
                                            name: '其他分支',
                                            value: 4,
                                            symbolSize: [70, 30],
                                            symbol: 'rectangle',
                                            itemStyle: {
                                                normal: {
                                                    label: {
                                                        show: true,
                                                        position: 'inside'
                                                    },
                                                    borderWidth: 2,
                                                    borderColor: 'black'
                                                }
                                            }
                                        },
                                    ]
                                }]
                        }
                    ]*/
                }
            ]
        };

        return (
            <ReactEcharts option={option}
                          notMerge={true}
                          ref={(e) => {
                              this.echarts_react = e;
                          }}
                          lazyUpdate={true}
                          style={{height:'240px'}}/>
		)
	}
}


export default OrgChart;



