import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Button, message, Tabs} from 'antd';
import {Scrollbars} from "react-custom-scrollbars";
import SupplierEdit from "./SupplierEdit";
import SupplierView from "./SupplierView";
const { TabPane } = Tabs;

@inject('DeliveryStore')
@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.newTabIndex = 1;
		this.state = {
			activeKey: "main",
			panes: [],
			mainObj: {}, //记录主合同的信息
		}
	}

	componentDidMount() {
		this.getConfirmSupplierTwoItemList();
		this.store.getCompanyList();
	}

	onChange = activeKey => {
		if (activeKey=="add") {
			this.add();
		}
		else {
			this.setState({ activeKey }, ()=>{
				const { panes } = this.state;
				const content = panes.filter(item=>item.key==activeKey)[0].content;
				// 主合同
				if (activeKey=="main") {
                  this.store.getConfirmSupplierTwoItemById({id: content.id}).then((res)=>{
					   const active = res;
					  // 获取当前展示的数据
					  this.store.supplierTwoItem = Object.assign({}, active);
					  this.store.suState = active.isSave == 2?true: false;
					  // 编辑重新刷新数据
					  if (active.isSave!=2) {
						  this.supplierRef2.init();
						  this.supplierRef2.getEnclosureList();
					  }
					  if(active.isSave==2) { // 详情
						  this.supplierRef.getEnclosureList();
					  }
				  });
				}
				else {  // 补充协议
					this.store.getConfirmSupplierSupplementByMainId().then((res) => {
						if (res) {
                            let array = res;
                            let filterObj = array.filter(item=>item.id==content.id)[0];
                            // 获取当前展示的数据
							this.store.supplierTwoItem = Object.assign({}, filterObj);
							this.store.suState = filterObj.isSave == 2?true: false;
							// 编辑重新刷新数据
							if (filterObj.isSave!=2) {
								this.supplierRef2.init();
								this.supplierRef2.getEnclosureList();
							}
							if(filterObj.isSave==2) { // 详情
								this.supplierRef.getEnclosureList();
							}
						}
					});
				}
			});
		}
	};

	onEdit = (targetKey, action) => {
		this[action](targetKey);
	};

	add = () => {
		this.store.addConfirmSupplierSupplement().then((res)=>{
            let content = res;
			const { panes } = this.state;
			const activeKey = panes.length==2?1:++this.newTabIndex;

			let obj = { title: '补充协议-'+activeKey, content: content, key: activeKey.toString(), closable: false}
			panes.splice(panes.length-1, 0, obj);
			//console.log(panes, content, '新增补充协议')
			// 获取当前展示的数据
			this.store.supplierTwoItem = Object.assign({}, content);
			this.store.suState = content.isSave == 2?true: false;

			this.setState({ panes, activeKey: activeKey.toString() }, ()=>{
				// 编辑重新刷新数据
				if (content.isSave!=2) {
					this.supplierRef2.init();
					this.supplierRef2.getEnclosureList();
				}
				if(content.isSave==2) { // 详情
					this.supplierRef.getEnclosureList();
				}
			});
		});
	};

	// 初始化数据
	getConfirmSupplierTwoItemList = () => {
		const {taskDetail} = this.store;
		this.store.getConfirmSupplierTwoItemList({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
			if(res){
				if (Array.isArray(res)&&res.length>0) {
					// 初始化默认值，列表第一项
					let param =res[0];
					let dataArray =res.map((item) => Object.assign(item, {selected: false}));
					const dindex = dataArray.findIndex((item) => {
						return item.id == param.id
					});
					dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
					this.store.supplierTwoItemList = Object.assign([], dataArray);
					this.store.supplierTwoItem = Object.assign({}, param);
					this.store.suState = param.isSave == 2?true: false;

					// 补充协议
					this.setState({
						mainObj: param,
						activeKey: "main"
					});
					this.store.mainId = param.id;
					this.getConfirmSupplierSupplementByMainId(param);

					// 编辑重新刷新数据
					if (param.isSave!=2) {
						this.supplierRef2.init();
						this.supplierRef2.getEnclosureList();
					}
					if(param.isSave==2) { // 详情
						this.supplierRef.getEnclosureList();
					}
				} else {
					this.store.supplierTwoItemList = Object.assign([]);
                    this.store.supplierTwoItem = Object.assign({});
                    this.store.suState = false;
				}
			}
		});
	}

	// 返回补充协议列表
	getConfirmSupplierSupplementByMainId = (param) => {
		this.store.getConfirmSupplierSupplementByMainId()
			.then((res)=>{
				// 主合同的数据
				let obj = { title: '主合同', content: param, closable: false, key: "main" };
				let addObj = { title: '+', content: ' ', key: 'add', closable: false };
				let newArray = res.length>0?res.map((item, index)=>{
                    const activeKey = index + 1;
					return { title: '补充协议-'+activeKey, content: item, key: activeKey.toString(), closable: false}
				}):[];
				let array = [obj, ...newArray, addObj];  // 拼接数据
				this.newTabIndex = res.length>0?res.length:1;
				this.setState({
					panes: array
				});
			});
	}

	// 删除补充协议后同步数据
	autosaveData = () => {
		const {mainObj} = this.state;
		this.getConfirmSupplierSupplementByMainId(mainObj);
		this.store.supplierTwoItem = Object.assign({}, mainObj);
		this.store.suState = mainObj.isSave == 2?true: false;
		this.setState({
			activeKey: "main"
		});
	}

	handleSelectedDaily = (item, index) => {
		let param = item;
		let stakeholderList = JSON.parse(JSON.stringify(this.store.supplierTwoItemList));
		let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		});
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.store.supplierTwoItemList = Object.assign([], dataArray);
		this.store.supplierTwoItem = Object.assign({}, param);
		this.store.suState = param.isSave == 2?true: false;

		// 补充协议
		this.setState({
			mainObj: param,
			activeKey: "main"
		});
		this.store.mainId = param.id;
		this.getConfirmSupplierSupplementByMainId(param);

		// 编辑重新刷新数据
		if (param.isSave!=2) {
			this.supplierRef2.init();
			this.supplierRef2.getEnclosureList();
		}
		if(param.isSave==2) { // 详情
			this.supplierRef.getEnclosureList();
		}
	}

	// 下一步
	nextStep = () => {
		/*let stakeholderList = JSON.parse(JSON.stringify(this.store.supplierTwoItemList));
		let isFlag = stakeholderList.some(function(computer){
			return computer.isSave != 2;
		});
		if(isFlag) {
			message.warn("请先填写完整该阶段的任务信息");
			return false;
		}*/
		const {currentStep, switchSteps} = this.props;
		switchSteps(currentStep + 1);
		//this.props.overCurrentStageTask();
	}

	transColor = (type) => {
		switch (type) {
			case 1:
				return "#F59A23";
				break;
			case 2:
				return "rgba(26, 188, 156, 1)";
				break;
			default:
				return "#555555";
				break;
		}
	}

	updateData = () => {
		this.supplierRef2.init();
		this.supplierRef2.getEnclosureList();
	}

	handleChangeTabs=(key)=> {
		this.setState({activeKey: key})
	}

	render() {
		const {supplierTwoItemList, suState} = this.store;
		const {activeKey} = this.state;
		const obj = {0: "待启动", 1: "进行中", 2: "已完成"};

		return (
			<div style={{marginLeft: "-30px", marginRight: "-30px"}}>
				<div className={"fl"}
				     style={{paddingRight: '0', width: '230px', borderRight: '1px solid #EBEBEB'}}>
					<Scrollbars
						style={{
							height: "calc(100vh - 230px)"
						}}
						autoHide={true}>
						{mobx.toJS(supplierTwoItemList).length === 0 ? <div className="empty_daily_list">
								<img src={'./images/delivery/u2030.png'} style={{width: '191px', height: '187px'}}/>
							</div> :
							mobx.toJS(supplierTwoItemList).map((item, index) => {
								return (
									<div onClick={() => {
										this.handleSelectedDaily(item, index)
									}}
									     className={item.selected == true ? "daily_item daily_active_item" : "daily_item"}>
										<div className="label_info">
											{/*<div>*/}
											{/*	{item.supplierType}*/}
											{/*</div>*/}
											<div>
												<div className="title">{item.supplierName}</div>
												<div className="desc_info mt15">签约时间：{item.contractAwardTime !== 0 && item.contractAwardTime !== null ? moment(item.contractAwardTime).format('YYYY.MM.DD') : '暂无'}</div>
											</div>
										</div>
										<div className="author_progress" style={{"paddingLeft":"10px"}}>
											<div className="author_date">状态:
												<span style={{
													color: this.transColor(item.state),
													marginLeft: "8px"
												}}>{obj[item.state]}</span>
											</div>

										</div>
										<div className="author_progress" style={{"paddingLeft":"10px"}}>
											<div className="author_date">类型:
												<span style={{
													marginLeft: "8px"
												}}>{item.supplierType}</span>
											</div>
										</div>
									</div>
								)
							})
						}
					</Scrollbars>
				</div>
				<div className={"fl"}
				     style={{
					     width: 'calc(100% - 240px)',
					     overflowX: "hidden",
					     padding: '0 15px 30px 0'
				     }}>

					{mobx.toJS(supplierTwoItemList).length === 0 ? <div className="empty_daily_content" style={{
				  }}><img src='./images/delivery/u2033.png ' style={{width: '336px', height: '336px'}}/></div>:null}
					<Tabs
						hideAdd
						tabBarGutter={10}
						className="cc-tabs mt20"
						onChange={this.onChange}
						activeKey={activeKey}
						type="editable-card"
						onEdit={this.onEdit}
					>
						{this.state.panes.map(pane => (
							<TabPane tab={pane.title} key={pane.key} closable={pane.closable}></TabPane>
						))}
					</Tabs>
					{mobx.toJS(supplierTwoItemList).length!=0&&<SupplierView
						onRef={(input) => { this.supplierRef = input; }}
						updateData={this.updateData}
						autosaveData={this.autosaveData}
						store={this.store}
						reStartHandover={this.props.reStartHandover}/>}
					{mobx.toJS(supplierTwoItemList).length!=0&&<SupplierEdit  store={this.store}
					                                                          onRef={(input) => { this.supplierRef2 = input; }}
																			  autosaveData={this.autosaveData}
					                                                          nextStep={this.props.overCurrentStageTask}/>}
					{/*<div className={"mt30 mb30"} style={{display:'flex', justifyContent:'center'}}>
						<Button style={{marginLeft:'20px'}} type='primary'
						onClick={this.nextStep}
						>下一步</Button>
					</div>*/}
				</div>
			</div>
		)
	}
}


export default TwoItem;



