import React from 'react';
import {observer, inject} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {DatePicker, Select, Button, Table, Pagination, Input, ConfigProvider, message, Icon, Modal, Radio, Popconfirm, Popover, Breadcrumb, Dropdown, Menu} from 'antd';
import EditableTableOneItem from "./EditableTableOneItem";
import AddPerosn from './AddPerosn';
import emitter from "@/utils/events";

@inject('DeliveryStore')
@inject('UserStore')
@withRouter
@observer
class OneItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			visible: false,
			stateValue: 0,
			id: "",
			dataSource:[],
			oneItemList:[],
			add: {
				visible: false,
				data: [],
			},
			projectId:null,
			taskId:null,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		//const projectDetail = mobx.toJS(this.store.projectDetail)
		//const taskDetail = mobx.toJS(nextProps.DeliveryStore.taskDetail)
		//console.log(this.store.projectId, nextProps.projectId, 'projectDetail')
		/*if(this.store.projectId!=nextProps.projectId) {
			this.getBuildTeamOneItemList();
		}*/
	}

	componentDidMount() {
		this.getBuildTeamOneItemList();
		this.emitter = emitter.addListener('getBuildTeamOneItemList', (res) => {
			this.getBuildTeamOneItemList();
		});
	}

	componentWillMount() {

	}


	// 添加成员
	handleAdd = () => {
		this.setState({
			add: Object.assign(this.state.add, {visible: true})
		}, () => {
			this.getSearch("");
		})
	}

	onChange = (type, e) => {
		this.store.salesParams[type] = e.target.value;
	}


	handleChange = (type, value) => {
		if (value == undefined) {  // 清空
			this.store.salesParams[type] = 10;
		} else {
			this.store.salesParams[type] = value;
		}
	}

	handleSearch = () => {
		this.store.getSalesOrderPageList();
	}

	handleOk = () => {
		const {id, stateValue} = this.state;
		const params = {
			id,
			state: typeof stateValue == "string" ? Number(stateValue) : stateValue
		}
		this.store.changeSalesOrderState(params).then((res) => {
			if (res) {
				message.success("操作成功");
				this.setState({
					visible: false,
				});
				this.handleSearch();
			}
		})
	};

	handleCancel = e => {
		this.setState({
			visible: false,
		});
	};


	confirm = (id) => {
		const params = {
			id: id
		}
		this.store.delSalesOrderState(params).then((res) => {
			if (res) {
				message.success("删除成功")
				this.store.getSalesOrderPageList();
			}
		})
	}


	delProject=(id)=>{
		this.store.delProject({"id":id}).then((res)=>{
			this.store.getProjectPageList('');
		})
	}

	getBuildTeamOneItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamOneItemList({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			let data = res.map((item,index)=>Object.assign(item, {keyIndex: index + 1}));
			this.store.buildTeamOneItem = data;
			this.setState({dataSource: data, oneItemList: data})
		})
	}

	updateState = (type, data) => {
		console.log(type, data)
		this.setState({
			[type]: Object.assign(this.state[type], data)
		})
	}

	// 列表搜索
	getSearch = (param) => {
		const { projectDetail } = this.store;
		const params = param;
		this.props.UserStore.getRoleData(params).then((res) => {
			this.setState({
				add: Object.assign(this.state.add, {data: res.body.filter((item)=> item.id!==projectDetail.masterId).map((item) => Object.assign(item, {selected: false}))})
			})
		});
	}


	saveContents=()=>{
		this.getBuildTeamOneItemList();
	}



	saveBuildTeamOneItem=(params)=>{
		this.store.saveBuildTeamOneItem(params).then((res)=>{
			if(res){
				this.getBuildTeamOneItemList()
			}
		})
	}

	saveItem=(row)=>{
		const {taskDetail} = this.store;
		const {oneItemList} = this.state;
		// 记录orders
		let length = oneItemList.length;
		let add = length>0?oneItemList[length-1].orders:0;

		let newItem={
			"buildTeamOne":taskDetail.id,
			'orders':add+1,
			"userName":"",
			"userId":"",
			"position":row.position,
			"phone":row.phone,
			"email":row.email,
			"duty":row.duty,
			"canDel":1,
		}
		if(typeof(row.id)!=="undefined"){
			newItem['id']=row.id
			newItem['orders']= row.orders
			newItem['userId']=row.userId
			newItem['canDel']=row.canDel
			newItem['position']=row.position
			newItem['phone']=row.phone
			newItem['duty']=row.duty
			newItem['email']=row.email
			newItem['userName']=row.userName
		}
		this.saveBuildTeamOneItem({"btoi":newItem})
		// console.log('newItem',row)
	}

	formatDataSource=()=>{
		const { dataSource} = this.state;
		const ds = dataSource.map((item, index)=>{
			if(index===0){
				return Object.assign(item, {"duty":"1、全力保障项目目标完成；\n" +
						"2、协调内外部资源，负责项目交付进度和质量控制；\n" +
						"3、安排内部其他项目组成员工作，做好分工安排；\n" +
						"4、保障项目中各类文档按期完成；\n" +
						"5、定期做高层汇报，并提出管理改进建议"})
			}else{
				return item
			}
		})
		return ds
	}

	overCurrentStage=()=>{
		// const cict = this.checkIsIncludeTeam();
		// if(!cict.result){
		// 	message.warn(cict.info)
		// 	return
		// }
		const ctpd = this.checkTeamPositionAndDuty();
		if(!ctpd.result){
			message.warn(ctpd.info)
			return
		}
		this.props.overCurrentStageTask();
	}

	doneAndNext=()=>{
		const { currentStep } = this.props;
		// const cict = this.checkIsIncludeTeam();
		// if(!cict.result){
		// 	message.warn(cict.info)
		// 	return
		// }
		const ctpd = this.checkTeamPositionAndDuty();
		if(!ctpd.result){
			message.warn(ctpd.info)
			return
		}
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	checkIsIncludeTeam=()=>{
		const { dataSource } = this.state;
		let resultObj = {"info":"至少选择一位团队成员",result:false};
        if(dataSource.length>=2){
			resultObj.result = true;
		}
		return resultObj
	}

	checkTeamPositionAndDuty=()=>{
		const { dataSource } = this.state;
		let resultObj = {"info":"团队成员职务和职责不能为空",result:false};
		const ds = dataSource.filter((item, index)=>{
			return index>0 && item.position==="" || item.duty===""
		})
		if(ds.length===0) resultObj.result = true
		return resultObj
	}


	render() {
		const {projectDetail, buildTeamState} = this.store;
		const { dataSource} = this.state;
		const { currentStep, switchSteps } = this.props;
		return (
			<div style={{height: 'auto'}}>
				<div className="mt20" style={{display:'flex', justifyContent:'space-between'}}>
					<div className="mb10">
						<Button
							ghost
							className="fl"
							type="primary"
							icon="plus"
							onClick={this.handleAdd}
							style={buildTeamState.state1===2?{display:'none'}:{display:''}}
						>
							添加组织成员
						</Button>
						<Button
							ghost
							className="fl"
							type="primary"
							icon="delete"
							style={{display:'none'}}
						>
							批量删除
						</Button>
						<Button
							ghost
							className="fl"
							type="primary"
							icon="poweroff"
							onClick={this.props.reStartBuildTeam}
							style={buildTeamState.state1===2?{display:''}:{display:'none'}}
						>
							重新启动
						</Button>
					</div>
				</div>
				<EditableTableOneItem
					id={'id'}
					store={this.store}
					dataSource={this.formatDataSource()}
					saveData={this.saveContents}
					saveItem={this.saveItem}
					disabled={buildTeamState.state1===2?true:false}
					pid={''}
				/>
				<div  className="mt30 mb30" style={buildTeamState.state1!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"
					> 完成该阶段任务</Button><Button  onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div  className="mt30 mb30" style={buildTeamState.state1===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'>下一步</Button>
				</div>
				<AddPerosn
					{...this.state.add}
					dataSource={this.formatDataSource()}
					updateState={this.updateState}
					getSearch={this.getSearch}
					getTableSearch={this.getBuildTeamOneItemList}
					updateOneItem={this.getBuildTeamOneItemList}
				/>
			</div>
		)
	}
}


export default OneItem;



