/*
* 交底施工内容
* qiufh@bocspace.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Button, Tabs, Table, message} from 'antd';
import * as mobx from "mobx";
// import TimeLine from 'react-gantt-timeline';
import {v4 as uuidv4} from 'uuid';

import MyUpload from '@/components/common/MyUploadNew';
import ContentTips from './ContentTips';
import ContractPunishment from './ContractPunishment';
import TechnologyContent from './TechnologyContent';
import SafetyContent from './SafetyContent';
import EditableTableUpload from "./EditableTableUpload";

import moment from 'moment';

import TimeLine from '../timeline/TimeLine';


const { TabPane } = Tabs;

const columns= [
	{
		title: '序号',
		dataIndex: 'key',
		key: 'key',
		align: 'center'
	},
	{
		title: "风险类型",
		dataIndex: 'type',
		key: 'type',
		align: 'center',
		editable: true
	},
	{
		title: "风险描述",
		dataIndex: 'describes',
		key: 'describes',
		align: 'center',
		width:'40%',
        render:(text, record) =>(
			<div style={{width: '100%',  height: '46px', lineHeight: '46px',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}>
				{text}
			</div>
		)


	},
	{
		title: "应对方案",
		dataIndex: 'programme',
		key: 'programme',
		align: 'center',
		width:'40%',
		render:(text, record) =>(
			<div style={{width: '100%',  height: '46px', lineHeight: '46px',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}>
				{text}
			</div>
		)
	}
	];

const config = {
	header: {
		top: {
			style: {
				background: "#F2F2F2",
				height: 20
			}
		}
	},
	taskList: {
		title: {
			label: "任务名称",
			style: {
				display:"flex",
				flexDirection:"column",
				alignItems:"center",
				background: "#F2F2F2",
				height: "60px",

			}
		},
	},
	dataViewPort: {
		task: {
			showLabel: false,
			style: {
				marginTop:9,
				borderRadius: 5,
				maxHeight:"15px",
			}
		}
	}
};

@inject('DeliveryStore')
@observer
class ConstructionContent extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
			projectId:null,
			currentStep:1,
			activeKey:"1",
			dataSource: [],
			attachments: [],
			timelineData: [], // 进度图数据
			makePlotInfo: {},  // 工程概况
			makePlotRisk:[], //重难点分析
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if(projectDetail.id!==this.state.projectId){
			this.setState({projectId:projectDetail.id})
			this.getEnclosureList();
			this.getConstructionScheduleDetail();
			this.getMakePlot();
			this.getDocumentList();
		}
	}

	componentDidMount() {
		const { projectDetail } = this.store;
		this.setState({projectId:mobx.toJS(projectDetail).id})
		this.getEnclosureList();
		this.getConstructionScheduleDetail();
		this.getMakePlot();
		this.getDocumentList();
		this.getCurrentStageState();
	}

	getCurrentStageState=()=>{
		const { taskDetail } = this.store;
		this.store.getTaskStepState({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			if(res){
				this.store.constructionState = res;
			}
		})
	}

	handleChangeTabs=(key)=> {
		this.setState({activeKey: key})
	}

	getEnclosureList=()=>{
		const { taskDetail } = this.store;
		this.store.getOnlyShowEnclosureListByTaskIdAndIndex({"taskId":mobx.toJS(taskDetail).id, "index":0}).then((res)=>{
			if(res){
				this.setState({dataSource: res});
			}

		})
	}

	changeCurrentStageStatus=()=>{
		const { taskDetail } = this.store;
		if(taskDetail.state===0){
			this.handleChangeTaskStepState({"taskId": taskDetail.id, "index":1, "state":1});
		}
	}

	// 附件
	handleAttment = (type, params) => {
		// 处理附件
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.setState({
			[type]: Object.assign([], arr)
		},()=>{
			this.changeCurrentStageStatus()
		});
	}

	getConstructionScheduleDetail = () => {
		const {projectDetail} = this.store;
        const { stages } = mobx.toJS(projectDetail);
		const planStep =stages.filter(item=> item.name==='规划阶段');
		const csTask = planStep[0]['tasks'].filter(item=> item.taskType==='constructionSchedule')[0];

		this.store.getConstructionScheduleDetail({"taskId": csTask.id}).then((res) => {
			let dataSource = typeof res == "string" ? JSON.parse(res) : res;
			let requireSource = Array.isArray(dataSource.vos)?(dataSource.vos).filter(item=>item.planBeginTime!=undefined):[];
			let data = requireSource.map((item)=>{
				return {id: item.id, start: item.planBeginTime, end: item.planEndTime, name: item.name, color: "#FACD91"}
			});

			this.setState({
				timelineData: Array.isArray(data) ? data : []

			});
		});
	}

	getMakePlot = () => {
		const projectId =this.store.projectId;
		this.store.getMakePlot({'projectId':projectId}).then((mres)=>{
			if(mres) {
				let res = JSON.parse(JSON.stringify(mres));
				this.setState({
					makePlotInfo: res.makePlotInfo,
					makePlotRisk: Array.isArray(res.makePlotRisk) ? res.makePlotRisk.map((item, index) => {
						return Object.assign(item, {key: index + 1})
					}) : [],
				});
			}
		});
	}

	getProjectDetail=()=>{
		const projectId =this.store.projectId;
		this.store.getProjectDetail({'id':projectId}).then((res)=>{
			this.store.projectDetail = res
		})
	}

	changeTaskStepState=(param)=>{

		this.store.changeTaskStepState(param).then((res)=>{
			this.getProjectDetail();
		})
	}

	handleChangeTaskStepState=(param)=>{
		const {  taskDetail } = this.store;
		this.store.getTaskStepState({"taskId":taskDetail.id}).then((res)=>{
			if(res){
				this.changeTaskStepState(param);
			}
		})
	}

	overCurrentStageTask=()=>{
		const {  taskDetail } = this.store;
		const { attachments } = this.state;
		if(attachments.length>0){
			attachments.map((item)=>{
				if(item.id==""||item.id==null){
					this.updateDocument(item);
				}
			})
		}else{
			this.changeTaskStepState({"taskId": taskDetail.id, "index":1, "state":1})
			message.warn("请上传安全交底签字文件")
			return false;
		}
		this.store.getTaskStepState({"taskId":taskDetail.id}).then((res)=>{
			if(res){
				this.store.overTaskStep({"taskId":taskDetail.id, "index":1}).then((res)=>{
					if(res){
						this.getProjectDetail();
						this.props.closeModal();
						this.store.constructionState = res
					}
				})
			}
		})

	}

	updateDocument=(file)=>{
		const { taskDetail } = this.store;
		let document = {
			id:file.id,
			name:file.name,
			taskId:taskDetail.id,
			fileUrl:file.filePath,
			fileSize:file.size,
			downloadCount:0,
			ownerId:'',
			ownerName:'',
			orders:0,
			isVisible:1,
			category:1,
		}
		this.store.updateDocument({'d': document}).then((res)=>{

		})
	}

  getDocumentList=()=>{
	  const { taskDetail } = this.store;
	  this.store.getDocumentList({'taskId':taskDetail.id}).then((res)=>{
          if(res){
          	const attachments =           	res.map((item, index)=>{
				return Object.assign({}, {
					"fileId":null,
					"fileName":item.fileUrl,
					"filePath":item.fileUrl,
					"id":item.id,
					"name":item.name,
					"status":"done",
					"uid":uuidv4(),
					"url":item.fileUrl
				})
			});
			this.setState({attachments: attachments})
		  }
	  });
  }

	handleRemove=(item)=>{
		this.delDocument(item.id)
	}

	delDocument=(id)=>{
		this.store.delDocument({'id':id}).then((res)=>{
			if(res){
				message.success("文件删除成功");
			}
		})
	}

	getScheduleNowposition=(data)=>{
		let result = 0;
		if(data.length>0){
			const { start } = data[0];
			let days = moment.duration(moment().valueOf()-start).asDays();
			result = parseInt(days)*24
		}
		return result;
	}

	reStartHandover=()=>{
		const {  taskDetail } = this.store;
		this.store.reStartTaskStep({"taskId":taskDetail.id, "index":1}).then((res)=>{
			if(res){
				this.store.constructionState = res;
				this.getProjectDetail();
			}
		})
	}

	render() {
		const { activeKey, dataSource, attachments, timelineData, makePlotInfo, makePlotRisk } = this.state;
		const { ossData, constructionState } = this.store;

		return (
			<div className="dailyModal" style={{paddingRight:"30px"}} >
					<div className="mt20" style={{borderBottom: "1px dashed #e6e6e6", paddingBottom: "30px"}}>
						<Button
							ghost
							type="primary"
							icon="poweroff"
							onClick={this.reStartHandover}
							style={constructionState.state1 === 2 ? {display: ''} : {display: 'none'}}
						>
							重新启动
						</Button>
						<Tabs className="cc-tabs mt20" defaultActiveKey="1" activeKey={activeKey} onChange={this.handleChangeTabs} tabBarGutter={10} type="card">
							<TabPane tab="项目重难点" key="1" style={{padding:"0 10px"}}>
								<ContentTips
									headPerson="项目经理"
									acceptPerson="施工工长"
									notice="请在项目正式进场前1-2天进行交底"
								/>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>工程概况</span></div>
								<table style={{width:"100%"}} className="suppliertable">
									<tr>
										<td>工程名称</td>
										<td>
											{makePlotInfo.name}
										</td>
										<td>工程地点</td>
										<td>
											{makePlotInfo.address}
										</td>
									</tr>
									<tr>
										<td>建设单位</td>
										<td>
											{makePlotInfo.partyUnits}
										</td>
										<td>施工单位</td>
										<td>
											{makePlotInfo.buildUnits}
										</td>
									</tr>
									<tr>
										<td>主要供应商</td>
										<td>
											{makePlotInfo.supplier==null?"":makePlotInfo.supplier.map((item)=>{
												return item.name
											}).join("，")}
										</td>
										<td>工程类型</td>
										<td>
											{makePlotInfo.projectConstructionType}
										</td>
									</tr>
									<tr>
										<td>建筑面积（m2）</td>
										<td>
											{makePlotInfo.buildArea}
										</td>
										<td>房间组成（间）</td>
										<td>
											{makePlotInfo.roomComposition}
										</td>
									</tr>
									<tr>
										<td>进场条件</td>
										<td>
											{makePlotInfo.enterCondition}
										</td>
										<td>承接内容</td>
										<td>
											{Array.isArray(makePlotInfo.productType)?makePlotInfo.productType.join("、 "):""}
										</td>
									</tr>
								</table>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>重点与难点分析</span></div>
								<Table
									columns={columns}
									dataSource={makePlotRisk}
									pagination={false}
									bordered
								/>
							</TabPane>
							<TabPane tab="施工进度" key="2" style={{padding:"0 10px"}}>
								<ContentTips
									headPerson="项目经理"
									acceptPerson="施工工长"
									notice="请在项目正式进场前1-2天进行交底"
								/>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>施工进度计划</span></div>
								{timelineData.length>0?<TimeLine
									data={timelineData.filter((item)=>{
										return item.name!=="材料运输"
									})}
									config={config}
									nonEditableName={true}
									itemheight={39}
									nowposition={this.getScheduleNowposition(timelineData)}
								/>:""}
							</TabPane>
							<TabPane tab="奖罚措施" key="3" style={{padding:"0 10px"}}>
								<ContentTips
									headPerson="项目经理"
									acceptPerson="施工工长"
									notice="请在项目正式进场前1-2天进行交底"
								/>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>合同惩罚措施约定</span></div>
								<ContractPunishment />
							</TabPane>
							<TabPane tab="技术交底" key="4" style={{padding:"0 10px"}}>
								<ContentTips
									headPerson="项目经理"
									acceptPerson="操作工人"
									notice="请在工人上工前做集体交底。"
									isTechnology={true}
								/>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>技术交底内容</span></div>
								<TechnologyContent />
							</TabPane>
							<TabPane tab="安全交底" key="5" style={{padding:"0 10px"}}>
								<ContentTips
									headPerson="项目经理"
									acceptPerson="操作工人"
									notice="请在工人上工前做集体交底，并要求工人"
									isSafety={true}
								/>
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>安全交底内容</span></div>
								<SafetyContent />
								<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}><span style={{fontFamily:'PingFang-SC-Bold\', \'PingFang SC Bold\', \'PingFang SC', fontweight:'700', color:"#d9001b"}}>*</span>安全交底确认文件</span></div>
								<MyUpload
									saveAttachment={this.handleAttment.bind(this, 'attachments')}
									onRemove={this.handleRemove}
									disabled={constructionState.state1 === 2? true: false}
									multiple={true}
									listType="picture-card"
									ossData={mobx.toJS(ossData)}
									fileList={attachments == null ? [] : attachments}
									journalType="journal"
								/>
							</TabPane>
						</Tabs>
					</div>
<div style={{padding:'0 10px'}}>
	<div className="mb20 mt20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>附件</span></div>
	<EditableTableUpload
		id={'id'}
		store={this.store}
		dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
		saveData={()=>{}}
		saveItem={()=>{}}
		changeTaskStepState={this.handleChangeTaskStepState}
		disabled={true}
		pid={''}
	/>
	<div  style={constructionState.state1 === 2 ? {
height:"60px"
	} : {display: 'none'}}></div>
</div>
				<div  className="mt30 mb30" style={constructionState.state1 !== 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button onClick={this.overCurrentStageTask}
					        ghost
					        type="primary"> 完成该阶段任务</Button>
				</div>
			</div>
		)
	}
}


export default ConstructionContent;



