/*
*交付管理-组建项目团队
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {
	Row,
	Col,
	Form,
	Input,
	message,
	Radio,
	Button,
	Drawer,
	Tree,
	Select,
	Table,
	Pagination,
	Typography,
	Icon
} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';
//import _ from "lodash";
//import PageSelect from "@/components/common/PageSelect";
const { DirectoryTree } = Tree;
const InputGroup = Input.Group;
const {Option} = Select;
const { Text } = Typography;

@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class BaseInfoItemTwo extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
			summary:BraftEditor.createEditorState(null),
			overallDescription:BraftEditor.createEditorState(null),
			customerExpectations:BraftEditor.createEditorState(null),
			promise:BraftEditor.createEditorState(null),
			visible: false,  // 客户弹框
			companyTree: [],
			selectedNode: {},
			params: {
				text: "",
				parentId: "",
				simpleName:"",
				level: 0,
				pageIndex: 1,
				pageSize: 10
			},
			orgType: [
				{"id":"1", "name":"集团"},
				{"id":"2", "name":"子集团"},
				{"id":"3", "name":"分子公司"},
			],
			companyPageList: [],
			totalCount: 0
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		this.props.onRef && this.props.onRef(this);
		this.getHandoverTwo();
		this.getCompanyTree();
		this.getPage();
	}

	componentWillMount(){

	}

	getHandoverTwo=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverTwo({"taskId": taskDetail.id}).then((res)=>{
			if(res){
				this.store.handoverTwoObj =res;
				this.props.form.setFieldsValue({
					customerExpectations: res.customerExpectations==null||res.customerExpectations==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(res.customerExpectations),
					promise: res.promise==null||res.promise==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(res.promise),
				});
			}
		})
	}

	checkFormValidate(){
		this.props.form.validateFields((err, values) => {
			if (!err) {
				//console.log('Received values of form: ', values);
				this.props.checkFormRequre(true)
			}else{
				this.props.checkFormRequre(false)
			}
		});
	}

	handleOnChange=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	handleBChange = (type, bfData) => {
		// console.log(type, BraftEditor.createEditorState(bfData).toHTML())
		this.props.updateHandOverBC(type,BraftEditor.createEditorState(bfData).toHTML())
	}

	updateStore = (param) =>{
		this.props.form.setFieldsValue({
			"companyId": param.id
		});
		this.props.updateHandOver3({
			 	companyId: param.id,
			    companyName: param.name
			});
	}

	showModal = () => {
		this.setState({
			visible: true,
		});
	};

	handleOk = e => {
		this.setState({
			visible: false,
		});
	};

	handleCancel = e => {
		//console.log(e);
		this.setState({
			visible: false,
		});
	};

	getClientHeight =() =>{
		var clientHeight=0;
		if(document.body.clientHeight&&document.documentElement.clientHeight)
		{
			var clientHeight = (document.body.clientHeight<document.documentElement.clientHeight)?document.body.clientHeight:document.documentElement.clientHeight;
		}
		else
		{
			var clientHeight = (document.body.clientHeight>document.documentElement.clientHeight)?document.body.clientHeight:document.documentElement.clientHeight;
		}
		return clientHeight;
	}

	getCompanyTree =()=>{
		this.store.getCompanyTree()
			.then((response)=>{
					if (response) {
						function transformTree (list) {
							const tree = []
							for (let i = 0, len = list.length; i < len; i++) {
								if (!list[i].parentId) {
									const item = queryChildren(list[i], list)
									tree.push(Object.assign(item,{"title":item.nickname, key:item.id}))
								}
							}
							return tree
						}

						function queryChildren (parent, list) {
							const children = []
							for (let i = 0, len = list.length; i < len; i++) {
								if (list[i].parentId === parent.id) {
									const item = queryChildren(list[i], list)
									if (item.children==undefined) {  // 子集
										children.push(Object.assign(item, {isLeaf: true, title:item.nickname, key:item.id}))
									}
									else {
										children.push(Object.assign(item, {isLeaf: false, title:item.nickname, key:item.id}))
									}
								}
							}
							if (children.length) {
								parent.children = children
							}
							return parent
						}

						this.setState({
							companyTree: transformTree(response)
						});

						if(response&&Array.isArray(response)){
							response.forEach((item)=>{
								if(item.id==="root"&&Object.keys(this.state.selectedNode).length===0){
									this.setState({
										selectedNode: Object.assign(item,{"title":item.nickname,"key":item.id})
									});
								}
							});
						}

					}
				});
	}

	handleSearch = () => {
		this.setState({
			params: Object.assign(this.state.params, { pageIndex: 1, pageSize: 10})
		}, ()=>{
			this.getPage();
		});
	}

	onChange = (type, e) => {
		this.setState({
		  params: Object.assign(this.state.params, {[type]: e.target.value})
		});
	}

	onChangeType = (type, value) => {
		this.setState({
			params: Object.assign(this.state.params, {[type]: value})
		});
	}

	getPage = () => {
		this.store.getCompanyPageList(this.state.params).then((response)=>{
			if (response&&response.body!=null) {
				this.setState({
				  companyPageList: response.body.map((item,index)=>Object.assign(item, {keyIndex: index+1})),
				  totalCount: response.totalCount
				});
			}
			else {
				this.setState({
					companyPageList: [],
					totalCount: 0
				});
			}
		});
	}

	showSizeChange = (current, pageSize) => {
		this.setState({
			params: Object.assign(this.state.params, {"pageIndex": 1, "pageSize": pageSize})
		}, ()=>{
			this.getPage();
		});
	}

	onPageChange = (pageNumber) => {
		this.setState({
			params: Object.assign(this.state.params, {"pageIndex": pageNumber})
		}, ()=>{
			this.getPage();
		});
	}

	showTotal = (total) => {
		return `共 ${total} 条`;
	}

	// 左树点击
	onSelect = (keys, event) => {
		const {node} = event;
		this.setState({
			selectedNode: node,
			params: Object.assign(this.state.params, {"parentId": node.props.id==='root'?'':node.props.id, pageIndex: 1, pageSize: 10})
		}, ()=> {
           this.getPage();
		});
	}

	setClick = (param) => {
		this.props.updateHandOver3({
			companyId: param.id,
			companyName: param.name
		});
		this.handleCancel();
	}

	render() {
		const { sureState, customerExpectations, promise, companyTree, orgType, params, totalCount, companyPageList  } = this.state;
		const { handoverTwoObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverTwoObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 4},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 20},
				md: {span: 20},
				sm: {span: 20},
			},
		};
		const controls = ['bold',  'text-color',  ]
		const columns = [
			{
				title: '序号',
				dataIndex: 'keyIndex',
				align: "center",
				width: "50px"
			},
			{
				title: '组织名称',
				dataIndex: 'name',
				align: "center",
				render: (text, record) => {
					return <div
						onClick={this.setClick.bind(this, record)}
						style={{cursor: "pointer", color: "#1890ff"}}>{text}</div>
				}
			},
			{
				title: '组织类型',
				dataIndex: 'level',
				align: "center",
				render: (text, record) => {
					return text===1?"集团":text===2?"子集团":"分公司"
				}
			},
			// {
			//   title: '组织层级',
			//   dataIndex: 'level',
			//   align: "center",
			//   width: "50px",
			// },
			{
				title: '组织简称',
				dataIndex: 'nickname',
				align: "center"
			},
			{
				title: '备注',
				dataIndex: 'remarks',
				align: "center",
				render: (text, record) => {
					return text==null||text==""?"": <Text
						style={{ width: 300 }}

					>
						{text}
					</Text>
				}
			},
		];
		return (
			<div className="mt15">
				<Form  {...formItemLayout} >
					{handoverState.state2===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<table className="suppliertable" style={{width: "100%"}}>
									<tr>
										<td>客户单位名称</td>
										<td>
											{fields.companyName}
										</td>
										<td>与广联达关系</td>
										<td>
											{fields.glodonRelationship}
										</td>
									</tr>
									<tr>
										<td>第几次合作</td>
										<td>
											{fields.cooperations}
										</td>
										<td>与智建美住关系</td>
										<td>
											{fields.bocspaceRelationship}
										</td>
									</tr>
									{
										fields.centralized===0?(<tr>
											<td>是否为集采项目</td>
											<td colSpan={3}>
												{fields.centralized===0?'否':'是'}
											</td>
										</tr>):(<tr>
											<td>是否为集采项目</td>
											<td>
												{fields.centralized===0?'否':'是'}
											</td>
											<td>集采单位名称</td>
											<td>
												{fields.centralizeCompany}
											</td>
										</tr>)
									}
									<tr>
										<td>客户提出的期望</td>
										<td colSpan={3}>
											<div style={{width: '100%', wordBreak:'break-all', whiteSpace:'pre-wrap', padding:"5px" }}>
												{
													fields.customerExpectations!==""?(<div dangerouslySetInnerHTML={{__html: fields.customerExpectations}} />):""
												}
											</div>
										</td>
									</tr>
									<tr>
										<td>关键承诺（含口头）</td>
										<td colSpan={3}>
											<div style={{width: '100%', wordBreak:'break-all', whiteSpace:'pre-wrap', padding:"5px" }}>
												{
													fields.promise!==""?(<div dangerouslySetInnerHTML={{__html: fields.promise}} />):""
												}
											</div>
										</td>
									</tr>
								</table>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
							<Form.Item label="客户单位名称" className="w">
								{getFieldDecorator('companyId', {
									initialValue: fields.companyId || "",
									rules: [{required: true, message: '此项是必填'}],
								})(
									<span onClick={this.showModal}>
										{fields.companyName}
										<Icon type="form" style={{cursor: "pointer", marginLeft: "5px"}}/>
									</span>
									/*<PageSelect
										style="600px"
										value={fields.companyId || ""}
										updateStore={this.updateStore}
									/>*/
								)}
							</Form.Item>
							  {/* <Form.Item  label="" className="w">
										{getFieldDecorator('companyName', {
										initialValue: fields.companyName == null ? "" : fields.companyName,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("companyName", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>*/}
								<Form.Item label="客户提出的期望" className="w">
								{getFieldDecorator('customerExpectations', {
									initialValue: fields.customerExpectations==null||fields.customerExpectations==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(fields.customerExpectations),
									rules: [{required: true, message: '此项是必填'}],
								})(
									<BraftEditor
										className="my-editor"
										onBlur={(e)=>{this.handleBChange('customerExpectations', e)}}
										controls={controls}
										contentStyle={{
											minHeight: 100,
											maxHeight: 100,
											overflowY: 'scroll',
											height: 'auto',
											width:'100%'
										}}
									/>
								)}
							</Form.Item>
							<Form.Item label="关键承诺（含口头）" className="w">
								{getFieldDecorator('promise', {
									//initialValue: promise,
									initialValue: fields.promise==null||fields.promise==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(fields.promise),
									rules: [{required: true, message: '此项是必填'}],
								})(
									<BraftEditor
										className="my-editor"
										onBlur={(e)=>{this.handleBChange('promise', e)}}
										controls={controls}
										contentStyle={{
											minHeight: 100,
											maxHeight: 100,
											overflowY: 'scroll',
											height: 'auto',
											width:'100%'
										}}
									/>
								)}
							</Form.Item>
								<Form.Item label="第几次合作" className="w">
									{getFieldDecorator('cooperations', {
										initialValue: fields.cooperations,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleOnChange('cooperations', e)}}>
											<Radio value={"第一次"}>第一次</Radio>
											<Radio value={"第二次"}>第二次</Radio>
											<Radio value={"第三次"}>第三次</Radio>
											<Radio value={"三次以上"}>三次以上</Radio>
										</Radio.Group>
									)}
								</Form.Item>
								<Form.Item label="是否为集采项目" className="w">
									{getFieldDecorator('centralized', {
										initialValue: fields.centralized,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleOnChange('centralized', e)}}>
											<Radio value={1}>是</Radio>
											<Radio value={0}>否</Radio>

										</Radio.Group>
									)}
								</Form.Item>
							{fields.centralized===1?<Form.Item  label="集采单位名称" className="w">
								{getFieldDecorator('centralizeCompany', {
									initialValue: fields.centralizeCompany == null ? "" : fields.centralizeCompany,
									rules: [{required: true, message: '此项是必填'}],
								})(
									<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("centralizeCompany", e)}} style={{width:'600px'}}  placeholder="请输入"  />
								)}
							</Form.Item>:null}

								<Form.Item label="与广联达关系" className="w">
									{getFieldDecorator('glodonRelationship', {
										initialValue: fields.glodonRelationship,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleOnChange('glodonRelationship', e)}}>
											<Radio value={"非常好"}>非常好</Radio>
											<Radio value={"一般"}>一般</Radio>
											<Radio value={"不了解"}>不了解</Radio>
											<Radio value={"很差"}>很差</Radio>
										</Radio.Group>
									)}
								</Form.Item>
								<Form.Item label="与智建美住关系" className="w">
									{getFieldDecorator('bocspaceRelationship', {
										initialValue: fields.bocspaceRelationship,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleOnChange('bocspaceRelationship', e)}}>
											<Radio value={"非常好"}>非常好</Radio>
											<Radio value={"一般"}>一般</Radio>
											<Radio value={"不了解"}>不了解</Radio>
											<Radio value={"很差"}>很差</Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
						</Row>
					}
				</Form>
				{/*客户名称弹框*/}
				<Drawer
					zIndex={1078}
					title="选择客户单位名称"
					width={"80%"}
					onClose={this.handleCancel}
					visible={this.state.visible}
					bodyStyle={{ paddingBottom: 80 }}
					maskClosable={false}
				>
					<div className="clearfix infor-main">
						<div className="infor-main-left2">
							{Array.isArray(companyTree)&&companyTree.length>0&&<DirectoryTree
								expandAction="doubleClick"
								defaultSelectedKeys={["root"]}
								defaultExpandAll={true}
								onSelect={this.onSelect}
								treeData={companyTree}
								height={this.getClientHeight()-200}
							/>}
						</div>
						<div className="infor-main-right2">
							<div className="clearfix mt10">
								<InputGroup className="fl" style={{width: 'auto'}}>
									<Input onChange={this.onChange.bind(this, 'text')}
										   value={params.text}
										   style={{width: 125, marginLeft: 5}}
										   placeholder="名称"/>
								</InputGroup>
								<InputGroup className="fl" style={{width: 'auto'}}>
									<Select style={{width: 125, marginLeft: 5}} onChange={this.onChangeType.bind(this, 'level')} allowClear={true} placeholder="类型">
										{orgType.map((item)=>{
											return <Option value={item.id} key={item.id}>{item.name}</Option>
										})}
									</Select>
								</InputGroup>
								<InputGroup className="fl" style={{width: 'auto'}}>
									<Input onChange={this.onChange.bind(this, 'simpleName')}
										   value={params.simpleName}
										   style={{width: 125, marginLeft: 5}}
										   placeholder="简称"/>
								</InputGroup>
								<Button
									style={{marginLeft: 5}}
									className="fl"
									type="primary"
									onClick={this.handleSearch}
								>
									搜索
								</Button>
							</div>
							<Table
								size="small"
								bordered
								className="m_table mt20"
								columns={columns}
								dataSource={companyPageList}
								rowKey={record => record.id}
								pagination={false}
								//loading={loading}
							/>
							<Pagination
								className="mt20 tr"
								current={Number(params.pageIndex)}
								pageSize={params.pageSize}
								total={totalCount}
								onShowSizeChange={this.showSizeChange}
								onChange={this.onPageChange}
								showSizeChanger
								showQuickJumper
								showTotal={this.showTotal}
							/>
						</div>
					</div>
				</Drawer>
			</div>
		)
	}
}






