import React from 'react';
import {
	Input,
	Form,
	Row,
	Col,
	Spin,
	Select,
	DatePicker,
	message,
	Button,
	InputNumber,
	Table,
	Popconfirm,
} from 'antd';
import * as mobx from "mobx";
import {observer} from "mobx-react";
import moment from 'moment';
import EditableTableUpload from "@/components/handover/EditableTableUpload";
import Document from "./Document";
import EditableTableInventory from './EditableTableInventory.js';
const {Option} = Select;
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = (type, e) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			handleSave({...record, ...{[type]: values[type]}});
		});
	};

	onChange = (type, value) => {
		const {record, handleSave} = this.props;
		handleSave({...record, ...{[type]: value}});
	}

	// 日期
	handDate = (type, date, dateString) => {
		let val = dateString == null || dateString == "" ? 0 : moment(dateString, 'YYYY-MM-DD').valueOf();
		const {record, handleSave} = this.props;
		handleSave({...record, ...{[type]: val}});
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;

		if (dataIndex == "nodeName") {   // 名称
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(
						<Select style={{width: "200px"}}
						        ref={node => (this.input = node)}
						        onChange={this.onChange.bind(this, dataIndex)}
						>
							<Option value="预付款-按合同额">预付款-按合同额</Option>
							<Option value="合同签订后-按合同额">合同签订后-按合同额</Option>
							<Option value="材料进场-按合同额">材料进场-按合同额</Option>
							<Option value="竣工验收-按结算金额">竣工验收-按结算金额</Option>
							<Option value="质保期-按结算金额">质保期-按结算金额</Option>
						</Select>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} /*else if (dataIndex == "dateTime") {   // 时间
			return editing ? (
				record["isSelected"]==1?
					<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex]== null || record[dataIndex] == 0 ? "":moment(moment(record[dataIndex]).format('YYYY-MM-DD'), "YYYY-MM-DD")
					})(<DatePicker
							value={record[dataIndex]== null || record[dataIndex] == 0 ? "":moment(moment(record[dataIndex]).format('YYYY-MM-DD'), "YYYY-MM-DD")}
							allowClear={false}
							style={{width: "150px"}}
							format="YYYY-MM-DD"
							placeholder="请选择"
							onChange={this.handDate.bind(this, "dateTime")}
						/>)}
				</Form.Item>:null
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					//onClick={this.toggleEdit}
				>
					{record[dataIndex] == null || record[dataIndex] == "" ? null : record[dataIndex].join("、")}
				</div>
			);
		}*/ else if (dataIndex == "payRate") {   // 比例
			return editing ? (
				record["isSelected"]==1?
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex] == null || record[dataIndex] == "" ? [] : record[dataIndex],
					})(
						<InputNumber
							style={{width: "200px"}}
							onBlur={this.save.bind(this, "payRate")}
							//onChange={this.handleChange2.bind(this, "civilizedConstructionValue",makePlotCost.civilizedConstructionMaxValue)}
							placeholder="请输入"/>
					)}
				</Form.Item>:null
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					//onClick={this.toggleEdit}
				>
					{record[dataIndex] == null || record[dataIndex] == "" ? null : record[dataIndex].join("、")}
				</div>
			);
		} else {
			return editing ? (
				record["isSelected"]==1?
					<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<Input placeholder="请输入" style={{width: "150px"}} ref={node => (this.input = node)} onPressEnter={this.save.bind(this, dataIndex)} onBlur={this.save.bind(this, dataIndex)}/>)}
				</Form.Item>:null
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					//onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

@Form.create()
@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			downloading: false,
			dataSource: [],
			supplierPayNode: [],
			selectedRowKeys: []
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		//this.getEnclosureList();
		this.props.onRef(this);
	}

	// 编辑态初始化数据
	init = () => {
		const {supplierTwoItem} = this.store;
		const obj = JSON.parse(JSON.stringify(supplierTwoItem));
		const {
			projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
			partySecondSafer = "", contractMoney = "", workBeginTime, wordEndTime, contractAwardTime,
			partySecondBankName = "", partySecondBankNumber = "", partySecondBank = "", partyFirstCompanyId = "",
			code, partySecond, projectName, partyFirstManager, partyFirstPhone, contractMoneyBig,contractDays, supplierPayNode
		} = obj;
		this.props.form.setFieldsValue({
			projectAddress,
			partySecondManager,
			partySecondPhone,
			partySecondIdCard,
			partySecondSafer,
			contractMoney: contractMoney==null||contractMoney==""?"":contractMoney,
			workBeginTime: workBeginTime==null||workBeginTime==0?"":moment(moment(workBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
			wordEndTime: wordEndTime==null||wordEndTime==0?"":moment(moment(wordEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
			contractAwardTime: contractAwardTime==null||contractAwardTime==0?"":moment(moment(contractAwardTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
			partySecondBankName,
			partySecondBankNumber,
			partySecondBank,
			partyFirstCompanyId,
			code, partySecond, projectName, partyFirstManager, partyFirstPhone, contractMoneyBig,contractDays
		});
		this.setState({
			supplierPayNode: Object.assign([], supplierPayNode),
			selectedRowKeys: supplierPayNode.filter(item=>item.isSelected==1).map(v=>v.id)
		}, ()=> {

			//console.log(supplierPayNode, this.state.supplierPayNode, 'supplierPayNode')

		});
	}

	// 完成
	handleSave = () => {
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {
				const {dataSource, supplierPayNode} = this.state;
				// 校验付款节点
				let supplier = JSON.parse(JSON.stringify(supplierPayNode)).filter(item=>item.isSelected==1);
				if(supplier.length>0&&supplier.some(v=>v.dateTime==""||v.dateTime==null||v.payRate==null||v.payRate=="")) {
					message.warn("请完善付款节点中的必填信息")
					return false;
				}
				// 校验附件
				const dsCheck = dataSource.filter((item)=>{
					return item.isMust===1&&item.enclosureItems.length===0
				});
				if(dsCheck.length>0){
					message.warn("请检查附件列表中必需上传项是否已经上传附件")
					return false;
				}

				const {
					projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
					partySecondSafer = "", contractMoney = "", workBeginTime, wordEndTime, contractAwardTime,
					partySecondBankName = "", partySecondBankNumber = "", partySecondBank = "", partyFirstCompanyId = ""
				} = values;
				const {supplierTwoItem} = this.store;
				const obj = JSON.parse(JSON.stringify(supplierTwoItem));
				delete obj.selected;
				const params = Object.assign(obj, {
					projectAddress,
					partySecondManager,
					partySecondPhone,
					partySecondIdCard,
					partySecondSafer,
					contractMoney,
					workBeginTime: workBeginTime == null || workBeginTime == "" ? 0 : moment(workBeginTime, 'YYYY-MM-DD').valueOf(),
					wordEndTime: wordEndTime == null || wordEndTime == "" ? 0 : moment(wordEndTime, 'YYYY-MM-DD').valueOf(),
					contractAwardTime: contractAwardTime == null || contractAwardTime == "" ? 0 : moment(contractAwardTime, 'YYYY-MM-DD').valueOf(),
					partySecondBankName,
					partySecondBankNumber,
					partySecondBank,
					partyFirstCompanyId,
					supplierPayNode: JSON.parse(JSON.stringify(this.state.supplierPayNode)),
					isSave: 2,

				});
				//console.log(params, '保存')
				// 保存
				this.store.saveConfirmSupplierTwoItem({csti: params}).then((res) => {
					if (res) {
						message.success("保存成功");
						this.store.supplierTwoItem = Object.assign({}, res);

						// 更改列表的数据
						let param = res;
						let stakeholderList = JSON.parse(JSON.stringify(this.store.supplierTwoItemList));
						if (param.isMain==1) {  // 主合同
							let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
							const dindex = dataArray.findIndex((item) => {
								return item.id == param.id
							});
							dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
							this.store.supplierTwoItemList = Object.assign([], dataArray);
						}

						// 如果全部完成时，更改该阶段的状态
						let isFlag = stakeholderList.some(function(computer){
							return computer.isSave != 2;
						});
						if(!isFlag) {
							this.props.nextStep();
						}

						// 点击完成时是否生成合同
						let isDocument = res.document==null || (typeof res.document == "object"&&Object.keys(res.document).length==0)?false:true;  // 是否有采购合同
						if (true) { //此处条件先改成true
                          this.buildWord(()=>{
	                          this.store.suState = true;
                          });
						} else {  // 切换状态
							this.store.suState = true;
						}
					}
				});




			}
		});
	}

	// 生成采购合同
	buildWord = (callback) => {
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {

				const {
					projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
					partySecondSafer = "", contractMoney = "", workBeginTime, wordEndTime, contractAwardTime,
					partySecondBankName = "", partySecondBankNumber = "", partySecondBank = "", partyFirstCompanyId = ""
				} = values;
				const {supplierTwoItem} = this.store;
				const obj = JSON.parse(JSON.stringify(supplierTwoItem));
				let supplierContractList = obj.supplierContractList==null?[]:obj.supplierContractList;
				if (supplierContractList.length>0) {
					supplierContractList.map(v=>{
						delete v.key;
						delete v.rate;
						return v;
					});
				}
				delete obj.selected;
				const params = Object.assign(obj, {
					projectAddress,
					partySecondManager,
					partySecondPhone,
					partySecondIdCard,
					partySecondSafer,
					contractMoney,
					workBeginTime: workBeginTime == null || workBeginTime == "" ? 0 : moment(workBeginTime, 'YYYY-MM-DD').valueOf(),
					wordEndTime: wordEndTime == null || wordEndTime == "" ? 0 : moment(wordEndTime, 'YYYY-MM-DD').valueOf(),
					contractAwardTime: contractAwardTime == null || contractAwardTime == "" ? 0 : moment(contractAwardTime, 'YYYY-MM-DD').valueOf(),
					partySecondBankName,
					partySecondBankNumber,
					partySecondBank,
					partyFirstCompanyId,
					supplierPayNode: JSON.parse(JSON.stringify(this.state.supplierPayNode)),
					supplierContractList: supplierContractList
				});

				this.store.buildContractDoc({csti:params}).then((res)=>{
                    this.setState({
	                    downloading: true
                    })
					this.getReportUrl({"id": res}, callback);
				});

			}
		});
	}

	// 自动生成合同
	buildWord2 = (callback) => {
		const {supplierTwoItem} = this.store;
		const obj = JSON.parse(JSON.stringify(supplierTwoItem));
		const {
			projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
			partySecondSafer = "", contractMoney = "", workBeginTime, wordEndTime, contractAwardTime,
			partySecondBankName = "", partySecondBankNumber = "", partySecondBank = "", partyFirstCompanyId = ""
		} = obj;
		let supplierContractList = obj.supplierContractList==null?[]:obj.supplierContractList;
		if (supplierContractList.length>0) {
			supplierContractList.map(v=>{
				delete v.key;
				delete v.rate;
				return v;
			});
		}
		delete obj.selected;
		const params = Object.assign(obj, {
			projectAddress,
			partySecondManager,
			partySecondPhone,
			partySecondIdCard,
			partySecondSafer,
			contractMoney,
			workBeginTime: workBeginTime == null || workBeginTime == "" ? 0 : moment(workBeginTime, 'YYYY-MM-DD').valueOf(),
			wordEndTime: wordEndTime == null || wordEndTime == "" ? 0 : moment(wordEndTime, 'YYYY-MM-DD').valueOf(),
			contractAwardTime: contractAwardTime == null || contractAwardTime == "" ? 0 : moment(contractAwardTime, 'YYYY-MM-DD').valueOf(),
			partySecondBankName,
			partySecondBankNumber,
			partySecondBank,
			partyFirstCompanyId,
			supplierPayNode: JSON.parse(JSON.stringify(this.state.supplierPayNode)),
			supplierContractList: supplierContractList
		});

		this.store.buildContractDoc({csti:params}).then((res)=>{
			this.setState({
				downloading: true
			})
			this.getReportUrl({"id": res}, callback);
		});

	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id, callback) => {
		let _this = this;
		_this.timer22 = setInterval(() => {
			_this.store.getDownloadUrl(id).then((url) => {
				if (url !== null && url !== "") {
					_this.setState({ downloading: false})
					// 重新保存任务
					_this.reSave();
					if (typeof callback=="function") {
						callback();
					}
					clearInterval(_this.timer22);
				} else {

				}
			});
		}, 3000);
	}

	saveContents=()=>{
		this.getEnclosureList();
	}

	saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:1,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:1}
		this.store.saveEnclosureMain(parmas).then((res)=>{
			this.getEnclosureList()
		})
	}

	getEnclosureList=()=>{
		const { taskDetail, supplierTwoItem } = this.store;
		if (mobx.toJS(supplierTwoItem).id==undefined||mobx.toJS(supplierTwoItem).id==null||mobx.toJS(supplierTwoItem).id=="") {
           return false;
		}
		this.store.getEnclosureListByItemId({"taskId":mobx.toJS(taskDetail).id, "indexs":0, itemId: mobx.toJS(supplierTwoItem).id}).then((res)=>{
			this.store.enclosureList = Object.assign([], res);
			this.setState({
				dataSource: Object.assign([], res),
				enclosureList: Object.assign([], res)})
		})
	}

	reSave = () => {
		const {
			projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
			partySecondSafer = "", contractMoney = "", workBeginTime, wordEndTime, contractAwardTime,
			partySecondBankName = "", partySecondBankNumber = "", partySecondBank = "", partyFirstCompanyId = ""
		} = this.props.form.getFieldsValue();
		const {supplierTwoItem} = this.store;
		const obj = JSON.parse(JSON.stringify(supplierTwoItem));
		let supplierContractList = obj.supplierContractList==null?[]:obj.supplierContractList;
		if (supplierContractList.length>0) {
			supplierContractList.map(v=>{
				delete v.key;
				delete v.rate;
				return v;
			});
		}
		delete obj.selected;
		const params = Object.assign(obj, {
			projectAddress,
			partySecondManager,
			partySecondPhone,
			partySecondIdCard,
			partySecondSafer,
			contractMoney,
			workBeginTime: workBeginTime == null || workBeginTime == "" ? 0 : moment(workBeginTime, 'YYYY-MM-DD').valueOf(),
			wordEndTime: wordEndTime == null || wordEndTime == "" ? 0 : moment(wordEndTime, 'YYYY-MM-DD').valueOf(),
			contractAwardTime: contractAwardTime == null || contractAwardTime == "" ? 0 : moment(contractAwardTime, 'YYYY-MM-DD').valueOf(),
			partySecondBankName,
			partySecondBankNumber,
			partySecondBank,
			partyFirstCompanyId,
			supplierPayNode: JSON.parse(JSON.stringify(this.state.supplierPayNode)),
			supplierContractList: supplierContractList
		});
		// 保存
		this.store.saveConfirmSupplierTwoItem({csti: params}).then((res) => {
			if (res) {
				this.store.supplierTwoItem = Object.assign({}, res);

				// 重置
				this.init();

				// 更改列表的数据
				let param = res;
				if (param.isMain==1) {  // 主合同
					let stakeholderList = JSON.parse(JSON.stringify(this.store.supplierTwoItemList));
					let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
					const dindex = dataArray.findIndex((item) => {
						return item.id == param.id
					});
					dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
					this.store.supplierTwoItemList = Object.assign([], dataArray);
				}
			}
		});
	}

	// 保存
	handleSaveOne = row => {
		const newData = [...this.state.supplierPayNode];
		const index = newData.findIndex(item => row.id === item.id);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row
		});
		this.setState({supplierPayNode: newData},()=>{
			message.success("保存成功");
			this.reSave();
		});
	};

	onSelectChange = selectedRowKeys => {
		//console.log('selectedRowKeys changed: ', selectedRowKeys);
		this.setState({ selectedRowKeys }, ()=> {
			let newData = [...this.state.supplierPayNode];
			let dataSource = newData.map((item)=>{
               if (selectedRowKeys.includes(item.id)) {
               	return Object.assign(item, {isSelected: 1})
               } else {
	               return Object.assign(item, {isSelected: 0})
               }
			});
			this.setState({supplierPayNode: dataSource},()=>{
				message.success("保存成功");
				this.reSave();
			});
		});
	};

	// 更新合同清单
	updateState = (type, data) => {
		let newData = JSON.parse(JSON.stringify(data));
		this.store.supplierTwoItem[type] = Object.assign([], newData);
		this.reSave();
	}

	// 删除合同
	handleDelete = (param) => {
		this.store.delConfirmSupplierSupplementById({id: param}).then((res)=>{
			if (res==true) {
				message.success("删除成功");
				// 重新初始化数据,初始化到主合同
				this.props.autosaveData();
			}
		});
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {span: 8},
			wrapperCol: {span: 15},
		};
		let fields = mobx.toJS(this.store.supplierTwoItem) || {};
		const {companyList} = this.store;
		const {downloading, dataSource, supplierPayNode, selectedRowKeys} = this.state;
		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange,
		};

		let isDocument = fields.document==null || (typeof fields.document == "object"&&Object.keys(fields.document).length==0)?false:true;  // 是否有采购合同

		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '序号',
				dataIndex: 'dictId',
				width: '50px',
				align: 'center'
			},
			{
				title: '节点',
				dataIndex: 'nodeName',
				width: '200px',
				align: 'center',
				editable: true,
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>时间</div>,
				dataIndex: 'dateTime',
				width: '150px',
				align: 'center',
				editable: true,
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>支付到比例（%）</div>,
				dataIndex: 'payRate',
				width: '200px',
				align: 'center',
				editable: true,
			},
			{
				title: '备注',
				dataIndex: 'remark',
				width: '200px',
				align: 'center',
				editable: true,
			}
		];
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSaveOne
				}),
			};
		});

		return (
			<div className={fields.isSave!=2?"":"hidden"}>
				<Spin spinning={downloading} tip="正在生成采购合同,请稍等...">
					<Form layout="inline" {...formItemLayout}>
						<div className="fr">
							<Button type="primary" onClick={this.handleSave}>完成合同</Button>
							{fields.isMain==1?null:
								<Popconfirm
									title="确定要删除吗?"
									onConfirm={this.handleDelete.bind(this, fields.id)}
									onCancel={()=>{}}
									okText="是"
									cancelText="否"
								>
									<Button className="ml15" type="dashed">删除合同</Button>
								</Popconfirm>
							}</div>
						<h1 style={{padding: "10px 25px", fontWeight: 700, fontSize: "14px"}}>合同基本信息</h1>
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="合同编号" className="w">
									{getFieldDecorator('code', {
										initialValue: fields.code || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="保存后自动生成（不可修改" disabled />
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="合同乙方" className="w">
									{getFieldDecorator('partySecond', {
										initialValue: fields.partySecond || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="项目名称" className="w">
									{getFieldDecorator('projectName', {
										initialValue: fields.projectName || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="项目地址" className="w">
									{getFieldDecorator('projectAddress', {
										initialValue: fields.projectAddress || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="甲方项目经理" className="w">
									{getFieldDecorator('partyFirstManager', {
										initialValue: fields.partyFirstManager || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="甲方联系方式" className="w">
									{getFieldDecorator('partyFirstPhone', {
										initialValue: fields.partyFirstPhone || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方项目经理" className="w">
									{getFieldDecorator('partySecondManager', {
										initialValue: fields.partySecondManager || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方联系方式" className="w">
									{getFieldDecorator('partySecondPhone', {
										initialValue: fields.partySecondPhone || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方身份证号" className="w">
									{getFieldDecorator('partySecondIdCard', {
										initialValue: fields.partySecondIdCard || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方安全负责人" className="w">
									{getFieldDecorator('partySecondSafer', {
										initialValue: fields.partySecondSafer || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="合同金额（元）" className="w">
									{getFieldDecorator('contractMoney', {
										initialValue: fields.contractMoney || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<InputNumber  placeholder="请输入数字" onBlur={this.reSave} className="w"/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="大写金额（元）" className="w">
									{getFieldDecorator('contractMoneyBig', {
										initialValue: fields.contractMoneyBig || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="预览时可自动生成(不可修改)" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="开工日期" className="w">
									{getFieldDecorator('workBeginTime', {
										initialValue: fields.workBeginTime==null||fields.workBeginTime==0?"":moment(moment(fields.workBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}]
									})(
										<DatePicker format="YYYY-MM-DD" placeholder="请选择" className="w" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="竣工日期" className="w">
									{getFieldDecorator('wordEndTime', {
										initialValue: fields.wordEndTime==null||fields.wordEndTime==0?"":moment(moment(fields.wordEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}]
									})(
										<DatePicker format="YYYY-MM-DD" placeholder="请选择" className="w" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="合同工期（天）" className="w">
									{getFieldDecorator('contractDays', {
										initialValue: fields.contractDays || "",
										rules: [{required: false, message: '此项是必填'}]
									})(
										<Input placeholder="竣工-开工+1（自动计算，不可更改）" disabled/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="合同签订日期" className="w">
									{getFieldDecorator('contractAwardTime', {
										initialValue: fields.contractAwardTime==null||fields.contractAwardTime==0?"":moment(moment(fields.contractAwardTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}]
									})(
										<DatePicker format="YYYY-MM-DD" placeholder="请选择" className="w" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
						</Row>
						<h1 style={{margin: "15px 0", padding: "10px 25px", fontWeight: 700, fontSize: "14px"}}>付款节点
							<span className="daily_tips">（请针对需要的付款节点打勾确认）</span>
						</h1>
						<div style={{padding: "0 25px"}}>
							<Table
								components={components}
								className="digital_table"
								rowClassName={() => 'editable-row'}
								dataSource={supplierPayNode}
								columns={column}
								pagination={false}
								bordered
								rowSelection={rowSelection}
								rowKey={record => record.id}
							/>
						</div>
						<h1 style={{margin: "15px 0", padding: "10px 25px", fontWeight: 700, fontSize: "14px"}}>甲乙方账户信息</h1>
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方开户名称" className="w">
									{getFieldDecorator('partySecondBankName', {
										initialValue: fields.partySecondBankName || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input placeholder="请输入" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方开户行" className="w">
									{getFieldDecorator('partySecondBank', {
										initialValue: fields.partySecondBank || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input disabled={fields.partySecondBank===null?true:false} placeholder="请去供应商库完善信息" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="乙方账号" className="w">
									{getFieldDecorator('partySecondBankNumber', {
										initialValue: fields.partySecondBankNumber || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Input disabled={fields.partySecondBankNumber===null?true:false} placeholder="请去供应商库完善信息" onBlur={this.reSave}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="甲方开票信息" className="w">
									{getFieldDecorator('partyFirstCompanyId', {
										initialValue: fields.partyFirstCompanyId || "",
										rules: [{required: true, message: '此项是必填'}]
									})(
										<Select style={{width: '100%'}} placeholder="请选择" onBlur={this.reSave}>
											{
												mobx.toJS(companyList).map((item) => {
													return <Option value={item.id}>{item.simpleName}</Option>
												})
											}
										</Select>
									)}
								</Form.Item>
							</Col>
						</Row>
						<h1 style={{padding: "10px 25px", fontWeight: 700, fontSize: "14px"}}>合同清单</h1>
						<div style={{ padding: "0 25px"}}>
							<EditableTableInventory
								fields={fields}
								disabled={false}
								dataSource={fields.supplierContractList==null?[]:fields.supplierContractList}
								store={this.store}
								updateState={this.updateState.bind(this, "supplierContractList")}
							/>
						</div>
						<div style={{margin: "15px 0", padding: "10px 25px"}}>
							<Button type="primary" onClick={this.buildWord}>生成采购合同</Button>
							{isDocument?<Document
								data={fields.document}
								store={this.store}
							/>:null}
						</div>
						<div style={{padding: "10px 25px"}}><h1 style={{
							display: "inline-block",
							fontSize: "14px",
							fontWeight: "700"
						}}>附件</h1>（请按照要求上传附件内容，标星为必须上传项，可自由添加附件项目中不含的相关内容）
						</div>
						<div style={{padding: "10px 25px"}}>
							<EditableTableUpload
								id={'id'}
								store={this.store}
								dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
								saveData={this.saveContents}
								saveItem={this.saveItem}
								disabled={false}
								pid={''}
							/>
						</div>
					</Form>
				</Spin>
			</div>
		)
	}
}



