/*
* Table组件
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	Table,
	Input,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	Select,
	message,
	InputNumber,
	notification,
	Popover,
	Switch
} from 'antd';
import moment from 'moment';
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';
import PersonAdd2 from "@/components/delivery/PersonAdd2";
import ProgressTarget from "./ProgressTarget";
import ProgressPromise from "./ProgressPromise";
import QualityTarget from "./QualityTarget";
import QualityPromise from "./QualityPromise";
import ProfitTarget from "./ProfitTarget";
import Cost from "./Cost";
import Marketing from "./Marketing";
import SafetyTarget from "./SafetyTarget";
import SafetyPromise from "./SafetyPromise";

const { TextArea } = Input;

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/*自定义编辑组件*/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	showCurrentCell=(keyIndex)=>{
		if(keyIndex===1){
			return false
		}
	}
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			//this.toggleEdit();
			delete values["beginTime"];
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	};
	//日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			handleSave({...record, ...values});
		});
	}
	updatePerson = (users) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (users.length) {
			userIds = users
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		// if(users.length>0){
		// 	data["masterName"] = userIds[0]['userName'];
		// 	data["masterId"] = userIds[0]['userId'];
		// }
		handleSave({...record, ...data});
	}

	updateData = (key, value)=>{
		console.log(key,value)
	}

	handleProgressTarget = (key, value)=>{
		console.log('progressTarget'+key,value)
		this.props.handleMainCell(key, value)
	}

	handleProgressPromise = (key, value)=>{
		console.log('promiseTarget'+key,value)
		this.props.handleMainCell(key, value)
	}

	handleQualityTarget =(key, value)=>{
		console.log('qualityTarget'+key,value)
		this.props.handleMainCell(key, value)
	}

	handleQualityPromise =(key, value)=>{
		console.log('qualityPromise'+key,value)
		this.props.handleMainCell(key, value)
	}

	handleProfit =(key,value)=>{
		this.props.handleMainCell(key, value)
	}

	handleCost =(key,value)=>{
		this.props.handleMainCell(key, value)
	}

	handleMarketing =(key,value)=>{
		this.props.handleMainCell(key, value)
	}

	handleSafetyTarget =(key, value)=>{
		console.log('safeTarget'+key,value)
		this.props.handleMainCell(key, value)
	}

	handleSafetyPromise =(key, value)=>{
		console.log('safePromise'+key,value)
		this.props.handleMainCell(key, value)
	}


	onBlur = () => {
		//this.toggleEdit();
	}
	handleRemoveItem=(id)=>{
		const { handleDelete } = this.props;
		handleDelete(id)
	}

	//设置开始日期不能选择的时间
	disabledStartDate = startValue => {
		const {record} = this.props;
		if (!startValue || !record.endTime) {
			return false
		}
		return startValue.valueOf() > record.endTime.valueOf()
	}
	//设置结束不能选择的时间
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() <= record.beginTime.valueOf()
	}
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,  handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'name') {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
						record['key']>=2?<div className="w clearfix">
							<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
								<Icon
									style={{
										width: '35px',
										color: '#F17E7E',
										fontSize: '20px',
										float: 'left'
									}}
									type="minus-circle"/>
							</Popconfirm>
							<Input
								style={{
									float: 'right',
									width: 'calc(100% - 35px)',
								}}
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								//onChange={this.save}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						</div>:<div className="w clearfix">
							<Input
								style={{
									float: 'right',
									width: 'calc(100% - 35px)',
								}}
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								//onChange={this.save}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div className="w clearfix">
					<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
								width: '35px',
								color: '#F17E7E',
								fontSize: '22px',
								float: 'left'
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							float: 'right',
							width: 'calc(100% - 35px)',
							minHeight: '31px'
						}}
						onClick={this.toggleEdit}
					>
						{children}
					</div>
				</div>
			);
		}else if (dataIndex == 'caozuo'){
			return record['dataType']==='item'?  (
				<Popconfirm title="确定要删除吗?" onConfirm={()=>{this.handleRemoveItem(`${record.id}`)}}>
					<img  style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" />
				</Popconfirm>
			):(<span></span>);
		}else if (dataIndex == 'item'){ // 规模
			return editing && record['dataType']==='item' ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea
							rows={3}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					style={{marginTop:"10px"}}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		} else if (dataIndex == 'targetValue'){ // 规模
			return editing && record['dataType']==='main'? (<Form.Item style={{margin: 0, width:'100%'}} className="w">
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: {}
				})(record['keyIndex']===1?<ProgressTarget updateData={this.handleProgressTarget} />:
					record['keyIndex']===2?<QualityTarget updateData={this.handleQualityTarget}  label='验收次数' />:
						record['keyIndex']===3?<ProfitTarget updateData={this.handleProfit}  />:
							record['keyIndex']===4?<Cost updateData={this.handleCost} label='给甲方付款后' />:
								record['keyIndex']===5?<Marketing updateData={this.handleMarketing} label='' />:
									record['keyIndex']===6?<SafetyTarget updateData={this.handleSafetyTarget} label='安全事故' label2='安全隐患' tipInfo='死亡、重伤、轻伤' tipInfo2='重大事故隐患' />:<span></span>

				)}
			</Form.Item>):(<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea
							rows={3}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			)
		}else if (dataIndex == 'promiseValue'){ // 规模
			return editing && record['dataType']==='main'? (<Form.Item style={{margin: 0, width:'100%'}} className="w">
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: {}
				})(
					record['keyIndex']===1?<ProgressPromise updateData={this.handleProgressPromise} />:
						record['keyIndex']===2?<QualityPromise updateData={this.handleQualityPromise} label='质量事故' />:
							record['keyIndex']===3?<span></span>:
								record['keyIndex']===4?<span></span>:
									record['keyIndex']===5?<span></span>:
										record['keyIndex']===6?<SafetyPromise updateData={this.handleSafetyPromise} label='安全事故' label2='安全隐患' tipInfo='死亡、重伤、轻伤' tipInfo2='重大事故隐患' />:<span></span>
				)}
			</Form.Item>):(<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<TextArea
							rows={3}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			)
		}else{ // 地点
			return (
				record[dataIndex]
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* 编辑table
* */
@observer
class EditableTableTwoItem extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 任务数据源
			count: 0,        // table的key
			primaryData: {
				"id": "",
				"item": "",
				"targetValue": "",
				"promiseValue": "",
			}, // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
			activeObj: {}, // 记录拆解前主任务数据
			activeSub: [], // 记录拆解前子任务数据
			subTaskDis: false, // 控制子任务的编辑状态
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource!==undefined && nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item==undefined?{}:item, {key: index})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"id": "",
					"item": "",
					"targetValue": "",
					"promiseValue": "",
				}) // 新增任务数据
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource ===undefined || this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource ===undefined ||  this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"id": "",
				"item": "",
				"targetValue": "",
				"promiseValue": "",
			}), // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
		});
	}

	componentWillUnMount() {

	}


	// 删除
	handleDelete = (id) => {
		const {taskDetail} = this.store;
		this.store.delBuildTeamTwoItem({"id":id}).then((res)=>{
			if(res){
				message.success("删除成功");
				this.store.getBuildTeamTwoItemList({"taskId":taskDetail.id}).then((res)=>{
					this.props.saveData(res)
				})
			}else{
				message.error("删除失败")
			}
		})
	};

	// 新增
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		const newData = Object.assign(primaryData, {key: count, id:"!"+uuidv4()});
		// const newData = Object.assign(primaryData, {key: count});
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			// this.props.saveData(this.state.dataSource);
		});
		const {id, ...newItem} = newData;
		this.props.saveItem(newItem);
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 更新保存数据
			// this.props.saveData(this.state.dataSource);
		});
		this.props.saveItem(newData[index]);
	};

	handleMainCell= (key, value) =>{
		let newObj = {}
		newObj[key] = value;
		const { buildTeamTwoMainObj } = this.store;
		this.store.saveBuildTeamTwoMain({"bttm":Object.assign(mobx.toJS(buildTeamTwoMainObj), newObj)}).then((res)=>{
			this.store.buildTeamTwoMainObj = res;
		})
	}

	// 折叠效果
	saveExpandedRows = (key) => {
		let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
		_this.setState({
			expandedKeys: keys
		}, () => {
			let obj = {[_this.props.id]: keys};
			let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
			_this.store.expandedKeys = Object.assign(expandedKeys, obj);
		});
	}

	// 查看
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 不可编辑
		}, () => {
			this.saveExpandedRows(record);
		});
	}


	// 取消保存
	onCalac = (key) => {
		this.saveExpandedRows(key);
		this.store.isEdit = false;
		// 更新tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
		this.subtask.clearState();
		this.setState({
			activeObj: Object.assign({})
		});
	}


	// 点击
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children == null ? [] : record.children.filter((item) => {
			return item.treeType === 'subtask'
		})
		const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
		if (disabled && subNumber > 0) {  // 详情态
			this.handleView(record.id)
		}
	}

	render() {
		const {dataSource, expandedKeys, subTaskDis} = this.state;
		const {disabled, tableType, selectedRows} = this.props;
		const isChaijie = expandedKeys.length > 0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		let columnEle = [
			{
				title: '序号', dataIndex: 'keyIndex', align: 'left',width:'5%', render:(text, record)=>{
					return(
						<div style={{marginTop:'10px'}}>{text}</div>
					)
				}
			},
			{
				title: '目标项',
				dataIndex: 'item',
				width:'10%',
				editable: !disabled,
				align: "left",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}
					</div>);
				}
			},
			{
				title: '内控目标值',
				dataIndex: 'targetValue',
				width:'45%',
				editable: !disabled && !isChaijie,
				align: "left",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%'}}
						className="pr">
						{Array.isArray(record.targetValue)?record.targetValue.map((item, index)=>{
							return (
								<div style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}>{item}</div>
							)
						}):text}
					</div>);
				}
			},
			{
				title: '对外承诺目标值',
				dataIndex: 'promiseValue',
				width:'45%',
				align: "left",
				editable: !disabled && !isChaijie,
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%'}}
						className="pr">
						{Array.isArray(record.promiseValue)?record.promiseValue.map((item, index)=>{
							return (
								<div style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}>{item}</div>
							)
						}):text}
					</div>);
				}
			},

		];
		if(!disabled){
			columnEle = [{
				title: '操作', dataIndex: 'caozuo', width:'5%', align: 'left', editable: !disabled,
				render: (text, record) => {
					return (<img style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" />);
				}
			}].concat(columnEle)
		}
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
					handleMainCell: this.handleMainCell
				}),
			};
		});

		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					rowKey={row => row.id}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTableTwoItem;




