/*
* 详情页
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Button} from 'antd';
import { Table } from 'antd';
import Document from "@/components/supplier/Document";
import moment from "moment/moment";
import Zmage from 'react-zmage';
import SendEmail from './SendEmail';

const dateFormat = 'YYYY-MM-DD';

@inject('DeliveryStore')
@observer
class View extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
            makePlotInfo: {},  // 工程概况
            buildTeamTwoMain: {}, //
            makePlotCost: {},
            makePlotPeople: [],
            makePlotMachine: [],
            makePlotMaterial: [],
            makePlotQuality: [],
            makePlotRisk: [],
            makePlotSafe: [],
            obj:{}
        }
	}

    componentDidMount() {
        this.getMakePlot();
    }

    getMakePlot = () => {
        const projectId =this.store.projectId;
        this.store.getMakePlot({'projectId':projectId}).then((mres)=>{
            if(mres) {
                let res = JSON.parse(JSON.stringify(mres));
                this.setState({
                    obj: mres,
                    makePlotInfo: res.makePlotInfo,
                    buildTeamTwoMain: res.buildTeamTwoMain,
                    makePlotCost: res.makePlotCost,
                    makePlotPeople: Array.isArray(res.makePlotPeople) ? res.makePlotPeople.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotMachine: Array.isArray(res.makePlotMachine) ? res.makePlotMachine.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotMaterial: Array.isArray(res.makePlotMaterial) ? res.makePlotMaterial.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotQuality: Array.isArray(res.makePlotQuality) ? res.makePlotQuality.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotRisk: Array.isArray(res.makePlotRisk) ? res.makePlotRisk.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                    makePlotSafe: Array.isArray(res.makePlotSafe) ? res.makePlotSafe.map((item, index) => {
                        return Object.assign(item, {key: index + 1})
                    }) : [],
                });
            }
        });
    }

	render() {
        const {
            makePlotInfo,
            buildTeamTwoMain,
            makePlotCost,
            makePlotPeople,
            makePlotMachine,
            makePlotMaterial,
            makePlotQuality,
            makePlotRisk,
            makePlotSafe,
            obj
        } = this.state;
        const columns1 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center'
            },
            {
                title: '目标项',
                dataIndex: 'age',
                key: 'age',
                align: 'center'
            },
            {
                title: '内控目标值',
                dataIndex: 'address',
                key: 'address',
                align: 'left',
                render: (text, record)=> {
                    if (record.key==1) {
                        const progressValueV4 = (day1, day2, day3) =>{
                            return [
                                <p>1、标箱进场时间：{day1}</p>,
                                <p>2、非标箱进场时间：{day2}</p>,
                                <p>3、装配完成时间：{day3}</p>
                            ]
                        }
                        let targetDay1 = buildTeamTwoMain.progressTargetTimeSure===1? moment(buildTeamTwoMain.progressTargetBaseTime).format(dateFormat):"T";
                        let targetDay2 = targetDay1==="T"?"T+"+buildTeamTwoMain.progressTargetNostandardDay+"日历天":moment(targetDay1).add(buildTeamTwoMain.progressTargetNostandardDay,"days").format(dateFormat);
                        let targetDay3 = targetDay1==="T"?"T+"+buildTeamTwoMain.progressTargetOverDay+"日历天":moment(targetDay1).add(buildTeamTwoMain.progressTargetOverDay,"days").format(dateFormat)
                        return (<div>
                            {progressValueV4(targetDay1,targetDay2,targetDay3)}
                        </div>)
                    } else if (record.key==2) {
                        return (<div>
                            <p>验收次数： ≤ {buildTeamTwoMain.qualityTargetCheckCount} 次 客户确认签字</p>
                        </div>)
                    } else if (record.key==3) {
                        return (<div>
                            <p>盈利：{buildTeamTwoMain.profitTargetRate}%</p>
                        </div>)
                    } else if (record.key==4) {
                        return (<div>
                            {buildTeamTwoMain.markingTargetStandard==1? <p>打造成为标杆项目，形成营销案例</p>:null}
                            {buildTeamTwoMain.markingTargetSatisfied==1? <p>顾客满意，能够给我们发表扬信</p>:null}
                        </div>)
                    } else if (record.key==5) {
                        return (<div>
                            <p>安全事故： ≤ {buildTeamTwoMain.safeTargetAccidentCount} 次 死亡、重伤、轻伤</p>
                            <p>安全隐患： ≤ {buildTeamTwoMain.safeTargetTreacherousCount} 次 重大事故隐患</p>
                        </div>)
                    }
                }
            },
        ];

        const dataSource1 = [
            {
                key: '1',
                age: '进度',
                address: '西湖区湖底公园1号',
            },
            {
                key: '2',
                age: '质量',
                address: '西湖区湖底公园1号',
            },
            {
                key: '3',
                age: '利润',
                address: '西湖区湖底公园1号',
            },
            {
                key: '4',
                age: '营销',
                address: '西湖区湖底公园1号',
            },
            {
                key: '5',
                age: '安全',
                address: '西湖区湖底公园1号',
            }
        ];

        const columns2 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "分项",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "人员数量",
                dataIndex: 'count',
                key: 'count',
                align: 'center',
                className:'vam',
            },
            {
                title: "进场时间",
                dataIndex: 'enterTime',
                key: 'enterTime',
                align: 'center',
                className:'vam',
                render: (text, record)=> {
                    return text==0||text==null?"":moment(text).format('YYYY-MM-DD')
                }
            },
            {
                title: "备注",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const columns3 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "设备名称",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "规格型号",
                dataIndex: 'specs',
                key: 'specs',
                align: 'center',
                className:'vam',
            },
            {
                title: "设备数量（台）",
                dataIndex: 'specs',
                key: 'specs',
                align: 'center',
                className:'vam',
            },
            {
                title: "备注",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const columns4 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "材料名称",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "进场日期",
                dataIndex: 'enterBeginTime',
                key: 'enterBeginTime',
                align: 'center',
                className:'vam',
                render: (text, record)=> {
                    return text==0||text==null?"":moment(record["enterBeginTime"]).format('YYYY-MM-DD') +"至"+moment(record["enterEndTime"]).format('YYYY-MM-DD')
                }
            },
            {
                title: "备注",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];
        const columns5 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "质量技术交底内容",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "交底人",
                dataIndex: 'buildTeamName',
                key: 'buildTeamName',
                align: 'center',
                className:'vam',
            },
            {
                title: "备注",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];
        const columns6 = [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "安全生产教育交底内容",
                dataIndex: 'name',
                key: 'name',
                align: 'center',
                editable: true,
                className:'vam',
            },
            {
                title: "交底人",
                dataIndex: 'buildTeamName',
                key: 'buildTeamName',
                align: 'center',
                className:'vam',
            },
            {
                title: "备注",
                dataIndex: 'remark',
                key: 'remark',
                align: 'left',
                width:"30%",
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }

            }];
        const columns7= [
            {
                title: '序号',
                dataIndex: 'key',
                key: 'key',
                align: 'center',
                className:'vam',
            },
            {
                title: "风险类型",
                dataIndex: 'type',
                key: 'type',
                align: 'center',
                className:'vam',
                editable: true,
                render: (text, record) => {
                    return (<div
                        style={{verticalAlign:"middle" }}>
                        {text}
                    </div>);
                }
            },
            {
                title: "风险描述",
                dataIndex: 'describes',
                key: 'describes',
                align: 'left',
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%', lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            },
            {
                title: "应对方案",
                dataIndex: 'programme',
                key: 'programme',
                align: 'left',
                render: (text, record) => {
                    return (<div
                        id={`${record.id}`}
                        style={{width: '100%',  lineHeight: '150%',  wordBreak:'break-all', whiteSpace:'pre-wrap' }}
                        className="pr">
                        {text}
                    </div>);
                }
            }];

        const state1 = {0: "", 1: "未签订", 2: "已签订"};
        const state2 = {0: "", 1: "未发生", 2: "已发生"};

        let isDocument = makePlotInfo.document==null || (typeof makePlotInfo.document == "object"&&Object.keys(makePlotInfo.document).length==0)?false:true;  // 是否有采购合同


        return (
			<div style={{padding: "0 15px"}}>
                <Button
                    ghost
                    type="primary"
                    icon="poweroff"
                    onClick={this.props.reStartHandover}
                >
                    重新启动
                </Button>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>一、工程概况</h1>
                <table className="suppliertable" style={{width: '100%'}}>
                    <tr>
                        <td>工程名称</td>
                        <td>
                            {makePlotInfo.name}
                        </td>
                        <td>工程地点</td>
                        <td>
                            {makePlotInfo.address}
                        </td>
                    </tr>
                    <tr>
                        <td>建设单位</td>
                        <td>
                            {makePlotInfo.partyUnits}
                        </td>
                        <td>施工单位</td>
                        <td>
                            {makePlotInfo.buildUnits}
                        </td>
                    </tr>
                    <tr>
                        <td>主要供应商</td>
                        <td>
                            {makePlotInfo.supplier==null?"":makePlotInfo.supplier.map((item)=>{
                                return item.name
                            }).join("，")}
                        </td>
                        <td>工程类型</td>
                        <td>
                            {makePlotInfo.projectConstructionType}
                        </td>
                    </tr>
                    <tr>
                        <td>建筑面积（m2）</td>
                        <td>
                            {makePlotInfo.buildArea}
                        </td>
                        <td>房间组成（间）</td>
                        <td>
                            {makePlotInfo.roomComposition}
                        </td>
                    </tr>
                    <tr>
                        <td>进场条件</td>
                        <td>
                            {makePlotInfo.enterCondition}
                        </td>
                        <td>承接内容</td>
                        <td>
                            {makePlotInfo.productType}
                        </td>
                    </tr>
                </table>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>二、管理目标</h1>
                <Table
                    className="digital_table agenda_nohover"
                    pagination={false}
                    bordered
                    dataSource={dataSource1}
                    columns={columns1} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>三、组织架构</h1>
                <div className="w" style={{textAlign: "center", padding: "15px 0"}}>
                    <Zmage
                        style={{width: "80%",
                            height: "350px"}}
                        src={makePlotInfo.orgImageUrl} />
                </div>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotInfo.organizationRemark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>四、成本策划</h1>
                <table className="makeplotTable">
                    <tr>
                        <th>序号</th>
                        <th>分项</th>
                        <th>内容</th>
                        <th>状态</th>
                        <th>金额（元）</th>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>
                            甲方合同
                        </td>
                        <td>{makePlotInfo.partyUnit}</td>
                        <td>
                            {state1[makePlotCost.firstContractState]}
                        </td>
                        <td>{makePlotCost.firstContractValue}</td>
                    </tr>
                    {makePlotCost&&makePlotCost.confirmSupplierTwoItems&&makePlotCost.confirmSupplierTwoItems.map((item, index)=>{
                        return <tr key={item.id}>
                            <td>{index+2}</td>
                            <td>
                                分包合同
                            </td>
                            <td>{item.supplierName}</td>
                            <td>
                                {state1[item.isActive]}
                            </td>
                            <td>{item.contractMoney}</td>
                        </tr>
                    })}
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+2)}</td>
                        <td>
                            文明施工
                        </td>
                        <td>五牌一图、马甲、警示标语、警示带、工人慰问等</td>
                        <td>
                            {state2[makePlotCost.civilizedConstructionState]}
                        </td>
                        <td>
                            {makePlotCost.civilizedConstructionValue}</td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+3)}</td>
                        <td>
                            招待费
                        </td>
                        <td></td>
                        <td>
                            {state2[makePlotCost.hospitalityState]}
                        </td>
                        <td>{makePlotCost.hospitalityValue}</td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+4)}</td>
                        <td>
                            风险附加
                        </td>
                        <td></td>
                        <td>
                            {state2[makePlotCost.additionalRiskState]}
                        </td>
                        <td>
                            {makePlotCost.additionalRiskValue}
                        </td>
                    </tr>
                    <tr>
                        <td>{makePlotCost&&makePlotCost.confirmSupplierTwoItems&&(makePlotCost.confirmSupplierTwoItems.length+5)}</td>
                        <td>
                            毛利润
                        </td>
                        <td></td>
                        <td>
                        </td>
                        <td>{makePlotCost.yields}</td>
                    </tr>
                </table>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotCost.remark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}> 五、进度策划</h1>
                <p className="mt15 mb15">1、工期控制目标</p>
                <table className="suppliertable" style={{width: '100%'}}>
                    <tr>
                        <td>甲方合同工期</td>
                        <td>
                            {makePlotCost.firstPartyTimeRange}
                        </td>
                        <td>供应商合同工期</td>
                        <td>
                            {makePlotCost.supplierTimeRange}
                        </td>
                    </tr>
                </table>
               <p className="mt15 mb15">2、工人进场计划</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotPeople}
                    columns={columns2} />
                <p className="mt15 mb15">3、机械设备需求计划</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotMachine}
                    columns={columns3} />
                <p className="mt15 mb15">4、主要材料需求计划</p>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotMaterial}
                    columns={columns4} />
                <p className="mt15 mb15">5、施工进度计划图</p>
                <div className="w" style={{textAlign: "center", padding: "15px 0"}}>
                    <Zmage
                        style={{
                            maxWidth: "75%",
                            height: "auto"
                        }}
                        src={makePlotInfo.planImageUrl} />
                </div>
                <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                     dangerouslySetInnerHTML={{__html: makePlotInfo.planImageRemark}}></div>
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>六、质量策划</h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotQuality}
                    columns={columns5} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>七、安全文明施工策划</h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotSafe}
                    columns={columns6} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>八、重点与难点分析</h1>
                <Table
                    pagination={false}
                    bordered
                    dataSource={makePlotRisk}
                    columns={columns7} />
                <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>生成的方案策划书</h1>
               {isDocument?<Document
                    data={makePlotInfo.document}
                    store={this.store}
                />:null}
                <SendEmail
                    obj={obj}
                    store={this.store}
                />
			</div>
		)
	}
}


export default View;



