/*
*交付管理-组建项目团队
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal, Select} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';

const {Option} = Select;

@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class BaseInfoTempItemThree extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
			summary:BraftEditor.createEditorState(null),
			overallDescription:BraftEditor.createEditorState(null),
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {
		this.props.onRef && this.props.onRef(this);
	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressTargetTimeSure",e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	handleBaseTime=(date, datestring)=>{
		this.props.updateData("progressTargetBaseTime",date.valueOf())
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	handleUpdate2=(type, e)=>{

		this.props.updateHandOverDate(type,e)
	}

	handleBChange = (type, bfData) => {
		// console.log(type, BraftEditor.createEditorState(bfData).toHTML())
		this.props.updateHandOverBC(type,BraftEditor.createEditorState(bfData).toHTML())
	}
	handleOnChange=(type, e)=>{
		console.log(type,e)
		this.props.updateHandOver(type,e)
	}

	checkFormValidate(){
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values);
				this.props.checkFormRequre(true)
			}else{
				this.props.checkFormRequre(false)
			}
		});
	}

	render() {
		const { sureState } = this.state;
		const { handoverThreeObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverThreeObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 4},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 20},
				md: {span: 20},
				sm: {span: 20},
			},
		};
		const controls = ['bold',  'text-color',  ]
		return (
			<div className="mt15">
				<Form  {...formItemLayout} >
					{handoverState.state3===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<table className="suppliertable" style={{width: "100%"}}>
									<tr>
										<td>临建产品类型</td>
										<td>
											{fields.productType.join("，")}
										</td>
										<td>价格敏感度</td>
										<td>
											{fields.priceSensitivity}
										</td>
									</tr>
									<tr>
										<td>预算区间（元）</td>
										<td>
											{fields.budget}
										</td>
										<td>箱体数量</td>
										<td>
											{fields.boxCount}
										</td>
									</tr>
									<tr>
										<td>建筑面积(m2)</td>
										<td>
											{fields.areaRage}
										</td>
										<td>管理人员数量（人）</td>
										<td>
											{fields.managerCount}
										</td>
									</tr>
									<tr>
										<td>期望设计完成时间</td>
										<td>
											{fields.expectedDesignTime===0?'':moment(fields.expectedDesignTime).format('YYYY-MM-DD')}
										</td>
										<td>劳务人员数量（人）</td>
										<td>
											{fields.workerCount}
										</td>
									</tr>
									<tr>
										<td>计划进场时间</td>
										<td>
											{fields.plannedArrivalTime===0?'':moment(fields.plannedArrivalTime).format('YYYY-MM-DD')}
										</td>
										<td>基础是否具备条件</td>
										<td>
											{fields.basicCondition}
										</td>
									</tr>
									<tr>
										<td>用电是否具备条件</td>
										<td colSpan={3}>
											{fields.powerCondition===0?'否':'是'}
										</td>
									</tr>
									<tr>
										<td>备注（注意要点）</td>
										<td colSpan={3}>
											<div style={{width: '100%', wordBreak:'break-all', whiteSpace:'pre-wrap', padding:"5px" }}>
												{
													fields.remark!==""?(<div dangerouslySetInnerHTML={{__html: fields.remark}} />):""
												}
											</div>
										</td>
									</tr>
								</table>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<Form.Item  label="临建产品类型" className="w">
										{getFieldDecorator('productType', {
										initialValue: fields.productType == null ? undefined : fields.productType,
										rules: [{required: true, message: '此项是必填'}],
									})(
											<Select mode="multiple"  placeholder={'请选择'}  style={{ width: 600 }} onChange={(e)=>{this.handleUpdate2("productType", e)}}>
												<Option value="办公楼">办公楼</Option>
												<Option value="食堂">食堂</Option>
												<Option value="管理人员宿舍">管理人员宿舍</Option>
												<Option value="会议室">会议室</Option>
												<Option value="展厅">展厅</Option>
												<Option value="公共卫浴间">公共卫浴间</Option>
												<Option value="劳务人员宿舍">劳务人员宿舍</Option>
												<Option value="劳务仓">劳务仓</Option>
												<Option value="含幕墙">含幕墙</Option>
												<Option value="含屋顶">含屋顶</Option>
												<Option value="便利店">便利店</Option>
											</Select>
									)}
								</Form.Item>
								<Form.Item label="价格敏感度" className="w">
									{getFieldDecorator('priceSensitivity', {
										initialValue: fields.priceSensitivity,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleOnChange('priceSensitivity', e)}}>
											<Radio value={"有付费能力，追求品质"}>有付费能力，追求品质</Radio>
											<Radio value={"普通标准，价格适中"}>普通标准，价格适中</Radio>
											<Radio value={"预算有限，满足刚需"}>预算有限，满足刚需</Radio>
										</Radio.Group>
									)}
								</Form.Item>
								<Form.Item  label="预算区间（元）" className="w">
										{getFieldDecorator('budget', {
										initialValue: fields.budget == null ? "" : fields.budget,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("budget", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="箱体数量" className="w">
									{getFieldDecorator('boxCount', {
											initialValue: fields.boxCount == null ? null : fields.boxCount,
											rules: [{
												pattern: /^[0-9]\d*$/,
												required: true,
												message: '只能输入大于等于0的整数'
											}]
										}
									)(
										<InputNumber ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("boxCount", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="建筑面积（m2）" className="w">
									{getFieldDecorator('areaRage', {
										initialValue: fields.areaRage == null ? "" : fields.areaRage,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("areaRage", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="管理人员数量（人）" className="w">
										{getFieldDecorator('managerCount', {
										initialValue: fields.managerCount == null ? "" : fields.managerCount,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("managerCount", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="劳务人员数量（人）" className="w">
										{getFieldDecorator('workerCount', {
										initialValue: fields.workerCount == null ? "" : fields.workerCount,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("workerCount", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="期望设计完成时间" className="w">
										{getFieldDecorator('expectedDesignTime', {
											initialValue: fields.expectedDesignTime == null || fields.expectedDesignTime == 0 ? "" : moment(moment(fields.expectedDesignTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}],
									})(
											<DatePicker
												className="w"
												style={{width:'600px'}}
												placeholder="请选择"
												format="YYYY-MM-DD"
												onChange={(e)=>{this.handleUpdate2("expectedDesignTime", moment(e).startOf('day').valueOf())}}
											/>
										// <Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("expectedDesignTime", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="计划进场时间" className="w">
										{getFieldDecorator('plannedArrivalTime', {
											initialValue: fields.plannedArrivalTime == null || fields.plannedArrivalTime == 0 ? "" : moment(moment(fields.plannedArrivalTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}],
									})(
											<DatePicker
												className="w"
												style={{width:'600px'}}
												placeholder="请选择"
												format="YYYY-MM-DD"
												onChange={(e)=>{this.handleUpdate2("plannedArrivalTime",moment(e).startOf('day').valueOf())}}
											/>
										// <Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("plannedArrivalTime", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item label="基础是否具备条件" className="w">
									{getFieldDecorator('basicCondition', {
										initialValue: fields.basicCondition,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleUpdate("basicCondition", e)}}>
											<Radio value={"已具备"}>已具备</Radio>
											<Radio value={"进场前可具备"}>进场前可具备</Radio>
											<Radio value={"不确定"}>不确定</Radio>
										</Radio.Group>
									)}
								</Form.Item>
								<Form.Item label="用电是否具备条件" className="w">
									{getFieldDecorator('powerCondition', {
										initialValue: fields.powerCondition,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group onChange={(e)=>{this.handleUpdate("powerCondition", e)}}>
											<Radio value={"已具备"}>已具备</Radio>
											<Radio value={"进场前可具备"}>进场前可具备</Radio>
											<Radio value={"不确定"}>不确定</Radio>
											<Radio value={"不具备"}>不具备</Radio>
										</Radio.Group>
									)}
								</Form.Item>

								<Form.Item label="备注（注意要点）" className="w">
									{getFieldDecorator('remark', {
										initialValue: BraftEditor.createEditorState(fields.remark),
										rules: [{required: false, message: '此项是必填'}],
									})(
										<BraftEditor
											className="my-editor"
											onBlur={this.handleBChange.bind(this, 'remark')}
											controls={controls}
											contentStyle={{
												minHeight: 100,
												maxHeight: 100,
												overflowY: 'scroll',
												height: 'auto',
												width:'100%'
											}}
										/>
									)}
								</Form.Item>
							</Col>
						</Row>
					}
				</Form>
			</div>
		)
	}
}






