import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';

@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class MainProItemThree extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
			summary:BraftEditor.createEditorState(null),
			overallDescription:BraftEditor.createEditorState(null),
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressTargetTimeSure",e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	handleBaseTime=(date, datestring)=>{
		this.props.updateData("progressTargetBaseTime",date.valueOf())
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}


	handleBChange = (type, editorState) => {
		// console.log(editorState.toHTML())
		if(type==='summary'){
			this.setState({summary: editorState});
		}
		if(type==='overallDescription'){
			this.setState({overallDescription: editorState});
		}
		if(type==='assistance'){
			this.setState({assistance: editorState});
		}
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	render() {
		const { sureState } = this.state;
		const { handoverThreeObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverThreeObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 4},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 20},
				md: {span: 20},
				sm: {span: 20},
			},
		};
		const controls = ['bold',  'text-color',  ]
		return (
			<div className="mt15">
				<Form  {...formItemLayout} >
					{handoverState.state3===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<table className="suppliertable" style={{width: "100%"}}>
									<tr>
										<td>主体项目类型</td>
										<td>
											{fields.bodyType}
										</td>
										<td>开发商</td>
										<td>
											{fields.developers}
										</td>
									</tr>
									<tr>
										<td>申报奖项情况</td>
										<td colSpan={3}>
											{fields.prize}
										</td>
									</tr>
								</table>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<Form.Item  label="主体项目类型" className="w">
										{getFieldDecorator('bodyType', {
										initialValue: fields.bodyType == null ? "" : fields.bodyType,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("bodyType", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="开发商" className="w">
										{getFieldDecorator('developers', {
										initialValue: fields.developers == null ? "" : fields.developers,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("developers", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item  label="申报奖项情况" className="w">
										{getFieldDecorator('prize', {
										initialValue: fields.prize == null ? "" : fields.prize,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("prize", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
							</Col>
						</Row>
					}
				</Form>
			</div>
		)
	}
}






