import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {Row, Col, Button, message} from 'antd';
import BaseInfoTempItemThree from './BaseInfoTempItemThree';
import MainProItemThree from './MainProItemThree';
import EditableTableUpload from './EditableTableUpload';


@withRouter
@observer
class ThreeItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			visible: false,
			stateValue: 0,
			id: "",
			btThreeList:[],
			dataSource:[],
			bitFormRequire:false
		}
	}

	componentDidMount() {
		this.getHandoverThree();
		this.getEnclosureList();
		// this.BaseInfoTempItemThree.checkFormValidate();
	}


	saveContents=(dataSource)=>{
		console.log("dataSource=========>", dataSource);
		this.setState({btThreeList:dataSource})
	}

	savePolicy=(dataSource)=>{

	}

	saveDecisionItem=(row)=>{
		 const {taskDetail} = this.store;
		 const {btThreeList} = this.state;
		 let newItem={
		 	"buildTeamThree":taskDetail.id,
			 "orders":btThreeList.length+1,
			 "item":row.item,
			 "level":row.level
		 }
		 if(typeof(row.id)!=="undefined"){
			 newItem['id']=row.id
			 newItem['orders']= row.orders
		 }
		 this.saveBuildTeamThreeItem({"btti":newItem})
         // console.log('newItem',row)
	}

	getPolicyDecisonLevelByLevel=()=>{
		[1,2,3,4].forEach(level=>{
			this.getPolicyDecision(level);
		})
	}

	getPolicyDecision=(level)=>{
		const { pDataList } = this.state;
		const { taskDetail, buildTeamTwoMainObj } = this.store;
		this.store.getPolicyDecisionList({"taskId":taskDetail.id, "level":level}).then((res)=>{
			const newPDataList =  pDataList.map((item, index)=>{
				if((index+1)===level){
					return Object.assign(item,{"masters":res})
				}else{
					return item
				}

			})
			this.setState({pDataList:newPDataList})
		})
	}

	savePolicyDecisionByLevel=(row)=>{
		const { taskDetail } = this.store;
		console.log("row=>",row)
		const { masters } = row;
		const userInfos = masters.map((item, index)=>{
			return Object.assign({"id":item.userId, "userName":item.userName})
		})
		this.store.savePolicyDecisionByLevel({"userInfos":userInfos, "taskId":taskDetail.id, "level":row.keyIndex}).then((res)=>{

		})
	}

	getBuildTeamThreeItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamThreeItemList({"taskId": taskDetail.id}).then((res)=>{
			this.setState({btThreeList:res})
		})
	}

	saveBuildTeamThreeItem = (parmas)=>{
		this.store.saveBuildTeamThreeItem(parmas).then((res)=>{
			this.getBuildTeamThreeItemList();
		})
	}

	doneAndNext=()=>{
		if(!this.checkFormRequire())return
		const { currentStep } = this.props;
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	overCurrentStage=()=>{
		if(!this.checkFormRequire())return
		this.props.overCurrentStageTask();
		this.getHandoverThree();
		this.getEnclosureList();
	}

	checkFormRequire=()=>{
		const { handoverThreeObj } = this.store;
		const { dataSource } = this.state;
		const hto = mobx.toJS(handoverThreeObj);
		if(hto.productType.length===0 || hto.productType===null ||
			hto.priceSensitivity ===null || hto.priceSensitivity ==="" ||
			hto.cooperations ===null || hto.cooperations ==="" ||
			hto.managerCount ===null || hto.managerCount ==="" ||
			hto.workerCount ===null || hto.workerCount ==="" ||
			hto.expectedDesignTime ===null || hto.expectedDesignTime ===0 ||
			hto.plannedArrivalTime ===null || hto.plannedArrivalTime ===0 ||
			hto.basicCondition ===null || hto.basicCondition ==="" ||
			hto.powerCondition ===null || hto.powerCondition ===""||
			hto.areaRage ===null || hto.areaRage ===""||
			hto.boxCount ===null || hto.boxCount ===""
	){
			message.warn("请检查必填字段是否已经全部填写")
			return false
		}
		const dsCheck = dataSource.filter((item,index)=>{
			return item.isMust===1&&item.enclosureItems.length===0
		})
		if(dsCheck.length>0){
			message.warn("请检查附件列表中必需上传项是否已经上传附件")
			return false
		}
		return true
	}

	getEnclosureList=()=>{
		const { taskDetail, projectDetail } = this.store;
		this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, indexs:3}).then((res)=>{
			this.store.enclosureList = res
			this.setState({dataSource: res, enclosureList: res})

		})
	}

	saveHandoverThree=(newObj)=>{
		const { handoverThreeObj, taskDetail } = this.store;

		this.store.saveHandoverThree({"ho":Object.assign(mobx.toJS(handoverThreeObj),{"taskId": taskDetail.id}, newObj)}).then((res)=>{
			if(res){
				this.getHandoverThree()
			}
		})
	}

	getHandoverThree=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverThree({"taskId": taskDetail.id}).then((res)=>{
			this.store.handoverThreeObj =res
		})
	}

	updateHandOver=(type, e)=>{
		let newObj = {};
		if (type=="boxCount") {  // 箱体数量
			newObj[type] = e.target.value==null||e.target.value==""?0:Number(e.target.value);
		}
		else {
			newObj[type] = e.target.value;
		}
		this.saveHandoverThree(newObj)
	}

	updateHandOverDate=(type, date)=>{
		let newObj = {};
		newObj[type] = date
		this.saveHandoverThree(newObj)
	}
	updateHandOverBC=(type, bfData)=>{
		let newObj = {};
		newObj[type] = bfData
		this.saveHandoverThree(newObj)
	}
	saveContents=()=>{
		this.getEnclosureList();
	}

	saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:3,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:3}
		this.store.saveEnclosureMain(parmas).then((res)=>{
			this.getEnclosureList()
		})
	}

	checkFormRequreBit=(result)=>{
		this.setState({bitFormRequire:result})
	}

	render() {
		const { handoverState} = this.store;
		const { dataSource } = this.state;
		const { currentStep, switchSteps } = this.props;

		return (
			<div style={{height: 'auto'}}>
				<div >
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.props.reStartHandover}
						style={handoverState.state3===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						重新启动
					</Button>
				</div>
				<div className="daily_tips">
					<p><span>小交温馨提醒：技术交接是帮助项目经理的设计人员了解技术设计所需信息，请认真填写哦~</span></p>
				</div>
				<div className="mt30">
					<div className="require_label">临建基本信息:</div>
					<BaseInfoTempItemThree onRef={ node => this.BaseInfoTempItemThree = node } checkFormRequre={this.checkFormRequreBit} updateHandOver={this.updateHandOver} updateHandOverDate={this.updateHandOverDate} updateHandOverBC={this.updateHandOverBC}  store={this.store} />
				</div>
				<div className="mt30">
					<div className="require_label">主体项目信息:</div>
					<MainProItemThree updateHandOver={this.updateHandOver} store={this.store} />
				</div>
				<div className="mt30">
					<div className="require_label">附件:</div>
					<EditableTableUpload
						id={'id'}
						store={this.store}
						dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
						saveData={this.saveContents}
						saveItem={this.saveItem}
						disabled={handoverState.state3===2?true:false}
						pid={''}
					/>
				</div>
				<div  className="mt30 mb30" style={handoverState.state3!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"
					> 完成该阶段任务</Button>
				</div>
				<div  className="mt30 mb30" style={handoverState.state3===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep-1)}} style={{marginLeft:'20px'}} type='primary'>上一步</Button>
				</div>
			</div>
		)
	}
}


export default ThreeItem;



