import React from 'react';
import {inject, observer} from 'mobx-react';
import {Col, Row, Tooltip, message, Button, Popconfirm} from 'antd';
import * as mobx from "mobx";

//@inject('DeliveryStore')
@observer
class StakeholdeView extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {}
	}

	// 编辑
	editDaily = () => {
		this.store.stakeState = true;
		let fields = mobx.toJS(this.store.stakeObj) || {};
		const {communicatorId, communicatorName} = fields;

		if (communicatorName != "" && communicatorId != "") {
			let communicator = [{userId: communicatorId, userName: communicatorName}]
			this.store.stakeObj = Object.assign(fields, {
				communicator
			});
		}
	}

	// 删除
	delDaily = (id) => {
		this.store.delStakeholder({id: id})
		.then((res) => {
			if (res) {
				message.success("删除成功");
				this.props.getTable();
			}
		});
	}

	transLeft = () => {
		let fields = mobx.toJS(this.store.stakeObj) || {};
		const {
			phone = "",
			position = "",
			sex = "",
			age = "",
			appellation = "",
			email = "",
			maritalStatus = "",
			monthlyIncome = "",
            wechat = "",
		} = fields;
		let array = [
			{name: "手机", value: phone},
			{name: "职位", value: position},
			{name: "性别", value: sex},
			{name: "年龄", value: age},
			{name: "称谓", value: appellation},
			{name: "微信号", value: wechat},
			{name: "邮箱", value: email},
			{name: "婚姻状况", value: maritalStatus},
			{name: "月收入", value: monthlyIncome}
			];
		//拓展数组去空值的方法
		Array.prototype.removeEmpty = function () {
			for (var i = 0; i < this.length; i++) {
				if (this[i].value == "" || typeof (this[i].value) == "undefind") {
					this.splice(i, 1);
					i--;
				}
			}
			return this;
		}
		let arr2 = array.removeEmpty();
		return arr2;
	}

	transRight = () => {
		let fields = mobx.toJS(this.store.stakeObj) || {};
		const {
			hobby = "",
			profile = ""
		} = fields;
		let array = [{name: "兴趣爱好", value: hobby},{name: "个人简介", value: profile}];
		//拓展数组去空值的方法
		Array.prototype.removeEmpty = function () {
			for (var i = 0; i < this.length; i++) {
				if (this[i].value == "" || typeof (this[i].value) == "undefind") {
					this.splice(i, 1);
					i--;
				}
			}
			return this;
		}
		let arr2 = array.removeEmpty();
		return arr2;
	}

	render() {
		let fields = mobx.toJS(this.store.stakeObj) || {};

		return (
			<div>
				<div className="daily_view_head">
					<div className="title_info">
						<div className="daily_view_title">{fields.name} - {fields.copingStrategy}</div>
					</div>
					<div className="buttons_zone">
						<Button className="title" type="link" onClick={this.editDaily}><img
							src="./images/delivery/u1479.png" style={{width: '16px', height: '16px'}}/>编辑</Button>
						<span className="gap"> </span>
						<Popconfirm title="确定删除?" onConfirm={this.delDaily.bind(this, fields.id)}>
							<Button className="title" type="link"><img src="./images/delivery/u1480.png" style={{
								width: '16px',
								height: '16px'
							}}/>删除</Button>
						</Popconfirm>
					</div>
				</div>
				{/*详情*/}
				<Row style={{marginTop: "50px"}}>
					<Col span={15}>
						<div className="center-point">
							<div className="center-left">
								{this.transLeft().map((item, index)=>{
									return <div key={"left"+index}>
										<span className="center-circle">
											{item.name} : {item.value}
										</span>
									</div>
								})}
							</div>
							{fields.photo !== null && fields.photo !== "" ? <img src={fields.photo} style={{
									width: '172px',
									height: '166px',
									borderRadius: "50%"
								}}/> :
								<div style={{
									width: '172px',
									height: '166px',
									lineHeight: "166px",
									textAlign: "center",
									background: "#4AA9F8",
									color: "#FFF",
									fontSize: "16px",
									fontWeight: "700",
									borderRadius: "50%"
								}}>
									{fields.name.slice(-2)}
								</div>}
							<div className="center-right">
								{this.transRight().map((item, index)=>{
									return <div key={"right"+index}>
										<span className="center-circle">
											{item.name} : {item.value}
										</span>
									</div>
								})}
							</div>
						</div>
					</Col>
					<Col span={9}>
						<table style={{marginTop: "60px"}} className="staketable w">
							<tr style={{height: "50px", lineHeight: "50px"}}>
								<td style={{background: "#F6FCFE", fontWeight: "600", width: '150px'}}>主要对接人</td>
								<td>{fields.communicatorName}</td>
							</tr>
							<tr>
								<td style={{background: "#F6FCFE", fontWeight: "600", width: '150px', height: "100px", lineHeight: "100px"}}>沟通方式</td>
								<td>{fields.communicationMode != null && fields.communicationMode.map(item => {
									return <p key={item.id}>{item.rule} {item.frequency==null||item.frequency==""?null:" - "+item.frequency}</p>
								})}</td>
							</tr>
							<tr>
								<td style={{background: "#F6FCFE", fontWeight: "600", width: '150px', height: "150px", lineHeight: "150px"}}>备注</td>
								<td>{fields.remark}</td>
							</tr>
						</table>
					</Col>
				</Row>
			</div>
		)
	}
}


export default StakeholdeView;



