import React from 'react';
import {observer, inject} from 'mobx-react';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {
	Form,
	Button,
	Select,
	Table,
	Input,
	message,
	Icon,
	Popconfirm, Spin,
} from 'antd';
import SupplierSelect from './SupplierSelectThree';
import {downloadFile} from "@/utils/download";


const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = (type, e) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			//this.toggleEdit();
			handleSave({...record, ...{[type]: values[type]}});
		});
	};

	onChange = (value) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('sex')) {
				values["sex"] = value;
			}
			handleSave({...record, ...values});
		});
	}

	onSupplierChange = (param) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('supplierId')) {
				values["supplierName"] = param.name;
				values["supplierId"] = param.id;
			}
			handleSave({...record, ...values});
		});
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;

		if (dataIndex == "supplierId") {   // 供应商名称
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(
						<div ref={node => (this.input = node)}>
							<SupplierSelect
								onSupplierChange={this.onSupplierChange}
								value={record["supplierName"]}
								//onBlur={this.toggleEdit}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{record["supplierName"]}
				</div>
			);
		} else if (dataIndex == "sex") {   // 性别
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(
						<Select style={{width: 60}} ref={node => (this.input = node)} onChange={this.onChange}
						        //onBlur={this.toggleEdit}
						>
							<Option value="男">男</Option>
							<Option value="女">女</Option>
						</Select>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<Input ref={node => (this.input = node)} onPressEnter={this.save.bind(this, dataIndex)} onBlur={this.save.bind(this, dataIndex)}/>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}


@inject('DeliveryStore')
@withRouter
@observer
class ThreeItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			dataSource: [],
			count: 0,
			selectedRowKeys: [],
			downloading: false
		}
	}

	componentDidMount() {
		this.getConfirmSupplierOneItemList();
	}

	// 获取列表
	getConfirmSupplierOneItemList = () => {
		const {taskDetail} = this.store;
		this.store.getConfirmSupplierThreeItemList({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
			this.setState({
				dataSource: Array.isArray(res) ? res.map((item, index) => {
					return Object.assign(item, {key: index + 1})
				}) : [],
				count: Array.isArray(res) ? res.length : 0
			})
		});
	}

	// 新增
	handleAdd = () => {
		const {count, dataSource} = this.state;
		// 校验
		if (count > 0) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("姓名是必填项");
				return false;
			}
			if (param.idCard == null || param.idCard == "") {
				message.warn("身份证号是必填项");
				return false;
			}
			if (param.phone == null || param.phone == "") {
				message.warn("手机号是必填项");
				return false;
			}
			if (param.supplierName == null || param.supplierName == "") {
				message.warn("所属供应商是必填项");
				return false;
			}
		}

		const {taskDetail} = this.store;
		const newData = {
			id: "",
			taskId: mobx.toJS(taskDetail).id,
			name: "",
			supplierId: "",
			idCard: "",
			phone: "",
			sex: "",
			age: "",
			workType: "",
			supplierName: ""
		};
		// 单行保存
		this.store.saveConfirmSupplierThreeItem({csti: newData}).then((res) => {
			if (res && res.id) {
				this.setState({
					dataSource: [...dataSource, Object.assign(res, {key: count + 1})],
					count: count + 1
				});
			}
		});
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row
		});
		this.setState({dataSource: newData});

		const param = JSON.parse(JSON.stringify(row));
		delete param.key;

		// 单行保存
		this.store.saveConfirmSupplierThreeItem({csti: param});
	};

	// 删除
	handleDelete = record => {
		const dataSource = [...this.state.dataSource];
		let newData = dataSource.filter(item => item.key !== record.key).map((vitem, vindex) => Object.assign(vitem, {key: vindex + 1}))
		this.setState({
			dataSource: newData, record,
			count: newData.length
		});
		if (record.id != "" && record.id != null) {
			this.store.delConfirmSupplierThreeItem({id: record.id}).then((res) => {
				if (res) {
					message.success("删除成功");
				}
			});
		}
	};

	overCurrentStage = () => {
		const {count, dataSource} = this.state;
		if (count == 0) {
			message.warn("请完善必填信息");
			return false;
		}
		// 校验
		if (count > 0) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("姓名是必填项");
				return false;
			}
			if (param.idCard == null || param.idCard == "") {
				message.warn("身份证号是必填项");
				return false;
			}
			if (param.phone == null || param.phone == "") {
				message.warn("手机号是必填项");
				return false;
			}
			if (param.supplierName == null || param.supplierName == "") {
				message.warn("所属供应商是必填项");
				return false;
			}
		}
		this.props.overCurrentStageTask();
	}

	onSelectChange = selectedRowKeys => {
		this.setState({selectedRowKeys});
	};

	// 批量删除
	checkAllDel = () => {
		const {selectedRowKeys} = this.state;
		if (selectedRowKeys.length == 0) {
			message.warn("请先选择要删除的信息");
			return false;
		}
		this.store.delConfirmSupplierThreeItemList({ids: selectedRowKeys}).then((res) => {
			if (res) {
				message.success("删除成功");
				this.setState({
					selectedRowKeys: []
				});
				this.getConfirmSupplierOneItemList();
			}
		});
	}

	// 导出Excel
	buildExcel = () =>{
		const {count, dataSource} = this.state;
		const {supplierState} = this.store;

		if (count == 0&&mobx.toJS(supplierState).state3 != 2) {
			message.warn("请完善必填信息");
			return false;
		}
		// 校验
		if (count > 0&&mobx.toJS(supplierState).state3 != 2) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("姓名是必填项");
				return false;
			}
			if (param.idCard == null || param.idCard == "") {
				message.warn("身份证号是必填项");
				return false;
			}
			if (param.phone == null || param.phone == "") {
				message.warn("手机号是必填项");
				return false;
			}
			if (param.supplierName == null || param.supplierName == "") {
				message.warn("所属供应商是必填项");
				return false;
			}
		}

		let data = JSON.parse(JSON.stringify(dataSource));
		let cstis = data.map((item)=>{
			if (item.key) delete item.key;
			return item;
		});
		this.store.buildConfirmSupplierThreeExcel({cstis}).then((res)=>{
			if (res) {
				this.setState({
					downloading: true
				})
				this.getReportUrl({"id": res});
			}
		});
	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id) => {
		let _this = this;
		_this.timer22 = setInterval(() => {
			_this.store.getDownloadUrl(id).then((url) => {
				if (url !== null && url !== "") {
					_this.setState({ downloading: false})
					downloadFile(url, "用工信息");
					clearInterval(_this.timer22);
				} else {

				}
			});
		}, 3000);
	}

	render() {
		const {supplierState} = this.store;
		const {dataSource, selectedRowKeys, downloading} = this.state;
		const {currentStep, switchSteps} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '序号',
				dataIndex: 'key',
				width: '80px',
				align: 'center'
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>姓名</div>,
				dataIndex: 'name',
				width: 100,
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>身份证号</div>,
				dataIndex: 'idCard',
				width: '200px',
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>手机号</div>,
				dataIndex: 'phone',
				width: '120px',
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: '性别',
				dataIndex: 'sex',
				width: 100,
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: '年龄',
				dataIndex: 'age',
				width: 100,
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: '工种',
				dataIndex: 'workType',
				width: 100,
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>所属供应商</div>,
				dataIndex: 'supplierId',
				width: 200,
				align: 'center',
				editable: supplierState.state3 === 2 ? false : true,
				render: (text, record) => {
					return (<div>
						{text == null || text == "" ? "" : record["supplierName"]}
					</div>)
				}
			},
			{
				title: '操作',
				dataIndex: 'opater',
				width: '60px',
				align: 'center',
				className: supplierState.state3===2?"hidden":"",
				render: (text, record) => <Popconfirm title="确定要删除吗?" onConfirm={() => this.handleDelete(record)}>
					<span style={{fontSize: "14px", color: "#57ACFF", cursor: "pointer"}}>删除</span>
				</Popconfirm>
			}
		];
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					dataSource: dataSource
				}),
			};
		});

		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange,
		};


		return (
			<div  style={{
				overflowX: "hidden",
			}}>
				<Spin spinning={downloading} tip="正在下载中,请稍等...">
				<Button
					className="mb10"
					ghost
					type="primary"
					icon="poweroff"
					onClick={this.props.reStartHandover}
					style={supplierState.state3 === 2 ? {display: ''} : {display: 'none'}}
				>
					重新启动
				</Button>
				<Button
					className="mb10"
					type="primary"
					icon="delete"
					ghost
					onClick={this.checkAllDel}
					style={supplierState.state3===2?{display:'none'}:{display:''}}
				>
					批量删除
				</Button>
				<Table
					rowSelection={supplierState.state3===2?null:rowSelection}
					components={components}
					className="digital_table"
					rowClassName={() => 'editable-row'}
					dataSource={dataSource}
					columns={column}
					pagination={false}
					bordered={supplierState.state3===2?true:false}
					rowKey={record => record.id}
				/>
				<Icon onClick={this.handleAdd}
				      className={supplierState.state3===2?"hidden":"mt20"}
				      type="plus-circle" style={{
					position: "relative",
					left: "28px",
					fontSize: "18px",
					color: "#609AF1",
					cursor: "pointer"
				}}/>
				<div className="mt30 mb30" style={supplierState.state3 !== 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button style={{marginRight: '20px'}}
					        ghost
					        type="primary"
					        onClick={this.buildExcel}>导出Excel</Button>
					<Button onClick={this.overCurrentStage} type="primary"> 完成该阶段任务</Button>
				</div>
				<div className="mt30 mb30" style={supplierState.state3 === 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button style={{marginRight: '20px'}}
					        ghost
					        type="primary"
					        onClick={this.buildExcel}>导出Excel</Button>
					<Button onClick={() => {
						switchSteps(currentStep - 1)
					}} style={{marginLeft: '20px'}} type='primary'>上一步</Button>
				</div>
				</Spin>
			</div>
		)
	}
}


export default ThreeItem;



