/*
* 分页、下拉选择
*dangw@bocspace.cn
*/
import React from 'react';
import {Select, Spin} from 'antd';
import {inject, observer} from 'mobx-react';
const {Option} = Select;

@inject('DeliveryStore')
@observer
class PageSelect2 extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
	}

	state = {
		fetching: false,
		text: "", //关键字
		companyData: [],
		pageIndex: 1,
		pageSize: 10,
	}

	componentDidMount() {
		this.onFocus();
	}

	onFocus = () => {
		this.setState({
			text: "", //关键字
			pageIndex: 1,
			pageSize: 10,
		}, () => {
			this.getCompanyList(); // 调用api方法
		});
	}

	companyScroll = e => {
		e.persist();
		const {target} = e;
		const {type} = this.props;
		if (target.scrollTop + target.offsetHeight === target.scrollHeight) {
			const {pageIndex, pageSize, text, companyData} = this.state;
			const nextScrollPage = pageIndex + 1;
			this.setState({
				pageIndex: nextScrollPage
			}, () => {
				const old = JSON.parse(JSON.stringify(companyData))
				const params = {
					text,
					pageIndex: this.state.pageIndex,
					pageSize,
					code: "",
				}
				this.store.getMachineByCode(type, params).then((res) => {
					if (res) {
						this.setState({
							companyData: [...old, ...res.body]
						});
					}
				});
			});
		}
	};

	fetchCompany = value => {
		this.setState({companyData: [], fetching: true, text: value}, () => {
			this.getCompanyList(); // 关键字模糊查询api
		});
	};

	getCompanyList = () => {
		const {text} = this.state;
		const {type} = this.props;
		const params = {
			text,
			pageIndex: 1,
			pageSize: 10,
			code: "",
		}
		this.store.getMachineByCode(type, params).then((res) => {
			this.setState({
				companyData: Object.assign([], Array.isArray(res.body) ? res.body : [])
			});
		});
	}

	setCheckNum = (val, e) => {
		let item = e.props.val||{};
		// 更新父组件的方法
		this.props.updateStore(item);
	}

	render() {
		const {companyData, fetching} = this.state;
		const {value, name, disabled} = this.props;
		let isFlag = companyData&&companyData.some(item=>item.id===value);
		return (
			<Select
				disabled={disabled}
				style={{width: '100%'}}
				showSearch
				placeholder="请选择"
				notFoundContent={fetching ? <Spin size="small"/> : null}
				onSearch={this.fetchCompany}
				onChange={this.setCheckNum}
				onPopupScroll={this.companyScroll}
				filterOption={false}
				onFocus={this.onFocus}
				value={isFlag?value:name}
			>
				{companyData && companyData.map(item => {
					return (
						<Option value={item.id} key={item.id} title={item.name} val={item}>
							{item.name}
						</Option>)
				})}
			</Select>
		)
	}
}


export default PageSelect2;



