/*
* 收件人
* */
import React from 'react';
import { Tag, Icon, Select, message } from 'antd';
import { TweenOneGroup } from 'rc-tween-one';
import {requestPost} from "@/utils/api";
import Config from "@/config";
import schemas from "@/utils/Schemas/journal";
const { Option } = Select;

class EditableTagEmail2 extends React.Component {
	state = {
		inputVisible: false,
		inputValue: [],  // 新增默认值
		emails: [],
		tags: []
	};

	componentWillReceiveProps(nextProps) {
		this.setState({
			tags: nextProps.data
		});
	}

	componentDidMount() {
        // 收件人数据
		this.init("");
	}

	init = (value) => {
		requestPost('getSendUserEmails', Config.journal.getSendUserEmails, schemas.journal.getSendUserEmails, {
			text: value,
			type: 0
		})
		.then((res)=>{
			this.setState({
				emails: res
			});
		});
	}

	handleClose = removedTag => {
		const tags = this.state.tags.filter(tag => tag !== removedTag);
		this.setState({tags},()=>{
			this.props.updateState(tags);
		});
	};

	showInput = () => {
		this.setState({ inputVisible: true }, () => this.input.focus());
	};

	saveInputRef = input => (this.input = input);

	forMap = tag => {
		const isLongTag = tag.length > 20;
		const tagElem = (
			<Tag
				key={tag}
				closable
				onClose={e => {
					e.preventDefault();
					this.handleClose(tag);
				}}
				style={{marginBottom: '3px'}}
			>
				{isLongTag ? `${tag.slice(0, 20)}...` : tag}
			</Tag>
		);
		return isLongTag ? (
			<span title={tag} style={{ display: 'inline-block' }}>
							{tagElem}
						</span>
		) : (
			tagElem
		);
	};

	handleChange = (value, e) => {
		const val = e.map((item)=>{
			return item.key;
		});
		this.setState({ inputValue: val});
	}

	handleSelectConfirm = () => {
		const { inputValue } = this.state;
		let { data } = this.props;
		if (inputValue!==null&& typeof inputValue == "object") {
			data = [...data, ...inputValue];
			this.setState({
				inputVisible: false,
				inputValue: [],
			}, ()=>{
				this.props.updateState(data);
			});
		}
	};

	render() {
		const {inputVisible, inputValue, emails, tags} = this.state;
		const {title} = this.props;
		const filteredOptions = emails.filter(o => !tags.includes(o));
		const tagChild = tags.map(this.forMap);
		return (
			<div style={{display: "inline-block"}}>
				<div style={{ display: "inline-block", marginBottom: '3px'}}>
					<TweenOneGroup
						enter={{
							scale: 0.8,
							opacity: 0,
							type: 'from',
							duration: 100,
							onComplete: e => {
								e.target.style = '';
							},
						}}
						leave={{ opacity: 0, width: 0, scale: 0, duration: 200 }}
						appear={false}
					>
						{tagChild}
					</TweenOneGroup>
				</div>
				{inputVisible && (
					<Select mode="tags"
					        size="small"
					        defaultOpen={true}
					        value={inputValue}
					        style={{ width: '220px' }}
					        ref={this.saveInputRef}
					        placeholder="请输入"
					        onBlur={this.handleSelectConfirm}
					        onPressEnter={this.handleChange}
					        onChange={this.handleChange}
					        className="tagEmail"
					>
						{filteredOptions.map((item)=>{
							return <Option key={item} title={item}>{item}</Option>
						})}
					</Select>
				)}
				{!inputVisible && (
					<Tag onClick={this.showInput} style={{ background: '#fff', borderStyle: 'dashed' }}>
						<Icon type="plus" /> {title}
					</Tag>
				)}
			</div>
		);
	}
}


export default EditableTagEmail2;



