import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';

@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class OthersItemTwo extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
			remark:BraftEditor.createEditorState(null),
			riskAdvice:BraftEditor.createEditorState(null),
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {
this.getHandoverTwo();
	}

	componentWillMount(){
	}

	getHandoverTwo=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverTwo({"taskId": taskDetail.id}).then((res)=>{
			if(res){
				this.store.handoverTwoObj =res;
				this.props.form.setFieldsValue({
					remark: res.remark==null||res.remark==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(res.remark),
					riskAdvice: res.riskAdvice==null||res.riskAdvice==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(res.riskAdvice),
				});
			}
		})
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressTargetTimeSure",e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	handleBaseTime=(date, datestring)=>{
		this.props.updateData("progressTargetBaseTime",date.valueOf())
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	handleBChange = (type, bfData) => {
		// console.log(type, BraftEditor.createEditorState(bfData).toHTML())
		this.props.updateHandOverBC(type,BraftEditor.createEditorState(bfData).toHTML())
	}

	render() {
		const { sureState, remark, riskAdvice } = this.state;
		const { handoverTwoObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverTwoObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 4},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 20},
				md: {span: 20},
				sm: {span: 20},
			},
		};
		const controls = ['bold',  'text-color',  ]
		return (
			<div className="mt15">
				<Form  {...formItemLayout} >
					{handoverState.state2===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<table className="suppliertable" style={{width: "100%"}}>
									<tr>
										<td>存在风险及建议</td>
										<td colSpan={3}>
											<div dangerouslySetInnerHTML={{__html: fields.riskAdvice}} />
										</td>
									</tr>
									<tr>
										<td>备注信息</td>
										<td colSpan={3}>
											<div dangerouslySetInnerHTML={{__html: fields.remark}} />
										</td>
									</tr>
								</table>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>

								<Form.Item label="存在风险及建议" className="w">
									{getFieldDecorator('riskAdvice', {
										initialValue: riskAdvice,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<BraftEditor
											className="my-editor"
											onBlur={(e)=>{this.handleBChange('riskAdvice', e)}}
											controls={controls}
											contentStyle={{
												minHeight: 100,
												maxHeight: 100,
												overflowY: 'scroll',
												height: 'auto',
												width:'100%'
											}}
										/>
									)}
								</Form.Item>
								<Form.Item label="备注信息" className="w">
									{getFieldDecorator('remark', {
										initialValue: remark,
										rules: [{required: false, message: '此项是必填'}],
									})(
										<BraftEditor
											className="my-editor"
											onBlur={(e)=>{this.handleBChange('remark', e)}}
											controls={controls}
											contentStyle={{
												minHeight: 100,
												maxHeight: 100,
												overflowY: 'scroll',
												height: 'auto',
												width:'100%'
											}}
										/>
									)}
								</Form.Item>
							</Col>
						</Row>
					}
				</Form>
			</div>
		)
	}
}






