import React from 'react';
import {observer, inject} from 'mobx-react';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Form, Button, Input, message, DatePicker, Select, Spin, Icon} from 'antd';
import moment from "moment";
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';
import Document from "@/components/supplier/Document";
import PersonAdd from "./PersonAdd";
import EditableTableUpload from "@/components/handover/EditableTableUpload";


const { Option } = Select;

@Form.create()
@inject('DeliveryStore')
@withRouter
@observer
class OneItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			projectId: null,
            fields: {},
            downloading: false,
            dataSource: [],
            dataSource2: [{first: "", last: ""}]
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if (projectDetail.id !== this.state.projectId) {
			this.setState({projectId: projectDetail.id})
			this.getConstructionScheduleList();
            this.getEnclosureList();
        }
	}

	componentDidMount() {
		this.getConstructionScheduleList();
		this.getEnclosureList();
	}

	getConstructionScheduleList = () => {
		const {taskDetail} = this.store;
		this.store.getConfirmEnterOneMain({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
		    this.setState({
                fields: res,
                dataSource2: res.entryConditionsJson==null||res.entryConditionsJson==""? [{first: "", last: ""}]:
                    JSON.parse(res.entryConditionsJson)
            });
		});
	}

    handleDate = (type, date, dateString) => {
        let value = dateString == null || dateString == "" ? 0 : moment(dateString, 'YYYY-MM-DD').valueOf();
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = value;
        data.entryConditionsJson = this.state.dataSource2 == null?"":JSON.stringify(this.state.dataSource2)
        this.store.saveConfirmEnterOneMain({mian: data}).then((res)=>{
            if (res) {
                message.success("保存成功");
                this.setState({
                    fields: Object.assign({}, res)
                });
            }
        });
    }

	// 完成并进入下一步
	doneAndNext = () => {
        // 校验
        let _this = this;
        const {currentStep} = _this.props;
        _this.checkFunction(()=>{
            // 是否重新生成策划书
            let fields = JSON.parse(JSON.stringify(_this.state.fields));
            let isDocument = fields.document==null;  // 是否有采购合同
            if(isDocument) {
                _this.buildWord(()=>{
                    _this.props.overCurrentStageTask();
                    _this.props.switchSteps(currentStep + 1);
                });}
            else {
                _this.props.overCurrentStageTask();
                _this.props.switchSteps(currentStep + 1);
            }
        });
	}

	onSave = () => {
        let fields = JSON.parse(JSON.stringify(this.state.fields));
        fields.entryConditionsJson = this.state.dataSource2 == null?"":JSON.stringify(this.state.dataSource2)
        this.store.saveConfirmEnterOneMain({mian: fields}).then((res)=>{
            if (res) {
                message.success("保存成功");
                this.setState({
                   fields: Object.assign({}, res)
                });
            }
        });
    }

    handleChange = (type, e) => {
	    let data = JSON.parse(JSON.stringify(this.state.fields));
	    data[type] = e;
	    if (type=="basicStructureForm") data["basicStrengthRequirement"] = "";
	    if (type=="powerProvider"&&e=="甲方") {
            data["powerStandard"] = "220V";
        }
        if (type=="powerProvider"&&e!="甲方") {
            data["powerStandard"] = "20KW";
        }
        this.setState({
            fields: Object.assign({}, data)
        }, ()=>{
            this.onSave();
        });
    }

    handleChange2 = (type, e) => {
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = e.target.value;
        this.setState({
            fields: Object.assign({}, data)
        });
    }

    handleBChange2 = (type, bfData) => {
        let value = BraftEditor.createEditorState(bfData).toHTML();
        let fields = JSON.parse(JSON.stringify(this.state.fields));
        fields[type] = value;
        this.setState({
            fields: Object.assign({}, fields)
        }, ()=>{
            this.onSave();
        });
    }

    buildWord = (callback) => {
        let fields = JSON.parse(JSON.stringify(this.state.fields));
        fields.entryConditionsJson = this.state.dataSource2 == null?"":JSON.stringify(this.state.dataSource2)
        this.store.buildConfirmEnterDoc({main:fields}).then((res)=>{
            this.setState({
                downloading: true
            });
            this.getReportUrl({"id": res}, callback);
        });
    }

    // 间隔3秒获取pdf地址
    getReportUrl = (id, callback) => {
        let _this = this;
        _this.timer22 = setInterval(() => {
            _this.store.getConfirmEnterDownloadUrl(id).then((url) => {
                if (url !== null && url !== "") {
                    _this.setState({ downloading: false})
                    // 重新保存任务
                    _this.getConstructionScheduleList();
                    if (typeof callback=="function") {
                        callback();
                    }
                    clearInterval(_this.timer22);
                } else {

                }
            });
        }, 3000);
    }

    updatePerson = (type, users) => {
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = users;
        this.setState({
            fields: Object.assign({}, data)
        }, ()=>{
             this.onSave();
        });
    }

    overCurrentStage = () => {
        // 校验
        let _this = this;
        this.checkFunction(()=>{
            // 是否重新生成策划书
            let fields = JSON.parse(JSON.stringify(_this.state.fields));
            let isDocument = fields.document==null;  // 是否有采购合同
            if(isDocument) {
                _this.buildWord(()=>{
                    _this.props.overCurrentStageTask();
                });}
            else {
                _this.props.overCurrentStageTask();
            }
        });
    }

    // 统一校验方法
    checkFunction = (callback) => {
        let {fields} = this.state;
        const {
            basicProvider,
            waterProvider,
            powerProvider,
            roadProvider,
            materialFieldProvider,
            craneSitesProvider,
            basicStructureForm,
            basicStrengthRequirement,
            basicLongitudinal,
            basicElevation,
            basicSize,
            waterStandard,
            powerStandard,
            roadStandard,
            materialFieldStandard,
            craneSitesStandard,
            explorativeTime,
            explorativePeople,
            explorativeAddress
        } = fields;
        if (basicProvider==null||basicProvider==""||
            waterProvider==null||waterProvider==""||
            powerProvider==null||powerProvider==""||
            roadProvider==null||roadProvider==""||
            materialFieldProvider==null||materialFieldProvider==""||
            craneSitesProvider==null||craneSitesProvider==""||
            basicStructureForm==null||basicStructureForm==""||
            basicStrengthRequirement==null||basicStrengthRequirement==""||
            basicLongitudinal==null||basicLongitudinal==""||
            basicElevation==null||basicElevation==""||
            basicSize==null||basicSize==""||
            waterStandard==null||waterStandard==""||
            powerStandard==null||powerStandard==""||
            roadStandard==null||roadStandard==""||
            materialFieldStandard==null||materialFieldStandard==""||
            craneSitesStandard==null||craneSitesStandard==""
        ) {
            message.warn("请完善进场条件必填信息");
            return false;
        }
        if (explorativeTime==null||explorativeTime==0) {
            message.warn("踏勘时间是必填项");
            return false;
        }
        if (explorativePeople==null||explorativePeople.length==0) {
            message.warn("参加人是必填项");
            return false;
        }
        if (explorativeAddress==null||explorativeAddress=="") {
            message.warn("踏勘地点是必填项");
            return false;
        }
        return callback();
    }

    getEnclosureList=()=>{
        const { taskDetail } = this.store;
        this.store.getOnlyShowEnclosureListByTaskIdAndIndex({"taskId":mobx.toJS(taskDetail).id, "index":0}).then((res)=>{
            this.setState({dataSource: res});
        })
    }

    add = () => {
        let dataSource2 = JSON.parse(JSON.stringify(this.state.dataSource2));
        this.setState({
            dataSource2: [...dataSource2, {first: "", last: ""}]
        });
    }

    remove = (index) => {
        let dataSource2 = JSON.parse(JSON.stringify(this.state.dataSource2));
        dataSource2.splice(index, 1);
        this.setState({
            dataSource2: [...dataSource2]
        });
    }

    handleChange3 = (type, index, e) => {
        let dataSource2 = JSON.parse(JSON.stringify(this.state.dataSource2));
        dataSource2[index][type] = e.target.value;
        this.setState({
            dataSource2: [...dataSource2]
        });
    }

    render() {
		const {currentStep, switchSteps} = this.props;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {span: 2},
			wrapperCol: {span: 12},
		};
        const formItemLayoutWithOutLabel = {
            wrapperCol: {
                xs: { span: 2, offset: 2 },
                sm: { span: 12, offset: 2 },
            },
        };
		const {fields, downloading, dataSource, dataSource2} = this.state;
		const {confirmEnterState} = this.store;
        const controls = ['bold',  'text-color']
        let isDocument = fields.document==null || (typeof fields.document == "object"&&Object.keys(fields.document).length==0)?false:true;  // 是否有采购合同
        const dateFormat = 'YYYY-MM-DD';
		return (
			<div>
                <Spin className="fixed-spin" spinning={downloading} tip="正在生成进场条件确认单,请稍等...">
				{/*编辑态*/}
				<div className={confirmEnterState.state1 === 2?"hidden":""}>
                    <div className="daily_tips">
                        <p><span>小交温馨提醒：请在正式进场前，确认基础条件要求，在正式进场前现场确认，争取甲方的签字验收哦~</span></p>
                    </div>
                    <div className="mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>进场条件</span>
                        {fields.optimalConfirmTime==null||fields.optimalConfirmTime==0?"":<span style={{fontWeight: 400, fontSize: "12px"}}>（请在计划进场前一周，即{moment(fields.optimalConfirmTime).format('YYYY年MM月DD日')} 之前确认以下信息，提醒甲方准备好进场条件）</span>}
                    </div>
                    <table className="makeplotTable">
                        <tr>
                            <th>序号</th>
                            <th>基础条件项目</th>
                            <th><span style={{
                                fontWeight: "400",
                                marginRight: "4px",
                                color: "#f5222d",
                                fontSize: "14px",
                                fontFamily: "SimSun, sans-serif"
                            }}>*</span>提供方</th>
                            <th>验收项</th>
                            <th><span style={{
                                fontWeight: "400",
                                marginRight: "4px",
                                color: "#f5222d",
                                fontSize: "14px",
                                fontFamily: "SimSun, sans-serif"
                            }}>*</span>验收标准</th>
                            <th>期望完成时间</th>
                        </tr>
                        <tr>
                            <td rowSpan={5} className="vm">1</td>
                            <td rowSpan={5} className="vm">
                                基础
                            </td>
                            <td rowSpan={5} className="vm">
                                <Select
                                    value={fields.basicProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "basicProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>
                                结构形式
                            </td>
                            <td>
                                <Select
                                    value={fields.basicStructureForm}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "basicStructureForm")}>
                                    <Option value={"独立基础"}>独立基础</Option>
                                    <Option value={"条形基础"}>条形基础</Option>
                                    <Option value={"筏板基础"}>筏板基础</Option>
                                    <Option value={"砖基础"}>砖基础</Option>
                                </Select>
                            </td>
                            <td rowSpan={5} className="vm">
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.basicPlanOverTime==null||fields.basicPlanOverTime==0?"":moment(moment(fields.basicPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "basicPlanOverTime")} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                设计强度要求
                            </td>
                            <td>
                                {
                                    fields.basicStructureForm=="砖基础"?<Select
                                        value={fields.basicStrengthRequirement}
                                        style={{ width: 120 }}
                                        onChange={this.handleChange.bind(this, "basicStrengthRequirement")}>
                                        <Option value={"MU15及以上"}>MU15及以上</Option>
                                        <Option value={"MU20及以上"}>MU20及以上</Option>
                                        <Option value={"MU25及以上"}>MU25及以上</Option>
                                    </Select>:
                                        <Select
                                            value={fields.basicStrengthRequirement}
                                            style={{ width: 120 }}
                                            onChange={this.handleChange.bind(this, "basicStrengthRequirement")}>
                                            <Option value={"C20及以上"}>C20及以上</Option>
                                            <Option value={"C25及以上"}>C25及以上</Option>
                                            <Option value={"C30及以上"}>C30及以上</Option>
                                        </Select>}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                坐标纵、横轴线偏差
                            </td>
                            <td>
                                <Select
                                    value={fields.basicLongitudinal}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "basicLongitudinal")}>
                                    <Option value={"&nbsp; ≤&nbsp; ±5mm"}>&nbsp; ≤&nbsp; ±5mm</Option>
                                    <Option value={"&nbsp; ≤&nbsp; ±10mm"}>&nbsp; ≤&nbsp; ±10mm</Option>
                                    <Option value={"&nbsp; ≤ ±15mm"}>&nbsp; ≤ ±15mm</Option>
                                </Select>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                平面标高偏差
                            </td>
                            <td>
                                <Select
                                    value={fields.basicElevation}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "basicElevation")}>
                                    <Option value={"&nbsp; ≤&nbsp; ±5mm"}>&nbsp; ≤&nbsp; ±5mm</Option>
                                    <Option value={"&nbsp; ≤&nbsp; ±10mm"}>&nbsp; ≤&nbsp; ±10mm</Option>
                                    <Option value={"&nbsp; ≤ ±15mm"}>&nbsp; ≤ ±15mm</Option>
                                </Select>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                外形尺寸偏差
                            </td>
                            <td>
                                <Select
                                    value={fields.basicSize}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "firstContractState")}>
                                    <Option value={"&nbsp; ≤&nbsp; ±5mm"}>&nbsp; ≤&nbsp; ±5mm</Option>
                                    <Option value={"&nbsp; ≤&nbsp; ±10mm"}>&nbsp; ≤&nbsp; ±10mm</Option>
                                    <Option value={"&nbsp; ≤ ±15mm"}>&nbsp; ≤ ±15mm</Option>
                                </Select>
                            </td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td>临水</td>
                            <td>
                                <Select
                                    value={fields.waterProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "waterProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>施工用水</td>
                            <td>
                                <Input
                                    value={fields.waterStandard}
                                    onChange={this.handleChange2.bind(this, "waterStandard")}
                                    onBlur={this.onSave}
                                    style={{ width: "100%" }}
                                    placeholder="请输入" />
                            </td>
                            <td>
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.waterPlanOverTime==null||fields.waterPlanOverTime==0?"":moment(moment(fields.waterPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "waterPlanOverTime")} />
                            </td>
                        </tr>
                        <tr>
                            <td>3</td>
                            <td>临电</td>
                            <td>
                                <Select
                                    value={fields.powerProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "powerProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>{fields.powerProvider=="甲方"?"电压":"功率"}</td>
                            <td>
                                <Input
                                    value={fields.powerStandard}
                                    onChange={this.handleChange2.bind(this, "powerStandard")}
                                    onBlur={this.onSave}
                                    style={{ width: "100%" }}
                                    placeholder="请输入" />
                            </td>
                            <td>
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.powerPlanOverTime==null||fields.powerPlanOverTime==0?"":moment(moment(fields.powerPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "powerPlanOverTime")} />
                            </td>
                        </tr>
                        <tr>
                            <td>4</td>
                            <td>道路</td>
                            <td>
                                <Select
                                    value={fields.roadProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "roadProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>车辆进场</td>
                            <td>
                                <Input
                                    value={fields.roadStandard}
                                    onChange={this.handleChange2.bind(this, "roadStandard")}
                                    onBlur={this.onSave}
                                    style={{ width: "100%" }}
                                    placeholder="请输入" />
                            </td>
                            <td>
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.roadPlanOverTime==null||fields.roadPlanOverTime==0?"":moment(moment(fields.roadPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "roadPlanOverTime")} />
                            </td>
                        </tr>
                        <tr>
                            <td rowSpan={2} className="vm">5</td>
                            <td rowSpan={2} className="vm">场地</td>
                            <td>
                                <Select
                                    value={fields.materialFieldProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "materialFieldProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>材料堆场</td>
                            <td>
                                <Input
                                    value={fields.materialFieldStandard}
                                    onChange={this.handleChange2.bind(this, "materialFieldStandard")}
                                    onBlur={this.onSave}
                                    style={{ width: "100%" }}
                                    placeholder="请输入" />
                            </td>
                            <td>
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.materialFieldPlanOverTime==null||fields.materialFieldPlanOverTime==0?"":moment(moment(fields.materialFieldPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "materialFieldPlanOverTime")} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <Select
                                    value={fields.craneSitesProvider}
                                    style={{ width: 120 }}
                                    onChange={this.handleChange.bind(this, "craneSitesProvider")}>
                                    <Option value={"甲方"}>甲方</Option>
                                    <Option value={"我方"}>我方</Option>
                                    <Option value={"第三方"}>第三方</Option>
                                </Select>
                            </td>
                            <td>吊车站位</td>
                            <td>
                                <Input
                                    value={fields.craneSitesStandard}
                                    onChange={this.handleChange2.bind(this, "craneSitesStandard")}
                                    onBlur={this.onSave}
                                    style={{ width: "100%" }}
                                    placeholder="请输入" />
                            </td>
                            <td>
                                <DatePicker
                                    format="YYYY-MM-DD"
                                    style={{ width: 120 }}
                                    value={fields.craneSitesPlanOverTime==null||fields.craneSitesPlanOverTime==0?"":moment(moment(fields.craneSitesPlanOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                    onChange={this.handleDate.bind(this, "craneSitesPlanOverTime")} />
                            </td>
                        </tr>
                    </table>
                    <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>备注</span></div>
                    <BraftEditor
                        className="my-editor"
                        onBlur={(e)=>{this.handleBChange2('remarkOne', e)}}
                        controls={controls}
                        contentStyle={{
                            minHeight: 100,
                            maxHeight: 100,
                            overflowY: 'scroll',
                            height: 'auto',
                            width:'100%'
                        }}
                        value={fields.remarkOne==null||fields.remarkOne==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(fields.remarkOne)}
                        placeholder="请输入其他需提前准备的内容"
                    />
                    <div style={{margin: "30px 0"}}>
                        <Button type="primary" onClick={this.buildWord}>生成进场条件确认单</Button>
                        {isDocument?<Document
                            data={fields.document}
                            store={this.store}
                        />:null}
                    </div>
                    <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>现场踏勘计划</span></div>
                    <Form layout="horizontal" {...formItemLayout}>
                        <Form.Item label="踏勘时间" className="w">
                            {getFieldDecorator('explorativeTime', {
                                initialValue: fields.explorativeTime==null||fields.explorativeTime==0?"":moment(moment(fields.explorativeTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
                                rules: [{required: true, message: '此项是必填'}]
                            })(
                                <DatePicker
                                    allowClear={false}
                                    format={dateFormat}
                                    placeholder="请选择"
                                    style={{ width: 150 }}
                                    onChange={this.handleDate.bind(this, "explorativeTime")} />
                            )}
                            <span className="daily_tips ml15">
                               建议选择进场时间的前1-2天
                            </span>
                        </Form.Item>
                        <Form.Item label="参加人" className="w">
                            {getFieldDecorator('explorativePeople', {
                                //initialValue: fields.explorativePeople || [],
                                rules: [{required: true, message: '此项是必填'}]
                            })(
                                <PersonAdd
                                    title={"添加参加人"}
                                    data={fields.explorativePeople == null || fields.explorativePeople.length == 0 ? []:fields.explorativePeople}
                                    updatePerson={this.updatePerson.bind(this, "explorativePeople")}
                                />
                            )}
                        </Form.Item>
                        <Form.Item label="踏勘地点" className="w">
                            {getFieldDecorator('explorativeAddress', {
                                initialValue: fields.explorativeAddress || "",
                                rules: [{required: true, message: '此项是必填'}]
                            })(
                                <Input
                                    onChange={this.handleChange2.bind(this, "explorativeAddress")}
                                    onBlur={this.onSave}
                                    placeholder="默认为项目地点，可修改" />
                            )}
                        </Form.Item>
                        {dataSource2.map((field, index) => (
                            <Form.Item
                                colon={false}
                                {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                                label={index === 0 ?
                                    <div className="f14" style={{lineHeight: "34px", paddingRight: "9px"}}>
                                        进场条件待办
                                        <Icon
                                            style={{ marginLeft: '2px' }}
                                            onClick={() => this.add()}
                                            type="plus-circle" />
                                        :
                                    </div> : ''}
                                labelAlign="center"
                                required={false}
                                key={index}
                                style={{ marginBottom: '0' }}
                            >
                                <div key={index} style={{ display: 'flex' }} align="baseline" className="w">
                                    <Form.Item
                                        {...field}
                                        rules={[{ required: false, message: '此项是必填' }]}
                                    >
                                        <Input placeholder="填写待办事项" value={field.first} onBlur={this.onSave} onChange={this.handleChange3.bind(this, "first", index)}/>
                                    </Form.Item>
                                    <Form.Item
                                        className="ml10"
                                        {...field}
                                        rules={[{ required: false, message: '此项是必填' }]}
                                    >
                                        <Input placeholder="填写进展状态" value={field.last} onBlur={this.onSave} onChange={this.handleChange3.bind(this, "last", index)}/>
                                    </Form.Item>
                                    {index === 0 ? null :<Icon type="minus-circle"
                                                               style={{position: "relative", top: "14px", left: "6px"}}
                                                               onClick={() => this.remove(index)}
                                    />}
                                </div>
                            </Form.Item>
                        ))}
                    </Form>
                    <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>附件</span></div>
                    <EditableTableUpload
                        id={'id'}
                        store={this.store}
                        dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
                        saveData={()=>{}}
                        saveItem={()=>{}}
                        disabled={true}
                        pid={''}
                    />
                </div>

				{/*详情态*/}
                <div style={{padding: "0 10px"}} className={confirmEnterState.state1 === 2?"":"hidden"}>
                    <Button
                        ghost
                        type="primary"
                        icon="poweroff"
                        onClick={this.props.reStartHandover}
                        style={confirmEnterState.state1 === 2 ? {display: ''} : {display: 'none'}}
                    >
                        重新启动
                    </Button>
                    <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>
                        进场条件
                        {fields.optimalConfirmTime==null||fields.optimalConfirmTime==0?"":<span style={{fontWeight: 400, fontSize: "12px"}}>（请在计划进场前一周，即{moment(fields.optimalConfirmTime).format('YYYY年MM月DD日')} 之前确认以下信息，提醒甲方准备好进场条件）</span>}
                    </h1>
                    <table className="makeplotTable">
                        <tr>
                            <th>序号</th>
                            <th>基础条件项目</th>
                            <th><span style={{
                                fontWeight: "400",
                                marginRight: "4px",
                                color: "#f5222d",
                                fontSize: "14px",
                                fontFamily: "SimSun, sans-serif"
                            }}>*</span>提供方</th>
                            <th>验收项</th>
                            <th><span style={{
                                fontWeight: "400",
                                marginRight: "4px",
                                color: "#f5222d",
                                fontSize: "14px",
                                fontFamily: "SimSun, sans-serif"
                            }}>*</span>验收标准</th>
                            <th>期望完成时间</th>
                        </tr>
                        <tr>
                            <td rowSpan={5} className="vm">1</td>
                            <td rowSpan={5} className="vm">
                                基础
                            </td>
                            <td rowSpan={5} className="vm">
                                {fields.basicProvider}
                            </td>
                            <td>
                                结构形式
                            </td>
                            <td>
                                {fields.basicStructureForm}
                            </td>
                            <td rowSpan={5} className="vm">
                                {fields.basicPlanOverTime==null||fields.basicPlanOverTime==0?"":moment(fields.basicPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                设计强度要求
                            </td>
                            <td>
                                {fields.basicStrengthRequirement}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                坐标纵、横轴线偏差
                            </td>
                            <td>
                                {fields.basicLongitudinal}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                平面标高偏差
                            </td>
                            <td>
                                {fields.basicElevation}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                外形尺寸偏差
                            </td>
                            <td>
                                {fields.basicSize}
                            </td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td>临水</td>
                            <td>
                                {fields.waterProvider}
                            </td>
                            <td>施工用水</td>
                            <td>
                                {fields.waterStandard}
                            </td>
                            <td>
                                {fields.waterPlanOverTime==null||fields.waterPlanOverTime==0?"":moment(fields.waterPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td>3</td>
                            <td>临电</td>
                            <td>
                                {fields.powerProvider}
                            </td>
                            <td>{fields.powerProvider=="甲方"?"电压":"功率"}</td>
                            <td>
                                {fields.powerStandard}
                            </td>
                            <td>
                                {fields.powerPlanOverTime==null||fields.powerPlanOverTime==0?"":moment(fields.powerPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td>4</td>
                            <td>道路</td>
                            <td>
                                {fields.roadProvider}
                            </td>
                            <td>车辆进场</td>
                            <td>
                                {fields.roadStandard}
                            </td>
                            <td>
                                {fields.roadPlanOverTime==null||fields.roadPlanOverTime==0?"":moment(fields.roadPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td rowSpan={2} className="vm">5</td>
                            <td rowSpan={2} className="vm">场地</td>
                            <td>
                                {fields.materialFieldProvider}
                            </td>
                            <td>材料堆场</td>
                            <td>
                                {fields.materialFieldStandard}
                            </td>
                            <td>
                                {fields.materialFieldPlanOverTime==null||fields.materialFieldPlanOverTime==0?"":moment(fields.materialFieldPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                {fields.craneSitesProvider}
                            </td>
                            <td>吊车站位</td>
                            <td>
                                {fields.craneSitesStandard}
                            </td>
                            <td>
                                {fields.craneSitesPlanOverTime==null||fields.craneSitesPlanOverTime==0?"":moment(fields.craneSitesPlanOverTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                    </table>
                    <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>备注</h1>
                    <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                         dangerouslySetInnerHTML={{__html: fields.remarkOne}}></div>
                    <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>生成的进场条件确认单</h1>
                    {isDocument?<Document
                        data={fields.document}
                        store={this.store}
                    />:null}
                    <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>现场踏勘计划</h1>
                    <table className="suppliertable" style={{width: '100%'}}>
                        <tr>
                            <td>踏勘时间</td>
                            <td>
                                {fields.explorativeTime==null||fields.explorativeTime==0?"":moment(fields.explorativeTime).format('YYYY-MM-DD')}
                            </td>
                        </tr>
                        <tr>
                            <td>参加人</td>
                            <td>
                                {fields.explorativePeople&&fields.explorativePeople.map(v=>v.userName).join("、")}
                            </td>
                        </tr>
                        <tr>
                            <td>踏勘地点</td>
                            <td>
                                {fields.explorativeAddress}
                            </td>
                        </tr>
                        <tr>
                            <td>进场条件待办</td>
                            <td>
                                <div className="ol-list">
                                    {
                                        fields.entryConditionsJson==null||fields.entryConditionsJson==""?"":
                                            JSON.parse(fields.entryConditionsJson).map((item,index)=>{
                                                return  <div key={index}>
                           <span className="dill_title ellsis" title={item.first} style={{color: "#333"}}>
                               {index+1+". "}
                               {item.first==""||item.first==undefined?"无":item.first}</span>
                                                    <span className="dill_title ellsis w40 ml5" style={{color: "#0ECE73"}} title={item.last}>{item.last}</span></div>
                                            })
                                    }
                                </div>
                            </td>
                        </tr>
                    </table>
                    <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>附件</h1>
                    <EditableTableUpload
                        id={'id'}
                        store={this.store}
                        dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
                        saveData={()=>{}}
                        saveItem={()=>{}}
                        disabled={true}
                        pid={''}
                    />
                </div>
				{/*底部*/}
				<div className="mt30 mb30" style={confirmEnterState.state1 !== 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button onClick={this.overCurrentStage}
                            ghost
                            type="primary"
                            > 完成该阶段任务</Button><Button
					onClick={this.doneAndNext} style={{marginLeft: '20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div className="mt30 mb30" style={confirmEnterState.state1 === 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button onClick={() => {
						switchSteps(currentStep + 1)
					}} style={{marginLeft: '20px'}} type='primary'>下一步</Button>
				</div>
                </Spin>
			</div>
		)
	}
}


export default OneItem;



