import React from 'react';
import {observer, inject} from 'mobx-react';
import moment from 'moment';
import {v4 as uuidv4} from 'uuid';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import { Button,  message, Form, Radio, Row,Col, Spin } from 'antd';

import EditableTableUpload from './EditableTableUpload';
import BackgroundItemOne from './BackgroundItemOne';
import EditableTableTwoItem from '../buildteam/EditableTableTwoItem';
import EditableTableThreeItemMatter from '../buildteam/EditableTableThreeItemMatter';
import EditableTableThreeItemPolicy from '../buildteam/EditableTableThreeItemPolicy';
import EditableTableOneItem from '../buildteam/EditableTableOneItem';
import EditableTableFourItem from "../buildteam/EditableTableFourItem";
import Document from "./Document";
import {downloadFile} from "@/utils/download";
import SendEmail from "./SendEmail";


const progressValueV4 = (day1, day2, day3) =>{

	return [
		`1、标箱进场时间：${day1}`,
		`2、非标箱进场时间：${day2}`,
		`3、装配完成时间：${day3}`
	]
}


const qualityValue = (label, count) =>{
	return [
		`${label}：≤ ${count}次 ${label==='验收次数'?'客户确认签字':''}`,
	]
}

const profitValue = (label, number) =>{
	return [
		`${label}：     ${number}%`,
	]
}

const costValue = (label, number) =>{
	return [
		`${label}：≥ ${number}天 给供应商付款`,
	]
}

const safetyValue = (number1,number2) =>{
	return [
		`安全事故：≤ ${number1}次 死亡、重伤、轻伤`,
		`安全隐患：≤ ${number2}次 重大事故隐患`,
	]
}

const marketingValue = (flag1, flag2) =>{
	if(flag1===1&&flag2===1){
		return [
			`1、挑战目标：顾客满意，发表扬信`,
			`2、形成该项目的营销案例（品控、质量、交付速度、电商、标准化、客户满意度等），打造成北京区域的标杆`,
		]
	}
	if(flag1===1&&flag2===0){
		return [
			`1、挑战目标：顾客满意，发表扬信`,
		]
	}
	if(flag1===0&&flag2===1){
		return [
			`1、形成该项目的营销案例（品控、质量、交付速度、电商、标准化、客户满意度等），打造成北京区域的标杆`,
		]
	}
	if(flag1===0&&flag2===0){
		return []
	}

}

const dateFormat = 'YYYY-MM-DD';

@inject('DeliveryStore')
@inject('UserStore')
@Form.create()
@withRouter
@observer
class OneItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			downloading: false,
			visible: false,
			stateValue: 0,
			id: "",
			dataSource:[],
			oneItemList:[],
			projectId:null,
			taskId:null,
			targetTableDataSource:[
				{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(0,0,0), "promiseValue": progressValueV4(0, 0,0)},
				{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",0), "promiseValue": qualityValue("质量事故",0)},
				{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitValue("盈利",0), "promiseValue": ""},
				{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"成本", "targetValue":costValue("给甲方付款后",5), "promiseValue": ""},
				{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"营销", "targetValue":marketingValue(0,0), "promiseValue": ""},
				{"keyIndex":6,  "id":"!"+uuidv4(), "orders":6, "item":"安全", "targetValue":safetyValue(0,0), "promiseValue": safetyValue(0,0)},
			],
			btThreeList:[],
			btFourList:[],
			btOneList:[],
			emailUsers:[],
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		const taskDetail = mobx.toJS(nextProps.DeliveryStore.taskDetail)
		if(projectDetail.id!==this.state.projectId /*|| taskDetail.id!==null&&taskDetail.id!==this.state.taskId*/){
			this.setState({projectId:projectDetail.id, taskId:taskDetail.id})
			this.getHandoverOne();
			this.getEnclosureList();
			this.getBuildTeamInfo();
		}
	}

	componentDidMount() {
		this.getHandoverOne();
		this.getEnclosureList();
		this.getBuildTeamInfo();
		const taskDetail = mobx.toJS(this.props.DeliveryStore.taskDetail);
		this.store.gerDefaultEmailUsers({
			taskId: taskDetail.id,
			type: "startmeeting",
			sendType: 2
		}).then((res)=>{
				if (res) {
					this.setState({
						emailUsers: res
					});
				}
		    });
	}

	componentWillMount() {

	}

	getHandoverOne=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverOne({"taskId": taskDetail.id}).then((res)=>{
			this.store.handoverOneObj =res
		});
	}

	saveHandoverOne=(newObj)=>{
		const { handoverOneObj, taskDetail } = this.store;

		this.store.saveHandoverOne({"ho":Object.assign(mobx.toJS(handoverOneObj),{"taskId": taskDetail.id}, newObj)}).then((res)=>{
			if(res){
				this.getHandoverOne()
			}
		})
	}


	// 添加成员
	handleAdd = () => {
		this.setState({
			add: Object.assign(this.state.add, {visible: true})
		}, () => {
			this.getSearch("");
		})
	}

	onChange = (type, e) => {
		this.store.salesParams[type] = e.target.value;
	}


	handleChange = (type, value) => {
		if (value == undefined) {  // 清空
			this.store.salesParams[type] = 10;
		} else {
			this.store.salesParams[type] = value;
		}
	}

	handleSearch = () => {
		this.store.getSalesOrderPageList();
	}

	handleOk = () => {
		const {id, stateValue} = this.state;
		const params = {
			id,
			state: typeof stateValue == "string" ? Number(stateValue) : stateValue
		}
		this.store.changeSalesOrderState(params).then((res) => {
			if (res) {
				message.success("操作成功");
				this.setState({
					visible: false,
				});
				this.handleSearch();
			}
		})
	};

	handleCancel = e => {
		this.setState({
			visible: false,
		});
	};


	confirm = (id) => {
		const params = {
			id: id
		}
		this.store.delSalesOrderState(params).then((res) => {
			if (res) {
				message.success("删除成功")
				this.store.getSalesOrderPageList();
			}
		})
	}


	delProject=(id)=>{
		this.store.delProject({"id":id}).then((res)=>{
			this.store.getProjectPageList('');
		})
	}



	getEnclosureList=()=>{
		const { taskDetail, projectDetail } = this.store;
		this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, "indexs":1}).then((res)=>{
			this.store.enclosureList = res
			this.setState({dataSource: res, enclosureList: res})

		})
	}

	updateState = (type, data) => {
		console.log(type, data)
		this.setState({
			[type]: Object.assign(this.state[type], data)
		})
	}

	// 列表搜索
	getSearch = (param) => {
		const { projectDetail } = this.store;
		const params = param;
		this.props.UserStore.getRoleData(params).then((res) => {
			this.setState({
				add: Object.assign(this.state.add, {data: res.body.filter((item)=> item.id!==projectDetail.masterId).map((item) => Object.assign(item, {selected: false}))})
			})
		});
	}


	saveContents=()=>{
		this.getEnclosureList();
	}


	saveBuildTeamOneItem=(params)=>{
		this.store.saveBuildTeamOneItem(params).then((res)=>{
			if(res){
				this.getEnclosureList()
			}
		})
	}

	getCurrentStageTasks=()=>{
		let result = []
		const { projectDetail, taskDetail } = this.store;
		 const { stageId } = mobx.toJS(taskDetail);
		 const { stages } = mobx.toJS(projectDetail);
		 if(stages!==undefined&&Array.isArray(stages)){
			 let currentStage = stages.filter((item)=>{
				 return item.id=== stageId
			 })
			 if(currentStage.length>0){
				 result = currentStage[0].tasks
			 }
		 }

		 return result
	 }

	 getBuildTeamInfo=()=>{
		const currentStageTasks =  this.getCurrentStageTasks();
		if(currentStageTasks.length===0) return
		const buildteamTask = currentStageTasks.filter((item)=>{
			return item.taskType==='buildTeam'
		})
		this.getBuildTeamTwoMain(buildteamTask[0]['id']);
		this.getBuildTeamThreeItemList(buildteamTask[0]['id']);
		this.getBuildTeamFourItemList(buildteamTask[0]['id']);
		this.getBuildTeamOneItemList(buildteamTask[0]['id']);
	 }

	 getCostValue = (res) =>{
		const { costTargetOne, costTargetTwo, costTargetThree, costTargetFour, costTargetThree2, costTargetFour2} = res;
		const dataInfo =[
			{"show":costTargetOne, "info":"分包费用: 参照合同"},
			{"show":costTargetTwo, "info":"安全文明施工: 合同额≤200万，费用≤2000元; 合同额＞200万，费用≤3000元"},
			{"show":costTargetThree, "info":"招待费: ≤合同额*2‰ 且 ≤2000元"},
			{"show":costTargetThree2, "info":"招待费（展厅适用）: ≤合同额的2‰ 且 ≤1500元"},
			{"show":costTargetFour, "info":"风险附加: ≤合同额*5‰ 且 ≤10000元"},
			{"show":costTargetFour2, "info":"风险附加（展厅适用）:≤合同额的5‰ 且 ≤5000元"},
		]
		let result = dataInfo.filter((item)=>{
			return item.show===1
		})
		result = result.map((item)=>{
			return item.info
		})
		return result
	}

	 getBuildTeamTwoMain=(id)=>{
		this.store.getBuildTeamTwoMain({"taskId":id}).then((res)=>{
            let targetDay1 = res.progressTargetTimeSure===1? moment(res.progressTargetBaseTime).format(dateFormat):"T";
			let targetDay2 = targetDay1==="T"?"T+"+res.progressTargetNostandardDay+"日历天":moment(targetDay1).add(res.progressTargetNostandardDay,"days").format(dateFormat);
			let targetDay3 = targetDay1==="T"?"T+"+res.progressTargetOverDay+"日历天":moment(targetDay1).add(res.progressTargetOverDay,"days").format(dateFormat)

			let promiseDay1 = res.progressPromiseTimeSure===1? moment(res.progressPromiseBaseTime).format(dateFormat):"T";
			let promiseDay2 = promiseDay1==="T"?"T+"+res.progressPromiseNostandardDay+"日历天":moment(promiseDay1).add(res.progressPromiseNostandardDay,"days").format(dateFormat);
			let promiseDay3 = promiseDay1==="T"?"T+"+res.progressPromiseOverDay+"日历天":moment(promiseDay1).add(res.progressPromiseOverDay,"days").format(dateFormat)
			let profitTargetValue=res.profitTarget===0?"不亏损":profitValue("盈利",res.profitTargetRate)
            this.setState({targetTableDataSource:[
					{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
					{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
					{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
					{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"成本", "targetValue":this.getCostValue(res), "promiseValue": ""},
					{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"营销", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
					{"keyIndex":6,  "id":"!"+uuidv4(), "orders":6, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
				]})
		})
	}

	getBuildTeamThreeItemList=(id)=>{
		this.store.getBuildTeamThreeItemList({"taskId": id}).then((res)=>{
			this.setState({btThreeList:res.map((item, index)=>{
				return Object.assign(item, {'keyIndex':index+1})
			})})
		})
	}

	getBuildTeamFourItemList=(id)=>{
		this.store.getBuildTeamFourItemList({"taskId":id}).then((res)=>{
           this.setState({"btFourList":res})
		})
	}

	getBuildTeamOneItemList=(id)=>{

		this.store.getBuildTeamOneItemList({"taskId":id}).then((res)=>{
			this.store.buildTeamOneItem = res
			this.setState({btOneList:res})
		})
	}

	formatDataSource=()=>{
		const { btOneList} = this.state;
		const ds = btOneList.map((item, index)=>{
			if(index===0){
				return Object.assign(item, {"duty":"1、全力保障项目目标完成；\n" +
						"2、协调内外部资源，负责项目交付进度和质量控制；\n" +
						"3、安排内部其他项目组成员工作，做好分工安排；\n" +
						"4、保障项目中各类文档按期完成；\n" +
						"5、定期做高层汇报，并提出管理改进建议"}, {"keyIndex":index+1})
			}else{
				return Object.assign(item,{"keyIndex":index+1})
			}
		})
		return ds
	}

	getUserIds=()=>{
		const { btOneList} = this.state;
		const ds = btOneList.map((item, index)=>{
			return {"userId":item.userId,"userName":item.userName}
		})
		return ds
	}

	getUserEmails=()=>{
		const { btOneList} = this.state;
		const ds = btOneList.map((item, index)=>{
			return item.email
		})
		return ds
	}

	updateHandOverOne=(type, e)=>{
		let newObj = {};
		newObj[type] = e.target.value
		this.saveHandoverOne(newObj)
	}

	overCurrentStage=()=>{
		if(!this.checkFormRequire())return
		this.props.overCurrentStageTask();
	}

	doneAndNext=()=>{
		if(!this.checkFormRequire())return
		const { currentStep } = this.props;
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	checkFormRequire=()=>{
		const cbi = this.checkBackgroundItem();
		this.BackgroundItemOne.checkFormValidate();
		if(!cbi.result){
			message.warn(cbi.info)
			return false
		}
		return true
	}

	checkBackgroundItem=()=>{

       const { handoverOneObj } = this.store;

       const hoo = mobx.toJS(handoverOneObj);

		let resultObj = {"info": "", result: true};
		if(hoo.background===""||hoo.background===null){
			resultObj.result = false
			resultObj.info = "背景-基本概况，项目意义不能为空"
			return resultObj
		}
		if(hoo.significance===""||hoo.significance===null){
			resultObj.result = false
			resultObj.info = "项目意义不能为空"
			return resultObj
		}
		if(hoo.contractSigning===-1){
			resultObj.result = false
			resultObj.info = "请检查甲方合同是否已签订?"
			return resultObj
		}
		if(hoo.designSigning===-1 || hoo.id ==null){
			resultObj.result = false
			resultObj.info = "请检查甲方设计方案是否已签订?"
			return resultObj
		}
		return resultObj

	}

saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:1,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:1}
	    this.store.saveEnclosureMain(parmas).then((res)=>{
		   this.getEnclosureList()
	    })
}

	// 间隔3秒获取pdf地址
	getReportUrl = (id) => {
		let _this = this;
		_this.timer22 = setInterval(() => {
			_this.store.getDocUrl2({"id":id}).then((url) => {
				if (url !== null && url !== "") {
					this.setState({ downloading: false})
					this.getHandoverOne();
					clearInterval(_this.timer22);
				} else {

				}
			});
		}, 3000);
	}


	checkBuildTeamState = ()=>{
		const currentStageTasks =  this.getCurrentStageTasks();
		if(currentStageTasks.length===0) return
		const buildteamTask = currentStageTasks.filter((item)=>{
			return item.taskType==='buildTeam'
		});

		return new Promise((resolve => {
			this.store.getBuildTeamState({"taskId":buildteamTask[0]["id"]}).then((res)=>{
				if(res&&res.state1===2&&res.state2===2&&res.state3===2&&res.state4===2){
                    resolve(true)
				}else{
					resolve(false)
				}
			})
		}))
	}



	buildStartMeetingPpt =()=>{
		const {handoverOneObj} = this.store;
		const hoo = mobx.toJS(handoverOneObj);
		this.checkBuildTeamState().then((res)=>{
			if(!res){
				message.warn("请检查组建项目团队四个阶段是否都已经完成！")
				return false
			}
			return new Promise((resolve => {
				let checkObj = {"info": "", result: true};
				if(hoo.background===""||hoo.background===null){
					checkObj =  {"info": "请检查项目背景是否已经填写！", result: false}
					resolve(checkObj)
				}
				if(hoo.significance===""||hoo.significance===null){
					checkObj =  {"info": "请检查项目意义是否已经填写！", result: false}
					resolve(checkObj)
				}
				if(hoo.contractSigning===-1){
					checkObj =  {"info": "请检查甲方合同是否已签订?", result: false}
					resolve(checkObj)
				}
				if(hoo.designSigning===-1){
					checkObj =  {"info": "请检查甲方设计方案是否已签订?", result: false}
					resolve(checkObj)
				}
				resolve(checkObj)
			})).then((res)=>{
				if(res.result){
					this.store.saveStartMeetingFile({"ho":mobx.toJS(handoverOneObj)}).then((res)=>{
						this.setState({
							downloading: true
						})
						this.getReportUrl(res)
					})
				}else{
					message.warn(res.info)
				}
			})
		})

	}

	handleContractSigning = (e)=>{
		let newObj = {"contractSigning":e.target.value?1:0}

		this.saveHandoverOne(newObj)
	}

	handleDesignSigning = (e)=>{
		let newObj = {"designSigning":e.target.value?1:0}
		this.saveHandoverOne(newObj)
	}

	render() {
		const { handoverState,handoverOneObj} = this.store;
		const  hoFields  = mobx.toJS(handoverOneObj);
		let isDocument = hoFields.document==null || (typeof hoFields.document == "object"&&Object.keys(hoFields.document).length==0)?false:true;  // 是否有采购合同
		const { dataSource, targetTableDataSource, btThreeList, btFourList, downloading} = this.state;
		const { currentStep, switchSteps } = this.props;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: { span: 12 },
			wrapperCol: { span: 12 },
		};
		return (
			<div style={{height: 'auto'}}>
				<Spin spinning={downloading} tip="正在生成启动会PPT,请稍等...">
				<div className="mt20" style={{display:'flex', justifyContent:'space-between'}}>
					<div>
						<Button
							className="fl"
							type="dashed"
							icon="plus"
							onClick={this.handleAdd}
							style={handoverState.state1===2?{display:'none'}:{display:'none'}}
						>
							添加组织成员
						</Button>
						<Button
							className="fl"
							type="dashed"
							icon="delete"
							style={{display:'none'}}
						>
							批量删除
						</Button>
						<Button
							ghost
							className="fl"
							type="primary"
							icon="poweroff"
							onClick={this.props.reStartHandover}
							style={handoverState.state1===2?{display:''}:{display:'none'}}
						>
							重新启动
						</Button>
					</div>
				</div>
				<div className="daily_tips">
					<p><span>小交温馨提醒：项目目标、决策机制和沟通机制如果已经在组建团队过程中完成，这里会自动带出，补充完成以下信息即可正式启动项目啦~</span></p>
				</div>

				<div className="mt30">
					<div className="require_label mb20">项目背景及意义:</div>
					<BackgroundItemOne onRef={ node => this.BackgroundItemOne = node } updateHandOverOne={this.updateHandOverOne} store={this.store} />
				</div>
				{
					handoverState.state1!==2?(
						<div className="mt30">
							<Form layout="inline">
								<Row>
									<Col style={{textAlign:'left'}} span={12}>
										<Form.Item   label="甲方合同是否已签订">
											{getFieldDecorator('contractSigning',{
												initialValue: hoFields.contractSigning == null ? undefined : hoFields.contractSigning,
												rules: [{required: true, message: '此项是必填'}],
											})(
												<Radio.Group onChange={this.handleContractSigning}>
													<Radio value={1}>是</Radio>
													<Radio value={0}>否</Radio>
												</Radio.Group>,
											)}
										</Form.Item>
									</Col>
									<Col  span={12}>
										<Form.Item  label="甲方设计方案是否已签订">
											{getFieldDecorator('designSigning',{
												initialValue: hoFields.designSigning	 == null ? undefined : hoFields.designSigning,
												rules: [{required: true, message: '此项是必填'}],
											})(
												<Radio.Group onChange={this.handleDesignSigning}>
													<Radio value={1}>是</Radio>
													<Radio value={0}>否</Radio>
												</Radio.Group>,
											)}
										</Form.Item>
									</Col>
								</Row>


							</Form>
						</div>
					):(
						<div className="mt30"  style={{fontSize:"14px",color:"#000"}}>
							<Row>
								<Col span={12}>
									甲方合同是否已签订：<span style={hoFields.contractSigning===1?{"color":"green"}:{"color":"red"}}>{hoFields.contractSigning===1?"是":"否"}</span>
								</Col>
								<Col span={12}>
									甲方设计方案是否已签订：<span style={hoFields.designSigning===1?{"color":"green"}:{"color":"red"}}>{hoFields.designSigning===1?"是":"否"}</span>
								</Col>
							</Row>
						</div>
					)
				}

				<div className="mt30">
					<div className="require_label mb20">项目目标:</div>
					<EditableTableTwoItem
						id={'id'}
						store={this.store}
						dataSource={targetTableDataSource}
						disabled={true}
						pid={''}
					/>
				</div>

				<div className="mt30">
					<div className="require_label mb20">项目运行机制:</div>
					<div className="mt20 mb10">1、决策机制:</div>
					<EditableTableThreeItemMatter
							id={'id'}
							store={this.store}
							dataSource={btThreeList}
							disabled={true}
							pid={''}
					/>
					<div className="mt20 mb10">2、沟通机制:</div>
				<EditableTableFourItem
					id={'id'}
					store={this.store}
					dataSource={btFourList.map((item, index)=>{
						let masters = [];
						if(item.userTeamName!==null&&item.userTeamId!==null) masters = [{"userName":item.userTeamName, "userId":item.userTeamId}]
						return Object.assign(item, {"keyIndex":index+1, "masters":masters})
					})}
					disabled={true}
					pid={''}
				/>
				</div>

				<div className="mt30">
					<div className="require_label mb20">项目团队成员职责:</div>
					<EditableTableOneItem
						id={'id'}
						store={this.store}
						dataSource={this.formatDataSource()}
						disabled={true}
						pid={''}
					/>
				</div>

				<div className="mt30">
					<div style={{margin: "15px 0", padding: "10px 25px"}}>
						{handoverState.state1!==2?(<Button type="primary" onClick={this.buildStartMeetingPpt}>生成启动会PPT</Button>):null}

						{isDocument?<Document
							data={hoFields.document}
							store={this.store}
						/>:null}
					</div>
					<SendEmail
						userIds = {this.getUserIds()}
						emailUsers={this.state.emailUsers}
						obj={mobx.toJS(this.store.handoverOneObj)}
						store={this.store}
					/>
				</div>

				<div className="mt30">
					<div className="require_label mb20">附件:<span className="require_tips">（请按照要求上传附件内容，标星为必须上传项，可自由添加附件项目中不含的相关内容）</span></div>
					<EditableTableUpload
						id={'id'}
						store={this.store}
						dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
						saveData={this.saveContents}
						saveItem={this.saveItem}
						disabled={handoverState.state1===2?true:false}
						pid={''}
					/>
				</div>
				<div  className="mt30 mb30" style={handoverState.state1!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"
					        > 完成该阶段任务</Button><Button  onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div  className="mt30 mb30" style={handoverState.state1===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'>下一步</Button>
				</div>
				</Spin>
			</div>
		)
	}
}


export default OneItem;



