/*
*交付管理-日报查看
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal, Popconfirm, notification} from 'antd';
import {v4 as uuidv4} from 'uuid';
import EditableTable from './EditableTable';
import EditableTableTomorrowMaterial from './EditableTableTomorrowMaterial';
import MyUpload from "@/components/common/MyUpload";
import copy from 'copy-to-clipboard';
import {Base64} from 'js-base64';
import Zmage from 'react-zmage'
const dateFormat = 'YYYY-MM-DD HH:mm:ss';
const dateFormat2 = 'YYYY-MM-DD';

@inject('DeliveryStore')
@withRouter
@observer
export default class DailyView extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
           shareModalVisible:false,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	getProgressStateInfo=(state)=>{
		let info = "";
		if(state===0){
			info = "正常"
		}
		if(state===1){
			info = "提前"
		}
		if(state===2){
			info = "延迟"
		}
		return info;
	}

	showDailyDocumnet=()=>{
		const { projectDetail, dailyDetail } = this.store;
		let dailyDocuments = [];
		//获取后缀
		function getExt(filePath) {
			// 去掉url的问号
			if (filePath.indexOf("?") !=-1) {
				filePath = filePath.split("?")[0];
			}
			//获取最后一个.的位置
			let index = filePath.lastIndexOf(".");
			//获取后缀
			let ext = filePath.substr(index + 1);
			return ext;
		}

		if(mobx.toJS(dailyDetail).dailyDocuments!==null&&mobx.toJS(dailyDetail).dailyDocuments.length>0){
			dailyDocuments = mobx.toJS(dailyDetail).dailyDocuments.map((item, index)=>{
				if (item) {
					let isVideo = /(avi|dat|mpg|wmv|asf|rm|rmvb|mov|flv|mp4|3gp|dv|divx|qt|asx)$/.test(getExt(item.fileUrl));
					return Object.assign({}, {
						"fileId":null,
						"fileName":item.fileUrl,
						"filePath":item.fileUrl,
						"id":"",
						"name":item.name,
						"status":"done",
						"uid":uuidv4(),
						"url": isVideo?'./images/video.png':item.fileUrl
					})
				}
			})
			dailyDocuments = dailyDocuments.filter((item,index)=>{
				return item.url!==null
			})
		}
		return dailyDocuments

	}

	getWeatherIconUrl=(weather)=>{
		let iconUrl = "./images/delivery/weather/clearday.png";
		if(weather==="晴"){
			iconUrl = "./images/delivery/weather/clearday.png";
		}
		if(weather==="多云"){
			iconUrl = "./images/delivery/weather/cloud.png";
		}
		if(weather==="阴"){
			iconUrl = "./images/delivery/weather/overcast.png";
		}
		if(weather==="雨"){
			iconUrl = "./images/delivery/weather/rain.png";
		}
		if(weather==="雪"){
			iconUrl = "./images/delivery/weather/snow.png";
		}
		if(weather==="大风"){
			iconUrl = "./images/delivery/weather/wind.png";
		}
		if(weather==="龙卷风"){
			iconUrl = "./images/delivery/weather/tornado.png";
		}
		if(weather==="雾霾"){
			iconUrl = "./images/delivery/weather/haze.png";
		}
		return iconUrl;

	}

	handleSelectedDaily=(item,index)=>{
		this.setState({currentItem:index})
		this.store.getDailyById({"id":item.id}).then((res)=>{
			this.store.dailyDetail = Object.assign({},res);
		})
	}

	getDailyListPage=()=>{
		const { projectDetail } = this.store;
		this.store.dailyParmas.projectId = mobx.toJS(projectDetail).id;
		this.store.dailyParmas.pageIndex = 1;
		this.store.getDailyListPage().then((res)=>{
			this.store.dailyList = Object.assign([], res.body);
			this.store.dailyParmas.pageIndex = res.pageIndex;
			this.store.dailyParmas.pageSize = res.pageSize;
			this.store.dailyParmas.totalCount = res.totalCount;
			if(res.body.length>0){
				this.handleSelectedDaily(res.body[0],0)
			}
		});
	}

	editDaily=()=>{
		this.store.dailyPageState = 2; //进入日报编辑状态
	}

	delDaily=()=>{
		const { dailyDetail } = this.store;
		this.store.delDaily({"id":dailyDetail.id}).then((res)=>{
			this.getDailyListPage();
		})
	}

    handleCloseModal=()=>{
		this.setState({shareModalVisible:false})
	}

	showModal=()=>{
		this.setState({shareModalVisible:true})
	}

copyHref=()=>{
	const { projectDetail, dailyDetail } = this.store;
	copy(`https://www.bocspace.cn/repair/#/delivery/dailyshare?projectid=${projectDetail.id}&dailyid=${dailyDetail.id}`);
	notification.success({
		message: '成功！',
		description: '成功复制到粘贴板！',
	});
}

	resolveNoArriveMaterials=(dailyDetail)=>{
		let result;
		if(dailyDetail.noArriveMaterials===null){
			result = "无";
		}else if(mobx.toJS(dailyDetail).noArriveMaterials.filter((item)=>{return item===null}).length===mobx.toJS(dailyDetail).noArriveMaterials.length){
			result = "无";
		}else{
			result = mobx.toJS(dailyDetail).noArriveMaterials.map((item)=>{
				if(item!==null && item.showName!==undefined){
					return item.showName
				}else{
					return "-"
				}
			}).join(", ")
		}
		return result
	}


	render() {
		const { shareModalVisible } = this.state;
		const { projectDetail, dailyDetail } = this.store;
		const dailyParmas = mobx.toJS(this.store.dailyParmas);
		const {type} = dailyParmas;

		return (
			<div >
				<div className="daily_view_head">
					<div className="title_info">
						<div className="daily_view_title">{`${projectDetail.name}-${dailyDetail.title}`}</div>
						<div className="daily_view_progress">施工进度 <span className="color_status">{this.getProgressStateInfo(dailyDetail.progressState)}</span></div>
					</div>
					<div className="buttons_zone">
						<Button className={type==0?"title":"hidden"} type="link" onClick={this.showModal} ><img src="./images/delivery/u389.png"  style={{width:'20px', height:'20px'}} />分享</Button>
						<span className={type==0?"gap":"hidden"}> </span>
						<Button className="title" type="link" onClick={this.editDaily}><img src="./images/delivery/u1479.png"  style={{width:'16px', height:'16px'}} />编辑</Button>
						<span className="gap"> </span>
						<Popconfirm title="确定删除?" onConfirm={this.delDaily}>
							<Button className="title"  type="link" ><img src="./images/delivery/u1480.png" style={{width:'16px', height:'16px'}} />删除</Button>
						</Popconfirm>
					</div>
				</div>
				<div className="weather_author">
					<img src={this.getWeatherIconUrl(dailyDetail.weather)} style={{width:'27px', height:'27px'}} />
					<div className="info">
						<div className="weather">
							{`${dailyDetail.weather}，${dailyDetail.windPower}`}
						</div>
						<div className="author">
							{dailyDetail.createUserName===null?'':dailyDetail.createUserName} {dailyDetail.createTime!==0?moment(dailyDetail.createTime).format(dateFormat):''}
						</div>
					</div>
				</div>
				<div className="daily_smalltip" style={{paddingLeft: "20px", color: "#02A7F0", marginBottom: 0}}>
					<span className="mr30"><span className="fwb mr5">箱体数量:</span> {dailyDetail.boxCount}套</span>
					<span className="mr30"><span className="fwb mr5">项目用途:</span> {dailyDetail.purpose}</span>
					<span className="mr30"><span className="fwb mr5">计划完成时间:</span> {dailyDetail.planOverTime==null||dailyDetail.planOverTime==0?"无":moment(dailyDetail.planOverTime).format(dateFormat2)}</span>
					<span className="mr30"><span className="fwb mr5">倒计时:</span> {dailyDetail.countdownDay}天</span>
				</div>
				<div className="daily_smalltip" style={{paddingLeft: "20px", color: "#02A7F0", margin: 0}}>
					<span className="mr30"><span className="fwb mr5">到场数量:</span> {dailyDetail.arrivalCount}间</span>
					<span className="mr30"><span className="fwb mr5">已安装数量:</span> {dailyDetail.installCount}间</span>
					<span className="mr30"><span className="fwb mr5">在途数量:</span> {dailyDetail.onWayCount}间</span>
					<div>
						<span className="mr30"><span className="fwb mr5">司机姓名:</span> {dailyDetail.driverName}</span>
						<span className="mr30"><span className="fwb mr5">手机号:</span> {dailyDetail.driverPhone}</span>
					</div>
					<div>
						<span className="mr30"><span className="fwb mr5">配送进展:</span>
							{
								dailyDetail.distributionJson==null||dailyDetail.distributionJson==""?"":
									JSON.parse(dailyDetail.distributionJson).map((item,index)=>{
										return  <div key={index}>
                           <span className="dill_title ellsis" title={item.first} style={{color: "#333"}}>
                               {index+1+". "}
							   {item.first==""||item.first==undefined?"无":item.first}</span>
											<span className="dill_title ellsis w40 ml5" style={{color: "#0ECE73"}} title={item.last}>{item.last}</span></div>
									})
							}
						</span>
					</div>
				</div>
				{
					dailyDetail.overallDescription!==null&&dailyDetail.overallDescription!==""&&<div className="overall_desc">
						<div className="desc_info">
							<div className="deaft_content_detail" dangerouslySetInnerHTML={{__html:dailyDetail.overallDescription}}></div>
						</div>
					</div>
				}
				<div className="daily_view_item">
					<div className="daily_view_label">今日施工内容</div>
					<div className="daily_view_content">
						<EditableTable
							id={'id'}
							store={this.store}
							dataSource={dailyDetail.contents}
							disabled={true}
							tableType={'today'}
							pid={''}
						/>
					</div>
				</div>
				{dailyDetail.contentRemarks==null||dailyDetail.contentRemarks==""?"":<div className="daily_view_item">
					<div className="daily_view_label">备注信息</div>
					<div className="daily_view_content">
						<p>{dailyDetail.contentRemarks}</p>
					</div>
				</div>}
				<div style={{display:'flex'}}>
					<div className="daily_view_item" >
						<div className="daily_view_label">今日工人数量</div>
						<div className="daily_view_content">
							{dailyDetail.workersCount}人
						</div>
					</div>
					<div className="daily_view_item" >
						<div className="daily_view_label">未按计划进场材料</div>
						<div className="daily_view_content">
							{this.resolveNoArriveMaterials(dailyDetail)}
						</div>
					</div>
					<div className="daily_view_item" >
						<div className="daily_view_label">是否影响施工进度</div>
						<div className="daily_view_content">
							{dailyDetail.materialInfluence}
						</div>
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">今日进场材料</div>
					<div className="daily_view_content">
						{dailyDetail.material}
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">现场照片</div>
					<div className="daily_view_content">
						<MyUpload
							disabled={true}
							multiple={true}
							listType="picture-card"
							fileList={this.showDailyDocumnet()}
							journalType="journal"
						/>
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">安全文明施工定点照片</div>
					<div className="daily_view_content">
						<div className="clearfix mt20 mb20">
							<div style={{ width: '250px', float: 'left' }}>监测点名称</div>
							<div style={{ width: '100px', textAlign: 'center', float: 'left', margin: '0 60px' }}>
								上传照片
							</div>
							<div style={{ width: '250px', textAlign: 'center', float: 'left' }}>上传时间</div>
						</div>
						{mobx.toJS(dailyDetail).dailyProjectPhotos!=null&&mobx.toJS(dailyDetail).dailyProjectPhotos.length>0&&mobx.toJS(dailyDetail).dailyProjectPhotos.map((item, index) => {
							return (<div key={'photos-' + index} className="clearfix mt10">
								<div style={{ width: '250px', float: 'left'}}>
									{item.place}
								</div>
								<div style={{ width: '100px', textAlign: 'center', float: 'left', margin: '0 60px' }}>
									{Array.isArray(item.imageDocs)&&item.imageDocs.length>0?<Zmage
										width="80"
										height="80"
										src={item.imageDocs[0].fileUrl} alt=""/>:<div style={{width: "150px", height: "80px"}}>

									</div>}
								</div>
								<div style={{ width: '250px', textAlign: 'center', float: 'left'}}>
									{item.createTime == 0 || item.createTime == null ? '' : moment(item.createTime).format('YYYY.MM.DD.HH:mm:ss')}
								</div>
							</div>);
						})}
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">现场施工总结</div>
					<div className="daily_view_content">
						{/*<p>*/}
						{/*	{dailyDetail.summary}*/}
						{/*</p>*/}
						<div className="deaft_content_detail" dangerouslySetInnerHTML={{__html:dailyDetail.summary}}></div>
					</div>
				</div>
				<div className="split-line"></div>
				<div className="daily_view_item">
					<div className="daily_view_label">明日施工计划</div>
					<div className="daily_view_content">
						<EditableTable
							id={'id'}
							store={this.store}
							dataSource={dailyDetail.tomorrowContents}
							disabled={true}
							tableType={'tomorrow'}
							pid={''}
						/>
					</div>
				</div>
				{dailyDetail.tomorrowPlanRemarks==null||dailyDetail.tomorrowPlanRemarks==""?null:<div className="daily_view_item">
					<div className="daily_view_label">备注信息</div>
					<div className="daily_view_content">
						<p>{dailyDetail.tomorrowPlanRemarks}</p>
					</div>
				</div>}
				<div className="daily_view_item">
					<div className="daily_view_label">明日天气</div>
					<div className="daily_view_content">
						<div className="weather_info">
							<img  src={this.getWeatherIconUrl(dailyDetail.tomorrowWeather)}  style={{width:'27px', height:'27px'}} />
							<div className="info">
								<div className="weather">
									{`${dailyDetail.tomorrowWeather}，${dailyDetail.tomorrowWindPower}`}
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">明日进场材料</div>
					<div className="daily_view_content">
						<p>{dailyDetail.tomorrowMaterial}</p>
					{/*<EditableTableTomorrowMaterial
						id={'id'}
						store={this.store}
						dataSource={dailyDetail.tomorrowPlanArriveMaterial}
						disabled={true}
						pid={''}
					/>*/}
					</div>
				</div>
				<div className="daily_view_item">
					<div className="daily_view_label">风险与协助</div>
					<div className="daily_view_content" style={{paddingBottom:'40px'}}>
						<div className="deaft_content_detail" dangerouslySetInnerHTML={{__html:dailyDetail.assistance}}></div>
					</div>
				</div>
				<Modal
					title="分享日报"
					visible={shareModalVisible}
					maskClosable={false}
					onCancel={this.handleCloseModal}
					footer={null}
					style={{ top:60 ,height:'100px', minWidth:'500px', maxWidth:'500px'}}
					bodyStyle={{padding:0}}
				>
					<div  style={{padding:'20px', textAlign:'left'}}>
						<p>
							{`https://www.bocspace.cn/repair/#/delivery/dailyshare?projectid=${projectDetail.id}&dailyid=${dailyDetail.id}`}
						</p>
					</div>
					<div className="daily_submit_zone">
						<Button onClick={this.copyHref} type="primary" style={{marginTop:'20px',marginRight:'20px'}} >
							复制链接
						</Button>
					</div>

				</Modal>
			</div>
		)
	}
}






