import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class Marketing extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			markingTargetStandard:false,
			markingTargetSatisfied:false,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleChange = (e,type) => {
		console.log(`checked = ${e.target.checked}`, type);
		this.props.updateData(type, e.target.checked?1:0)
	};

	render() {
		const { markingTargetStandard, markingTargetSatisfied } = this.state;
		const { buildTeamTwoMainObj } = this.store;
		const { label } = this.props;
		let fields = mobx.toJS(buildTeamTwoMainObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};
		return (
			<div className="mt15">
				<Form {...formItemLayout} >
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
							<Form.Item label={label}  labelAlign="left" className="w" style={{ marginBottom:"0"}}>
								<div style={{display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
									<Checkbox className="cost-check" checked={fields.markingTargetStandard} onChange={(e)=>{this.handleChange(e,"markingTargetStandard")}}>
										打造成为标杆项目，形成营销案例
									</Checkbox>
									<Checkbox className="cost-check" checked={fields.markingTargetSatisfied} onChange={(e)=>{this.handleChange(e,"markingTargetSatisfied")}}>
										顾客满意，能够给我们发表扬信
									</Checkbox>
								</div>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</div>
		)
	}
}






