import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class BackgroundItemOne extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {
		this.props.onRef && this.props.onRef(this);
	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressTargetTimeSure",e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	handleBaseTime=(date, datestring)=>{
		this.props.updateData("progressTargetBaseTime",date.valueOf())
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOverOne(type,e)
	}

	checkFormValidate(){
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values);
			}
		});
	}


	render() {
		const { sureState } = this.state;
		const { handoverOneObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverOneObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 20},
				sm: {span: 20},
			},
		};

		return (
			<div className="mt15">
				<Form >
					{handoverState.state1===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<Form.Item label="背景-基本概况" className="w">
									<div
										style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
										className="pr">
										{handoverOneObj.background}
									</div>
								</Form.Item>
								<Form.Item label="项目意义" className="w">
								<div
										style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
										className="pr">
										{handoverOneObj.significance}
									</div>
								</Form.Item>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<Form.Item label="背景-基本概况" className="w">
										{getFieldDecorator('background', {
										initialValue: fields.background == null ? "" : fields.background,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input.TextArea ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("background", e)}}  rows={6} placeholder="描述项目性质、地点、规模、供应范围、项目周期等基本情况。" style={{width:'100%',minWidth:'800px'}} />
									)}
								</Form.Item>
								<Form.Item label="项目意义" className="w">
									{getFieldDecorator('significance', {
										initialValue: fields.significance == null ? "" : fields.significance,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input.TextArea onBlur={(e)=>{this.handleUpdate("significance", e)}} rows={6} placeholder="描述此项目成功能产生的价值，如打造品牌形象，树立区域标杆等。"  style={{width:'100%',minWidth:'800px'}} />
									)}
								</Form.Item>
							</Col>
						</Row>
					}
				</Form>
			</div>
		)
	}
}






