import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {message, Form, InputNumber, Icon, Select, Table, Popconfirm, Button, Row, Col} from 'antd';
import TwoItemView from './TwoItemView';
const { Option } = Select;
import PageSelect2 from "@/components/common/PageSelect2";
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
class EditableCell extends React.Component {
	state = {
		editing: true,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			handleSave({...record, ...values});
		});
	};

	onChange = (type, e) => {
		const {record, handleSave} = this.props;
		if (record["resType"]!=e) {   // 不相等时清空
			handleSave({...record, ...{
					[type]: e,
					"name": "",
					"omId": ""
				}});
		}
		else  {
			handleSave({...record, ...{[type]: e}});
		}
	}

	updateStore = (param) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			values["name"] = param.name;
			values["omId"] = param.id;
			handleSave({...record, ...values});
		});
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;
		if (dataIndex == "resType") {   // 资源类型
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(
						<Select style={{width: 200}}
								ref={node => (this.input = node)}
								value={record[dataIndex]}
								onChange={this.onChange.bind(this, "resType")}
						>
							<Option value="人工">人工</Option>
							<Option value="机械">机械</Option>
						</Select>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == "name") {   // 名称
			return editing ? (
				<Form.Item style={{margin: 0}} className="w">
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex] == null || record[dataIndex] == "" ? [] : record[dataIndex],
					})(
						<div className="w">
							<PageSelect2
								disabled={record["resType"]==null||record["resType"]==""?true:false}
								type={record["resType"]=="机械"?"getMachinePageList":"getWorkerPageList"}
								name={record["name"]}
								value={record["omId"]|| ""}
								updateStore={this.updateStore}
							/>
						</div>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{record[dataIndex] == null || record[dataIndex] == "" ? null : record[dataIndex].join("、")}
				</div>
			);
		} else {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<InputNumber style={{width: "100%"}} ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

@Form.create()
@inject('DeliveryStore')
@withRouter
@observer
class ThreeItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			tableDatasource: [],  // 表格数据源
			dataSource: [],
			dataSourceTwo: [],
			loading: false,
			main: {
				"efficiency":100,
				"currentEfficiency": 100
			}
		}
	}

	componentDidMount() {
		const {taskDetail} = this.store;
		this.store.getScheduleResourcesListByTaskId({
			"type": 1,
			"taskId": mobx.toJS(taskDetail).id
		}).then((res) => {
			this.setState({
				tableDatasource: res==null?[]:res
			});
		});
		this.calculationSchedule("1");
		this.store.getConstructionMainByProjectId().then((res)=>{
			if (res) {
				this.setState({
					main: res
				});
			}
		});
	}

	getDetailWorkContentByProjectId = () => {
		this.setState({
			loading: true
		}, ()=>{
			this.store.getDetailWorkContentByProjectId(1)
				.then((res) => {
					if (res) {
						this.setState({
							loading: false,
							dataSourceTwo: Array.isArray(res) ? res : []
						});
					} else {
						this.setState({
							loading: false,
						});
					}
				});
		});
	}

	getConstructionScheduleDetail = () => {
		const {taskDetail} = this.store;
		this.store.getScheduleDetailListByTaskId({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
			this.setState({
				dataSource: Array.isArray(res) ? res : []
			});
		});
	}

	// 计算排期
	calculationSchedule = (type) => {
		const {taskDetail} = this.store;
		let time = moment().format('YYYY-MM-DD');
		const params = {
			taskId: mobx.toJS(taskDetail).id,
			projectId: this.store.projectId,
			dateTime: moment(time).valueOf()
		}
		this.store.getCurrentContents(params)
			.then((res) => {
				if (res) {
					if (type!="1") {
						message.success("操作成功");
					}
					this.setState({
						dataSource: Array.isArray(res) ? res : []
					});

					this.getDetailWorkContentByProjectId();
				}
			});
	}

	// 表格新增
	tableAdd = () => {
		const {tableDatasource} = this.state;
		const {taskDetail} = this.store;
		const newData = {
			id: "",
			projectId: this.store.projectId,
			taskId: mobx.toJS(taskDetail).id,
			name: "",
			code: "",
			counts: null,
			omId: "",
			resType: ""
		};
		let data = [...tableDatasource, newData];
		this.saveScheduleResourcesList(data);
	}

	// 表格删除
	tableDel = (index) => {
		const tableDatasource = [...this.state.tableDatasource];
		tableDatasource.splice(index, 1);
		this.saveScheduleResourcesList(tableDatasource);
		message.success("删除成功");
	}

	// 表格报存
	tableSave = (row) => {
		const newData = [...this.state.tableDatasource];
		const index = newData.findIndex(item => row.id === item.id);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row
		});
		this.saveScheduleResourcesList(newData);
	}

	saveScheduleResourcesList = (data) => {
		const params = {
			list: data,
			type: 1
		}
		this.store.saveScheduleResourcesList(params)
			.then((res)=>{
				this.setState({
					tableDatasource: res
				});
			});
	}

	handleChange2 = (type, value) => {
		let obj = JSON.parse(JSON.stringify(this.state.main));
		obj[type] = value;
		this.setState({
			main: Object.assign({}, obj)
		});
	}

	handBlur2 = () => {
		this.store.saveConstructionMain(this.state.main).then((res)=>{
			if (res) {
				message.success("操作成功");
				this.setState({
					main: Object.assign({}, res)
				});
			}
		});
	}

	render() {
		const {currentStep} = this.props;
		const {dataSource, tableDatasource, dataSourceTwo, loading, main} = this.state;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '操作',
				dataIndex: 'opater',
				width: '80px',
				align: 'center',
				render: (text, record, index) => <Popconfirm title="确定要删除吗?" onConfirm={() => this.tableDel(index)}>
					<Icon type="minus-circle" style={{fontSize: "18px", color: "#E9796B", cursor: "pointer"}}/>
				</Popconfirm>
			},
			{
				title: '序号',
				dataIndex: 'key',
				width: '80px',
				align: 'center',
				render:(text,record,index)=>`${index+1}`,
			},
			{
				title: '资源类型',
				dataIndex: 'resType',
				align: 'center',
				width: '200px',
				editable: true,
			},
			{
				title: '名称',
				dataIndex: 'name',
				align: 'center',
				editable: true,
			},
			{
				title: '数量',
				dataIndex: 'counts',
				align: 'center',
				editable: true,
			}
		];
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.tableSave
				}),
			};
		});
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {span: 3},
			wrapperCol: {span: 12},
		};
		return (
			<div className="pb50">
				<Form layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}
						 style={{
							 margin: "0"
						 }}>
						<Col xs={24} sm={24} md={18} lg={12} xl={12}>
							<Form.Item label="工时系数" className="w">
								{getFieldDecorator('currentEfficiency')(
									<div className="w">
										<InputNumber
											className="w"
											min={0}
											max={500}
											formatter={value => `${value}%`}
											parser={value => value.replace('%', '')}
											onChange={this.handleChange2.bind(this, "currentEfficiency")}
											onBlur={this.handBlur2}
											value={main.currentEfficiency}
										/>
									</div>
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
				<Button disabled={loading} onClick={this.calculationSchedule} type="primary">计算排期</Button>
				<Table
					components={components}
					className="digital_table mt15 mb15"
					rowClassName={() => 'editable-row'}
					dataSource={tableDatasource}
					columns={column}
					pagination={false}
					bordered
					rowKey={record => record.id}
				/>
				<Icon onClick={this.tableAdd}
					  type="plus-circle"
					  style={{
						  position: "relative",
						  left: "28px",
						  fontSize: "18px",
						  color: "#609AF1",
						  cursor: "pointer",
						  marginBottom: "15px"
					  }}/>
					<div ref={node => {
						this.container = node;
					}}>
						<TwoItemView
							disabled={loading}
							type="three"
							currentStep={currentStep}
							parentContainer={this.container}
							dataSource={dataSource}
							dataSourceTwo={dataSourceTwo}
							reStartHandover={this.props.reStartHandover}
							store={this.store}/>
					</div>
			</div>
		)
	}
}


export default ThreeItem;



