import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class Cost extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleChange = (e,type) => {
		console.log(`checked = ${e.target.checked}`, type);
		this.props.updateData(type, e.target.checked?1:0)
	};


	render() {
		const {  } = this.state;
		const { buildTeamTwoMainObj } = this.store;
		const { label } = this.props;
		let fields = mobx.toJS(buildTeamTwoMainObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};

		return (
			<div className="mt15">
				<Form {...formItemLayout} >
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
							<Form.Item label={""} labelAlign="left" className="w" style={{ marginBottom:"0"}}>
								<div style={{display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
									<Checkbox checked={fields.costTargetOne} onChange={(e)=>{this.handleChange(e,"costTargetOne")}}>
										分包费用：参照合同
									</Checkbox>
									<div style={{display:'flex' ,alignItems:'top'}}>
										<div><Checkbox checked={fields.costTargetTwo} onChange={(e)=>{this.handleChange(e,"costTargetTwo")}}></Checkbox><span style={{paddingLeft:'8px'}}>安全文明施工：</span></div>
										<div >合同额≤200万，费用≤2000元<br />合同额＞200万，费用≤3000元</div>
									</div>
									<Checkbox className="cost-check" checked={fields.costTargetThree} onChange={(e)=>{this.handleChange(e,"costTargetThree")}}>招待费：≤合同额*2‰  且  ≤2000元</Checkbox>
									<Checkbox className="cost-check" checked={fields.costTargetThree2} onChange={(e)=>{this.handleChange(e,"costTargetThree2")}}>招待费（展厅适用）：≤合同额的2‰ 且 ≤1500元</Checkbox>
									<Checkbox className="cost-check" checked={fields.costTargetFour} onChange={(e)=>{this.handleChange(e,"costTargetFour")}}>风险附加：≤合同额*5‰  且  ≤10000元</Checkbox>
									<Checkbox className="cost-check" checked={fields.costTargetFour2} onChange={(e)=>{this.handleChange(e,"costTargetFour2")}}>风险附加（展厅适用）：≤合同额的5‰ 且 ≤5000元</Checkbox>
								</div>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</div>
		)
	}
}






