import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {v4 as uuidv4} from 'uuid';
import {withRouter, Link} from "react-router-dom";
import {DatePicker, Select, Button, Table, Pagination, Input, ConfigProvider, message, Icon, Modal, Radio, Popconfirm, Popover, Breadcrumb, Dropdown, Menu} from 'antd';
import {downloadFile, download} from "@/utils/download";

import BaseInfoItemTwo from './BaseInfoItemTwo';
import ContractItemTwo from './ContractItemTwo';
import OthersItemTwo from './OthersItemTwo';
import EditableTableUpload from './EditableTableUpload';
import EditableTableInventory from "./EditableTableInventory";

const InputGroup = Input.Group;
const {Option} = Select;
const {RangePicker} = DatePicker;
const { Search } = Input;

@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			dataSource:[],
			bttiList:[],
			visible: false,
			stateValue: 0,
			id: "",
			baseInfoFormRequire:false,
			contractFormRequire:false,
			dataSourceInventory: []
		}
	}

	componentDidMount() {
		this.getHandoverTwo();
		this.getEnclosureList();
		// this.BaseInfoItemTwo.checkFormValidate();
		// this.ContractItemTwo.checkFormValidate();
	}


	getBuildTeamTwoItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamTwoItemList({"taskId": taskDetail.id}).then((res)=>{
			this.setState({bttiList:res})
		})
	}

	saveBuildTeamTwoItem=(params)=>{
		this.store.saveBuildTeamTwoItem(params).then((res)=>{
			this.getBuildTeamTwoItemList();
		})
	}


	saveContents=(dataSource)=>{
		this.setState({bttiList:dataSource})
	}

	convertToTableData=()=>{
		const {dataList, bttiList} = this.state;

		return dataList.map((item,index)=>{
					return Object.assign(item, {"dataType":"main"})
				}).concat(
					bttiList.map((item,index)=>{
						return Object.assign(item, {"dataType":"item", "keyIndex":dataList.length+1+index})
					})
				)
	}

	// 通过wrappedComponentRef获取子组件的ref
	saveFormRef = formRef => {
		this.formRef = formRef;
	};

	overCurrentStage=()=>{
		if(!this.checkFormRequire())return
		this.props.overCurrentStageTask();
		this.getHandoverTwo();
		this.getEnclosureList();
	}

	doneAndNext=()=>{
		if(!this.checkFormRequire())return
		const { currentStep } = this.props;
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	checkFormRequire=()=>{
		const { handoverTwoObj } = this.store;
		const { dataSource } = this.state;
		const hto = mobx.toJS(handoverTwoObj);
		if(
			hto.companyName ===null || hto.companyName ==="" ||
			hto.cooperations ===null || hto.cooperations ==="" ||
			hto.glodonRelationship ===null || hto.glodonRelationship ==="" ||
			hto.bocspaceRelationship ===null || hto.bocspaceRelationship ==="" ||
			hto.customerExpectations ===null || hto.customerExpectations ==="" || hto.customerExpectations ==="<p></p>" ||
			hto.promise ===null || hto.promise ==="" || hto.promise ==="<p></p>"||
			hto.purpose ===null || hto.purpose ==="" ||
			hto.turnoverCount ===null || hto.turnoverCount ===""
	    ){
			message.warn("请检查项目基本情况是否已经全部填写")
			return false
		}

		if( hto.centralized ===1 && (hto.centralizeCompany ==="" || hto.centralizeCompany ===null)
		){
			message.warn("集采单位名称是否已经填写")
			return false
		}

		if(
			hto.deliverRange ===null || hto.deliverRange ==="" ||
			hto.deliverNode ===null || hto.deliverNode ==="" ||
			hto.requirementBeginTime ===null || hto.requirementEndTime ===null ||
			hto.contractMoney===null
		){
			message.warn("请检查合同解读是否已经全部填写")
			return false
		}
		const dsCheck = dataSource.filter((item,index)=>{
			return item.isMust===1&&item.enclosureItems.length===0
		})
		if(dsCheck.length>0){
			message.warn("请检查附件列表中必需上传项是否已经上传附件")
			return false
		}
		return true
	}

	getEnclosureList=()=>{
		const { taskDetail, projectDetail } = this.store;
		this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, indexs:2}).then((res)=>{
			this.store.enclosureList = res
			this.setState({dataSource: res, enclosureList: res})

		})
	}

	getHandoverTwo=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverTwo({"taskId": taskDetail.id}).then((res)=>{
			if (res) {
				this.store.handoverTwoObj =res;
				this.setState({
					dataSourceInventory: res.contractList==null?[]:res.contractList.map((v, vindex)=>Object.assign(v, {rate: "13%", key: vindex+1}))
				});
			}
		});
	}

	saveHandoverTwo=(newObj)=>{
		const { handoverTwoObj, taskDetail } = this.store;
		this.store.saveHandoverTwo({"ho":Object.assign(mobx.toJS(handoverTwoObj),{"taskId": taskDetail.id}, newObj)}).then((res)=>{
			if(res){
				this.getHandoverTwo()
			}
		})
	}

	updateHandOver=(type, e)=>{
		let newObj = {};
		if (type=="turnoverCount") {  // 周转次数
			newObj[type] = e.target.value==null||e.target.value==""?0:Number(e.target.value);
		}
		else {
			newObj[type] = e.target.value;
		}
		this.saveHandoverTwo(newObj)
	}

	updateHandOver2=(type, value)=>{
		let newObj = {};
		newObj[type] = value
		this.saveHandoverTwo(newObj)
	}

	updateHandOver3=(newObj)=>{
		this.saveHandoverTwo(newObj)
	}


	updateHandOverBC=(type, bfData)=>{
		let newObj = {};
		newObj[type] = bfData
		this.saveHandoverTwo(newObj)
	}

	saveContents=()=>{
		this.getEnclosureList();
	}

	checkFormRequreBackground=(result)=>{
		this.setState({baseInfoFormRequire:result})
	}

	checkFormRequreContract=(result)=>{
		this.setState({contractFormRequire:result})
	}

	saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:2,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:2}
		this.store.saveEnclosureMain(parmas).then((res)=>{
			this.getEnclosureList()
		})
	}

	// 更新合同清单
	updateState = (type, data) => {
		this.setState({
			[type]: Object.assign([], data)
		});
		let newData = JSON.parse(JSON.stringify(data));
		if (newData.length>0) {
			newData.map(v=>{
				if (v.key) delete v.key;
				if (v.rate) delete v.rate;
				return v;
			});
		}
		this.updateHandOverBC("contractList", newData);
	}

	render() {
		const {handoverState} = this.store;
		const {dataList, bttiList, dataSource, dataSourceInventory} = this.state;
		const { currentStep, switchSteps } = this.props;
		return (
			<div className="mt30">
				<div >
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.props.reStartHandover}
						style={handoverState.state2===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						重新启动
					</Button>
				</div>
				<div className="daily_tips">
					<p><span>小交温馨提醒：商务交接可以帮助项目经理快速了解客户及项目主要信息，请商务耐心给项目经理解答哦~</span></p>
				</div>
				<div className="mt30">
					<div className="require_label">项目基本情况:</div>
					<BaseInfoItemTwo updateHandOver3={this.updateHandOver3} updateHandOver2={this.updateHandOver2} onRef={ node => this.BaseInfoItemTwo = node } checkFormRequre={this.checkFormRequreBackground}  updateHandOverBC={this.updateHandOverBC} updateHandOver={this.updateHandOver} store={this.store} />
				</div>
				<div className="mt30">
					<div className="require_label">合同解读:</div>
					<ContractItemTwo
						updateHandOver3={this.updateHandOver3} updateHandOver2={this.updateHandOver2}
						onRef={ node => this.ContractItemTwo = node } checkFormRequre={this.checkFormRequreContract} updateHandOver={this.updateHandOver} updateHandOver2={this.updateHandOver2} store={this.store} />
				</div>
				<div className="mt30">
					<div className="require_label">合同清单:</div>
					<EditableTableInventory
						disabled={handoverState.state2===2?true:false}
						dataSource={dataSourceInventory}
						store={this.store}
						updateState={this.updateState.bind(this, "dataSourceInventory")}
					/>
				</div>
				<div className="mt30">
					<div className="require_label">其他:</div>
					<OthersItemTwo updateHandOverBC={this.updateHandOverBC} updateHandOver={this.updateHandOver} store={this.store} />
				</div>
				<div className="mt30">
					<div className="require_label mb10">附件:</div>
					<EditableTableUpload
						id={'id'}
						store={this.store}
						dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
						saveData={this.saveContents}
						saveItem={this.saveItem}
						disabled={handoverState.state2===2?true:false}
						pid={''}
					/>
				</div>
				<div  className="mt30 mb30" style={handoverState.state2!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"
					        > 完成该阶段任务</Button><Button onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div className="mt30 mb30" style={handoverState.state2===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'>下一步</Button>
				</div>
			</div>
		)
	}
}


export default TwoItem;



