/*
* 发送邮件
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Button, message} from 'antd';
import EditableTagEmail2 from './EditableTagEmail2';
import PersonAdd2 from './PersonAdd';

@observer
class SendEmail extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
            emails: [], // 邮件
            userIds:this.props.userIds || [], // 用户
            ccEmails: [],
            ccUserIds: [],
		}
	}

	componentDidMount() {
	 this.reset();
    }

    componentWillReceiveProps(nextProps){
        if (nextProps.userIds != this.props.userIds) {
            this.setState({
                userIds:nextProps.userIds
            });
        }
        if (nextProps.emailUsers != this.props.emailUsers) {
            const users = nextProps.emailUsers.map(item=>{return {"userId": item.id, "userName": item.userName}})
            this.setState({
                ccUserIds: users
            });
        }
    }

    sendReport = () => {
        let _this = this;
        const {emails, userIds, ccEmails, ccUserIds} = _this.state;
        const isemailUserIds = userIds == null ? [] : userIds;
        const isemails = emails == null ? [] : emails;
        // 选择收件人
        if (isemailUserIds.length == 0 && isemails.length == 0) {
            message.warn("请先选择邮件收件人");
            return false;
        }
        const {obj} = this.props;
        const params = {
            ho: obj,
            emails: emails,
            userIds: userIds == null ? [] : userIds.map(item => item.userId),
            ccEmails,
            ccUserIds: ccUserIds == null ? [] : ccUserIds.map(item => item.userId),
        }
        this.store.sendStartMeetingEmail(params)
            .then((res) => {
                if (res == true) {
                    message.success("邮件发送成功");
                    //this.reset();
                }
            });
    }

    updateState = (type, data) => {
        this.setState({
            [type]: Object.assign([], data)
        });
    }

    reset = () => {
        this.setState({
            emails: [], // 邮件
            userIds:[], // 用户
            ccEmails: [], // 抄送邮件
            ccUserIds: [], // 抄送用户
        });
    }

	render() {
        const formItemLayoutWithOutLabel = {
            wrapperCol: {
                xs: {span: 24, offset: 0},
                sm: {span: 20, offset: 3},
            }
        };
        const {emails, userIds, ccEmails, ccUserIds} = this.state;

        return (
              <Form>
                  <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>内容发送</h1>
                  <Form.Item {...formItemLayoutWithOutLabel}>
                   <Button
                       style={{position: 'relative', top: '4px'}}
                       className="fl"
                       type="link"
                       onClick={this.sendReport}>
                       点击发送邮件</Button>
               </Form.Item>
               <Form.Item {...formItemLayoutWithOutLabel}>
                   <div className="fl mr25 ml15">
                       <span>收件人：</span>
                       <EditableTagEmail2
                           title="添加收件人地址"
                           data={emails}
                           updateState={this.updateState.bind(this, 'emails')}
                       />
                   </div>
                   <div className="fl" style={{minWidth:'80px'}}>
                       <PersonAdd2
                           data={userIds}
                           updatePerson={this.updateState.bind(this, 'userIds')}
                           disabled={false}
                           title="添加收件人"
                       />
                   </div>
               </Form.Item>
               <Form.Item {...formItemLayoutWithOutLabel}>
                   <div className="fl mr25 ml15">
                       <span>抄送人：</span>
                       <EditableTagEmail2
                           title="添加抄送人地址"
                           data={ccEmails}
                           updateState={this.updateState.bind(this, 'ccEmails')}
                       />
                   </div>
                   <div className="fl" style={{minWidth:'80px'}}>
                       <PersonAdd2
                           data={ccUserIds}
                           updatePerson={this.updateState.bind(this, 'ccUserIds')}
                           disabled={false}
                           title="添加抄送人"
                       />
                   </div>
               </Form.Item>
           </Form>
		)
	}
}


export default SendEmail;



