import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {v4 as uuidv4} from 'uuid';
import {withRouter, Link} from "react-router-dom";
import {DatePicker, Select, Button, Table, Pagination, Input, ConfigProvider, message, Icon, Modal, Radio, Popconfirm, Popover, Breadcrumb, Dropdown, Menu} from 'antd';
import MyUpload2 from "@/components/common/MyUpload2";
import {downloadFile, download} from "@/utils/download";
import EditableTableTwoItem from "./EditableTableTwoItem";
const InputGroup = Input.Group;
const {Option} = Select;
const {RangePicker} = DatePicker;
const { Search } = Input;

const dateFormat = 'YYYY-MM-DD';

const progressValue = (day1, day2, day3) =>{

	return [
		`1、进场时间：图纸确认后${day1}天内材料开始进场（备注：需要甲方具备进场条件）`,
		`2、定制箱工厂生产时间：图纸签字后，${day2}天全部材料进场完毕`,
		`3、现场安装时间：图纸签字后，${day3}天全部安装完毕`
	]
}

const progressValueV4 = (day1, day2, day3) =>{

	if(day2===null || day2==="T+0"){
		return [
			`1、进场时间：${day1}`,
			`2、装配完成时间：${day3}`
		]
	}else{
		return [
			`1、进场时间：${day1}`,
			`2、非标箱进场时间：${day2}`,
			`3、装配完成时间：${day3}`
		]
	}

}


const qualityValue = (label, count) =>{
	return [
		`${label}：≤ ${count}次 ${label==='验收次数'?'客户确认签字':''}`,
	]
}

const profitValue = (label, number) =>{
	return [
		`${label}：     ${number===null?0:number}%`,
	]
}

const costValue = (label, number) =>{
	return [
		`${label}：≥ ${number}天 给供应商付款`,
	]
}


const safetyValue = (number1,number2) =>{
	return [
		`安全事故：≤ ${number1}次 死亡、重伤、轻伤`,
		`安全隐患：≤ ${number2}次 重大事故隐患`,
	]
}

const marketingValue = (flag1, flag2) =>{
	if(flag1===1&&flag2===1){
		return [
			`1、打造成为标杆项目，形成营销案例`,
			`2、顾客满意，能够给我们发表扬信`,
		]
	}
	if(flag1===1&&flag2===0){
		return [
			`顾客满意，能够给我们发表扬信`,
		]
	}
	if(flag1===0&&flag2===1){
		return [
			`打造成为标杆项目，形成营销案例`,
		]
	}
	if(flag1===0&&flag2===0){
		return []
	}

}





@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			dataList:[],
			bttiList:[],
			visible: false,
			stateValue: 0,
			id: "",
			buildTeamTwoMainObj:null

		}
	}

	componentDidMount() {
       this.getBuildTeamTwoMain();
       this.getBuildTeamTwoItemList();
	}

	getCostValue = () =>{
		const {  buildTeamTwoMainObj } = this.store;
		const { costTargetOne, costTargetTwo, costTargetThree, costTargetFour, costTargetThree2, costTargetFour2} = mobx.toJS(buildTeamTwoMainObj);
		const dataInfo =[
			{"show":costTargetOne, "info":"分包费用: 参照合同"},
			{"show":costTargetTwo, "info":"安全文明施工: 合同额≤200万，费用≤2000元; 合同额＞200万，费用≤3000元"},
			{"show":costTargetThree, "info":"招待费: ≤合同额*2‰ 且 ≤2000元"},
			{"show":costTargetThree2, "info":"招待费（展厅适用）: ≤合同额的2‰ 且 ≤1500元"},
			{"show":costTargetFour, "info":"风险附加: ≤合同额*5‰ 且 ≤10000元"},
			{"show":costTargetFour2, "info":"风险附加（展厅适用）:≤合同额的5‰ 且 ≤5000元"},
		]
		let result = dataInfo.filter((item)=>{
			return item.show===1
		})
		result = result.map((item)=>{
			return item.info
		})
		return result
	}


	getBuildTeamTwoMain=()=>{
		const { taskDetail, buildTeamTwoMainObj, buildTeamState } = this.store;
		this.store.getBuildTeamTwoMain({"taskId":taskDetail.id}).then((res)=>{
            this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res);
            let targetDay1 = res.progressTargetTimeSure===1? moment(res.progressTargetBaseTime).format(dateFormat):"T";
			let targetDay2 = targetDay1==="T"?"T+"+res.progressTargetNostandardDay+"日历天":moment(targetDay1).add(res.progressTargetNostandardDay,"days").format(dateFormat);
			let targetDay3 = targetDay1==="T"?"T+"+res.progressTargetOverDay+"日历天":moment(targetDay1).add(res.progressTargetOverDay,"days").format(dateFormat)

			let promiseDay1 = res.progressPromiseTimeSure===1? moment(res.progressPromiseBaseTime).format(dateFormat):"T";
			let promiseDay2 = promiseDay1==="T"?"T+"+res.progressPromiseNostandardDay+"日历天":moment(promiseDay1).add(res.progressPromiseNostandardDay,"days").format(dateFormat);
			let promiseDay3 = promiseDay1==="T"?"T+"+res.progressPromiseOverDay+"日历天":moment(promiseDay1).add(res.progressPromiseOverDay,"days").format(dateFormat)

			let profitTargetValue=res.profitTarget===0?"不亏损":profitValue("盈利",res.profitTargetRate)

			let dl = [
				{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
				{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
				{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
				{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"成本", "targetValue":this.getCostValue()},
				{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"营销", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
				{"keyIndex":6,  "id":"!"+uuidv4(), "orders":6, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
			]

			if(buildTeamState.state2===2){
				if(res.costTargetFour2===0&&res.costTargetThree2===0&&res.costTargetOne===0&&res.costTargetTwo===0&&res.costTargetThree===0&&res.costTargetFour===0&&res.markingTargetSatisfied===0&&res.markingTargetStandard===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else if(res.costTargetOne===0&&res.costTargetTwo===0&&res.costTargetFour2===0&&res.costTargetThree===0&&res.costTargetThree2===0&&res.costTargetFour===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"营销", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else if(res.markingTargetSatisfied===0&&res.markingTargetStandard===0){
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"成本", "targetValue":this.getCostValue()},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}else{
					dl = [
						{"keyIndex":1,  "id":"!"+uuidv4(), "orders":1, "item":"进度", "targetValue":progressValueV4(targetDay1,targetDay2,targetDay3), "promiseValue": progressValueV4(promiseDay1, promiseDay2, promiseDay3)},
						{"keyIndex":2,  "id":"!"+uuidv4(), "orders":2, "item":"质量", "targetValue":qualityValue("验收次数",res.qualityTargetCheckCount), "promiseValue": qualityValue("质量事故",res.qualityPromiseAccidentCount)},
						{"keyIndex":3,  "id":"!"+uuidv4(), "orders":3, "item":"利润", "targetValue":profitTargetValue, "promiseValue": ""},
						{"keyIndex":4,  "id":"!"+uuidv4(), "orders":4, "item":"成本", "targetValue":this.getCostValue()},
						{"keyIndex":5,  "id":"!"+uuidv4(), "orders":5, "item":"营销", "targetValue":marketingValue(res.markingTargetSatisfied,res.markingTargetStandard), "promiseValue": ""},
						{"keyIndex":6,  "id":"!"+uuidv4(), "orders":5, "item":"安全", "targetValue":safetyValue(res.safeTargetAccidentCount, res.safeTargetTreacherousCount), "promiseValue": safetyValue(res.safePromiseAccidentCount, res.safePromiseTreacherousCount)},
					]
				}
			}

            this.setState({dataList:dl, buildTeamTwoMainObj:res})
		})
	}

	getBuildTeamTwoItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamTwoItemList({"taskId": taskDetail.id}).then((res)=>{
			this.setState({bttiList:res})
		})
	}

	saveBuildTeamTwoItem=(params)=>{
		this.store.saveBuildTeamTwoItem(params).then((res)=>{
			this.getBuildTeamTwoItemList();
		})
	}


	saveContents=(dataSource)=>{
		console.log("dataSource=>", dataSource);
		this.setState({bttiList:dataSource})
	}



	saveItem=(row)=>{
		const {taskDetail} = this.store;
		const {dataList, bttiList} = this.state;
		let newItem={
			"buildTeamTwo":taskDetail.id,
			"orders":dataList.length+bttiList.length+1,
			"targetValue":row.targetValue,
			"promiseValue":row.promiseValue,
			"item":row.item,
		}
		if(row.id!==null || row.id!==undefined){
			newItem['id']=row.id
			newItem['orders']= row.orders
		}
		this.saveBuildTeamTwoItem({"btti":newItem})
	}

	convertToTableData=()=>{
		const {dataList, bttiList} = this.state;
		return dataList.map((item,index)=>{
					return Object.assign(item, {"dataType":"main"})
				}).concat(
					bttiList.map((item,index)=>{
						return Object.assign(item, {"dataType":"item", "keyIndex":dataList.length+1+index})
					})
				)
	}

	overCurrentStage=()=>{
		const cpt = this.checkProfitTarget();
		if(!cpt.result){
			message.warn(cpt.info)
			return
		}

		// 校验必填字段
		let buildTeamTwoMainObj = mobx.toJS(this.store.buildTeamTwoMainObj);
		if(buildTeamTwoMainObj.progressTargetTimeSure===1&&(buildTeamTwoMainObj.progressTargetBaseTime==null||buildTeamTwoMainObj.progressTargetBaseTime==0)){
		  message.warn("内控目标值，进场时间是必填项");
		  return false;
		}
		if(buildTeamTwoMainObj.progressPromiseTimeSure===1&&(buildTeamTwoMainObj.progressPromiseBaseTime==null||buildTeamTwoMainObj.progressPromiseBaseTime==0)){
			message.warn("对外承诺目标值，进场时间是必填项");
			return false;
		}

		this.props.overCurrentStageTask();
		setTimeout(()=>{
			this.getBuildTeamTwoMain();
			this.getBuildTeamTwoItemList();
		},500)
	}

	doneAndNext=()=>{
		const { currentStep } = this.props;
		const cpt = this.checkProfitTarget();
		if(!cpt.result){
			message.warn(cpt.info)
			return
		}

		// 校验必填字段
		const buildTeamTwoMainObj = mobx.toJS(this.store.buildTeamTwoMainObj);
		if(buildTeamTwoMainObj.progressTargetTimeSure===1&&(buildTeamTwoMainObj.progressTargetBaseTime==null||buildTeamTwoMainObj.progressTargetBaseTime==0)){
			message.warn("内控目标值，进场时间是必填项");
			return false;
		}
		if(buildTeamTwoMainObj.progressPromiseTimeSure===1&&(buildTeamTwoMainObj.progressPromiseTimeSure==null||buildTeamTwoMainObj.progressPromiseTimeSure==0)){
			message.warn("对外承诺目标值，进场时间是必填项");
			return false;
		}

		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	reStartBuildTeam=()=>{
		this.props.reStartBuildTeam();
		setTimeout(()=>{
			this.getBuildTeamTwoMain();
		},500)

	}

	checkProfitTarget=()=>{
		let resultObj = {"info":"盈利值不能为空",result:false};
		const { buildTeamTwoMainObj} = this.store;
		if(buildTeamTwoMainObj.profitTarget===1&&buildTeamTwoMainObj.profitTargetRate!==null&&buildTeamTwoMainObj.profitTargetRate>0){
			resultObj.result = true
		}
		if(buildTeamTwoMainObj.profitTarget===0){
			resultObj.result = true
		}
		return resultObj

	}

	render() {
		const {buildTeamState} = this.store;
		const {dataList, bttiList} = this.state;
		const { currentStep, switchSteps } = this.props;
		return (
			<div className="mt30">
				<div className="mb10">
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.reStartBuildTeam}
						style={buildTeamState.state2===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						重新启动
					</Button>
				</div>
				<EditableTableTwoItem
					id={'id'}
					store={this.store}
					dataSource={this.convertToTableData()}
					saveData={this.saveContents}
					saveItem={this.saveItem}
					disabled={buildTeamState.state2===2?true:false}
					pid={''}
				/>
				<div  className="mt30 mb30" style={buildTeamState.state2!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button
						ghost
						type="primary"
						onClick={this.overCurrentStage}> 完成该阶段任务</Button><Button onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div  className="mt30 mb30" style={buildTeamState.state2===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'>下一步</Button>
				</div>
			</div>
		)
	}
}


export default TwoItem;



