import React from 'react';
import {observer, inject} from 'mobx-react';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Form, Button, Select, Table, Input, message, Icon, Popconfirm, Typography, Popover} from 'antd';
import SupplierSelect from './SupplierSelect';
const { Paragraph } = Typography;

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: true,//false,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			if (values.hasOwnProperty('otherReason')) {
				handleSave({...record, ...{"otherReason": values["otherReason"]}});
			} else {
				handleSave({...record, ...values});
			}
		});
	};

	onChange = (value) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('selectReason')) {
				values["selectReason"] = value;
				handleSave({...record, ...{"selectReason": value}});
			}
		});
	}

	onSupplierChange = (param) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('name')) {
				values["name"] = param.name;
				values["supplierId"] = param.id;
				values["supplier"] = Object.assign({}, param);
			}
			handleSave({...record, ...values});
		});
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, dataSource} = this.props;
		const {editing} = this.state;

		if (dataIndex == "name") {   // 名称
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(
						<div ref={node => (this.input = node)} style={{width: "180px"}}>
							<SupplierSelect
								value={record["name"]}
								onSupplierChange={this.onSupplierChange}
								//onBlur={this.toggleEdit}
								dataSource={dataSource}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == "selectReason") {   // 选择原因
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex] == null || record[dataIndex] == "" ? [] : record[dataIndex],
					})(
						<Select style={{width: 100}}
						        mode="multiple"
						        ref={node => (this.input = node)}
						        onChange={this.onChange}
						       // onBlur={this.toggleEdit}
						>
							<Option value="价格最低">价格最低</Option>
							<Option value="产能保证">产能保证</Option>
							<Option value="质量最好">质量最好</Option>
							<Option value="配合度高">配合度高</Option>
							<Option value="距离最近">距离最近</Option>
						</Select>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{record[dataIndex] == null || record[dataIndex] == "" ? null : record[dataIndex].join("、")}
				</div>
			);
		} else {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{paddingRight: 24, minHeight: "34px"}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			dataSource,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}


@inject('DeliveryStore')
@withRouter
@observer
class OneItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			dataSource: [],
			count: 0,
			projectId:null,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if(projectDetail.id!==this.state.projectId){
			this.setState({projectId:projectDetail.id})
			this.getConfirmSupplierOneItemList();
		}
	}

	componentDidMount() {
		this.getConfirmSupplierOneItemList();
	}

	// 获取列表
	getConfirmSupplierOneItemList = () => {
		const {taskDetail} = this.store;
		this.store.getConfirmSupplierOneItemList({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
			this.setState({
				dataSource: Array.isArray(res) ? res.map((item, index) => {
					return Object.assign(item, {key: index + 1})
				}) : [],
				count: Array.isArray(res) ? res.length : 0
			})
		});
	}

	// 新增
	handleAdd = () => {
		const {count, dataSource} = this.state;

		// 校验
		if (count > 0) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("供应商名称是必填项");
				return false;
			}
			if (param.selectReason == null || (Array.isArray(param.selectReason) && param.selectReason.length == 0)) {
				message.warn("选择原因是必填项");
				return false;
			}
		}

		const {taskDetail} = this.store;
		const newData = {
			id: "",
			taskId: mobx.toJS(taskDetail).id,
			name: "",
			supplierId: "",
			selectReason: [],
			otherReason: "",
			supplier: {
				name: "",
				contacts: "",
				phone: "",
				type: "",
				cases: ""
			}
		};

		// 单行保存
		this.store.saveConfirmSupplierOneItem({csoi: newData}).then((res) => {
			if (res && res.id) {
				this.setState({
					dataSource: [...dataSource, Object.assign(res, {key: count + 1})],
					count: count + 1
				});
			}
		});
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row
		});
		this.setState({dataSource: newData});

		const param = JSON.parse(JSON.stringify(row));
		delete param.key;

		// 单行保存
		this.store.saveConfirmSupplierOneItem({csoi: param});
	};

	// 删除
	handleDelete = record => {
		const dataSource = [...this.state.dataSource];
		let newData = dataSource.filter(item => item.key !== record.key).map((vitem, vindex) => Object.assign(vitem, {key: vindex + 1}))
		this.setState({
			dataSource: newData, record,
			count: newData.length
		});
		if (record.id != "" && record.id != null) {
			this.store.delConfirmSupplierOneItem({id: record.id}).then((res) => {
				if (res) {
					message.success("删除成功");
				}
			});
		}
	};

	// 完成该阶段
	overCurrentStage = () => {
		const {count, dataSource} = this.state;
		if (count == 0) {
			message.warn("请完善必填信息");
			return false;
		}

		// 校验
		if (count > 0) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("供应商名称是必填项");
				return false;
			}
			if (param.selectReason == null || (Array.isArray(param.selectReason) && param.selectReason.length == 0)) {
				message.warn("选择原因是必填项");
				return false;
			}
		}
		this.props.overCurrentStageTask();
	}

	// 完成并进入下一步
	doneAndNext = () => {
		const {count, dataSource} = this.state;
		if (count == 0) {
			message.warn("请完善必填信息");
			return false;
		}

		// 校验
		if (count > 0) {
			let param = dataSource[count - 1];
			if (param.name == null || param.name == "") {
				message.warn("供应商名称是必填项");
				return false;
			}
			if (param.selectReason == null || (Array.isArray(param.selectReason) && param.selectReason.length == 0)) {
				message.warn("选择原因是必填项");
				return false;
			}
		}
		const {currentStep} = this.props;
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep + 1)
	}

	render() {
		const {supplierState} = this.store;
		const {dataSource} = this.state;
		const {currentStep, switchSteps} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{
				title: '操作',
				dataIndex: 'opater',
				width: '80px',
				align: 'center',
				className: supplierState.state1 == 2 ? "hidden" : "",
				render: (text, record) => <Popconfirm title="确定要删除吗?" onConfirm={() => this.handleDelete(record)}>
					{record.key == "1" ? null :
						<Icon type="minus-circle" style={{fontSize: "18px", color: "#E9796B", cursor: "pointer"}}/>}
				</Popconfirm>
			},
			{
				title: '序号',
				dataIndex: 'key',
				width: '80px',
				align: 'center'
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>供应商名称</div>,
				dataIndex: 'name',
				width: '200px',
				align: 'center',
				editable: supplierState.state1 === 2 ? false : true,
			},
			{
				title: '供应商类型',
				dataIndex: 'supplier.type',
				width: '150',
				align: 'center',
			},
			{
				title: '联系人',
				dataIndex: 'supplier.contacts',
				width: '150',
				align: 'center',
			},
			{
				title: '联系方式',
				dataIndex: 'supplier.phone',
				width: 150,
				align: 'center'
			},
			{
				title: '合作案例',
				dataIndex: 'supplier.cases',
				width: "300px",
				align: 'center',
				render: (text, record) => {
					return (<div
						style={{
							width: '300px',
							lineHeight: '30px',
							wordBreak: 'break-all',
							whiteSpace: 'pre-wrap',
							textAlign: 'left'
						}}>
						<Popover content={<div style={{
							width: '320px',
							lineHeight: '30px',
							wordBreak: 'break-all',
							whiteSpace: 'pre-wrap',
							textAlign: 'left'
						}}>
							{text}
						</div>} placement="left">
							<Paragraph ellipsis={{ rows: 3 }}>
								{text}
							</Paragraph>
						</Popover>
					</div>);
				}
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>选择原因</div>,
				dataIndex: 'selectReason',
				width: 100,
				align: 'center',
				editable: supplierState.state1 === 2 ? false : true,
				render: (text, record) => {
					return (<div>
						{text == null || text == "" ? "" : text.join("、")}
					</div>)
				}
			},
			{
				title: '特殊原因',
				dataIndex: 'otherReason',
				width: 100,
				align: 'center',
				editable: supplierState.state1 === 2 ? false : true,
			}
		];
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					dataSource: dataSource
				}),
			};
		});

		return (
			<div style={{
				overflowX: "hidden"
			}}>
				<Button
					className="mb10"
					ghost
					type="primary"
					icon="poweroff"
					onClick={this.props.reStartHandover}
					style={supplierState.state1 === 2 ? {display: ''} : {display: 'none'}}
				>
					重新启动
				</Button>
				<Table
					components={components}
					className="digital_table"
					rowClassName={() => 'editable-row'}
					dataSource={dataSource}
					columns={column}
					pagination={false}
					bordered
				/>
				<Icon onClick={this.handleAdd}
				      className={supplierState.state1 == 2 ? "hidden" : "mt20"}
				      type="plus-circle" style={{
					position: "relative",
					left: "28px",
					fontSize: "18px",
					color: "#609AF1",
					cursor: "pointer"
				}}/>
				<div className="mt30 mb30" style={supplierState.state1 !== 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"
					> 完成该阶段任务</Button>
					<Button
					onClick={this.doneAndNext} style={{marginLeft: '20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div className="mt30 mb30" style={supplierState.state1 === 2 ? {
					display: 'flex',
					justifyContent: 'center'
				} : {display: 'none'}}>
					<Button onClick={() => {
						switchSteps(currentStep + 1)
					}} style={{marginLeft: '20px'}} type='primary'>下一步</Button>
				</div>
			</div>
		)
	}
}


export default OneItem;



