import React from 'react';
import {Modal, Button} from 'antd';
import {observer} from "mobx-react";
import moment from "moment";
import {downloadFile} from "@/utils/download";

@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			previewVisible: false,
			previewImage: '',
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	// 预览
	handlePreview = () => {
		const {data} = this.props;
		const {fileUrl, name} = data;
		if (fileUrl==""||fileUrl==null) return;
		this.store.getDocumentUrl({url: fileUrl}).then((res) => {
			if (res) {
				this.setState({
					previewImage: res,
					previewVisible: true,
					name: name
				});
			}
		});
	}

	// 下载
	downloadUrl = () => {
		const {data} = this.props;
		const {fileUrl, name} = data;
		if (fileUrl==""||fileUrl==null) return;
        this.info();
		downloadFile(fileUrl, name);
	}

	handleCancel = (type) => this.setState({[type]: false});

    info = () => {
        Modal.info({
            title: '文件正在下载中，请耐心等待',
            onOk() {}
        });
    }

	render() {
		const {previewVisible, previewImage, name} = this.state;
		const {data} = this.props;

		return (
			<div className="mt15">
				<img src='./images/ppt.png' alt="" width="85px" style={{verticalAlign:"top"}}/>
				<p style={{display: "inline-block", margin: "15px 0 0 10px"}}>
					<div>
						{data&&data.name}
						<Button type="link" onClick={this.handlePreview}>预览</Button>
						<Button type="link" onClick={this.downloadUrl}>下载</Button>
					</div>
					<div className="mt15">
						{data&&data.createTime !== 0 && data.createTime !== null&&data.createTime !== undefined ? moment(data.createTime).format('YYYY-MM-DD HH:mm') : '暂无'}
						&nbsp;&nbsp;生成</div>
				</p>
				{/*预览*/}
				<Modal
					title={name}
					width="65%"
					style={{minHeight: '500px', minWidth: '800px'}}
					visible={previewVisible}
					footer={null}
					onCancel={this.handleCancel.bind(this, 'previewVisible')}>
					<iframe
						src={previewImage}
						width='100%'
						height='100%'
						style={{minHeight: '500px'}}
						frameBorder='1'>
					</iframe>
				</Modal>
			</div>
		)
	}
}



