import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {Row, Col, Button, message} from 'antd';
import EditableTableThreeItemPolicy from './EditableTableThreeItemPolicy';
import EditableTableThreeItemMatter from './EditableTableThreeItemMatter';

@withRouter
@observer
class ThreeItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			visible: false,
			stateValue: 0,
			id: "",
			pDataList:[
				{
					keyIndex:1,
					level:'一级决策',
					masters:[]
				},
				{
					keyIndex:2,
					level:'二级决策',
					masters:[]
				},
				{
					keyIndex:3,
					level:'三级决策',
					masters:[]
				},
				{
					keyIndex:4,
					level:'四级决策',
					masters:[]
				}
			],
			btThreeList:[],
			formRequire:{"result":false,"info":"决策等级不能为空"}

		}
	}

	componentDidMount() {
this.getPolicyDecisonLevelByLevel();
this.getBuildTeamThreeItemList();
	}


	saveContents=(dataSource)=>{
		console.log("dataSource=========>", dataSource);
		this.setState({btThreeList:dataSource})
	}

	savePolicy=(dataSource)=>{

	}

	saveDecisionItem=(row)=>{
		 const {taskDetail} = this.store;
		 const {btThreeList} = this.state;

		// 记录orders
		let length = btThreeList.length;
		let add = length>0?(btThreeList[length-1].orders>6?btThreeList[length-1].orders:6):6;

		 let newItem={
		 	"buildTeamThree":taskDetail.id,
			 "orders":add+1,
			 "item":row.item,
			 "level":row.level
		 }
		 if(typeof(row.id)!=="undefined"){
			 newItem['id']=row.id
			 newItem['orders']= row.orders
		 }
		 this.saveBuildTeamThreeItem({"btti":newItem})
         // console.log('newItem',row)
	}

	getPolicyDecisonLevelByLevel=()=>{
		[1,2,3,4].forEach(level=>{
			this.getPolicyDecision(level);
		})
	}

	getPolicyDecision=(level)=>{
		const { pDataList } = this.state;
		const { taskDetail, buildTeamTwoMainObj } = this.store;
		this.store.getPolicyDecisionList({"taskId":taskDetail.id, "level":level}).then((res)=>{
			const newPDataList =  pDataList.map((item, index)=>{
				if((index+1)===level){
					return Object.assign(item,{"masters":res})
				}else{
					return item
				}

			})
			this.setState({pDataList:newPDataList})
		})
	}

	savePolicyDecisionByLevel=(row)=>{
		const { taskDetail } = this.store;
		console.log("row=>",row)
		const { masters } = row;
		const userInfos = masters.map((item, index)=>{
			return Object.assign({"id":item.userId, "userName":item.userName})
		})
		this.store.savePolicyDecisionByLevel({"userInfos":userInfos, "taskId":taskDetail.id, "level":row.keyIndex}).then((res)=>{
			this.getPolicyDecisonLevelByLevel()
		})
	}

	getBuildTeamThreeItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamThreeItemList({"taskId": taskDetail.id}).then((res)=>{
			this.setState({btThreeList:res})
		})
	}

	saveBuildTeamThreeItem = (parmas)=>{
		this.store.saveBuildTeamThreeItem(parmas).then((res)=>{
			this.getBuildTeamThreeItemList();
		})
	}

	doneAndNext=()=>{
		const { currentStep } = this.props;
		const plOne = this.checkPolicyByLevelOne();
		if(!plOne.result){
			message.warn(plOne.info)
			return
		}
		const mList = this.checkMatter();
		if(!mList.result){
			message.warn(mList.info)
			return
		}
		const cpb = this.checkSyncPolicyByLevel();
		if(!cpb.result){
			message.warn(cpb.info)
			return
		}
		this.props.overCurrentStageTask();
		this.props.switchSteps(currentStep+1)
	}

	overCurrentStage=()=>{
		const { pDataList, formRequire } = this.state;
		const plOne = this.checkPolicyByLevelOne();
		if(!plOne.result){
			message.warn(plOne.info)
			return
		}
		const mList = this.checkMatter();
		if(!mList.result){
			message.warn(mList.info)
			return
		}
		const cpb = this.checkSyncPolicyByLevel();
		if(!cpb.result){
			message.warn(cpb.info)
			return
		}
		this.props.overCurrentStageTask();
		this.getPolicyDecisonLevelByLevel();
		this.getBuildTeamThreeItemList();
	}

	checkPolicyByLevelOne=()=>{
		const { pDataList } = this.state;
		let resultObj = {"info":"至少选择一位一级决策人员",result:false};
		pDataList.map((item,index)=>{
			if(index===0){
				if(item.masters.length===0){
					resultObj.result = false
				}else{
					resultObj.result = true
				}
			}
		})
		return resultObj
	}

	checkSyncPolicyByLevel=()=>{
		const { pDataList, btThreeList } = this.state;
		let resultObj = {"info":"请更新最新决策等级选项",result:true};
        const usefulPList = pDataList.filter((item) =>{return item.masters.length>0} );
		btThreeList.forEach((item)=>{
			const upl= usefulPList.filter((upItem)=>{
				 return upItem.keyIndex === item.level
			})
			if(upl.length===0) resultObj.result = false
		})
		return resultObj
	}

	checkMatter=()=> {
		let resultObj = {"info": "决策等级,决策事项不能为空", result: false};
		const {btThreeList} = this.state;
		const result = btThreeList.filter((item, index) => {
			return item.level === 0 || item.item===""
		})
		if (result.length === 0) resultObj.result = true
		return resultObj
	}

	checkForm=(info)=>{
		this.setState(info)
	}

	render() {
		const { buildTeamState} = this.store;
		const { pDataList, btThreeList } = this.state;
		const { currentStep, switchSteps } = this.props;

		return (
			<div style={{height: 'auto'}}>
				<div className="mb10">
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.props.reStartBuildTeam}
						style={buildTeamState.state3===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						重新启动
					</Button>
				</div>
				<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} className='task_detail_zone'
				     style={{
				     	 //overflowY:'scroll',
					     //minHeight:'600px',
					     //height:'calc(100vh - 100px)'
				     }}
				>
					<Col style={{ overflowY:'auto', overflowX:'auto' }} span={9}>
						<EditableTableThreeItemPolicy
							id={'id'}
							store={this.store}
							dataSource={buildTeamState.state3===2?pDataList.filter(item => item.masters.length>0):pDataList}
							saveData={this.savePolicy}
							saveData2={this.savePolicyDecisionByLevel}
							disabled={buildTeamState.state3===2?true:false}
							pid={''}
						/>
					</Col>
					<Col style={{borderLeft:'1px solid #f2f2f2'}} span={15}>
						<EditableTableThreeItemMatter
							id={'id'}
							onRef={ node => this.EditableTableThreeItemMatter = node }
							store={this.store}
							dataSource={btThreeList.map((item,index)=>{
								return Object.assign(item,{"keyIndex":index+1})
							})}
							saveData={this.saveContents}
                            saveItem={this.saveDecisionItem}
							disabled={buildTeamState.state3===2?true:false}
							checkForm={this.checkForm}
							pDataList={pDataList.filter(item => item.masters.length>0)}
							pid={''}
						/>
					</Col>
				</Row>
				<div  className="mt30 mb30" style={buildTeamState.state3!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage}
					        ghost
					        type="primary"> 完成该阶段任务</Button><Button onClick={this.doneAndNext} style={{marginLeft:'20px'}} type='primary'> 完成并进入下一步</Button>
				</div>
				<div  className="mt30 mb30" style={buildTeamState.state3===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep+1)}} style={{marginLeft:'20px'}} type='primary'>下一步</Button>
				</div>
			</div>
		)
	}
}


export default ThreeItem;



