import React from 'react';
import {Row, Col, Form, Button, Input, Modal, Spin, Table, message, Divider, Checkbox} from 'antd';
import {withRouter} from 'react-router-dom';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';
import {downloadFile} from "@/utils/download";
import SendMailModal from "./SendMailModal";
import MyUpload from "@/components/common/MyUpload4Affairs";
import Zmage from 'react-zmage';

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: false,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			this.toggleEdit();
			handleSave({...record, ...values});
		});
	};

	onChange = (e) => {
		const check = e.target.checked;
		const {record, handleSave, dataSource} = this.props;

		// 记录长度,校验
		let dataArray = JSON.parse(JSON.stringify(dataSource));
		let array = dataArray.filter(item=>item.isSelected==1);
		if (check&&array.length>=10) {
			message.warn("已达上限，“有无”选项最多可勾选显示10人");
			return false;
		} else {
			this.form.validateFields((error, values) => {
						if (values.hasOwnProperty('isSelected')) {
							values["isSelected"] = check==true?1:0;
						}
						handleSave({...record, ...values});
					});
		}
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;

		if (dataIndex == "station") {
			if (record["isEdit"] == 1) {
				return editing ? (
					<Form.Item style={{margin: 0}}>
						{form.getFieldDecorator(dataIndex, {
							rules: [
								{
									required: false,
									message: `${title} is required.`,
								},
							],
							initialValue: record[dataIndex],
						})(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
					</Form.Item>
				) : (
					<div
						// className="editable-cell-value-wrap"
						style={{minHeight: "34px"}}
						// onClick={this.toggleEdit}
					>
						{children}
					</div>
				);
			} else {
				return <div>
					{children}
				</div>
			}
		} else if (dataIndex == "isSelected") {
			return <Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title} is required.`,
						},
					],
					initialValue: record[dataIndex],
				})(<Checkbox onChange={this.onChange} checked={true} />)}
			</Form.Item>
		} else {
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
				</Form.Item>
			) : (
				<div

					style={{minHeight: "34px"}}
					// onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			dataSource,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

@Form.create()
@withRouter
@inject('DeliveryStore')
@observer
class FiveCards extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
	}

	state = {
		modal1Visible: false,
		modal2Visible: false,
		modal3Visible: false,
		modal4Visible: false,
		fileList: [],
		loading: false,
		fetching: false,
		text: "", //关键字
		pageIndex: 1,
		pageSize: 10,
		companyData: [],
		isCallSuccess: false,
		downloading: false,
		dataSource: [],
		count: 8,
		selectedRows:[],
		selectedRowKeys: [],
		projectType:null,
		constructionPeriod:null,
	}

	componentDidMount() {
	  this.getLastConstruction();
	  this.getBuildTeamOneItemListByProjectId();
	  this.getCurrentStageState();
	}

	getLastConstruction = () => {
		const {projectDetail} = this.store;
		const param = {
			projectId: projectDetail.id
		}
		this.store.getConstruction(param).then((res)=>{

			if(res!=null&&res.projectId!==null){
				// const tmpRes = Object.assign(res,{"projectType":"类型"});
				const {projectName = "", projectType = "", buildScale = "", buildUnit = "", constructionUnit = "", constructionPeriod = "", projectUrl="",planUrl="", constructionManagers=[]} = res;

				this.props.form.setFieldsValue({
					projectName,
					projectType,
					buildScale,
					buildUnit,
					constructionUnit,
					constructionPeriod,
					projectUrl,
					planUrl
				});


				this.store.construction = Object.assign({}, res);

				// 管理人员
				if(constructionManagers!=null&&Array.isArray(constructionManagers)&&constructionManagers.length>0) {
					this.setState({
						dataSource: constructionManagers.map((item, index)=>{
							return Object.assign(item, {key: index})
						}),
						count: constructionManagers.length,
						// selectedRowKeys:constructionManagers.map((item, index)=>{
						// 	return index
						// }),
					});
				}
			}
		});
	}

	getBuildTeamOneItemListByProjectId=()=>{
		const {projectDetail} = this.store;
		const param = {
			projectId: projectDetail.id
		}
		this.store.getBuildTeamOneItemListByProjectId(param).then((res)=>{
          const ds= res.map((item,index)=>{
			  return {
				station: item.position,
				name: item.userName,
				phone: item.phone,
				level: item.positionLevel,
				key: index
			}
		  })
		  this.setState({dataSource:ds, selectedRowKeys:ds.map((item,index)=>{return index}), selectedRows:ds})
		})
	}

	getCurrentStageState=()=>{
		const { taskDetail } = this.store;
		this.store.getTaskStepState({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			if(res){
				this.store.fiveCardState = res;
			}
		})
	}

	// 新增
	handleAdd = () => {
		const {count, dataSource} = this.state;
		//新增成员最多只能勾选十个，超出时再添加选择人员已达上限
		let dataArray = JSON.parse(JSON.stringify(dataSource));
		let array = dataArray.filter(item=>item.isSelected==1);
		if (array.length>=10) {
         message.warn("已达上限，“有无”选项最多可勾选显示10人");
         return false;
		}
		const newData = {
			key: count,
			station: "",
			name: "",
			phone: "",
			level: 3,
			isSelected: 1,
			isEdit: 1
		};
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1
		});
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row
		});
		this.setState({dataSource: newData});
	};

	//显示发送邮件对话框
	showSendMailModal = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {

				// 校验管理人员
				const {dataSource, selectedRowKeys, selectedRows} = this.state;
				if(selectedRowKeys.length>10){
					message.warn("管理人员人数不能大于10人");
					return false
				}
				if(selectedRowKeys.length===0){
					message.warn("至少选择一名管理人员");
					return false
				}

				let dataArray = JSON.parse(JSON.stringify(selectedRows));
				//console.log("da=>", dataArray);
				let array = dataArray.filter(item=>item.isSelected==1)
				if (array.some((item) => {
					return item.station == "" || item.station == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.name == "" || item.name == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.phone == "" || item.phone == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}

				this.setState({modal3Visible: true});
			}
		});
	}

	//发送制作文件
	sendMakeFile = (param) => {
		const {projectName = "", projectType = "", buildScale = "", buildUnit = "", constructionUnit = "", constructionPeriod = ""} = this.props.form.getFieldsValue();
		const {construction} = this.store;
		const {dataSource} = this.state;
		const {projectUrl, planUrl} = mobx.toJS(construction);
		let dataArray = JSON.parse(JSON.stringify(dataSource));
		let userid = localStorage.getItem("userid");
		const c = {
			projectName,
			projectType,
			buildScale,
			buildUnit,
			constructionUnit,
			constructionPeriod,
			projectUrl,
			planUrl,
			constructionManagers: dataArray.map((item) => {
				delete item.key;
				delete item.isEdit;
				return item;
			}),
			createUserId: userid
		}

		// 保存
		this.store.sendConstrctionEmail({'c': c, 'emails': param})
			.then((res) => {
				if (res) {
					this.handleAfterSend();
				}
			});
	}

	// 保存本地
	inter3sRun = () => {
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {
				const {projectName = "", projectType = "", buildScale = "", buildUnit = "", constructionUnit = "", constructionPeriod = ""} = values;
				const {construction} = this.store;
				const {dataSource, selectedRowKeys, selectedRows} = this.state;
				if(selectedRowKeys.length>10){
					message.warn("管理人员人数不能大于10人");
					return false
				}
				if(selectedRowKeys.length===0){
					message.warn("至少选择一名管理人员");
					return false
				}
				const {projectUrl, planUrl} = mobx.toJS(construction);
				let dataArray = JSON.parse(JSON.stringify(selectedRows));
				let userid = localStorage.getItem("userid");

				// 校验管理人员
				let array = dataArray
				if (array.some((item) => {
					return item.station == "" || item.station == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.name == "" || item.name == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.phone == "" || item.phone == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}


				const params = {
					projectName,
					projectType,
					buildScale,
					buildUnit,
					constructionUnit,
					constructionPeriod,
					projectUrl,
					planUrl,
					constructionManagers: dataArray.map((item) => {
						delete item.key;
						return item;
					}),
					createUserId: userid
				}

				// 保存
				this.store.saveConstruction({c: params, saveType: 0});
				this.setState({isCallSuccess: false, downloading: true})
				this.store.saveConstrctionFile({'c': params}).then((res) => {
					if (res) {
						this.getReportUrl({"id": res}, projectName);
					}
				});
			}
		});
	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id, projectName) => {
		let _this = this;
		_this.timer22 = setInterval(() => {
			_this.store.getDocUrl2(id).then((url) => {
				if (url !== null && url !== "") {
					_this.setState({isCallSuccess: true, downloading: false})
					this.info();
					downloadFile(url, `${projectName}五牌一图`);
					clearInterval(_this.timer22);
				} else {

				}
			});
		}, 3000);
	}

	handleCancel = (modalVisible) => {
		this.setState(modalVisible);
	};

	handleAfterSend = () => {
		this.setState({modal3Visible: false});
		this.setState({modal4Visible: true});
	}

	handleAttment = (type, param) => {
		const url = param && param[0].url;
		this.store.construction[type] = url
	}

	info = () => {
		Modal.info({
			okText: "知道了",
			title: '文件生成成功，正在下载中，请耐心等待',
			onOk() {}
		});
	}

	getProjectDetail=()=>{
		const projectId =this.store.projectId;
		this.store.getProjectDetail({'id':projectId}).then((res)=>{
			this.store.projectDetail = res
		})
	}

	overCurrentStageTask=()=>{
		const {  taskDetail } = this.store;
		const {dataSource} = this.state;
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {

				let dataArray = JSON.parse(JSON.stringify(dataSource));

				// 校验管理人员
				let array = dataArray.filter(item=>item.isSelected==1)
				if (array.some((item) => {
					return item.station == "" || item.station == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.name == "" || item.name == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}
				if (array.some((item) => {
					return item.phone == "" || item.phone == null;
				})) {
					message.warn("管理人员信息未完善，勾选的岗位信息需填写完整后提交");
					return false;
				}

				this.store.getTaskStepState({"taskId":taskDetail.id}).then((res)=>{
					if(res){
						this.store.overTaskStep({"taskId":taskDetail.id, "index":1}).then((res)=>{
							if(res){
								this.store.fiveCardState = res;
								this.getProjectDetail();
							}
						})
					}
				})

			}
		});
	}

	reStartHandover=()=>{
		const {  taskDetail } = this.store;
		this.store.reStartTaskStep({"taskId":taskDetail.id, "index":1}).then((res)=>{
			if(res){
				this.store.fiveCardState = res;
				this.getProjectDetail();
			}
		})
	}

	selectRow = (record) => {
		const selectedRowKeys = [...this.state.selectedRowKeys];
		if (selectedRowKeys.indexOf(record.key) >= 0) {
			selectedRowKeys.splice(selectedRowKeys.indexOf(record.key), 1);
		} else {
			selectedRowKeys.push(record.key);
		}
		this.setState({ selectedRowKeys });
	}
	onSelectedRowKeysChange = (selectedRowKeys, selectedRows) => {
		this.setState({ selectedRowKeys, selectedRows });
	}

	handleSaveConstruction = (type, e)=>{
		const { projectType, constructionPeriod } = mobx.toJS(this.store.construction);
		let currentValue = null;
		if(projectType!==this.props.form.getFieldValue(type)&&this.props.form.getFieldValue(type)!==""&&this.props.form.getFieldValue(type)!==null){
			currentValue = this.props.form.getFieldValue(type)
		}else{
			return
		}
		let obj = {}
		obj[type] = currentValue
		this.store.saveConstruction({"c":Object.assign(mobx.toJS(this.store.construction), obj)}).then((res)=>{
			if(res){
				this.getLastConstruction();
				this.getBuildTeamOneItemListByProjectId();
				this.getCurrentStageState();
			}
		})
	}


	render() {
		const {modal1Visible, downloading, modal3Visible, modal4Visible, dataSource, selectedRows, selectedRowKeys} = this.state;
		const {construction, fiveCardState} = this.store;
		const {projectUrl, planUrl} = mobx.toJS(construction);
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 3},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 21},
			}
		};
		const {getFieldDecorator} = this.props.form;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columns = [
			{title: '序号', dataIndex: 'key', align: 'center', width: '10%', render:(text)=>{return parseInt(text)+1}},
			{
				title: '岗位',
				dataIndex: 'station',
				width: '30%',
				align: 'center',
				editable: true,
			},
			{
				title: '姓名',
				dataIndex: 'name',
				width: '30%',
				align: 'center',
				editable: true,
			},
			{
				title: '联系电话',
				dataIndex: 'phone',
				width: '30%',
				align: 'center',
				editable: true,
			},
		];
		const column = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					dataSource: dataSource
				}),
			};
		});

		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectedRowKeysChange,
		};



		return (
			<div className='ml50 pr'>
				<Spin spinning={downloading} tip="生成制作文件较大，下载预计需要2~5分钟左右，请耐心等待..." className="spin">
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.reStartHandover}
						style={fiveCardState.state1 === 2 ? {display: ''} : {display: 'none'}}
					>
						重新启动
					</Button>
					<Row>
						<Col span={24}>
							<Form  {...formItemLayout}>
								<Form.Item label="工程名称" className="edit_form_bold">
									{getFieldDecorator('projectName', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("projectName", e)}}
										style={{width: '300px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
								</Form.Item>
								<Form.Item label="工程类型" className="edit_form_bold">
									{getFieldDecorator('projectType', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("projectType", e)}}
										style={{width: '300px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
								</Form.Item>
								<Form.Item label="建筑规模" className="edit_form_bold">
									{getFieldDecorator('buildScale', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("buildScale", e)}}
										style={{width: '300px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
								</Form.Item>
								<Form.Item label="建设单位" className="edit_form_bold">
									{getFieldDecorator('buildUnit', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("buildUnit", e)}}
										style={{width: '300px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
								</Form.Item>
								<Form.Item label="施工单位" className="edit_form_bold">
									{getFieldDecorator('constructionUnit', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("constructionUnit", e)}}
										style={{width: '300px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
								</Form.Item>
								<Form.Item label="建设工期" className="edit_form_bold">
									{getFieldDecorator('constructionPeriod', {
										rules: [
											{
												required: true,
												message: '该项是必填',
											},
										],
									})(<Input
										maxLength={17}
										onBlur={(e)=>{this.handleSaveConstruction("constructionPeriod", e)}}
										style={{width: '262px'}}
										placeholder="请输入"
										disabled={fiveCardState.state1!==2?false:true}
									/>)}
									<span className="f12 ml5">日历天</span>
								</Form.Item>
								<Form.Item label="项目图片" className="edit_form_bold">
									{getFieldDecorator('projectUrl', {
										rules: [
											{
												required: false,
												message: '该项是必填',
											},
										],
									})(<div>
										{projectUrl == "" || projectUrl == null ? "无" : <Zmage src={projectUrl}
										                                                        style={{
											                                                        width: "102px",
											                                                        height: "102px",
											                                                        display: "inline-blcok"
										                                                        }}/>}
										{/* <div className="dib" style={{
											verticalAlign: "top",
											marginLeft: projectUrl == "" || projectUrl == null ? 0 : "15px"
										}}>
											<MyUpload
												className='bc-upload'
												saveAttachment={this.handleAttment.bind(this, 'projectUrl')}
												disabled={false}
												multiple={false}
												listType="picture-card"
												fileList={[]}
												journalType="journal"
											/>
										</div> */}
									</div>)}
								</Form.Item>
								<Form.Item label="管理人员" className="edit_form_bold">
									{getFieldDecorator('constructionManagers', {
										rules: [
											{
												required: false,
												message: '该项是必填',
											},
										],
									})(<div>
										<Table
											components={components}
											rowSelection={rowSelection}
											onRow={(record) => ({
												onClick: () => {
													this.selectRow(record);
												},
											})}
											className="digital_table"
											rowClassName={() => 'editable-row'}
											bordered={false}
											dataSource={dataSource}
											columns={column}
											pagination={false}
										/>
										{/* <Button type="dashed" block icon="plus" className="mt20"
										        onClick={this.handleAdd}>新增成员</Button> */}
									</div>)}
								</Form.Item>
								<Form.Item label="进度计划图" className="edit_form_bold">
									{getFieldDecorator('planUrl', {
										rules: [
											{
												required: false,
												message: '该项是必填',
											},
										],
									})(<div>
										{planUrl == "" || planUrl == null ? "无" : <Zmage src={planUrl} style={{
											width: "102px",
											height: "102px",
											display: "inline-blcok"
										}}/>}
										{/* <div className="dib" style={{
											verticalAlign: "top",
											marginLeft: planUrl == "" || planUrl == null ? 0 : "15px"
										}}>
											<MyUpload
												className='bc-upload'
												saveAttachment={this.handleAttment.bind(this, 'planUrl')}
												disabled={false}
												multiple={false}
												listType="picture-card"
												fileList={[]}
												journalType="journal"
											/>
										</div> */}
									</div>)}
								</Form.Item>
							</Form>
						</Col>
					</Row>
					<Row>
						<Col span={24}>
							<div
								style={{
									marginTop: '0',
									right: 0,
									bottom: 0,
									width: '100%',
									background: '#fff',
									textAlign: 'center',
									zIndex: 9999
								}}
							>
								<Button
									onClick={this.showSendMailModal}
									icon='mail'
									shape="round"
									type="primary">
									发送制作文件
								</Button>
								<Divider type="vertical"/>
								<Button
									onClick={this.inter3sRun}
									shape="round">
									保存至本地
								</Button>
								<span className="daily_tips ml15 f12">打印要求：600mm*900mm</span>
							</div>
						</Col>
					</Row>
					<Row className="mt30">
						<div  style={fiveCardState.state1 !== 2 ? {
							display: 'flex',
							justifyContent: 'center'
						} : {display: 'none'}}>
							<Button  onClick={this.overCurrentStageTask}
							         ghost
							         type="primary"
							> 完成该阶段任务</Button>
						</div>
					</Row>
					<Row>
						<div style={{height:'20px'}}></div>
					</Row>
				</Spin>
				<Modal
					visible={modal1Visible}
					title=""
					onCancel={() => {
						this.handleCancel({modal1Visible: false})
					}}
					footer={null}
					width={940}
					bodyStyle={{textAlign: 'center', height: '600px', verticalAlign: 'middle'}}
				>
					<div style={{marginTop: '100px'}}><img
						width='116px'
						height="116px"
						src='./images/u192.png'/>
					</div>
					<div style={{marginTop: '35px', fontSize: '24px', fontWeight: 'bold'}}><h1>名片申请成功</h1></div>
					<p style={{marginTop: '35px'}}>名片预计2个工作日内可打印完毕，打印完成后将电话或邮件通知，请耐心等待。</p>
				</Modal>
				<Modal
					visible={modal4Visible}
					title=""
					onCancel={() => {
						this.handleCancel({modal4Visible: false})
					}}
					footer={null}
					width={300}
					bodyStyle={{textAlign: 'center', height: '300px', verticalAlign: 'middle'}}
				>
					<div style={{marginTop: '30px'}}><img
						width='80px'
						height="80px"
						src='./images/u380.svg'/>
					</div>
					<div style={{marginTop: '10px', fontSize: '24px', fontWeight: 'bold'}}><h1>发送成功</h1></div>
					<div
						style={{
							marginTop: '15px',
							right: 0,
							bottom: 0,
							width: '100%',
							padding: '10px 16px',
							background: '#fff',
							textAlign: 'center',
							zIndex: 9999
						}}
					>
						<Button
							style={{marginRight: 8}}
							shape="round"
							onClick={() => {
								this.handleCancel({modal4Visible: false})
							}}
							type="primary">
							关闭
						</Button>

					</div>
				</Modal>
				<SendMailModal
					type="fivecards"
					visible={modal3Visible}
					sendBuildBusinessEmail={this.sendMakeFile}
					closeModal={() => {
						this.handleCancel({modal3Visible: false})
					}}
					afterSend={this.handleAfterSend}
					store={this.store}
				/>
			</div>
		)
	}
}


export default FiveCards;



