    init2 = (callback) => {
        const {
            makePlotInfo,
            buildTeamTwoMain,
            makePlotCost,
            makePlotPeople,
            makePlotMachine,
            makePlotMaterial,
            makePlotQuality,
            makePlotRisk,
            makePlotSafe} = this.state;
        const People = JSON.parse(JSON.stringify(makePlotPeople))
        const Machine = JSON.parse(JSON.stringify(makePlotMachine))
        const Material = JSON.parse(JSON.stringify(makePlotMaterial))
        const Quality = JSON.parse(JSON.stringify(makePlotQuality))
        const Risk = JSON.parse(JSON.stringify(makePlotRisk))
        const Safe = JSON.parse(JSON.stringify(makePlotSafe))

        const params = {
            makePlotInfo,
            buildTeamTwoMain,
            makePlotCost,
            makePlotPeople: People.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
            makePlotMachine: Machine.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
            makePlotMaterial:Material.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
            makePlotQuality:Quality.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
            makePlotRisk:Risk.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
            makePlotSafe:Safe.map((item)=>{
                if (item.key) delete item.key;
                return item;
            }),
        }
        this.store.buildMakePlotDoc({makePlot:params}).then((res)=>{
            this.setState({
                downloading: true
            });
            this.getReportUrl({"id": res}, callback);
        });
    }

    // 间隔3秒获取pdf地址
    getReportUrl = (id, callback) => {
        let _this = this;
        _this.timer22 = setInterval(() => {
            _this.store.getMakePlotDownloadUrl(id).then((url) => {
                if (url !== null && url !== "") {
                    _this.setState({ downloading: false})
                    // 重新保存任务
                    _this.getMakePlot();
                    if (typeof callback=="function") {
                        callback();
                    }
                    clearInterval(_this.timer22);
                } else {

                }
            });
        }, 3000);
    }

    // 完成
    saveNow = () => {
	    let _this = this;
        this.checkFunction(()=>{
            // 校验
            let makePlotInfo = JSON.parse(JSON.stringify(_this.state.makePlotInfo));
            if (makePlotInfo["orgImageUrl"]==null||makePlotInfo["orgImageUrl"]=="") {  // 生成组织图
                _this.supplierRef.uploadImg()
                    .then((res)=>{
                        makePlotInfo["orgImageUrl"] = res;
                        _this.store.saveMakePlotInfo({info: makePlotInfo}).then((res)=>{
                            if (res) {
                                _this.setState({ makePlotInfo: res},()=>{

                                    // 是否重新生成策划书
                                    let fields = res;
                                    let isDocument = fields.document==null;  // 是否有采购合同
                                    if(isDocument) {
                                        _this.init2(()=>{
                                        _this.props.overCurrentStageTask();
                                    });}
                                    else {
                                        _this.props.overCurrentStageTask();
                                    }
                                });
                            }
                        });
                    });
            } else {
                // 是否重新生成策划书
                let fields = JSON.parse(JSON.stringify(_this.state.makePlotInfo));
                let isDocument = fields.document==null;  // 是否有采购合同
                if(isDocument) {
                    _this.init2(()=>{
                        _this.props.overCurrentStageTask();
                    });}
                else {
                    _this.props.overCurrentStageTask();
                }
            }
        });
    }

    // 统一校验方法
    checkFunction = (callback) => {
        const {
            makePlotCost,
            makePlotPeople,
            makePlotMachine,
            makePlotMaterial,
            makePlotQuality,
            makePlotRisk,
            makePlotSafe} = this.state;
        let count = makePlotPeople.length;
        let count2 = makePlotMachine.length;
        let count3 = makePlotMaterial.length;
        let count4 = makePlotQuality.length;
        let count6 = makePlotRisk.length;
        let count5 = makePlotSafe.length;

        const {firstContractState,
            civilizedConstructionState,
            hospitalityState,
            hospitalityValue,
            additionalRiskState,
            additionalRiskValue,
            civilizedConstructionValue,
            confirmSupplierTwoItems} = makePlotCost;
        if (firstContractState==null||firstContractState==0||
            civilizedConstructionState==null||civilizedConstructionState==0||
                hospitalityState==null||hospitalityState==0||
                additionalRiskState==null||additionalRiskState==0
           // hospitalityValue==""||
           // additionalRiskValue==""||
             //   civilizedConstructionValue==""
            ) {
            message.warn("请完善成本策划必填信息");
            return false;
        }
        if (confirmSupplierTwoItems.length>0&&confirmSupplierTwoItems.some(item=>item.isActive==0||item.isActive==null)) {
            message.warn("请完善成本策划必填信息");
            return false;
        }
        // 校验
        if (count > 0) {
            let param = makePlotPeople[count - 1];
            if (param.name == null || param.name == ""||param.count == null || param.count == ""||param.enterTime == null || param.enterTime == ""||param.enterTime == 0) {
                message.warn("请完善工人进场计划必填信息");
                return false;
            }
        }
        if (count2 > 0) {
            let param = makePlotMachine[count2 - 1];
            if (param.name == null || param.name == ""||param.specs == null || param.specs == ""||param.count == null || param.count == "") {
                message.warn("请完善机械设备需求计划必填信息");
                return false;
            }
        }
        if (count3 > 0) {
            let param = makePlotMaterial[count3 - 1];
            if (param.name == null || param.name == ""||param.enterBeginTime == null || param.enterBeginTime == ""||
                param.enterBeginTime == 0||param.enterEndTime == null || param.enterEndTime == ""||param.enterEndTime == 0) {
                message.warn("请完善主要材料需求计划必填信息");
                return false;
            }
        }
        if (count4 > 0) {
            let param = makePlotQuality[count4 - 1];
            if (param.name == null || param.name == ""||param.buildTeamOneId == null || param.buildTeamOneId == "") {
                message.warn("请完善质量策划必填信息");
                return false;
            }
        }
        if (count5 > 0) {
            let param = makePlotSafe[count5 - 1];
            if (param.name == null || param.name == ""||param.buildTeamOneId == null || param.buildTeamOneId == "") {
                message.warn("请完善安全文明施工策划必填信息");
                return false;
            }
        }
        if (count6 > 0) {
            let param = makePlotRisk[count6 - 1];
            if (param.type == null || param.type == ""||param.describes == null || param.describes == ""||param.programme == null || param.programme == "") {
                message.warn("请完善重点与难点分析必填信息");
                return false;
            }
        }

        return callback();
    }

