/*
* Table组件
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	Button,
	Table,
	Input,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	Select,
	message,
	InputNumber,
	notification,
	Popover
} from 'antd';
import moment from 'moment';
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';

const {Option} = Select;
const { TextArea } = Input;

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

const dutyList =[
	"1、协助项目经理解决工厂应急问题；\n" +
	"2、配合项目经理做好项目关系管理；\n" +
	"3、督导PMO工作落地情况；\n" +
	"4、督导项目进度；\n" +
	"5、参加项目关键里程碑会议；\n" +
	"6、重大事件决策",

	"1、全力保障项目目标完成；\n" +
	"2、协调内外部资源，负责项目交付进度和质量控制；\n" +
	"3、安排内部其他项目组成员工作，做好分工安排；\n" +
	"4、保障项目中各类文档按期完成；\n" +
	"5、定期做高层汇报，并提出管理改进建议",

	"1、负责工厂订单、生产；\n" +
	"2、现场质量、安全、进度保证；\n" +
	"3、每日工作情况汇报及明日计划",

	"1、针对东小口项目验证交付标准化流程，改进和完善，并思考数字化落地方案；\n" +
	"2、辅助项目经理完成交付过程中所需的作业文件；\n" +
	"3、跟进各部门安排的标准化作业文件提交落地情况；\n" +
	"4、补充陕建华山云商-东小口功能箱产品上架和维护。",

	"1、当项目需要设计参与时，与商务经理和大项目经理探索客户需求，商讨设计解决方案；\n" +
	"2、确定需要的设计专业，安排好设计人员的工作，保障如期交付设计方案。",

	"推进产品标准化工作，搜集现场案例",

	"1、负责维护好客户关系，尤其是关键干系人情绪，使其对项目满意；\n" +
	"2、负责项目内外的商务沟通与协调工作，及时反馈客户信息；\n" +
	"3、及时跟进客户的回款工作。",
]



/*自定义编辑组件*/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			handleSave({...record, ...values});
		});
	};


	updatePerson = (users) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (users.length) {
			userIds = users
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		// if(users.length>0){
		// 	data["masterName"] = userIds[0]['userName'];
		// 	data["masterId"] = userIds[0]['userId'];
		// }
		handleSave({...record, ...data});
	}

	onBlur = () => {
		//this.toggleEdit();
	}

	handleChangeSelect=(e)=>{

		const {record, handleSave} = this.props;
		handleSave({...record, ...{"position":e,  'duty':this.getDutyByPosition(e)}});
	}

	getDutyByPosition=(p)=>{
		if(p==="项目总监"){
			return dutyList[0]
		}
		if(p==="项目经理"){
			return dutyList[1]
		}
		if(p==="生产经理"){
			return dutyList[2]
		}
		if(p==="项目PMO"){
			return dutyList[3]
		}
		if(p==="设计经理"){
			return dutyList[4]
		}
		if(p==="产品经理"){
			return dutyList[5]
		}
		if(p==="商务经理"){
			return dutyList[6]
		}
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,  handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'orders') {  // 序号
			return (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					>
					{children}
				</div>
			);
		}else if (dataIndex == 'caozuo') {  // 操作
			return record['canDel']===0? (
				<div></div>
			) : (<div
					style={{minHeight: '31px'}}>
					{children}
				</div>
			);
		}else if (dataIndex == 'position'){ // position
			return record['canDel']!==0? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == '' ? undefined  : record[dataIndex],
					})(

						<Select placeholder={'请选择'}  style={{ width: 120 }} onChange={this.handleChangeSelect}>
						<Option value="项目总监">项目总监</Option>
						<Option value="项目经理">项目经理</Option>
						<Option value="生产经理">生产经理</Option>
						<Option value="项目PMO">项目PMO</Option>
						<Option value="设计经理">设计经理</Option>
						<Option value="产品经理">产品经理</Option>
						<Option value="商务经理">商务经理</Option>
						</Select>
					)}
				</Form.Item>
			) : (<div
					style={{minHeight: '31px'}}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}else if(dataIndex == 'duty') {
			return editing && false? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
						<TextArea
							rows={5}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
					//onClick={this.toggleEdit}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}else{
			return  record['canDel']!==0 && record['userId']==="" ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					style={{minHeight: '31px'}}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* 编辑table
* */
@observer
class EditableTableOneItem extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 任务数据源
			count: 0,        // table的key
			primaryData: {
				"orders":0,
				"canDel":1,
				"userName":"",
				"phone":"",
				"email":"",
				"position":"",
				"duty":""
			}, // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
			activeObj: {}, // 记录拆解前主任务数据
			activeSub: [], // 记录拆解前子任务数据
			subTaskDis: false, // 控制子任务的编辑状态
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {key: index})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"orders":0,
					"canDel":1,
					"userName":"",
					"phone":"",
					"email":"",
					"position":"",
					"duty":""
				}) // 新增任务数据
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource ===undefined || this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource ===undefined ||  this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"orders":0,
				"canDel":1,
				"userName":"",
				"phone":"",
				"email":"",
				"position":"",
				"duty":""
			}), // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
		});
	}

	componentWillUnMount() {

	}


	// 删除
	handleDelete = (record) => {
		const {id, key} = record;
		if (id !== "" && id !== null && !id.startsWith("!")) { // 删除原来已有的
			this.store.delNode(id).then((res) => {
				if (res) {
					message.success("删除成功");
					const dataSource = [...this.state.dataSource];
					this.setState({
						dataSource: dataSource.filter(item => item.key !== key)
					}, () => {
						this.props.saveData(this.state.dataSource);
					});
				} else {
					message.error("删除失败")
				}
			});
		} else { // 删除新增的
			const dataSource = [...this.state.dataSource];
			this.setState({
				dataSource: dataSource.filter(item => item.key !== key)
			}, () => {
				this.props.saveData(this.state.dataSource, key);
			});
		}
	};

	// 新增
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		const newData = Object.assign(primaryData, {key: count, id:"!"+uuidv4()});
		// const newData = Object.assign(primaryData, {key: count});
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			this.props.saveData(this.state.dataSource);
		});
		const {id, ...newItem} = newData;
		this.props.saveItem(newItem);
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 更新保存数据
			// this.props.saveData(this.state.dataSource);
		});
		this.props.saveItem(newData[index])
	};

	// 折叠效果
	saveExpandedRows = (key) => {
		let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
		_this.setState({
			expandedKeys: keys
		}, () => {
			let obj = {[_this.props.id]: keys};
			let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
			_this.store.expandedKeys = Object.assign(expandedKeys, obj);
		});
	}

	// 查看
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 不可编辑
		}, () => {
			this.saveExpandedRows(record);
		});
	}


	// 取消保存
	onCalac = (key) => {
		this.saveExpandedRows(key);
		this.store.isEdit = false;
		// 更新tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
		this.subtask.clearState();
		this.setState({
			activeObj: Object.assign({})
		});
	}


	// 点击
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children == null ? [] : record.children.filter((item) => {
			return item.treeType === 'subtask'
		})
		const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
		if (disabled && subNumber > 0) {  // 详情态
			this.handleView(record.id)
		}
	}

	handleRemoveItem=(id)=>{
		const {taskDetail } = this.store
		this.store.delBuildTeamItemOne({"id":id}).then((res)=>{
            this.props.saveData();
		})
	}

	showUserInfo=(level)=>{
		if(level==="1"){
			return "项目总监"
		}
		if(level==="2"){
			return "项目经理"
		}
		if(level==="3"){
			return "生产经理"
		}
		if(level==="4"){
			return "项目PMO"
		}
		if(level==="5"){
			return "设计经理"
		}
		if(level==="6"){
			return "产品经理"
		}
		if(level==="7"){
			return "商务经理"
		}

	}

	render() {
		const {dataSource, expandedKeys, subTaskDis} = this.state;
		const {disabled, tableType, selectedRows} = this.props;
		const isChaijie = expandedKeys.length > 0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const rowSelection = {
			onChange: (selectedRowKeys, selectedRows) => {
				console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
				selectedRows(selectedRows)
			}
		};
		let columnEle = [
			{
				title: '序号',
				dataIndex: 'keyIndex',
				width: '50px',
				align: "center",
			},
			{
				title: '姓名',
				dataIndex: 'userName',
				width: '150px',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}
					</div>);
				}
			},
			{
				title: '手机号',
				dataIndex: 'phone',
				width: '14%',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}
					</div>);
				}
			},
			{
				title: '邮箱',
				dataIndex: 'email',
				width: '14%',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}
					</div>);
				}
			},
			{
				title: '项目职务',
				dataIndex: 'position',
				width: '14%',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{!isNaN(Number(text))?this.showUserInfo(text):text}
					</div>);
				}
			},
			{
				title: '项目职责',
				dataIndex: 'duty',
				editable: !disabled && !isChaijie,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: 'auto', lineHeight: '30px',  wordBreak:'break-all', whiteSpace:'pre-wrap', textAlign:'left'}}
						className="pr">
						{text}
					</div>);
				}
			},

		];
		if(!disabled){
			columnEle = columnEle.concat([{
				title: '操作', dataIndex: 'caozuo', align: 'left', width:'10%', editable: !disabled, render: (text, record) => {
					return (<span><Button
						ghost
						type="primary"
						icon="delete"
						onClick={()=>{this.handleRemoveItem(`${record.id}`)}}>删除</Button></span>)
				}
			}])
		}
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
				}),
			};
		});

		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					rowKey={row => row.id}
					//rowSelection={!disabled?rowSelection:null}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTableOneItem;




