import React from 'react';
import {observer, inject} from 'mobx-react';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {} from 'antd';

@inject('DeliveryStore')
@withRouter
@observer
class TechnologyContent extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			projectId: null,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if (projectDetail.id !== this.state.projectId) {

		}
	}

	componentDidMount() {

	}


	render() {
		const {} = this.props;
		const {} = this.state;
		const {} = this.store;

		return (
			<div className="technology-content">
				<div >
					<p>1、起吊：在箱体的下部用两条吊装带拴致两端箱角吊装孔，利用人字梯或移动脚手架，工人爬到箱箱房屋顶部，专用吊车及挂钩直接伸到顶部箱角吊装孔挂好幷轻微启动让挂钩受力，工人回到移动脚手架上并安全落地。吊车起吊。起吊时严禁箱下臂下站人，预防脱钩伤人。</p>
				</div>
				<div className="content-block">
					<p>2、安装：起吊后有两人牵着吊装带平稳移到基础处慢慢下降并有四人在四个角处扶稳摆放到使用方指定位置放稳，再有一人用升降机到顶部取下挂钩。第二个箱子按同样程序吊下来与第一个箱在顶部有两个M12X30镀锌螺丝连接，底部有两个M12X100镀锌螺丝连接。依次类推。装完后应用磁力线坠校直校正，校正后把上下螺丝进行紧固。垂直度允许偏差为5mm。两箱拼接处安装三元乙丙密封条。</p>
				</div>
				<div className="content-block">
					<p>3、钢构件安装质量要求</p>
					<p>  （1）结构件表面干净，无油污，泥砂和灰尘等杂物。（2）构件在运输、存放和安装过程中损坏的涂层应补涂。（3）安装允许偏差应满足公司内部质量检查标准。（4）连接节点的全部紧固件，应紧固，无松动。</p>
				</div>
				<div className="content-block">
					<p>4、墙板与屋面安装质量要求</p>
					<p>  （1）屋面、墙板应平整清洁，接槎顺直，纵横搭接缝均呈直线，檐口和墙面下端呈直线，无错钻孔，接缝无翘曲。（2）屋面安装允许偏差应满足公司内部标准的要求。</p>
				</div>
				<div className="content-block">
					<p>5、门、窗安装</p>
					<p>  （1）门窗框安装应横平竖直，高低一致，平面一致。（2）框与柱连接应牢固，不晃动，缝隙均匀。（3）门窗表面应平整、光滑、大面无划痕、碰伤。</p>
				</div>
				<div className="content-block">
					<p>6、五金件安装质量要求</p>
					<p>  （1）五金件应装配可靠，转滑动处不得有影响使用性能的松动和卡阻现象。</p>
					<p>  （2）外轮廊不应有裂纹，毛刺，机械损伤，漆膜镀层不应有脱落现象。</p>
					<p>  （3）钉端部必须圆正，光滑，不应高出组合件表面，铆钉应牢固，无松动。</p>
					<p>  （4）锁、窗锁钩，插销应开关灵活，外型完整，无明显影响美观的缺陷。</p>
				</div>

			</div>
		)
	}
}


export default TechnologyContent;



