/*
* 项目信息 新的模板
* dangw@glodon.com
* */
import React from 'react';
import {Input, Form, Row, Col, Popconfirm, Select, Icon, Popover, message} from 'antd';
import MyUpload from "@/components/common/MyUpload4Affairs";
import Zmage from 'react-zmage';
import PersonAdd from "@/components/stakeholder/PersonAdd";
import CommunicationWay from "./CommunicationWay";
import * as mobx from "mobx";
import {observer} from "mobx-react";

const {TextArea} = Input;
const {Option} = Select;

@Form.create()
@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	handleAttment = (type, param) => {
		const url = param && param[0].url;
		this.store.stakeObj[type] = url;
	}

	// 主对接人
	updatePerson = (params) => {
		this.store.stakeObj['communicator'] = params;
	}

	// 沟通机制
	updateWay = (params) => {
		this.store.stakeObj['communicationMode'] = params;
	}

	// 取消
	handleCancle = () => {
		this.props.getTable();
	}

	// 保存
	handleSave = () => {
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.warn("请完善信息后再提交");
				return false;
			} else {
				const {
					name = "",
					copingStrategy = "",
					phone = "",
					position = "",
					sex = "",
					age = "",
					appellation = "",
					email = "",
					maritalStatus = "",
					monthlyIncome = "",
					hobby = "",
					profile = "",
					remark = "",
                    wechat=""
				} = values;
				const {stakeObj,taskDetail} = this.store;
				const {photo, communicationMode, communicator} = mobx.toJS(stakeObj);
				const {id} = mobx.toJS(taskDetail);

				// 校验
				if (communicator==undefined||communicator.length==0) {
					message.warn("主对接人是必填项");
					return false;
				}
				if (communicationMode==null||(Array.isArray(communicationMode)&&communicationMode.length==0)) {
					message.warn("沟通方式是必填项");
					return false;
				}
				const newId = mobx.toJS(stakeObj).id;
				const params = {
					id: newId.startsWith("!")?"":newId,
					taskId:id,
					name,
					copingStrategy,
					phone,
					position,
					sex,
					age,
					appellation,
					email,
					maritalStatus,
					monthlyIncome,
					hobby,
					profile,
					photo,
					remark,
                    wechat,
					communicatorId:communicator[0].userId,
					communicatorName:communicator[0].userName,
					communicationMode: communicationMode.map((item)=>{
						delete item.selected
						return Object.assign({}, item);
					})
				}

				this.store.saveStakeholder({s: params})
				   .then((res)=>{
                        if (res) {
                        	message.success("保存成功");
                        	this.props.getTable();
                        }
			    	});
				//console.log(params, '保存', communicator)
			}
		})
	}

	// 删除沟通人
	handleDel = (index) => {
		let obj = JSON.parse(JSON.stringify(this.store.stakeObj));
		let communicationMode = obj.communicationMode;
		communicationMode.splice(index, 1);
		this.store.stakeObj['communicationMode'] = Object.assign([], communicationMode);
	}

	render() {
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {span: 6},
			wrapperCol: {span: 14},
		};
		const formItemLayout2 = {
			labelCol: {span: 3},
			wrapperCol: {span: 19},
		};
		const copingStrategy = [{id: "1", name: "A类-令其满意"}, {id: "2", name: "B类-重点管理"}, {
			id: "3",
			name: "C类-随时告知"
		}, {id: "4", name: "D类-监督管理"},]
		const content = (
			<table className="staketable">
				<tr>
					<th>分类</th>
					<th>策略</th>
					<th>描述</th>
				</tr>
				<tr>
					<td>A</td>
					<td>令其满意</td>
					<td className="tl">
						<p>打交道的策略：</p>
						<p>1、让其满意</p>
						<p>2、至少不得罪</p>
						<p>3、争取支持</p>
						<p>4、化解敌意</p>
					</td>
				</tr>
				<tr>
					<td>B</td>
					<td>重点管理</td>
					<td className="tl">
						<p>客户、主管领导是典型的这类干系人</p>
						<p>打交道的策略：</p>
						<p>1、重点管理</p>
						<p>2、争取支持</p>
					</td>
				</tr>
				<tr>
					<td>C</td>
					<td>随时告知</td>
					<td className="tl">
						<p>打交道的策略：</p>
						<p>1、对支持项目的干系人及时通报项目的进度以获取支持</p>
						<p>2、对项目持反对意见的干系人，尽量保密，以降低干扰</p>
					</td>
				</tr>
				<tr>
					<td>D</td>
					<td>
						<p>监督</p>
						<p>（花最小的精力）</p>
					</td>
					<td className="tl">
						<p>打交道的策略：</p>
						<p>1、在不影响项目的前提下花最少的精力去监控</p>
						<p>2、不让他们干扰项目即可</p>
					</td>
				</tr>
			</table>
		);
		let fields = mobx.toJS(this.store.stakeObj) || {};

	//console.log(fields, 'fields')


		return (
			<Form layout="inline" {...formItemLayout}>
				<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}
				     style={{
					     margin: "0",
					     borderBottom: "1px solid rgba(242, 242, 242, 1)",
					     position: "relative",
					     height: "50px"
				     }}>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="干系人姓名" className="w">
							{getFieldDecorator('name', {
								initialValue: fields.name || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label={
							<span>
                              应对策略&nbsp;
								<Popover content={content} placement="leftTop" trigger="hover">
									      <Icon type="question-circle"
									            style={{marginLeft: "0px", fontSize: '16px', color: '#4AA9F8'}}
									            theme="filled"/>
								        </Popover>
                            </span>} className="w">
							{getFieldDecorator('copingStrategy', {
								initialValue: fields.copingStrategy || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Select style={{width: "100%"}} placeholder="请选择">
									{copingStrategy.map((v, i) => {
										return <Option key={"copingStrategy" + v.id} value={v.name}>{v.name}</Option>
									})}
								</Select>
							)}
						</Form.Item>
					</Col>
					<div style={{
						position: "absolute",
						right: "10px",
						top: "14px"
					}}>
						<Popconfirm title="确定取消吗?" onConfirm={this.handleCancle}>
							<span className="stake_title">取消</span>
						</Popconfirm>
						<span className="stake_title ml5" onClick={this.handleSave}>保存</span>
					</div>
				</Row>
				<h1 style={{
					padding: "10px 15px",
					fontWeight: 700,
					fontSize: "14px"
				}}>干系人基本信息</h1>
				<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin: "0"}}>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="手机" className="w">
							{getFieldDecorator('phone', {
								initialValue: fields.phone || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="职位" className="w">
							{getFieldDecorator('position', {
								initialValue: fields.position || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="性别" className="w">
							{getFieldDecorator('sex', {
								initialValue: fields.sex || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Select style={{width: "100%"}}>
									<Option value="男">男</Option>
									<Option value="女">女</Option>
								</Select>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="年龄" className="w">
							{getFieldDecorator('age', {
								initialValue: fields.age || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Input placeholder="请输入(可估算区间)"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="称谓" className="w">
							{getFieldDecorator('appellation', {
								initialValue: fields.appellation || "",
								rules: [{required: true, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
                    <Col xs={24} sm={24} md={18} lg={12} xl={12}>
                        <Form.Item label="微信号" className="w">
                            {getFieldDecorator('wechat', {
                                initialValue: fields.wechat || "",
                                rules: [{required: true, message: '此项是必填'}]
                            })(
                                <Input placeholder="请输入"/>
                            )}
                        </Form.Item>
                    </Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="邮箱" className="w">
							{getFieldDecorator('email', {
								initialValue: fields.email || "",
								rules: [{required: fields.communicationMode&&fields.communicationMode.some(item=>item.rule=="日报")?true:false, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="婚姻状况" className="w">
							{getFieldDecorator('maritalStatus', {
								initialValue: fields.maritalStatus || "",
								rules: [{required: false, message: '此项是必填'}]
							})(
								<Select style={{width: "100%"}}>
									<Option value="未婚">未婚</Option>
									<Option value="已婚">已婚</Option>
								</Select>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={18} lg={12} xl={12}>
						<Form.Item label="月收入" className="w">
							{getFieldDecorator('monthlyIncome', {
								initialValue: fields.monthlyIncome || "",
								rules: [{required: false, message: '此项是必填'}]
							})(
								<Input placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item label="兴趣爱好" className="w" {...formItemLayout2}>
							{getFieldDecorator('hobby', {
								initialValue: fields.hobby || "",
								rules: [{required: false, message: '此项是必填'}]
							})(
								<TextArea rows={4} placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item label="个人简介" className="w" {...formItemLayout2}>
							{getFieldDecorator('profile', {
								initialValue: fields.profile || "",
								rules: [{required: false, message: '此项是必填'}]
							})(
								<TextArea rows={4} placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item label="个人照片" className="w" {...formItemLayout2}>
							{getFieldDecorator('photo', {
								rules: [
									{
										required: false,
										message: '该项是必填',
									},
								],
							})(<div>
								{fields.photo == "" || fields.photo == null ? null : <Zmage src={fields.photo}
								                                                            style={{
									                                                            width: "102px",
									                                                            height: "102px",
									                                                            display: "inline-blcok"
								                                                            }}/>}
								<div className="dib" style={{
									verticalAlign: "top",
									marginLeft: fields.photo == "" || fields.photo == null ? 0 : "15px"
								}}>
									<MyUpload
										saveAttachment={this.handleAttment.bind(this, 'photo')}
										disabled={false}
										multiple={false}
										listType="picture-card"
										fileList={[]}
										journalType="journal"
									/>
								</div>
							</div>)}
						</Form.Item>
					</Col>
				</Row>
				<h1 style={{
					padding: "10px 15px",
					fontWeight: 700,
					fontSize: "14px"
				}}>干系人沟通管理</h1>
				<Row>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item
							label={<span><span style={{
								fontWeight: "400",
								marginRight: "4px",
								color: "#f5222d",
								fontSize: "14px",
								fontFamily: "SimSun, sans-serif"
							}}>*</span>主对接人</span>}
							className="w" {...formItemLayout2}>
							{getFieldDecorator('communicator', {
								initialValue: fields.communicator || [],
								rules: [{required: false, message: '此项是必填'}]
							})(
								<PersonAdd
									data={fields.communicator}
									updatePerson={this.updatePerson}
								/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item
							label={<span><span style={{
								fontWeight: "400",
								marginRight: "4px",
								color: "#f5222d",
								fontSize: "14px",
								fontFamily: "SimSun, sans-serif"
							}}>*</span>沟通方式</span>}
							className="w" {...formItemLayout2}>
							{getFieldDecorator('communicationMode', {
								initialValue: fields.communicationMode || [],
								rules: [{required: false, message: '此项是必填'}]
							})(
								<CommunicationWay
									data={fields.communicationMode}
									updateWay={this.updateWay}
									handleDel={this.handleDel}
								/>
							)}
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24} lg={24} xl={24}>
						<Form.Item label="备注" className="w" {...formItemLayout2}>
							{getFieldDecorator('remark', {
								initialValue: fields.remark || "",
								rules: [{required: false, message: '此项是必填'}]
							})(
								<TextArea rows={4} placeholder="请输入"/>
							)}
						</Form.Item>
					</Col>
				</Row>
			</Form>
		)
	}
}



