/*
* 供应商编辑组件
* dangw@glodon.com
* */
import React from 'react';
import {Popconfirm, message, Table, Button, Form} from 'antd';
import * as mobx from "mobx";
import {observer} from "mobx-react";
import moment from "moment";
import EditableTableUpload from "@/components/handover/EditableTableUpload";
import Document from "./Document";
import EditableTableInventory from './EditableTableInventory.js';

@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: []
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		//this.getEnclosureList();
		this.props.onRef(this);
	}

	handleEdit = () => {
		this.store.suState = false;
		this.props.reStartHandover();
		//
		const {supplierTwoItem} = this.store;
		const obj = JSON.parse(JSON.stringify(supplierTwoItem));
		delete obj.selected;
		const params = Object.assign(obj, {
			isSave: 1
		});
		// 保存
		this.store.saveConfirmSupplierTwoItem({csti: params}).then((res) => {
			if (res) {
				// 更改列表的数据
				let param = res;
				this.store.supplierTwoItem = Object.assign({}, res);
				this.props.updateData();
				if (param.isMain==1) {  // 主合同
					let stakeholderList = JSON.parse(JSON.stringify(this.store.supplierTwoItemList));
					let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
					const dindex = dataArray.findIndex((item) => {
						return item.id == param.id
					});
					dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
					this.store.supplierTwoItemList = Object.assign([], dataArray);
				}
			}
		});
	}

	saveContents=()=>{
		this.getEnclosureList();
	}

	saveItem=(row)=>{
		const { dataSource } = this.state;
		const { taskDetail } = this.store;
		let em = {taskId:taskDetail.id, name:'',indexs:1,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
		if(row.id!==undefined&& row.id[0]!=="!"){
			em['id'] = row.id
			em['orders']= row.orders
			em['enclosureItems'] = row.enclosureItems
			em['templates'] = row.templates
			em['cases'] = row.cases
			em['name']= row.name
		}
		const parmas = {"em":em, taskId:taskDetail.id, indexs:1}
		this.store.saveEnclosureMain(parmas).then((res)=>{
			this.getEnclosureList()
		})
	}

	getEnclosureList=()=>{
		const { taskDetail, supplierTwoItem } = this.store;
		this.store.getEnclosureListByItemId({"taskId":mobx.toJS(taskDetail).id, "indexs":0, itemId: mobx.toJS(supplierTwoItem).id}).then((res)=>{
			this.store.enclosureList = res
			this.setState({dataSource: res, enclosureList: res})
		})
	}

	// 删除合同
	handleDelete = (param) => {
		this.store.delConfirmSupplierSupplementById({id: param}).then((res)=>{
			if (res==true) {
				message.success("删除成功");
				// 重新初始化数据,初始化到主合同
				this.props.autosaveData();
			}
		});
	}

	render() {
		let fields = mobx.toJS(this.store.supplierTwoItem) || {};
		let company = mobx.toJS(this.store.companyList) || [];
		const {projectAddress = "", partySecondManager = "", partySecondPhone = "", partySecondIdCard = "",
			partySecondSafer = "", contractMoney = "",workBeginTime,wordEndTime,contractAwardTime,
			partySecondBankName="",partySecondBankNumber="",partySecondBank="",partyFirstCompanyId="",
			code="",
			partySecond="",
			projectName="",
			partyFirstManager="",
			partyFirstPhone="",
			contractMoneyBig="",
			contractDays="",
			document,
			supplierPayNode,
			supplierContractList
		} =  fields;
		let active = company.filter(item=>item.id==partyFirstCompanyId)[0]||{};
		const {dataSource} = this.state;
		let isDocument = document==null || (typeof document == "object"&&Object.keys(document).length==0)?false:true;  // 是否有采购合同
		const columns = [
			{
				title: '序号',
				dataIndex: 'dictId',
				width: '50px',
				align: 'center'
			},
			{
				title: '节点',
				dataIndex: 'nodeName',
				width: '200px',
				align: 'center',
			},
			{
				title:'时间',
				dataIndex: 'dateTime',
				width: '150px',
				align: 'center',
				/*render: (text, record)=> {
					return text==0||text==null?"":moment(text).format('YYYY-MM-DD')
				}*/
			},
			{
				title: "支付到比例（%）",
				dataIndex: 'payRate',
				width: '200px',
				align: 'center',
			},
			{
				title: '备注',
				dataIndex: 'remark',
				width: '200px',
				align: 'center',
			}
		];

		return (
			<div className={fields.isSave==2?"w":"hidden"}>
				<div className="fr" style={{padding: "0 35px"}}>
					<Button type="primary" onClick={this.handleEdit}>编辑合同</Button>
					{fields.isMain==1?null:
						<Popconfirm
							title="确定要删除吗?"
							onConfirm={this.handleDelete.bind(this, fields.id)}
							onCancel={()=>{}}
							okText="是"
							cancelText="否"
						>
							<Button className="ml15" type="dashed">删除合同</Button>
						</Popconfirm>
					}
				</div>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>合同基本信息</h1>
				<table className="suppliertable">
					<tr>
						<td>合同编号</td>
						<td>
							{code}
						</td>
						<td>合同乙方</td>
						<td>
							{partySecond}
						</td>
					</tr>
					<tr>
						<td>项目名称</td>
						<td>
							{projectName}
						</td>
						<td>项目地址</td>
						<td>
							{projectAddress}
						</td>
					</tr>
					<tr>
						<td>甲方项目经理</td>
						<td>
							{partyFirstManager}
						</td>
						<td>甲方联系方式</td>
						<td>
							{partyFirstPhone}
						</td>
					</tr>
					<tr>
						<td>乙方项目经理</td>
						<td>
							{partySecondManager}
						</td>
						<td>乙方联系方式</td>
						<td>
							{partySecondPhone}
						</td>
					</tr>
					<tr>
						<td>乙方身份证号</td>
						<td>
							{partySecondIdCard}
						</td>
						<td>乙方安全负责人</td>
						<td>
							{partySecondSafer}
						</td>
					</tr>
					<tr>
						<td>合同金额（元）</td>
						<td>
							{contractMoney}
						</td>
						<td>大写金额（元）</td>
						<td>
							{contractMoneyBig}
						</td>
					</tr>
					<tr>
						<td>开工日期</td>
						<td>
							{workBeginTime !== 0 && workBeginTime !== null ? moment(workBeginTime).format('YYYY年MM月DD日') : ''}
						</td>
						<td>竣工日期</td>
						<td>
							{wordEndTime !== 0 && wordEndTime !== null ? moment(wordEndTime).format('YYYY年MM月DD日') : ''}
						</td>
					</tr>
					<tr>
						<td>合同工期</td>
						<td>
							{contractDays}天
						</td>
						<td>合同签订日期</td>
						<td>
							{contractAwardTime !== 0 && contractAwardTime !== null ? moment(contractAwardTime).format('YYYY年MM月DD日') : ''}
						</td>
					</tr>
				</table>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>甲乙方账户信息</h1>
				<table className="suppliertable">
					<tr>
						<td>乙方开户名称</td>
						<td>
							{partySecondBankName}
						</td>
						<td>开票公司名称</td>
						<td>
							{active.name||""}
						</td>
					</tr>
					<tr>
						<td>乙方开户行</td>
						<td>
							{partySecondBank}
						</td>
						<td>纳税人识别号</td>
						<td>
							{active.sh||""}
						</td>
					</tr>
					<tr>
						<td>乙方账号</td>
						<td>
							{partySecondBankNumber}
						</td>
						<td>地址</td>
						<td>
							{active.address||""}
						</td>
					</tr>
					<tr>
						<td style={{borderColor: "#FFF", background: "#FFF"}}></td>
						<td style={{borderColor: "#FFF", borderRightColor: "#e6e6e6", background: "#FFF"}}></td>
						<td>开户行</td>
						<td>
							{active.bank||""}
						</td>
					</tr>
					<tr>
						<td style={{borderColor: "#FFF", background: "#FFF"}}></td>
						<td style={{borderColor: "#FFF", borderRightColor: "#e6e6e6", background: "#FFF"}}></td>
						<td>账号</td>
						<td>
							{active.bankCard||""}
						</td>
					</tr>
				</table>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>付款节点</h1>
				<div style={{padding: "0 35px"}}>
					<Table
						className="digital_table"
						rowClassName={() => 'editable-row'}
						dataSource={supplierPayNode&&supplierPayNode.filter(i=>i.isSelected==1).map((v, vindex)=>Object.assign(v, {dictId: vindex+1}))||[]}
						columns={columns}
						pagination={false}
						bordered
					/>
				</div>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>合同清单</h1>
				<div style={{ padding: "0 35px"}}>
					<EditableTableInventory
						disabled={true}
						dataSource={supplierContractList==null?[]:supplierContractList}
						store={this.store}
					/>
				</div>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>生成的采购合同</h1>
				<div style={{padding: "10px 35px"}}>
					{isDocument?<Document
						data={document}
						store={this.store}
					/>:null}
				</div>
				<h1 style={{padding: "10px 35px", fontWeight: 700, fontSize: "14px"}}>附件</h1>
				<div style={{padding: "10px 35px"}}>
					<EditableTableUpload
						id={'id'}
						store={this.store}
						dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
						saveData={this.saveContents}
						saveItem={this.saveItem}
						disabled={true}
						pid={''}
					/>
				</div>
			</div>
		)
	}
}



