/*
* 选择供应商组件 第三个表
* dangw@glodon.com
* */
import React from 'react';
import {Select, Spin} from 'antd';
import {inject, observer} from "mobx-react";
import * as mobx from "mobx";
const {Option} = Select;

@inject('DeliveryStore')
@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeliveryStore;
		this.state = {
			fetching: false,
			text: "", //关键字
			companyData: [],
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		this.setState({
			text: "", //关键字
		}, () => {
			this.getCompanyList(); // 调用api方法
		});
	}

	fetchCompany = value => {
		this.setState({companyData: [], fetching: true, text: value}, () => {
			this.getCompanyList(); // 关键字模糊查询api
		});
	};

	getCompanyList = () => {
		//const {text} = this.state;
		const {taskDetail} = this.store;
		this.store.getConfirmSupplierOneItemList({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
			this.setState({
				companyData: Object.assign([], Array.isArray(res) ? res : [])
			});
		});
	}

	setCheckNum = (val, e) => {
		let item = e.props.val||{};
		this.props.onSupplierChange(item);
	}

	render() {
		const {companyData, fetching} = this.state;
		const {value} = this.props;

		return (
			<Select
				onBlur={this.props.onBlur}
				style={{width: '100%'}}
				showSearch
				placeholder="请选择"
				notFoundContent={fetching ? <Spin size="small"/> : null}
				onSearch={this.fetchCompany}
				onChange={this.setCheckNum}
				filterOption={false}
				value={value}
			>
				{companyData && companyData.map(item => {
					return (
						<Option value={item.id} key={item.id} title={item.name} val={item}>
							{item.name}
						</Option>)
				})}
			</Select>
		)
	}
}



