import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Button, Table, DatePicker, message, Form, InputNumber,Input, Popover, Icon, Select, Modal} from 'antd';
import TwoItemView from './TwoItemView';

const { Option } = Select;
const { confirm } = Modal;
const EditableContext = React.createContext();
const EditableRow = ({ form, index, ...props }) => (
    <EditableContext.Provider value={form}>
        <tr {...props} />
    </EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
class EditableCell extends React.Component {
    state = {
        editing: false,
    };

    toggleEdit = () => {
        const editing = !this.state.editing;
        this.setState({ editing }, () => {
            if (editing) {
                this.input.focus();
            }
        });
    };

    save = (type, e) => {
        const { record, handleSave } = this.props;
        this.form.validateFields((error, values) => {
            if (error && error[e.currentTarget.id]) {
                return;
            }
            this.toggleEdit();
            handleSave({...record, ...{[type]: values[type]}});
        });
    };

    // 新增下一行
	handleAdd = () => {
		const { record, handleSaveNew } = this.props;
		let recordData = JSON.parse(JSON.stringify(record));
		const id = recordData.id;
		// 记录当前下一行数据
		let active = Object.assign(recordData, {
			id: "",
			name: "新增任务",
			remark: "",
			prerequisiteName: "",
			needWorkTime: 0,
			planBeginTime: recordData.planBeginTime,
			planEndTime: recordData.planBeginTime,
			adjunction: 1
		})
		handleSaveNew(id, active);
	}

	// 删除当前行
	handleDelete = () => {
		const { record, handleDel } = this.props;
		let recordData = JSON.parse(JSON.stringify(record));
		const id = recordData.id;
		handleDel(id);
	}

	// 前置任务
	handleChangeSel = (e) => {
		const { record, handleSave } = this.props;
		handleSave({...record, ...{"prerequisiteName": e}});
	}

    renderCell = form => {
        this.form = form;
        const { children, dataIndex, record, title, dataSource } = this.props;
        const { editing } = this.state;
		const content = (
			<div>
				<p style={{color: "#1791FF", cursor: "pointer"}} onClick={this.handleAdd}><Icon type="plus-circle" />&nbsp;在下方插入行</p>
				<p style={{color: "#E97C6F", cursor: "pointer"}} onClick={this.handleDelete}><Icon type="minus-circle" />&nbsp;删除所在行</p>
			</div>
		);
        if (dataIndex == "needWorkTime") {   // 时间
            return editing&&record.level == 3 || record.adjunction==1 ? (
                <Form.Item style={{margin: 0}}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(
                        <InputNumber ref={node => (this.input = node)}
                                     //onPressEnter={this.save.bind(this, dataIndex)}
                                     onBlur={this.save.bind(this, dataIndex)}/>
                    )}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{paddingRight: 24, minHeight: "34px"}}
                    onClick={record.level == 3?this.toggleEdit:null}
                >
                    {children}
                </div>
            );
        }
		else if (dataIndex=="prerequisiteName"&&record.adjunction==1) {  // 前置任务
			const parentId =  record.parentId;
			let filterData = dataSource.filter(item=>item.parentId==parentId&&item.id!=record.id);  // 过滤
			return <Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title} is required.`,
						},
					],
					initialValue: record[dataIndex],
				})(
					<Select
						style={{ width: "100%" }}
						onChange={this.handleChangeSel}>
						{filterData.map((item, index)=>{
							return <Option value={item.name} key={item.id==null||item.id==""?"prerequisiteName-"+index:item.id}>{item.name}</Option>
						})}
					</Select>
				)}
			</Form.Item>}
        else {
            return editing&&record.level == 3 || record.adjunction? (
                <Form.Item style={{margin: 0}}>
					{record.adjunction==1&&dataIndex=="name"?
						<Popover placement="rightTop" content={content} trigger="click">
							<span style={{cursor: "pointer", width: "16px", fontSize: "18px", display: "inline-block", position: "relative", top: "-3px"}}>...</span>
						</Popover> :""}
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<Input ref={node => (this.input = node)}
                              onBlur={this.save.bind(this, dataIndex)}/>)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{
                        paddingLeft: dataIndex=="name"?record.level * 8 + "px":0,
                        paddingRight: 24, minHeight: "34px"}}
                    onClick={record.level == 3?this.toggleEdit:null}
                >
                    {dataIndex=="name"? <span>{record.parentId!==null?
						<Popover placement="rightTop" content={content} trigger="click">
							<span style={{width: "16px", fontSize: "18px", display: "inline-block", position: "relative", top: "-3px"}}>...</span>
						</Popover> :""} {(record.code==null?"":record.code) + " " + record.name}</span>: children}
                </div>
            );
        }
    };

    render() {
        const {
            editable,
            dataIndex,
            title,
            record,
            index,
            handleSave,
			handleSaveNew,
			handleDel,
			dataSource,
            children,
            ...restProps
        } = this.props;
        return (
            <td {...restProps}>
                {editable ? (
                    <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
                ) : (
                    children
                )}
            </td>
        );
    }
}


@inject('DeliveryStore')
@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			dataSource: [],
			begin: 0,
			end: 0,
			data: [], // 右侧数据
			dataSourceTwo: [], // 表二数据
			loading: false,
		}
	}

	componentDidMount() {
		let constructionState = mobx.toJS(this.store.constructionState);
		if (constructionState.state2 === 2) {
			this.getDetailWorkContentByProjectId();
			this.getConstructionScheduleDetail();
		} else {
			this.getConstructionScheduleDetail();
		}
	}

	getConstructionScheduleDetail = () => {
		const {taskDetail} = this.store;
		this.setState({
			loading: true
		}, ()=> {
			this.store.getScheduleDetailListByTaskId({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
				this.setState({
					dataSource: Array.isArray(res) ? res : [],
					loading: false
				});
			});
		});
	}

	overCurrentStage = () => {
		// 校验
		let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
		let requireSource = dataSource.filter(item=>item.planBeginTime!=undefined);
		if (dataSource.length==0) {
			message.warn("请先完成施工要素");
			return false;
		}
		if (requireSource.some(item=>item.planBeginTime==null||item.planBeginTime==""||item.planBeginTime==0)) {
			message.warn("请完善当前阶段的必填信息");
			return false;
		}
		// 详情态
		this.props.overCurrentStageTask();
		this.getDetailWorkContentByProjectId();
	}

	// 可用范围
	disabledDate = (current) => {
		const { begin, end} = this.state;
		return current <begin || current > end;
	}

	handDate = (record, date, dateString) => {
		const param = Object.assign({}, record, {"earlyPlanBeginTime": dateString == null || dateString == "" ? 0 : moment(dateString, 'YYYY-MM-DD').valueOf()})
		let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
		const index = dataSource.findIndex(item=>item.id==param.id);
		// 插入数据
		dataSource.splice(index, 1, param);

		let taskDetail = mobx.toJS(this.store.taskDetail);
		const params = {
			taskId: taskDetail.id,
			contents: dataSource
		}
		this.store.saveScheduleWorkContent(params)
			.then((res) => {
				if (res) {
					message.success("操作成功");
					// 重查列表
					this.getConstructionScheduleDetail();
					this.props.getConfirmSupplierState();
				}
			});
	}

	// 获取焦点
	onFocus = (record) => {
	    if (record.level!=undefined&&record.level == 2) {
            const params = {
                detailId: record.detailId
            }
            this.store.getPlanBeginTimeRange(params)
                .then((res)=>{
                    let data = typeof res == "string" ? JSON.parse(res) : res;
                    if (Array.isArray(data)&&data.length>0) {
                        this.setState({
                            begin: data[0],
                            end: data[1]
                        });
                    }
                });
        } else if (record.level!=undefined&&record.level == 3) {
            const params = {
                detailId: record.detailId
            }
            this.store.getMaterialTimeRange(params)
                .then((res)=>{
                    let data = typeof res == "string" ? JSON.parse(res) : res;
                    if (Array.isArray(data)&&data.length>0) {
                        this.setState({
                            begin: data[0],
                            end: data[1]
                        });
                    }
                });
        }
	}

    handleSave = row => {
		let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
		const param = JSON.parse(JSON.stringify(row));
		const index = dataSource.findIndex(item=>item.id==param.id);
		// 插入数据
		dataSource.splice(index, 1, param);

		let taskDetail = mobx.toJS(this.store.taskDetail);
        const params = {
			taskId: taskDetail.id,
			contents: dataSource
		}
		this.store.saveScheduleWorkContent(params)
			.then((res) => {
				if (res) {
					message.success("操作成功");
					// 重查列表
					this.getConstructionScheduleDetail();
					this.props.getConfirmSupplierState();
				}
			});
    };

    // 插入当前行
    handleSaveNew = (id, row) => {
		let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
		const index = dataSource.findIndex(item=>item.id==id)+1;
		// 插入数据
		dataSource.splice(index, 0, row);
		let taskDetail = mobx.toJS(this.store.taskDetail);
		const params = {
			taskId: taskDetail.id,
			contents: dataSource
		}
		this.store.saveScheduleWorkContent(params)
			.then((res) => {
				if (res) {
					message.success("操作成功");
					// 重查列表
					this.getConstructionScheduleDetail();
					this.props.getConfirmSupplierState();
				}
			});
	}

	// 删除当前行
	handleDel = (id) => {
    	let _this = this;
		confirm({
			title: '确定要删除吗?',
			okText: '是',
			okType: 'danger',
			cancelText: '否',
			onOk() {
				let dataSource = JSON.parse(JSON.stringify(_this.state.dataSource));
				const index = dataSource.findIndex(item=>item.id==id);
				// 插入数据
				dataSource.splice(index, 1);
				let taskDetail = mobx.toJS(_this.store.taskDetail);
				const params = {
					taskId: taskDetail.id,
					contents: dataSource
				}
				_this.store.saveScheduleWorkContent(params)
					.then((res) => {
						if (res) {
							message.success("操作成功");
							// 重查列表
							_this.getConstructionScheduleDetail();
							_this.props.getConfirmSupplierState();
						}
					});
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	// 计算排期
	calculationSchedule = () => {
		const {constructionState} = this.store;
    	if (constructionState.state1 != 2) {
			message.warn("请完成施工要素");
			return false;
		}
		let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
    	this.setState({
			loading: true
		}, ()=> {
			this.store.calculationSchedule(dataSource)
				.then((res) => {
					if (res) {
						message.success("操作成功");
						this.setState({
							dataSource: Array.isArray(res) ? res : [],
							loading: false,
						});
					}
				});
		});
	}

	getDetailWorkContentByProjectId = () => {
    	this.setState({
			loading: true
		}, ()=> {
			this.store.getDetailWorkContentByProjectId(0)
				.then((res) => {
					if (res) {
						this.setState({
							loading: false,
							dataSourceTwo: Array.isArray(res) ? res : []
						});
					}
				});
		});
	}

	render() {
		const {constructionState} = this.store;
		const {currentStep, switchSteps} = this.props;
		const {dataSource, dataSourceTwo, loading} = this.state;
		const columns = [
			{
				title: '施工任务',
				dataIndex: 'name',
				key: 'name',
				align: 'left',
                editable: true
			},
			{
				title: '工作内容',
				dataIndex: 'remark',
				key: 'remark',
				align: 'left',
                editable: true
			},
			{
				title: '前置任务',
				dataIndex: 'prerequisiteName',
				key: 'prerequisiteName',
				align: 'left',
                editable: true
			},
			{
				title: '计划用时（天）',
				dataIndex: 'needWorkTime',
				key: 'needWorkTime',
				align: 'center',
				width: "150px",
                editable: true
			},
			{
				title: <div><span style={{
					fontWeight: "400",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span>计划开始时间</div>,
				dataIndex: 'earlyPlanBeginTime',
				key: 'earlyPlanBeginTime',
				align: 'center',
				width: "200px",
				render: (text, record) => {
					return (<div>
						<DatePicker
							allowClear={false}
							key={record.id}
							style={{width: "120px"}}
							format="YYYY-MM-DD"
							placeholder="请选择"
							value={text == null || text == 0 ? (record.planBeginTime == null || record.planBeginTime == 0 ? "" : moment(moment(record.planBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD")) : moment(moment(text).format('YYYY-MM-DD'), "YYYY-MM-DD")}
							onChange={this.handDate.bind(this, record)}
						/>
					</div>)
				}
			},
			{
				title: '计划完成时间',
				dataIndex: 'planEndTime',
				key: 'planEndTime',
				align: 'center',
				width: "200px",
				render: (text, record) => {
					return (<div>
						{text !== 0 && text !== null &&text !== undefined ? moment(text).format('MM月DD日') : ''}
					</div>)
				}
			}
		];
        const components = {
            body: {
                row: EditableFormRow,
                cell: EditableCell,
            },
        };
        const colum = columns.map(col => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.handleSave,
					handleSaveNew: this.handleSaveNew,
					handleDel: this.handleDel,
					dataSource: dataSource
                }),
            };
        });
		return (
			<div className={constructionState.state2 === 2?"":"mt30"}>
				<div ref={node => {
						this.container = node;
					}}>
					{/*编辑态*/}
					<div className={constructionState.state2 === 2 ? "hidden" : ""}>
						<Button
							disabled={loading}
							onClick={this.calculationSchedule} type="primary" className="mb15">计算排期</Button>
						<Table
							dataSource={dataSource}
                            components={components}
							columns={colum}
							pagination={false}
							bordered
							rowClassName={(record) => {
								if (record.level == 0) {
									return "construct_trone";
								} else if (record.level == 1) {
									return "construct_trtwo";
								} else if (record.level == 2) {
									return "construct_trthree";
								} else if (record.level == 3) {
                                    return "construct_trfour editable-row";
                                } else {
									return "";
								}
							}}
							className="agenda_nohover construction_table digital_table"
						/>
					</div>
					{/*详情态*/}
					{constructionState.state2 === 2 && <TwoItemView
						disabled={loading}
					    parentContainer={this.container}
						dataSource={dataSource}
						dataSourceTwo={dataSourceTwo}
						reStartHandover={this.props.reStartHandover}
						store={this.store}/>}
					<div className="mt30 mb30" style={constructionState.state2 !== 2 ? {
						display: 'flex',
						justifyContent: 'center'
					} : {display: 'none'}}>
						<Button onClick={this.overCurrentStage}
						        ghost
						        type="primary"
								disabled={loading}
						> 完成该阶段任务</Button>
					</div>
					<div className="mt10" style={constructionState.state2 === 2 ? {
						display: 'flex',
						justifyContent: 'center'
					} : {display: 'none'}}>
						<Button
							disabled={loading}
							onClick={() => {
							switchSteps(currentStep - 1)
						}} style={{marginLeft: '20px'}} type='primary'>上一步</Button>
					</div>
				</div>
			</div>
		)
	}
}


export default TwoItem;



