import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Upload, DatePicker, Row, Col, Form, Card, Switch, List, Input, InputNumber, Checkbox, message, Radio, Button, Icon, Spin, Modal} from 'antd';


@inject('DeliveryStore')
@withRouter
@Form.create()
@observer
export default class QualityPromise extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {

	}

	componentWillMount(){
	}

	handleNumChange=(e, key)=>{
		// console.log(e.target.value)
		this.props.updateData(key, e.target.value)
	}

	render() {
		const {  } = this.state;
		const { buildTeamTwoMainObj } = this.store;
		const { label } = this.props;
		let fields = mobx.toJS(buildTeamTwoMainObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};

		return (
			<div className="mt15">
				<Form {...formItemLayout} labelAlign="left"  >
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
							<Form.Item  label={label} className="w build-two-form-label" style={{ marginBottom:"0"}}>
								≤ {getFieldDecorator('qualityPromiseAccidentCount', {
								initialValue: fields.qualityPromiseAccidentCount,
								rules: [{required: false, message: '此项是必填'}],
							})(
								<InputNumber className="build-two-input" onBlur={(e)=>{this.handleNumChange(e, 'qualityPromiseAccidentCount')}} placeholder="请输入数字" />
							)} 次 {label==='验收次数'?'客户确认签字':''}
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</div>
		)
	}
}






