/*
*交付管理-组建项目团队
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {
	Upload,
	DatePicker,
	Row,
	Col,
	Form,
	Card,
	Switch,
	List,
	Input,
	InputNumber,
	Checkbox,
	message,
	Radio,
	Button,
	Icon,
	Spin,
	Modal,
	Select
} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';
import _ from "lodash";

@inject('DeliveryStore')
@withRouter
@Form.create("")
@observer
export default class ContractItemTwo extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			sureState:0,//defalut 0
			summary:BraftEditor.createEditorState(null),
			overallDescription:BraftEditor.createEditorState(null),
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {


	}

	componentDidMount() {
		this.props.onRef && this.props.onRef(this);
	}

	componentWillMount(){
	}

	handleTimeSure=(e)=>{
		this.setState({
			sureState:e.target.value
		})
        // this.props.updateData("TimeSure", e.target.value)
		this.saveBuildTeamTwoMain("progressTargetTimeSure",e.target.value );
	}

	saveBuildTeamTwoMain=(type,value)=>{
		const { buildTeamTwoMainObj } = this.store;
		let obj={}
		obj[type] = value;
		let newData = Object.assign(mobx.toJS(buildTeamTwoMainObj),obj);
		this.store.saveBuildTeamTwoMain({"bttm":newData}).then((res)=>{
			this.store.buildTeamTwoMainObj = Object.assign(mobx.toJS(buildTeamTwoMainObj), res)
		})
	}


	handleDateChange=(date, type)=>{
		this.props.updateHandOver2(type,date)
	}

	handleDaysChange=(e, type)=>{
		// console.log(e.target.value)
		// this.props.updateData(type, e.target.value)
		this.saveBuildTeamTwoMain(type,e.target.value );
	}

	handleUpdate=(type, e)=>{
		this.props.updateHandOver(type,e)
	}

	handleUpdate2=(type, e)=>{
		this.props.updateHandOver2(type,e)
	}

	handleBChange = (type, editorState) => {
		// console.log(editorState.toHTML())
		if(type==='summary'){
			this.setState({summary: editorState});
		}
		if(type==='overallDescription'){
			this.setState({overallDescription: editorState});
		}
		if(type==='assistance'){
			this.setState({assistance: editorState});
		}
	}

	checkFormValidate(){
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values);
				this.props.checkFormRequre(true)
			}else{
				this.props.checkFormRequre(false)
			}
		});
	}

	convertStrToArr=(str)=>{
		let result = []
		let newStr = _.trim(str,'[');
		newStr = _.trim(newStr,']');
		result=  Array.isArray(newStr.split(","))?newStr.split(","):[]
		result = result.map((item)=>{
			return _.trim(item)
		})
		return result
	}

	handleChangeSelect=(type, value)=>{
		const val = Array.isArray(value)&&value.length==0?"":value.toString()
		this.props.updateHandOver2(type, val)
	}

	// 产品类型
	changeBoxtype = (value) =>{
		const obj = {1: 19800, 2: 15600, 3: 13300};
		this.props.updateHandOver3({
			"boxType": value,
			"boxMoney": obj[value]
		});
		this.props.form.setFieldsValue({
			"boxMoney": obj[value]
		});
	}

	render() {
		const { sureState } = this.state;
		const { handoverTwoObj, handoverState } = this.store;
		let fields = mobx.toJS(handoverTwoObj);  // 基本信息
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 4},
				md: {span: 4},
				sm: {span: 4},
			},
			wrapperCol: {
				xs: {span: 20},
				md: {span: 20},
				sm: {span: 20},
			},
		};
		const controls = ['bold',  'text-color',  ]
		const boxTypeData = [{ name: '高档位', value: 1 }, { name: '中档位', value: 2 }, { name: '普通箱', value: 3 }];
		return (
			<div className="mt15">
				<Form  {...formItemLayout} >
					{handoverState.state2===2?
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
							<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<table className="suppliertable" style={{width: "100%"}}>
									<tr>
										<td>项目交付范围</td>
										<td colSpan={3}>
											{fields.deliverRange}
										</td>
									</tr>
									<tr>
										<td>项目用途</td>
										<td colSpan={3}>
											{this.convertStrToArr(fields.purpose).join("、")}
										</td>
									</tr>
									<tr>
										<td>付款节点</td>
										<td colSpan={3}>
											{fields.deliverNode}
										</td>
									</tr>
									<tr>
										<td>工期要求</td>
										<td colSpan={3}>
											{moment(fields.requirementBeginTime).format("YYYY-MM-DD")} 至 {moment(fields.requirementEndTime).format("YYYY-MM-DD")}
										</td>
									</tr>
									<tr>
										<td>合同金额</td>
										<td colSpan={3}>
											{fields.contractMoney}元
										</td>
									</tr>
									<tr>
										<td>产品类型</td>
										<td>
											{fields.boxType!=""&&fields.boxType!=null?boxTypeData.filter(item=>item.value==fields.boxType)[0].name:""}
										</td>
										<td>产品单价</td>
										<td>
											{fields.boxMoney}
										</td>
									</tr>
									<tr>
										<td>周转次数(次)</td>
										<td colSpan={3}>
											{fields.turnoverCount}
										</td>
									</tr>
								</table>
							</Col>
						</Row>:
						<Row gutter={[{xs: 24, sm: 24, md: 24, lg: 24, xl: 24}, 15]}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24} style={{padding: 0}}>
								<Form.Item  label="项目交付范围" className="w">
										{getFieldDecorator('background', {
										initialValue: fields.deliverRange == null ? "" : fields.deliverRange,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("deliverRange", e)}} style={{width:'600px'}}  placeholder="请输入"  />
									)}
								</Form.Item>
								<Form.Item label="项目用途" className="w">
									{getFieldDecorator('purpose', {
										initialValue:fields.purpose===null? undefined: this.convertStrToArr(fields.purpose),
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Select mode="multiple"
												style={{width:'600px'}}
												value={this.convertStrToArr(fields.purpose)}
												placeholder={'请选择'}
												onChange={this.handleChangeSelect.bind(this, "purpose")}>
											<Option value="办公">办公</Option>
											<Option value="住宿">住宿</Option>
											<Option value="会议">会议</Option>
											<Option value="接待">接待</Option>
											<Option value="展示">展示</Option>
											<Option value="休闲">休闲</Option>
											<Option value="零售">零售</Option>
											<Option value="便民">便民</Option>
										</Select>
									)}
								</Form.Item>
								<Form.Item label="付款节点" className="w">
									{getFieldDecorator('deliverNode', {
										initialValue: fields.deliverNode,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input.TextArea onBlur={(e)=>{this.handleUpdate("deliverNode", e)}} rows={6} placeholder="请输入"  style={{width:'100%',minWidth:'800px'}} />
									)}
								</Form.Item>
								<Form.Item  label="工期要求" className="w">
									<div style={{display:'flex', alignItems:'center'}}>
									{getFieldDecorator('requirementBeginTime', {
													initialValue: fields.requirementBeginTime == null || fields.requirementBeginTime == 0 ? "" : moment(moment(fields.requirementBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
													rules: [{required: true, message: '此项是必填'}],
												})(
													<DatePicker
														onChange={(e)=>{this.handleDateChange(e.valueOf(),'requirementBeginTime')}}
														style={{width: "288px"}}
														placeholder="开始时间"
														format="YYYY-MM-DD"
														/>
									)}<div style={{margin:'0 9px'}}>-</div>{getFieldDecorator('requirementEndTime', {
														initialValue: fields.requirementEndTime == null || fields.requirementEndTime == 0 ? "" : moment(moment(fields.requirementEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
														rules: [{required: true, message: '此项是必填'}],
													})(
														<DatePicker
															onChange={(e)=>{this.handleDateChange(e.valueOf(),'requirementEndTime')}}
															style={{width: "288px"}}
															placeholder="结束时间"
															format="YYYY-MM-DD"
															/>
										)}
									</div>
								</Form.Item>
								<Form.Item  label="合同金额（元）" className="w">
										{getFieldDecorator('contractMoney', {
										initialValue: fields.contractMoney == null ? null : fields.contractMoney,
										rules: [{required: true, message: '此项是必填'}],
									})(
										<InputNumber style={{width:'600px'}} ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate2("contractMoney", e.target===null?null:parseInt(e.target.value))}}  placeholder="请输入金额"  />
									)}
								</Form.Item>
							<Form.Item label="产品类型" className="w">
								{getFieldDecorator('boxType', {
									initialValue: fields.boxType || "",
									rules: [{required: false, message: '此项是必填'}],
								})(
									<Select onChange={this.changeBoxtype}
											style={{width:'600px'}}
									>
										{boxTypeData.map((item, index) => {
											return <Option key={'boxTypeData' + index} value={item.value}>{item.name}</Option>;
										})}
									</Select>
								)}
							</Form.Item>
							<Form.Item label={'产品单价(元)'} className="w">
								{getFieldDecorator('boxMoney', {
									initialValue: fields.boxMoney || "",
									rules: [{required: false, message: '此项是必填'}],
								})(
									<InputNumber placeholder="请输入" style={{width:'600px'}} disabled/>
								)}
							</Form.Item>
							<Form.Item label={'周转次数(次)'} className="w">
								{getFieldDecorator('turnoverCount', {
									initialValue: fields.turnoverCount,
									rules: [{
										pattern: /^[0-9]\d*$/,
										required: false,
										message: '只能输入大于等于0的整数'
									}]
								})(
									<InputNumber placeholder="请输入" style={{width:'600px'}} ref={node => (this.input = node)} onBlur={(e)=>{this.handleUpdate("turnoverCount", e)}}/>
								)}
							</Form.Item>
							</Col>
						</Row>
					}
				</Form>
			</div>
		)
	}
}






