import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import url from 'url';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {
    Upload,
    DatePicker,
    Row,
    Col,
    Form,
    Card,
    Switch,
    List,
    Input,
    InputNumber,
    Checkbox,
    message,
    Radio,
    Button,
    Icon,
    Spin,
    Modal
} from 'antd';
import ReactEcharts from 'echarts-for-react';

import fine from '@/assets/fine.png';
import fog from '@/assets/fog.png';
import rain from '@/assets/rain.png';
import snow from '@/assets/snow.png';


@inject('DeliveryStore')
@withRouter
@observer
export default class Signboard extends React.Component {
    constructor(props) {
        super(props)
        this.store = this.props.DeliveryStore;
        this.state = {
            weatherData: {},
            daily: {},
            photos: [],
            detailData: {}
        }
    }


    componentDidMount() {
        const {projectDetail} = this.store;
        const projectId = mobx.toJS(projectDetail).id;
        this.store.getWeatherByProjectId({"projectId": projectId})
            .then((res) => {
                this.setState({
                    weatherData: res
                });
            });
        this.store.getSignboardInfo({"projectId": projectId}).then((res) => {
            let str = JSON.parse(res);
            this.setState({
                daily: str.daily,
                photos: str.photos,
                detailData: str.project
            });
        });
    }

    componentWillMount() {

    }

    transName = (type) => {
        switch (type) {
            case type.includes("雨"):
                return rain;
                break;
            case type.includes("雪"):
                return snow;
                break;
            case type.includes("雾"):
                return fog;
                break;
            case type.includes("晴"):
                return fine;
                break;
            default:
                return fine;
        }
    }

    render() {
        const {weatherData, daily, photos, detailData} = this.state;
        const progessData = {1: "正常", 2: "提前", 3: "延后"};
        const option4 = {
            title: {
                text: detailData.projectImplementation==null||detailData.projectImplementation.currentRate==""?"0%":detailData.projectImplementation.currentRate+"%",
                textStyle: {
                    color: '#2096FF',
                    fontSize: 30,
                    fontWeight:'400',
                },
                subtext: '进度正常',
                subtextStyle: {
                    color: '#FFF',
                    fontSize: 12,
                    fontWeight:'400',
                },
                itemGap: -4,
                left: 'center',
                top: 'center'
            },
            angleAxis: {
                max: 100,
                clockwise: false,
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                splitLine: {
                    show: false
                }
            },
            radiusAxis: {
                type: 'category',
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                splitLine: {
                    show: false
                }
            },
            polar: {
                center: ['50%', '50%'],
                radius: '100%'
            },
            series: [{
                type: 'bar',
                data: [{
                    name: detailData.projectImplementation==null||detailData.projectImplementation.currentRate==""?0:detailData.projectImplementation.currentRate + '\n' + '{a|' +
                    '进度'+ detailData.projectImplementation==null?"正常":progessData[detailData.projectImplementation.progress],
                    value: detailData.projectImplementation==null||detailData.projectImplementation.currentRate==""?0.01:detailData.projectImplementation.currentRate,
                    itemStyle: {
                        normal: {
                            color: '#2096FF',
                        }
                    },
                }],
                coordinateSystem: 'polar',
                roundCap: true,
                barWidth: 10,
                barGap: '-100%',
                z: 2,
            },{
                type: 'bar',
                data: [{
                    value: 100,
                    itemStyle: {
                        color: 'rgba(30,69,101, 0.8)',
                        shadowColor: 'rgba(0, 0, 0, 0.2)',
                        shadowBlur: 5,
                        shadowOffsetY: 2
                    }
                }],
                coordinateSystem: 'polar',
                roundCap: true,
                barWidth: 5,
                barGap: '-70%',
                z: 1,
            }]
        };
        const colorList = ['#2096FF', '#3CFFC4', '#0057DC', '#4B6BFD'];
        const option5 = {
            tooltip: {
                trigger: 'item'
            },
            color: colorList,
            series: [{
                type: 'pie',
                center: ['50%', '50%'],
                radius: ['50%', '65%'],
                minAngle: 10,
                avoidLabelOverlap: true,
                roseType: 'radius',
                hoverOffset: 15,
                itemStyle: {
                    color: (params) => {
                        return colorList[params.dataIndex];
                    },
                },
                label: {
                    show: true,
                    position: 'outer',
                    width: 0,
                    height: 0,
                    lineHeight: 0,
                    backgroundColor: 'auto',
                    padding: [2, -2, 2, -2],
                    borderRadius: 2,
                    distanceToLabelLine: 0,
                    formatter: '{top|{b}}\n{bottom|{c}}',
                    align: 'center',
                    rich: {
                        top: {
                            verticalAlign: 'bottom',
                            padding: [10, 10, 0, 10],
                            align: 'center',
                        },
                        bottom: {
                            padding: [0, 10, 10, 10],
                            verticalAlign: 'top',
                            align: 'center',
                        },
                    },
                },
                data: [
                    {
                        name: '待安装',
                        value: daily==null?0:(daily.onWayCount + daily.arrivalCount - daily.installCount)
                    },
                    {
                        name: '在途',
                        value: daily.onWayCount==null?0:daily.onWayCount
                    },
                    {
                        name: '已安装',
                        value: daily.installCount==null?0:daily.installCount,
                    },
                    {
                        name: '进场',
                        value: daily.arrivalCount==null?0:daily.arrivalCount
                    }],
            }],
        };

        return (<div
                className="w"
                 style={{
                     height: "calc(100vh - 90px)",
                     margin: 0,
                     padding: '0',
                     marginTop: "-13px",
                     background: '#15589A'
                 }}>
                <Row style={{height: '100%', padding: "0 10px"}}>
                    <Col span={5} className="h">
                        <Row className="h">
                            <Col span={24} style={{height: '33%', padding: '10px 5px 5px'}}>
                                <div className="bigScreen-daily pr">
										<div className="pr w">
											<span className="title ellsis w60" title={detailData.name}>{detailData.name}</span>
											<span className="title-city">{detailData&&detailData.region&&JSON.parse(detailData.region)[1]}</span></div>
										<div style={{
											display: "flex",
											justifyContent: "center",
											alignItems:"center",
											marginTop: "15px"
										}}>
											<img src={weatherData.weatherName&&this.transName(weatherData.weatherName)} className="title-img"/>
											<div className="title-weather">
												<div className="tl mt10">{weatherData.weatherName} <span style={{marginLeft: "15px"}}> {weatherData.minTemp} - {weatherData.maxTemp} ℃</span></div>
												<div className="tl mt10">{weatherData.windDirection==null?"风力 ":weatherData.windDirection} <span style={{marginLeft: "15px"}}>{weatherData.windPower}</span></div>
											</div>
										</div>
										<div className="title2">最近更新时间：{weatherData.projectUpdateTime==0||weatherData.projectUpdateTime==null||weatherData.projectUpdateTime==""?"无":moment(weatherData.projectUpdateTime).format("YYYY.MM.DD HH:mm:ss")}</div>
									</div>
                            </Col>
                            <Col span={24} style={{height: '66%', padding: '5px'}}>
                                {photos.length>0&&<div className="bigScreen-daily">
                                    <div className="title">{photos==null?"":photos[0].place}</div>
                                    <img
                                        style={{
                                            width: '100%',
                                            maxWidth: '100%',
                                            height: 'calc(100% - 36px)',
                                        }}
                                        src={photos==null?"":photos[0].imageDocs[0].fileUrl}
                                        alt=""/>
                                </div>}
                            </Col>
                        </Row>
                    </Col>
                    <Col span={15} className="h">
                        <Row style={{height: '50%'}}>
                            <Col span={10} style={{padding: '10px 5px 5px 5px', height: '100%'}}>
                                <div className="bigScreen-daily tl h">
										<p className="title" style={{ display: "block" }}>
											<span>进场时间：</span>
											<span style={{fontWeight: "400"}}>{detailData.projectImplementation==null||detailData.projectImplementation.beginTime==0||detailData.projectImplementation.beginTime==null||detailData.projectImplementation.beginTime==""?"无":moment(detailData.projectImplementation.beginTime).format("YYYY年MM月DD日")}</span>
										</p>
										<p className="title" style={{ display: "block", marginTop: '-10px' }}>
											<span>完工时间：</span>
											<span style={{fontWeight: "400"}}>{detailData.projectImplementation==null||detailData.projectImplementation.endTime==0||detailData.projectImplementation.endTime==null||detailData.projectImplementation.endTime==""?"无":moment(detailData.projectImplementation.endTime).format("YYYY年MM月DD日")}</span>
										</p>
										<ReactEcharts
											height={"calc(100% - 150px)"}
											option={option4}
											notMerge={true}
											lazyUpdate={true}
										/>
									</div>
                            </Col>
                            <Col span={14} style={{padding: '10px 5px 5px 5px', height: '100%'}}>
                                <div className="bigScreen-daily tc h">
                                        <p className="title" style={{display: "block"}}>箱房总量：
                                            <span
                                                style={{fontWeight: "400"}}> {detailData.boxCount?detailData.boxCount:0}
                                                间
                    </span>
                                        </p>
                                        <p className="title" style={{display: "block", marginTop: '-10px'}}>到场数量：
                                            <span
                                                style={{fontWeight: "400"}}>{ daily.arrivalCount==null?0:daily.arrivalCount}
                                                间
                    </span>
                                        </p>
                                        <ReactEcharts
                                            height={"calc(100% - 150px)"}
                                            option={option5}
                                            notMerge={true}
                                            lazyUpdate={true}
                                        />
                                    </div>
                            </Col>
                        </Row>
                        <Row style={{height: '50%'}}>
                            <Col span={24} style={{height: "100%", padding: '5px 5px 10px'}}>
                                {photos.length>0&&<div className="bigScreen-daily">
                                    <div
                                        className="title">{photos == null ? "" : photos[1].place}</div>
                                    <img
                                        style={{
                                            width: '100%',
                                            maxWidth: '100%',
                                            height: 'calc(100% - 36px)',
                                        }}
                                        src={photos == null ? "" : photos[1].imageDocs[0].fileUrl}
                                        alt=""/>
                                </div>}
                            </Col>
                        </Row>
                    </Col>
                    <Col span={4} className="h">
                        <Row className="h">
                            <Col span={24} style={{height: '66%', padding: '10px 5px 5px 5px'}}>
                                {photos.length>0&&<div className="bigScreen-daily">
                                    <div
                                        className="title"> {photos == null ? "" : photos[2].place}</div>
                                    <img
                                        style={{
                                            width: '100%',
                                            maxWidth: '100%',
                                            height: 'calc(100% - 36px)',
                                        }}
                                        src={photos == null ? "" : photos[2].imageDocs[0].fileUrl}
                                        alt=""/>
                                </div>}
                            </Col>
                            <Col span={24} style={{height: '33%', padding: '5px'}}>
                                {photos.length>0&&<div className="bigScreen-daily">
                                    <div
                                    className="title">{photos == null ? "" : photos[3].place}</div>
                                    <img
                                    style={{
                                    width: '100%',
                                    maxWidth: '100%',
                                    height: 'calc(100% - 36px)',
                                }}
                                    src={photos == null ? "" : photos[3].imageDocs[0].fileUrl}
                                    alt=""/>
                                    </div>}
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </div>
        )
    }
}






