/*
* 进度图分段显示组件
* dangw@bocspace.cn
* */
import React, { Component } from "react";
import moment from "moment";

import Timeline, { TimelineHeaders, SidebarHeader, DateHeader } from "react-calendar-timeline";
import 'react-calendar-timeline/lib/Timeline.css';

export default class TimelineTable extends Component {
	constructor(props) {
		super(props);
		this.state= {
			groups: [],
			items: [],
			dataSource: [],
			defaultTimeStart:  moment().add(-12, 'day'),
			defaultTimeEnd:  moment().add(12, 'day'),
			visibleTimeStart: moment().add(-12, 'day'),
			visibleTimeEnd: moment().add(12, 'day'),
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (JSON.parse(JSON.stringify(this.state.dataSource)) != JSON.parse(JSON.stringify(nextProps.dataSource))) {
			this.setState({
				dataSource: nextProps.dataSource
			}, ()=> {
				this.init();
			});
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource
		}, ()=> {
			this.init();
		});
	}

	init = () => {
		const {dataSource} = this.state;
		let newData = JSON.parse(JSON.stringify(dataSource));
		let items = [];
		let groups = [];
		let minTimes = [];  // 计算最小值
		let maxTimes = [];  // 计算最大值
		newData.forEach((item)=>{
			let obj = Object.assign({}, {
				id: item.id,
				group: item.parentId==null?0:item.parentId,
				title: "",//item.name,
				start_time: item.planBeginTime==0||item.planBeginTime==null?moment():item.planBeginTime,
				end_time: item.planEndTime==0||item.planEndTime==null?moment():item.planEndTime,
				bgColor: item.currentProgress == 100||item.needWorkTime==0?"#F5F5F5":"#2196F3"
			});
			let obj2 = Object.assign({}, {
				id: item.parentId,
				title: item.name,
				height: 39
			});
			items.push(obj);
			groups.push(obj2);

			if (item.planBeginTime!=0&&item.planBeginTime!=null) {
				minTimes.push(item.planBeginTime);
			}
			if (item.planEndTime!=0&&item.planEndTime!=null) {
				maxTimes.push(item.planEndTime);
			}

		});

		//groups 去重

		// 封装去重方法
		function removalRepeat (arr) {
			let map = new Map()
			for (let i of arr) {
				map.set(i.id, i)
			}
			arr = [...map.values()]
			//console.log(arr)
			return arr;
		}
		let groupData = removalRepeat(groups);
		this.setState({
			items,
		 	groups: groupData,
			defaultTimeStart: Math.min(...minTimes),
			defaultTimeEnd: Math.max(...maxTimes),
			visibleTimeStart: Math.min(...minTimes),
		});
	}

	itemRenderer = ({
						item,
						timelineContext,
						itemContext,
						getItemProps,
						getResizeProps
					}) => {
		const { left: leftResizeProps, right: rightResizeProps } = getResizeProps();
		//const borderColor = itemContext.resizing ? "red" : item.bgColor;
		const backgroundColor = itemContext.selected
			? itemContext.dragging
				? "red"
				: item.selectedBgColor
			: item.bgColor;
		return (
			<div
				{...getItemProps({
					style: {
						backgroundColor,
						color: item.color,
						//borderColor,
					}
				})}
			>
				{itemContext.useResizeHandle ? <div {...leftResizeProps} /> : null}
				<div
					style={{
						height: itemContext.dimensions.height,
						overflow: "hidden",
						paddingLeft: 3,
						textOverflow: "ellipsis",
						whiteSpace: "nowrap"
					}}
				>
					{itemContext.title}
				</div>
				{itemContext.useResizeHandle ? <div {...rightResizeProps} /> : null}
			</div>
		);
	};

	handleTimeChangeFirst = (visibleTimeStart, visibleTimeEnd, updateScrollCanvas) => {
		//console.log("first", visibleTimeStart, visibleTimeEnd);
		this.setState({ visibleTimeStart, visibleTimeEnd });
	};

	render() {
		const {items, groups, defaultTimeStart, defaultTimeEnd, visibleTimeStart, visibleTimeEnd} = this.state;
		let defaultTimeStart2 =  moment().add(-12, 'day');
		let defaultTimeEnd2 =  moment().add(12, 'day')
		return (
			items.length>0&&<Timeline
				groups={groups}
				items={items}
				timeSteps={{
				    day: 1
				}}
				//defaultTimeStart={defaultTimeStart}
				//defaultTimeEnd={defaultTimeEnd2}
				itemRenderer={this.itemRenderer}
				itemTouchSendsClick={false}
				canMove={false}
				canResize={false}
				canSelect={false}
				visibleTimeStart={visibleTimeStart}
				visibleTimeEnd={defaultTimeEnd}
				onTimeChange={this.handleTimeChangeFirst}
			>
				<TimelineHeaders className="sticky">
					<SidebarHeader>
						{({ getRootProps }) => {
							return <div {...getRootProps()} className="constructionSchedule-title">任务名称</div>
						}}
					</SidebarHeader>
					<DateHeader unit="primaryHeader" />
					<DateHeader />
				</TimelineHeaders>
		</Timeline>
		);
	}
}



