/*
* 日报 邮件设置
*dangw@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import { Row, Col, Radio, Icon, message } from 'antd';
import PersonAdd2 from '../buildteam/PersonAdd2';
const plainOptions = [
	{ value: 1, label: '智建美住' },
	{ value: 2, label: '广联达' }
];

@inject('DeliveryStore')
@withRouter
@observer
export default class EmailSetting extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			active: {},
			otherCompanyPeople: [],
			customer: [],
			teamPeople: [],
			sendType: 1
		}
	}

	componentDidMount() {
      const {projectId} = this.props;
      if (projectId!=null) {
		  this.store.getDailyEmailSetting({projectId: projectId}).then((res)=>{
			  let people = res.otherCompanyPeople;
			  this.setState({
				  active: res,
				  otherCompanyPeople: res.otherCompanyPeople==null?[]:people.map(item=>{return {"userId": item.id, "userName": item.userName}}),
				  customer: res.customer,
				  teamPeople: res.teamPeople,
				  sendType: res.sendType
			  });
		  });
	  }
	}

	updatePerson = (users) => {
		this.setState({
			otherCompanyPeople: users
		}, ()=>{
			this.handleSave();
		});
	}

	onChange = (e) => {
		this.setState({
			sendType: e.target.value
		}, ()=>{
			this.handleSave();
		});
	}

	handleBack = () => {
		this.store.dailyEmail = false;
	}

	// 保存
	handleSave = () => {
		const {otherCompanyPeople, customer, teamPeople, sendType} = this.state;
		let active = JSON.parse(JSON.stringify(this.state.active));
		let people = JSON.parse(JSON.stringify(otherCompanyPeople));
		const params = Object.assign(active, {
			otherCompanyPeople: people.map(item=>{return {"id": item.userId, "userName": item.userName}}),
			customer: customer,
			teamPeople: teamPeople,
			sendType: sendType
		});
		this.store.saveDailyEmailSetting({des: params}).then((res)=>{
			if (res) {
				message.success("操作成功");
			}
		});
	}

	render() {
		const {otherCompanyPeople, customer, teamPeople, sendType} = this.state;

		return (
			<div style={{padding: "10px 30px"}}>
				<Row>
					<Col span={24} style={{paddingBottom: "15px", borderBottom: "1px dashed #e6e6e6"}}>
						<Icon type="left-circle" style={{color: "#6CB6FF"}} />  <span onClick={this.handleBack} className="emailSetting-back">返回日报列表</span>
					</Col>
					<Col span={24}>
						<div className="daily_tips">
							<p><span style={{fontSize: 12}}>小交温馨提醒：日报提交之后，会自动发送邮件至下列相关成员邮箱，可添加您需要通知的其他成员</span></p>
						</div>
					</Col>
				</Row>
				<p className="emailSetting-title">客户（关键干系人）</p>
				{
					customer.length>0?customer.map((item, index)=>{
						return <div className="emailSetting-p" key={"customer"+index}>
							{item.userName}
						</div>
					}):"无"
				}
				<p className="emailSetting-title">项目组成员</p>
				{
					teamPeople.length>0?teamPeople.map((item, index)=>{
						return <div className="emailSetting-p" key={"teamPeople"+index}>
							{item.userName}
						</div>
					}):"无"
				}
				<p className="emailSetting-title">公司内部其他人员</p>
				<PersonAdd2
					data={otherCompanyPeople}
					updatePerson={this.updatePerson}
					disabled={false}
				/>
				<p className="emailSetting-title">默认发送方设置</p>
				<Radio.Group options={plainOptions} onChange={this.onChange} value={sendType} />
			</div>
		)
	}
}






