import React from 'react';
import {inject, observer} from 'mobx-react';
import {withRouter} from "react-router-dom";
import {Modal, Row, Col, Checkbox, message, Button, Icon} from 'antd';

@inject('DeliveryStore')
@withRouter
@observer
export default class CommunicationWay extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			visible: false,
			defaultData: [],
			data: [],
			oldSelect: [],
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (JSON.stringify(nextProps.data) != JSON.stringify(this.props.data)) {
			this.setState({
				oldSelect: nextProps.data||[]
			}, () => {
				this.init();
			});
		}
	}

	componentDidMount() {
		this.store.getBuildTeamFourItemListByProjectId().then((res) => {
			this.setState({
				oldSelect: this.props.data||[],
				defaultData: res.map((item) => Object.assign(item, {selected: false}))
			},()=>{
				this.init();
			});
		});
	}

	showModal = () => {
		this.setState({
			visible: true,
		}, () => {
			this.store.getBuildTeamFourItemListByProjectId().then((res) => {
				this.setState({
					defaultData: res.map((item) => Object.assign(item, {selected: false}))
				}, () => {
					this.init();
				});
			});
		});
	};

	// 初始化
	init = () => {
		let defaultDatas = JSON.parse(JSON.stringify(this.state.defaultData));
		//  将已选定的用户做标记
		let arr = defaultDatas.map((item) => {
			let obj = {}
			if ([...this.state.oldSelect].some(v => v.id === item.id)) {
				obj = Object.assign(item, {selected: true})
			} else {
				obj = Object.assign(item, {selected: false})
			}
			return obj;
		});
		this.setState({
			data: Object.assign([], arr)
		});
	}

	handleOk = e => {
		this.setState({
			visible: false,
		}, ()=>{
			let defaultDatas = JSON.parse(JSON.stringify(this.state.data));
			let active = defaultDatas.filter(item=>item.selected==true);
            this.props.updateWay(active);
		});
	};

	handleCancel = e => {
		this.setState({
			visible: false,
		});
	};

	onChange = (type, e) => {
		let defaultDatas = JSON.parse(JSON.stringify(this.state.data));
		defaultDatas[type].selected = e.target.checked;
		this.setState({
			data: defaultDatas
		});
	}

	// 删除
	handleDel = (index) => {
      this.props.handleDel(index);
	}

	render() {
		const {visible, data, oldSelect} = this.state;

		return (
			<div className="">
				<Button type='link' className="dib" style={{verticalAlign: "top",position: 'relative',
					top: '5px'}} onClick={this.showModal}>选择</Button>
				<div className="dib">
					{oldSelect!=undefined&&oldSelect!=null&&oldSelect.map((item, index)=>{
						return <p key={item.id}>
							{item.rule} {item.frequency==null||item.frequency==""?null:" - "+item.frequency} <Icon type="delete" onClick={this.handleDel.bind(this, index)} style={{color: "#57ACFF"}}/>
						</p>
					})}
				</div>
				<Modal
					title="选择沟通方式"
					visible={visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					centered
				>
					<Row>
						{data.map((item, index) => {
							return <Col span="8" key={item.id}>
								<Checkbox checked={item.selected}
								          onChange={this.onChange.bind(this, index)}>{item.rule}</Checkbox>
							</Col>
						})}
					</Row>
				</Modal>
			</div>
		)
	}
}






