/*
* Table组件
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	Row,
	Col,
	Table,
	Input,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	Select,
	message,
	InputNumber,
	notification,
	Popover,
	Switch,
	Button
} from 'antd';
import moment from 'moment';
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';
import MyUpload from "./MyUploadNew";
import {converSize} from '@/utils/utils';
import {download, downloadFile} from "@/utils/download";
import {requestPost} from "@/utils/api";
import Config from "@/config";
import journalSchemas from "@/utils/Schemas/journal";

const { TextArea } = Input;

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/*自定义编辑组件*/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			handleSave({...record, ...values});
		});
	};
	//日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			handleSave({...record, ...values});
		});
	}

	handleRemoveItem=(id)=>{
        const { handleDelete } = this.props;
		handleDelete(id)
	}

	onBlur = () => {
		//this.toggleEdit();
	}

	convertHttp2Https=(url)=>{
		let fileUrl = url
		if(fileUrl.indexOf("http://")!==-1){
			fileUrl = 'https://'+fileUrl.split('http://')[1];
		}
		fileUrl = decodeURIComponent(fileUrl.split('?')[0]);
		return fileUrl
	}

	// 附件
	handleAttment = (arr, record) => {
		const es = arr.map((item, index)=>{
			return Object.assign({}, {
				"mainId":record.id,
				"orders":index,
				"name":item.name,
				"fileSize":item.fileSize,
				"createTime":moment(item.createTime).valueOf(),
				"fileUrl":this.convertHttp2Https(item.fileUrl),
				"fileType":item.fileType,
				"category":0
			})
		})

		this.props.handleAttmentsAdd({es:es, mainId:record.id})
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,  handleDelete, ossData} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'name') {
			return editing&&record['canDel']===1 ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
					<div className="w clearfix">
							<Input
								style={{
									float: 'right',
									width: 'calc(100%)',
								}}
								defaultValue={record[dataIndex]}
								ref={node => (this.input = node)}
								//onChange={this.save}
								onPressEnter={this.save}
								onBlur={this.save}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div
					style={{
						float: 'right',
						width: 'calc(100%)',
						minHeight: '31px'
					}}
				>
					<span style={{
						fontWeight: "400",
						marginRight: "2px",
						color: "#f5222d",
						fontSize: "14px",
						fontFamily: "SimSun, sans-serif",
						display: record['isMust']==1?"inline":"none"
					}}>*</span>{children}
				</div>
			);
		}else if (dataIndex == 'caozuo'){
			return  (
				<div style={{display:'flex', alignItems:'center'}}>
					{record['key']>=2?(record["canDel"]==1?<Popconfirm title="确定要删除吗?" onConfirm={()=>{this.handleRemoveItem(`${record.id}`)}}>
								<img  style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" />
							</Popconfirm>:null)
						:null}
					<MyUpload
								saveAttachment={(arr)=>{this.handleAttment(arr, record)}}
								disabled={false}
								multiple={true}
								listType="text"
								ossData={mobx.toJS(ossData)}
								fileList={[]}
								journalType="journal"
							/>
				</div>
			);
		}else if (dataIndex == 'templates'){
			return  (
				<div style={{display:'flex', alignItems:'center'}}>
					<img style={{width:'33px',heigth:'33px', cursor:'pointer'}} src="./images/delivery/u2608.png" /><Button type="link">模板下载</Button>
				</div>
			);
		}else if (dataIndex == 'cases'){
			return  (
				<div style={{display:'flex', alignItems:'center'}}>
					<img style={{width:'33px',heigth:'33px', cursor:'pointer'}} src="./images/delivery/u2608.png" /><Button type="link">案例下载</Button>
				</div>
			);
		}else{
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* 编辑table
* */
@observer
class EditableTableUpload extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 任务数据源
			count: 0,        // table的key
			primaryData: {
				"id": "",
				"name": "",
				"nameList": "",
				"ownerList": "",
				"fileSizeList": "",
				"createTimeList":"",
				"templates":[],
				"cases":[]
			}, // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			modalVisible:false,
			renameModalVisible:false,
			modalTitle:'',
			fileUrl:"",
			fileName:"",
			eMainId:null,
			renameFile:null,
			currentItem:null,
			isImageOrDoc:'doc'
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {key: index})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"id": "",
					"name": "",
					"nameList": "",
					"ownerList": "",
					"fileSizeList": "",
					"createTimeList":"",
					"templates":[],
					"cases":[]
				}) // 新增任务数据
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource ===undefined || this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource ===undefined ||  this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"id": "",
				"name": "",
				"nameList": "",
				"ownerList": "",
				"fileSizeList": "",
				"createTimeList":"",
				"templates":[],
				"cases":[]
			}), // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks: [],
		});
	}

	componentWillUnMount() {

	}


	// 删除
	handleDelete = (id) => {
		const {taskDetail} = this.store;
		this.store.delEnclosureMain({"id":id}).then((res)=>{
			if(res){
				message.success("删除成功");
                this.props.saveData();
			}else{
				message.error("删除失败")
			}
		})
	};

	// 新增
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		const newData = Object.assign(primaryData, {key: count, id:"!"+uuidv4()});
		// const newData = Object.assign(primaryData, {key: count});
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			// this.props.saveData(this.state.dataSource);
		});
		this.props.saveItem(newData)
	};

	// 保存
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		console.log('newdata==>',newData)
		this.setState({dataSource: newData}, () => {
			// 更新保存数据
			// this.props.saveData(this.state.dataSource);
		});
		this.props.saveItem(mobx.toJS(newData)[index])
	};

	//附件新增
	handleAttmentsAdd = (params)=>{

        this.store.saveEnclosureItemList(params).then((res)=>{
               this.props.saveData()
		})
	}

	formatTC = ()=>{

	}

	showText=(text, numSub)=>{
		if(text == null){
			return "";
		}
		if(text.length > numSub){
			return text.substring(0, numSub - 1) + "...";
		}
		return text;
	}

	trans = (file) =>{
		let src;
		if (/\.(doc|docx)$/.test(file)) {
			src = './images/delivery/share_word.png'
		} else if (/\.(xls|xlsx)$/.test(file)) {
			src = './images/delivery/share_excel.png'
		} else if (/\.(pdf)$/.test(file)) {
			src = './images/delivery/share_pdf.png'
		} else if (/\.(zip|rar)$/.test(file)) {
			src = './images/delivery/share_zip.png'
		} else if (/\.(ppt|pptx)$/.test(file)) {
			src= './images/delivery/share_ppt.png'
		} else if(/\.(mp4|rmvb|avi|ts)$/.test(file)){
			src= './images/delivery/share_vodie.png'
		} else if(/\.(exe)$/.test(file)){
			src= './images/delivery/exe_default.jpg'
		} else if(/\.(bmp|jpg|png|gif|jpeg|webp)$/.test(file)){
			src= './images/delivery/share_png.png'
		} else {
			src = './images/delivery/share_other.png'
		}
		return src;
	}


	// 手动下载
	downloadUrl = (item) => {
		const {fileUrl, name} = item;
		const isImage = (/\.(doc|docx)$/.test(name) || /\.(xls|xlsx)$/.test(name) || /\.(pdf)$/.test(name) || /\.(zip|rar)$/.test(name)) || (/\.(ppt|pptx)$/.test(name));
		if (isImage) {
			downloadFile(fileUrl, name);  // 直接下载pdf
		} else {
			this.downloadImage(fileUrl, name);
		}
	}

	// 直接下载
	downloadImage = (filePath, name) => {
		//获取最后一个.的位置
		var index = filePath.lastIndexOf(".");
		//获取后缀
		var ext = filePath.substr(index + 1);

		// 直接下载图片
		var x = new XMLHttpRequest();
		x.open("GET", filePath, true);
		x.responseType = 'blob';
		x.onload = function (e) {
			download(x.response, name, "image/" + ext);
		}
		x.send();
	}

	getDocumentUrl = (url) => {
		return requestPost('getDocumentUrl', Config.journal.getDocumentUrl, journalSchemas.journal.getDocumentUrl, {url: url})
	}

	showModal = async (item) => {
		const isImage = (/\.(doc|docx)$/.test(item.fileUrl) || /\.(xls|xlsx)$/.test(item.fileUrl) || /\.(pdf)$/.test(item.fileUrl) || /\.(zip|rar)$/.test(item.fileUrl)) || (/\.(ppt|pptx)$/.test(item.fileUrl));
		if(isImage){ // when true, it's document
			this.getDocumentUrl(decodeURIComponent(item.fileUrl)).then((res)=>{
				this.setState({
					modalVisible: true,
					modalTitle: item.name,
					fileUrl:res,
					currentItem:item,
					isImageOrDoc:'doc'
				});
			})
		}else{ // when false, it's image
			this.setState({isImageOrDoc: 'image', fileUrl:item.fileUrl, modalVisible: true, modalTitle: item.name, currentItem:item})
		}

	};
	handleCancel = e => {
		this.setState({
			modalVisible: false,
			fileUrl:''
		});
	};

	delItem=(id)=>{
		this.store.delEnclosureItem({id:id}).then((res)=>{
			if(res){
				message.success("删除成功");
				this.props.saveData();
			}else{
				message.error("删除失败")
			}
		})
	}

	handleRenameOk=()=>{
		const { fileName, renameFile, eMainId } = this.state;
		let newFile = Object.assign(renameFile, {"name":fileName})
		this.store.saveEnclosureItem({e:newFile, mainId:eMainId}).then((res)=>{
			if(res){
				message.success("重命名成功");
				this.props.saveData();
				this.setState({renameModalVisible:false})
			}else{
				message.error("重命名失败")
			}

		})
	}

	handleRenameCancel=()=>{
		this.setState({
			renameModalVisible: false,
			fileName:'',
			eMainId: null,
		});
	}

	handleRenameChange=(e)=>{
		this.setState({fileName:e.target.value})
	}

	showRenameModal=(item,mainId)=>{
		this.setState({renameModalVisible: true, renameFile: item, fileName: item.name, eMainId:mainId })
	}

	render() {
		const {dataSource, expandedKeys, modalVisible, modalTitle, currentItem, isImageOrDoc ,fileUrl, renameModalVisible, fileName, renameFile} = this.state;
		const {disabled} = this.props;
		const modalHeader=(
			(
				<Row >
					<Col span={12} className="leftLabelHeader">
						<p ><span >{modalTitle}</span></p>
					</Col>
					<Col span={12} className="rightLabelHeader">
						<p >
							<Button type='default' icon='download' onClick={(e)=>{this.downloadUrl(currentItem,e)}}  >下载</Button>
						</p>
					</Col>
				</Row>
			)
		)
		const isChaijie = expandedKeys.length > 0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		let columnEle = [
			{
				title: '序号', dataIndex: 'keyIndex', align: 'center',width:"5%"
			},
			{
				title: '附件项目', dataIndex: 'name', align: 'left', editable: !disabled && !isChaijie,
			},
			{
				title: '附件名称', dataIndex: 'nameList', align: 'left',
				render: (text, record) => {
					return	record['enclosureItems']!==undefined&&record['enclosureItems'].length>0?record['enclosureItems'].map((item,index)=>{
							return(
								<div
									id={`${record.id}`}
									style={{width: '100%', height: 'auto', lineHeight: '30px', textAlign:'left'}}
									className="pr">
									{
										<div style={{display:'flex',  justifyContent:'space-between', alignItems:'center'}}>
											<div>
												<img  style={{verticalAlign: 'middle', cursor:'pointer', padding:0}} width='25' height="25 "  src={this.trans(item.name)} alt=""/>
												<Button  style={{padding:'5px'}} onClick={()=>{this.showModal(item)}} title={item.name} type="link" >{disabled?this.showText(item.name,55):this.showText(item.name,20)}</Button>
											</div>
											<div>
												{disabled?<Button onClick={()=>{this.downloadUrl(item)}} type="link">下载</Button>:<img onClick={()=>{this.downloadUrl(item)}} style={{verticalAlign: 'middle', marginRight: '8px', cursor:'pointer'}} width='15' height="15" src="./images/delivery/download.png"  title="下载" alt="下载"/>}
												{disabled?"":<span><Popconfirm title="确定要删除吗?" onConfirm={()=>{this.delItem(item.id)}}><img style={{verticalAlign: 'middle', marginRight: '8px', cursor:'pointer'}} width='15' height="15" src="./images/delivery/delete.png" title="删除"   alt="删除"/></Popconfirm>
													<img onClick={()=>{this.showRenameModal(item,record['id'])}} style={{verticalAlign: 'middle', marginRight: '8px', cursor:'pointer'}} width='15' height="15" src="./images/delivery/rename.png"   title="重命名" alt="重命名"/></span>}
											</div>
										</div>
									}
								</div>
							)
						}):<span></span>
				}
			},
			{
				title: '大小',
				dataIndex: 'fileSizeList',
				width:'5%',
				align: "center",
				render: (text, record) => {
					return	record['enclosureItems']!==undefined&&record['enclosureItems'].length>0?record['enclosureItems'].map((item,index)=>{
							return(
								<div
									id={`${record.id}`}
									style={{width: '100%', height: 'auto', lineHeight: '30px', textAlign:'center'}}
									className="pr">
									{converSize(item.fileSize)}
								</div>
							)
						}):<span></span>
				}
			},
			{
				title: '上传人',
				dataIndex: 'ownerNameList',
				width:'5%',
				align: "center",
				render: (text, record) => {
					return	record['enclosureItems']!==undefined&&record['enclosureItems'].length>0?record['enclosureItems'].map((item,index)=>{
							return(
								<div
									id={`${record.id}`}
									style={{width: '100%', height: 'auto', lineHeight: '30px', textAlign:'center'}}
									className="pr">
									{item.ownerName}
								</div>
							)
						}):<span></span>
				}
			},
			{
				title: '上传时间',
				dataIndex: 'createTimeList',
				width:'150px',
				align: "center",
				render: (text, record) => {
					return	record['enclosureItems']!==undefined&&record['enclosureItems'].length>0? record['enclosureItems'].map((item,index)=>{
							return(
								<div
									id={`${record.id}`}
									style={{width: '100%', height: 'auto', lineHeight: '30px', textAlign:'center'}}
									className="pr">
									{moment(item.createTime).format("YYYY-MM-DD")}
								</div>
							)
						}):<span></span>
				}
			}

		];
		if(!disabled){
			columnEle = [{
				title: '操作', dataIndex: 'caozuo', align: 'center', width:'5%', editable: !disabled,
				render: (text, record) => { //rendcell 实现操作业务
					return (<div>
						<img style={{width:'19px',heigth:'19px', cursor:'pointer'}} src="./images/delivery/u1226.png" /><Button type="link">上传</Button>
					</div>);
				}
			}].concat(columnEle).concat([			{
				title: '模板',
				dataIndex: 'templates',
				align: "center",
				width:'10%',
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						className="pr">
						{record['canDel']===0?record["templates"].length>0?record["templates"].map((item, index)=>{
							return(<div style={{display:"flex"}}><img  onClick={()=>{this.showModal(item)}} title={item.name} style={{"width":"33px", "height":"33px", padding:'0'}} src="./images/delivery/u2608.png" /> <Button style={{padding:'0'}} onClick={()=>{this.downloadUrl(item)}} title={item.name} type="link">{this.showText(item.name,10)}</Button></div>)
						}):<div style={{display:"flex"}}><img  style={{"width":"33px", "height":"33px"}} src="./images/delivery/u2607.png" /> <Button style={{color:"rgb(127,127,127)",padding:'0'}} type="link">暂无模板</Button></div>:<span></span>}

					</div>);
				}
			},
				{
					title: '案例',
					dataIndex: 'cases',
					align: "center",
					width:'10%',
					render: (text, record) => {
						return (<div
							id={`${record.id}`}
							className="pr">
							{record['canDel']===0?record["cases"].length>0?record["cases"].map((item, index)=>{
								return(<div style={{display:"flex"}}><img  onClick={()=>{this.showModal(item)}} style={{"width":"33px", "height":"33px", padding:'0'}} title={item.name} src="./images/delivery/u2608.png" /> <Button style={{padding:'0'}} onClick={()=>{this.downloadUrl(item)}} title={item.name} type="link">{this.showText(item.name,8)}</Button></div>)
							}):<div style={{display:"flex"}}><img style={{"width":"33px", "height":"33px", padding:'0'}} src="./images/delivery/u2607.png" /> <Button style={{color:"rgb(127,127,127)",padding:'0'}} type="link">暂无案例</Button></div>:<span></span>}
						</div>);
					}
				}])
		}
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
					ossData:this.store.ossData,
					handleAttmentsAdd:this.handleAttmentsAdd,
				}),
			};
		});

		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					rowKey={row => row.id}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>}
				<Modal
					title={modalHeader}
					visible={modalVisible}
					onCancel={this.handleCancel}
					footer={null}
					style={{ top: 70 ,height:'calc(100vh - 150px)', minWidth:'1000px'}}
					bodyStyle={{padding:0}}
					width={'calc(100% - 400px)'}
				>
					<div style={isImageOrDoc==='doc'?{height:'calc(100vh - 200px)'}:{height:'calc(100vh - 200px)',textAlign:'center',backgroundColor:'#000',paddingTop:'40px'}}>
						{
							isImageOrDoc==='doc'?<iframe
									src={fileUrl}
									width="100%" height="100%" frameBorder="1" style={{minHeight:'600px'}}></iframe> :
								<img src={fileUrl}  height='600px'  style={{minHeight:'600px',}} alt='' />
						}
					</div>
				</Modal>

				<Modal
					title={"文件重命名"}
					visible={renameModalVisible}
					onOk={this.handleRenameOk}
					onCancel={this.handleRenameCancel}
					style={{ top: 70 ,height:'300px', minWidth:'600px'}}
					bodyStyle={{padding:0}}
				>
					<div style={{display:'flex', justifyContent:'center', alignItems:'center', height:'100px'}}>
						<Input
							style={{
								width: '500px',

							}}
                            value={fileName}
							onChange={this.handleRenameChange}
						/>
					</div>
				</Modal>

			</div>
		);
	}
}

export default EditableTableUpload;




