import React from 'react';
import {inject, observer} from 'mobx-react';
import url from 'url';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Button} from 'antd';
import OneItem from "./OneItem";
import TwoItem from "./TwoItem";
import ThreeItem from "./ThreeItem";

@inject('DeliveryStore')
@withRouter
@observer
export default class HandOver extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
            currentItem:0,
			projectId:null,
			currentStep:1,
			taskId:null,
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		const projectDetail = mobx.toJS(nextProps.DeliveryStore.projectDetail)
		if(projectDetail.id!==this.state.projectId){
			this.setState({projectId:projectDetail.id})
			this.updateTask(this.getTaskIdByType(projectDetail, "handover"));
		}
	}


	componentDidMount() {
		this.getHandoverState();
		this.store.getSts();
	}

	componentWillMount(){

	}

	getTaskIdByType=(projectDetail, type)=>{
		let taskId = null;
		const { stages } = projectDetail
		if(stages.length>0){
			taskId = stages[0]['tasks'].filter((item)=>{return item.taskType===type})[0]['id']
		}
		return taskId
	}

	updateTask=(id)=>{
		this.store.getTaskById({'id':id}).then((res)=>{
			if(res){
				this.store.taskDetail = res
			}
		})
	}

	getDailyListPage=()=>{
		const { projectDetail } = this.store;
		this.store.dailyParmas.projectId = mobx.toJS(projectDetail).id;
		this.setState({projectId:mobx.toJS(projectDetail).id})
		this.store.getDailyListPage().then((res)=>{
			this.store.dailyList = Object.assign([], res.body);
			this.store.dailyParmas.pageIndex = res.pageIndex;
			this.store.dailyParmas.pageSize = res.pageSize;
			this.store.dailyParmas.totalCount = res.totalCount;
			if(res.body.length>0){
				this.handleSelectedDaily(res.body[0],0)
			}
		});
	}

	getHandoverState=()=>{
		const { taskDetail } = this.store;
		this.store.getHandoverState({"taskId":mobx.toJS(taskDetail).id}).then((res)=>{
			if(res){
				this.store.handoverState = res;
			}
		})
	}

	getProgressStateInfo=(state)=>{
		let info = "";
		if(state===0){
			info = "正常"
		}
		if(state===1){
			info = "提前"
		}
		if(state===2){
			info = "延迟"
		}
		return info;
	}

	handleSelectedDaily=(item,index)=>{
		this.setState({currentItem:index})
		this.store.getDailyById({"id":item.id}).then((res)=>{
			this.store.dailyDetail = Object.assign({},res);
		})
	}

	addNewLog=()=>{
		this.store.dailyPageState = 1;
	}

	getTodayContentInfo = (item) => {
		if(item.contents.length>=2){

			return `${item.contents[0]['position']}${item.contents[0]['productCategory']}完成${item.contents[0]['actualProgress']}%，${item.contents[1]['position']}${item.contents[1]['productCategory']}完成${item.contents[1]['actualProgress']}%`
		}
		if(item.contents.length>0){
			return `${item.contents[0]['position']}${item.contents[0]['productCategory']}完成${item.contents[0]['actualProgress']}%`
		}

	}

	switchSteps=(step)=>{
       this.setState({currentStep: step})
	}

	getBuildTeamStateInfo=(state)=>{
		if(state===0){
			return "待启动"
		}
		if(state===1){
			return "进行中"
		}
		if(state===2){
			return "已完成"
		}
	}

	getBuildTeamStateIcon=(state)=>{
		if(state===0){
			return "./images/delivery/u1055.png"
		}
		if(state===1){
			return "./images/delivery/u1053.png"
		}
		if(state===2){
			return "./images/delivery/u1232.png"
		}
	}

	overCurrentStageTask=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.overHandoverStep({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.handoverState = res;
				this.getProjectDetail();
			}
		})
	}

	reStartHandover=()=>{
		const { currentStep } = this.state;
		const {  taskDetail } = this.store;
		this.store.reStartHandover({"taskId":taskDetail.id, "index":currentStep}).then((res)=>{
			if(res){
				this.store.handoverState = res;
				this.getProjectDetail()
			}
		})
	}

	getProjectDetail=()=>{
		let search = this.props.location.search;
		const projectId = url.parse(search, true).query.projectId;
		this.store.getProjectDetail({'id':projectId}).then((res)=>{
			this.store.projectDetail = res
		})
	}


	render() {
		const { currentStep } = this.state;
		const { handoverState } = this.store;

		return (
			<div   className="dailyModal">
				<div className="mt20 f14">
					<div style={{display:"flex",justifyContent:'center', alignItems:'center'}}>
                        <div style={{display:"flex", flexDirection:"column",alignItems:'center'}}>
						   <div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(handoverState).state1)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(handoverState).state1)}</div>
						   <div style={{display:"flex", alignItems:'center'}}>
							   <div className="build-team-line-white"> </div>
							   <Button onClick={()=>{this.switchSteps(1)}} type={currentStep===1?'primary':'link'} className={currentStep===1?'active-button':'no-active-button'}>启动准备</Button>
							   <div className={currentStep===1?'build-team-line-green':'build-team-line-gray'}> </div>
						   </div>
					    </div>
						<div style={{display:"flex", flexDirection:"column", alignItems:'center'}}>
							<div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(handoverState).state2)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(handoverState).state2)}</div>
							<div style={{display:"flex", alignItems:'center'}}>
								<div className={currentStep===2?'build-team-line-green':'build-team-line-gray'}> </div>
								<Button onClick={()=>{this.switchSteps(2)}} type={currentStep===2?'primary':'link'} className={currentStep===2?'active-button':'no-active-button'}>商务交接</Button>
								<div className={currentStep===2?'build-team-line-green':'build-team-line-gray'}> </div>
							</div>
						</div>
						<div style={{display:"flex", flexDirection:"column", alignItems:'center'}}>
							<div className="mb5"><img src={this.getBuildTeamStateIcon(mobx.toJS(handoverState).state3)} style={{width:'15px',height:'15px', marginRight: "5px"}} />{this.getBuildTeamStateInfo(mobx.toJS(handoverState).state3)}</div>
							<div style={{display:"flex", alignItems:'center'}}>
								<div className={currentStep===3?'build-team-line-green':'build-team-line-gray'}> </div>
								<Button onClick={()=>{this.switchSteps(3)}} type={currentStep===3?'primary':'link'} className={currentStep===3?'active-button':'no-active-button'}>技术交接</Button>
								<div className="build-team-line-white"> </div>
							</div>
						</div>
					</div>
				</div>
				<div className="mt30">
					{
						currentStep === 1 ?
							<OneItem
								overCurrentStageTask={this.overCurrentStageTask}
								reStartHandover={this.reStartHandover}
								switchSteps={this.switchSteps}
								currentStep={currentStep}
								store={this.store}/> : currentStep === 2 ?
							<TwoItem
								overCurrentStageTask={this.overCurrentStageTask}
								reStartHandover={this.reStartHandover}
								switchSteps={this.switchSteps}
								currentStep={currentStep}
								store={this.store}/> : currentStep === 3 ?
							<ThreeItem
								switchSteps={this.switchSteps}
							    currentStep={currentStep}
							    overCurrentStageTask={this.overCurrentStageTask}
								reStartHandover={this.reStartHandover}
							    store={this.store}/> :null
					}

				</div>
			</div>
		)
	}
}






