import React from 'react';
import {Row, Col, Form, Button, Input, Select, Modal, Spin, InputNumber, message, Divider, Icon} from 'antd';
import {withRouter} from 'react-router-dom';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';
import {downloadFile, download} from "@/utils/download";
import SendMailModal from "@/components/affairs/SendMailModal";
const {Option} = Select;

@Form.create()
@withRouter
@inject('AffairsStore')
@inject('DeliveryStore')
@observer
class NamePlate extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.AffairsStore;
		this.deliveryStore = this.props.DeliveryStore;
	}

	state = {
		modal1Visible: false,
		modal2Visible: false,
		modal3Visible: false,
		modal4Visible: false,
		fileList: [],
		loading: false,
		isCallSuccess: false,
		downloading: false,
	}

	//显示发送邮件对话框
	showSendMailModal = ()=>{
		this.setState({modal3Visible:true})
	}

	//发送制作文件
	sendMakeFile = ()=>{

	}

	inter3sRun = ()=>{
		const projectDetail = mobx.toJS(this.deliveryStore.projectDetail);
		const ratingplate = {code: projectDetail.number, name: projectDetail.name};
		this.setState({isCallSuccess:false, downloading:true});
		this.store.saveNameplateFile({'n':ratingplate}).then((res)=>{
			if(res){
				this.getReportUrl({"id":res});
			}
		});
	}


	info = () => {
		Modal.info({
			title: '文件正在下载中，请耐心等待',
			onOk() {}
		});
	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id) => {
		let _this = this;
		_this.timer22 = setInterval(() => {
			_this.store.getDocUrl2(id).then((url) => {
				if (url !== null && url !== "") {
					const ratingplate = mobx.toJS(this.deliveryStore.projectDetail);
					this.setState({isCallSuccess:true, downloading:false})
					downloadFile(url,`${ratingplate.name}铭牌`);
                    // 提示，手动关闭
					this.info();
					clearInterval(_this.timer22);
				}
				else {

				}
			});
		}, 3000);
	}

	handleCancel = (modalVisible) => {
		this.setState(modalVisible);
	};

	handleAfterSend = ()=>{
		this.setState({modal3Visible:false});
		this.setState({modal4Visible:true});
	}

	render() {
		const {modal1Visible, downloading, modal3Visible, modal4Visible} = this.state;
		const projectDetail = mobx.toJS(this.deliveryStore.projectDetail);
		const {nameplateUrl} = projectDetail;

		return (
			<div style={{padding: "10px 30px"}} className='pr'>
				<Spin spinning={downloading} tip="正在下载,请稍等...">
					<Row>
						<Col span={24}>
							<div className="daily_tips">
								<p><span style={{fontSize: 12}}>小交温馨提醒：请在工程接近完工时，根据房屋数量，打印房屋铭牌，贴在如图所示位置，方便用户扫码报修哦~</span></p>
							</div>
						</Col>
					</Row>
					<Row className="mt30">
						<Col span={8}>
							<div className="nameplate-title">
								<p>【打印要求】</p>
								<p className="nameplate-content">1、尺寸：210*130*0.8mm；</p>
								<p className="nameplate-content">2、工艺：铝板腐蚀填漆，四色；</p>
								<p className="nameplate-content">3、其他：圆角、贴背胶。</p>
							</div>
							<img
								style={{marginTop: "40px"}}
								width="348"
								height="215"
								src={nameplateUrl==null||nameplateUrl==""?"https://bocspace.oss-cn-beijing.aliyuncs.com/ci/nameplate_img.png":nameplateUrl} alt=""/>
							<div
								style={{
									marginTop:'30px',
									textAlign: 'center'
								}}
							>
								<Button
									onClick={this.showSendMailModal}
									icon='mail'
									shape="round"
									type="primary">
									发送制作文件
								</Button>
								<Divider type="vertical" />
								<Button
									onClick={this.inter3sRun}
									shape="round" >
									保存至本地
								</Button>
							</div>
						</Col>
						<Col span={16} className="tc">
							<img
								width="100%"
								height="477"
								style={{minWidth: "730px", maxWidth: "730px"}}
								src="https://bocspace.oss-cn-beijing.aliyuncs.com/ci/nameplate.png" alt=""/>
						</Col>
					</Row>
				</Spin>
				<Modal
                    visible={modal1Visible}
                    title=""
                    onCancel={()=>{this.handleCancel({modal1Visible: false})}}
                    footer={null}
                    width={940}
                    bodyStyle={{textAlign:'center',height:'600px',verticalAlign:'middle'}}
                >
                    <div style={{marginTop:'100px'}}><img
                        width='116px'
                        height="116px"
                        src='./images/u192.png'/>
                    </div>
                    <div style={{marginTop:'35px',fontSize:'24px',fontWeight:'bold'}}><h1>名片申请成功</h1></div>
                    <p style={{marginTop:'35px'}}>名片预计2个工作日内可打印完毕，打印完成后将电话或邮件通知，请耐心等待。</p>
                </Modal>
                <Modal
                    visible={modal4Visible}
                    title=""
                    onCancel={()=>{this.handleCancel({modal4Visible: false})}}
                    footer={null}
                    width={300}
                    bodyStyle={{textAlign:'center',height:'300px',verticalAlign:'middle'}}
                >
                    <div style={{marginTop:'30px'}}><img
                        width='80px'
                        height="80px"
                        src='./images/u380.svg'/>
                    </div>
                    <div style={{marginTop:'10px',fontSize:'24px',fontWeight:'bold'}}><h1>发送成功</h1></div>
                    <div
                        style={{
                            marginTop:'15px',
                            right: 0,
                            bottom: 0,
                            width: '100%',
                            padding: '10px 16px',
                            background: '#fff',
                            textAlign: 'center',
                            zIndex: 9999
                        }}
                    >
                        <Button
                            style={{marginRight: 8}}
                            shape="round"
                            onClick={()=>{this.handleCancel({modal4Visible: false})}}
                            type="primary">
                            关闭
                        </Button>

                    </div>
                </Modal>
				 <SendMailModal
				  type="ratingplate"
                  visible={modal3Visible}
                  sendBuildBusinessEmail={this.sendMakeFile}
                  closeModal={()=>{this.handleCancel({modal3Visible: false})}}
                  afterSend={this.handleAfterSend}
                  store={this.store}
                />
			</div>
		)
	}
}


export default NamePlate;



