/*
* Table组件 合同清单组件
* */
import React from 'react';
import {
    Table,
    Input,
    Popconfirm,
    Form,
    Icon,
    Button,
    InputNumber,
    message,
    Modal,
    Row,
    Col,
    Checkbox,
    Spin
} from 'antd';
import numeral from 'numeral';
import {observer} from "mobx-react";
import moment from 'moment';
import _ from "lodash";
const { Search } = Input;

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
    <EditableContext.Provider value={form}>
        <tr {...props} />
    </EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
    state = {
        editing: false,
    };

    toggleEdit = () => {
        const editing = !this.state.editing;
        this.setState({editing}, () => {
            if (editing) {
                this.input.focus();
            }
        });
    };

    save = e => {
        const {record, handleSave} = this.props;
        this.form.validateFields((error, values) => {
            if (error && error[e.currentTarget.id]) {
                return;
            }
            this.toggleEdit();
            const dataIndex = e.currentTarget.id;
            let obj = {};
            if (dataIndex == 'count') {
                let count = values["count"] == null || values["count"] == undefined ? 0 : values["count"];
                let prize = record["prize"] == null || record["prize"] == undefined ? 0 : record["prize"];
                obj.total = count * prize;
            }
            if (dataIndex == 'prize') {
                let count = record["count"] == null || record["count"] == undefined ? 0 : record["count"];
                let prize = values["prize"] == null || values["prize"] == undefined ? 0 : values["prize"];
                obj.total = count * prize;
            }
            handleSave({...record, ...values, ...obj});
        });
    };

    renderCell = form => {
        this.form = form;
        const {children, dataIndex, record, title} = this.props;
        const {editing} = this.state;

        if (dataIndex == 'count' || dataIndex == 'prize' || dataIndex == 'total') {  // 数字金额
            console.log(record,dataIndex, record[dataIndex], 'record[dataIndex]')
            return editing ? (
                <Form.Item style={{margin: 0}}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<InputNumber ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{minHeight: '31px'}}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        } else {
            return editing ? (
                <Form.Item style={{margin: 0}}>
                    {form.getFieldDecorator(dataIndex, {
                        rules: [
                            {
                                required: false,
                                message: `${title} is required.`,
                            },
                        ],
                        initialValue: record[dataIndex],
                    })(<Input ref={node => (this.input = node)} onPressEnter={this.save} onBlur={this.save}/>)}
                </Form.Item>
            ) : (
                <div
                    className="editable-cell-value-wrap"
                    style={{minHeight: '31px'}}
                    onClick={this.toggleEdit}
                >
                    {children}
                </div>
            );
        }
    };

    render() {
        const {
            editable,
            dataIndex,
            title,
            record,
            index,
            handleSave,
            children,
            ...restProps
        } = this.props;
        const isFlag = record!=undefined&&(record.bocdesignId!=null||record.bocdesignId!=undefined);
        return (
            <td {...restProps}>
                {editable &&!isFlag? (
                    <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
                ) : (
                    children
                )}
            </td>
        );
    }
}

@observer
class EditableTableInventory extends React.Component {
    constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            dataSource: [],
            count: 0,
            visible: false,
            projectList: [],
            initLoading: true,
            loading: false,
            pageIndex: 1,
            totalCount: 0,
            checkedKeys: [], // 选中的keys
            searchVal: ""
        };
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (JSON.stringify(nextProps.dataSource) != JSON.stringify(this.props.dataSource)) {
            this.setState({
                dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource,
                count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
            }, ()=>{
                // 更新数据 keys
                this.initKeys();
            });
        }
    }

    componentDidMount() {
        if (this.props.dataSource) {
            this.setState({
                dataSource: this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource,
                count: this.props.dataSource == null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
            }, ()=>{
                // 更新数据 keys
                this.initKeys();
            });
        }
    }

    initKeys = () => {
        let bocdesignIds = JSON.parse(JSON.stringify(this.state.dataSource));
        let array = [];
        bocdesignIds.forEach(item=>{
            if (item.bocdesignId!=undefined&&item.bocdesignId!=null) {
                array.push(item.bocdesignId);
            }
        });
        // 去重
        let array2=Array.from(new Set(array))
        this.setState({
            checkedKeys: array2
        });
    }

    handleDelete = key => {
        const dataSource = [...this.state.dataSource];
        let data = dataSource.filter(item => item.key !== key).map((v, vindex) => Object.assign(v, {key: vindex + 1}));
        this.setState({
            dataSource: data,
            count: data.length
        });
        this.props.updateState(data);
        message.success("删除成功");
    };

    handleAdd = () => {
        const {count, dataSource} = this.state;
        const newData = {
            key: count + 1,
            id: "",
            type: 1,
            taskId: "",
            supplierId: "",
            supplierTwoId: "",
            position: "",
            positionId: "",
            categoryType: "",
            categoryTypeId: "",
            productDetail: "",
            productDetailId: "",
            units: "",
            count: null,
            prize: null,
            total: null,
            remarks: "",
            specification: "",
            rate: "13%"
        };
        this.setState({
            dataSource: [...dataSource, newData],
            count: count + 1,
        });
        this.props.updateState([...dataSource, newData]);
    };

    handleSave = row => {
        const newData = [...this.state.dataSource];
        const index = newData.findIndex(item => row.key === item.key);
        const item = newData[index];
        newData.splice(index, 1, {
            ...item,
            ...row,
        });
        this.setState({dataSource: newData});
        this.props.updateState(newData);
    };

    handleOk = e => {
        let checkedKeys = JSON.parse(JSON.stringify(this.state.checkedKeys));
        if (checkedKeys.length==0) {
            message.warn("请先选中任意一项")
            return false;
        }
        const params = {
            projectId: this.store.projectId,
            ids: checkedKeys
        }
        this.store.syncBocdesignByIds(params).then((res)=>{
            if (res.code==1) {
                this.props.updateState(res.supplierContractList==null?[]:res.supplierContractList)
                this.setState({
                    visible: false,
                    checkedKeys: [],
                    searchVal: "",
                    pageIndex: 1,
                });
            }
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
            checkedKeys: [],
            searchVal: "",
            pageIndex: 1,
        });
    };

    showModal = () => {
        const params = {
            name: "",
            userId: "",
            pageIndex: 1,
            pageSize: 10
        }
        this.store.getProjectList(params)
            .then((res) => {
                if (res) {
                    this.setState({
                        initLoading: false,
                        visible: true,
                        projectList: res.body,
                        totalCount: res.totalCount,
                        pageIndex: 1
                    });
                }
            });
    };

    onChange = (e) => {
        const {value, checked} = e.target;
        if (checked==true) {// 选中
            let checkedKeys = JSON.parse(JSON.stringify(this.state.checkedKeys));
            checkedKeys.push(value);
            this.setState({
                checkedKeys
            });
        } else { // 取消
            let checkedKeys = JSON.parse(JSON.stringify(this.state.checkedKeys));
            this.setState({
                checkedKeys: checkedKeys.filter(item=>item!=value)
            });
        }
    }

    // 加载更多
    onLoadMore = () => {
        this.setState({
            loading: true,
            pageIndex: this.state.pageIndex + 1
        }, () => {
            const params = {
                name: this.state.searchVal,
                userId: "",
                pageIndex: this.state.pageIndex,
                pageSize: 10
            }
            this.store.getProjectList(params)
                .then((res) => {
                    if (res) {
                        this.setState({
                            loading: false,
                            projectList: this.state.projectList.concat(res.body)
                        });
                    }
                });
        });
    }

    onSearch = e => {
        const val = _.trim(e.target.value);
        this.setState({
           searchVal: val,
           pageIndex: 1,
        }, ()=> {
            const params = {
                name: val,
                userId: "",
                pageIndex: 1,
                pageSize: 10
            }
            this.store.getProjectList(params)
                .then((res) => {
                    if (res) {
                        this.setState({
                            projectList: Array.isArray(res.body)?res.body:[]
                        });
                    }
                });
        });
    }

    render() {
        const {dataSource, visible, projectList, initLoading, loading, totalCount, checkedKeys, searchVal} = this.state;
        const columnsOld = [
            {
                title: '操作',
                dataIndex: 'operation',
                align: "center",
                width: "70px",
                className: !this.props.disabled ? "" : "hidden",
                render: (text, record) =>
                    <Popconfirm title="确定要删除吗?" onConfirm={() => this.handleDelete(record.key)}>
                        <Icon
                            style={{
                                color: '#F17E7E',
                                fontSize: '18px'
                            }}
                            type="minus-circle"/>
                    </Popconfirm>
            },
            {
                title: '序号',
                dataIndex: 'key',
                width: '70px',
                align: "center",
            },
            {
                title: '部位',
                dataIndex: 'position',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '产品类别',
                dataIndex: 'categoryType',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '产品明细',
                dataIndex: 'productDetail',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '规格尺寸',
                dataIndex: 'specification',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '单位',
                dataIndex: 'units',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '数量',
                dataIndex: 'count',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            },
            {
                title: '含税单价',
                dataIndex: 'prize',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text == null || text == 0 ? 0 : numeral(text).format('0,0')
            },
            {
                title: '合计金额',
                dataIndex: 'total',
                align: "center",
                width: "10%",
                render: (text, record) =>
                    text == null || text == 0 ? 0 : numeral(text).format('0,0')
            },
            {
                title: '税率',
                dataIndex: 'rate',
                align: "center",
                width: '50px',
            },
            {
                title: '备注',
                dataIndex: 'remarks',
                editable: !this.props.disabled ? true : false,
                align: "center",
                width: "10%",
            }
        ];
        const components = {
            body: {
                row: EditableFormRow,
                cell: EditableCell,
            },
        };
        const columns = columnsOld.map(col => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    editable: col.editable,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    handleSave: this.handleSave,
                }),
            };
        });
        const {disabled} = this.props;
        const loadMore =
            !initLoading && !loading ? (
                <div
                    style={{
                        textAlign: 'center',
                        marginTop: 12,
                        height: 32,
                        lineHeight: '32px',
                    }}
                >
                    <Button onClick={this.onLoadMore}>加载更多</Button>
                </div>
            ) : <div
                style={{
                    textAlign: 'center',
                    marginTop: 12,
                }}
            >
                <Spin tip="正在加载,请稍等..."/>
            </div>;
        let arrquantitynew = dataSource.map(itemnew => {
            return itemnew.total==null?0:itemnew.total; //单个
        });
        let total = dataSource.length>0?arrquantitynew.reduce((n,m) => n + m):0;
        return (
            <div>
                {disabled ? null :
                    <Button
                        onClick={this.showModal}
                        ghost
                        type="primary"
                        icon="plus-circle"
                        style={{paddingLeft: "8px", paddingRight: "8px", marginTop: "10px"}}
                    >
                        同步报价清单
                    </Button>}
                <Table
                    components={components}
                    rowClassName={() => 'editable-row'}
                    className="expand_table bpm_table mt10"
                    bordered
                    dataSource={dataSource}
                    columns={columns}
                    pagination={false}
                    footer={() => '合计: '+numeral(total).format('0,0')}
                />
                {disabled ? null :
                    <Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '18px'}}
                          type="plus-circle"/>}
                <Modal
                    title="选择智选中的项目方案报价清单"
                    visible={visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Search placeholder="输入方案名称" onChange={this.onSearch} style={{ width: 200 }} value={searchVal}/>
                    <Row style={{marginTop: "15px", minHeight: "100px", maxHeight: "400px", overflowY: "auto"}}>
                        {projectList.map((item, index) => {
                            return <Col span={12} key={item.id} className="tc">
                                <Checkbox onChange={this.onChange} value={item.id}
                                          checked={checkedKeys.includes(item.id)}>{item.name}</Checkbox>
                                <p style={{
                                    fontSize: "12px",
                                    color: "#AAA",
                                    marginTop: "2px"
                                }}>{item.createTime==null||item.createTime==0?"":moment(item.createTime).format("YYYY-MM-DD")}</p>
                            </Col>
                        })}
                    </Row>
                    {totalCount == projectList.length ||projectList.length==0 ? null : loadMore}
                </Modal>
            </div>
        );
    }
}

export default EditableTableInventory;




