import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {DatePicker, Select, Button, Table, Pagination, Input, ConfigProvider, message, Icon, Modal, Radio, Popconfirm, Popover, Breadcrumb, Dropdown, Menu} from 'antd';

import EditableTableFourItem from "./EditableTableFourItem";


@withRouter
@observer
class FourItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			visible: false,
			stateValue: 0,
			id: "",
		}
	}

	componentDidMount() {
        this.getBuildTeamFourItemList();
	}

	getBuildTeamFourItemList=()=>{
		const { taskDetail } = this.store;
		this.store.getBuildTeamFourItemList({"taskId":taskDetail.id}).then((res)=>{
           this.store.btFourItemList = res;
		})
	}


	saveContents=(dataSource)=>{
		console.log("dataSource=>", dataSource);
	}

	saveFourItem=(row)=>{
		const { masters, key, keyIndex, ...newItem} = row;
		console.log(row, masters, 'masters')
		if(masters.length>0){
			newItem['userTeamName']=masters[0]['userName']
			newItem['userTeamId']=masters[0]['userId']
		}else{
			newItem['userTeamName']=""
			newItem['userTeamId']=""
		}
		this.saveBuildTeamFourItem({"btfi":Object.assign(newItem,{"used":newItem['used']?1:0})})
	}

	saveBuildTeamFourItem = (parmas)=>{
		this.store.saveBuildTeamFourItem(parmas).then((res)=>{
            this.getBuildTeamFourItemList();
		})
	}

	overCurrentStage=()=>{
		const cfr = this.checkFormRequre();
		if(!cfr.result){
			message.warn(cfr.info)
			return
		}
		this.props.overCurrentStageTask();
		this.getBuildTeamFourItemList();
	}

	checkFormRequre=()=>{
		let resultObj = {"info":"频次和负责人不能为空",result:false};
		const { btFourItemList } = this.store;
		// console.log('btFourItemList=>',mobx.toJS(btFourItemList))
		const bfil = mobx.toJS(btFourItemList).filter((item,index)=>{

			return item.used===1 &&  (item.frequency===null || item.frequency==="" || item.userTeamName===null || item.userTeamName==="")
		})
		if(bfil.length===0){
			resultObj.result = true
		}
		return resultObj
	}


	render() {
		const {buildTeamState, btFourItemList} = this.store;
		const { currentStep, switchSteps } = this.props;
		const { } = this.state;
		return (
			<div style={{height: 'auto'}}>
				<div className="mb10">
					<Button
						ghost
						type="primary"
						icon="poweroff"
						onClick={this.props.reStartBuildTeam}
						style={buildTeamState.state4===2?{display:'',cursor:'pointer'}:{display:'none'}}
					>
						重新启动
					</Button>
				</div>
				<EditableTableFourItem
					id={'id'}
					store={this.store}
					dataSource={mobx.toJS(btFourItemList).map((item, index)=>{
						let masters = [];
						if(item.userTeamName!==null&&item.userTeamId!==null&&item.userTeamName!==""&&item.userTeamId!=="") masters = [{"userName":item.userTeamName, "userId":item.userTeamId}]
						return Object.assign(item, {"keyIndex":index+1, "masters":masters})
					})}
					saveData={this.saveContents}
					saveItem={this.saveFourItem}
					disabled={buildTeamState.state4===2?true:false}
					pid={''}
				/>
				<div  className="mt30 mb30" style={buildTeamState.state4!==2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={this.overCurrentStage} ghost
					        type="primary"> 完成该阶段任务</Button>
				</div>
				<div  className="mt30 mb30" style={buildTeamState.state4===2?{display:'flex', justifyContent:'center' }:{display: 'none'}}>
					<Button onClick={()=>{switchSteps(currentStep-1)}} style={{marginLeft:'20px'}} type='primary'>上一步</Button>
				</div>
			</div>
		)
	}
}


export default FourItem;



