import React from 'react';
import {inject, observer} from 'mobx-react';
import {Icon, Input, Col, Row, Modal, Avatar,message} from 'antd';
import { Scrollbars } from 'react-custom-scrollbars';
import * as _ from 'lodash'

const {Search} = Input;
const colorList = ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae', '#F08080', '#EEE685', '#9400D3', '#D1EEEE', '#90EE90']; // 十个颜色

@inject('UserStore')
@inject('DeliveryStore')
@observer
class UserList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			text: ''
		}
		this.getData = this.getData.bind(this);
		this.handleClickDebounce = _.debounce(this.getData, 500, { 'maxWait': 1000 });
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {

	}

	componentWillUnmount() {
		this.handleClickDebounce.cancel();
	}

	// 提交
	handleOk = () => {
		const { taskDetail, projectDetail } = this.props.DeliveryStore;
		let dataArray = JSON.parse(JSON.stringify(this.props.data));
		const users = dataArray.filter((res)=>{return res.selected==true});

		// 记录orders
		let length = this.props.dataSource.length;
		let add = length>0?this.props.dataSource[length-1].orders:0;

		users.forEach((item, index)=>{
			const btoiObj ={
				buildTeamOne:taskDetail.id,
				orders: add+(index+1),
				canDel:1,
				userName:item.realname,
				userId:item.id,
				phone:item.phone,
				email:item.email,
				position:'',
				duty:''
			}
			this.props.DeliveryStore.saveBuildTeamOneItem({"btoi":btoiObj}).then((res)=>{
				if(res){
					this.props.updateState('add', {visible: false});
					this.props.getTableSearch(" ");
					this.props.updateOneItem();
				}
			})
		})

	};

	// 取消
	handleCancel = () => {
		this.props.updateState('add', {visible: false})
	};

	// 点击列表选中
	handClick = (param) => {
		let dataArray = JSON.parse(JSON.stringify(this.props.data));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.props.updateState('add', {
			data: Object.assign([], dataArray)
		});
	}

	// 点击列表删除
	handClickDel = (param) => {
		let dataArray = JSON.parse(JSON.stringify(this.props.data));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selected: false}))
		this.props.updateState('add', {
			data: Object.assign([], dataArray)
		});
	}

	// 搜索
	/*onSearch = (value) => {
		const param = _.trim(value);
		this.props.getSearch(param);
	}*/

	// 模糊搜索
	onChange = (e) => {
		this.setState({
			text: _.trim(e.target.value)
		});
		this.handleClickDebounce()
	}

	getData(){
		const param = this.state.text;
		this.props.getSearch(param);
	}

	render() {
		const  {visible, data} = this.props;

		return (
			<Modal
				maskClosable={false}
				closable={false}
				okText={'提交'}
				title="添加成员"
				visible={visible}
				onOk={this.handleOk}
				onCancel={this.handleCancel}
			>
				<Row type="flex" justify="space-between" className="">
					<Col span={12} className='pr20'>
						{/*<Search
							placeholder="输入姓名/手机号"
							style={{width: '100%'}}
							onChange={this.onChange}
							onSearch={this.onSearch}
						/>*/}
						<Input
							placeholder="输入姓名/手机号"
							suffix={<Icon type="search" />}
							onChange={this.onChange}
						/>
						<Scrollbars
							autoHide
							autoHideTimeout={1000}
							autoHideDuration={200}
							autoHeight
							autoHeightMin={0}
							autoHeightMax={350}
							thumbMinSize={30}
							universal={true}
						>
							<ul className="userlist_ul">
								{data.length > 0 && data.map((item, index) => {
									return <li
										key={index}
										className="pr"
										onClick={this.handClick.bind(this, item)}>
										<div className="ul_name">{item.realname}</div>
										<div className="ul_station">{item.station}</div>
										<div className="ul_email">{item.email}</div>
										<Icon
											style={{
												position: 'absolute',
												top: '10px',
												right: '15px',
												fontSize: '16px',
												color: 'rgba(75,164,190,1)'
											}}
											className={item.selected?"active":"hidden"} type="check" />
									</li>
								})
								}
							</ul>
						</Scrollbars>
					</Col>
					<Col span={12} className="tl" style={{
						minHeight: '350px',
						borderLeft: '1px solid #e6e6e6'
					}}>
						<p className="pl20">已选成员({data&&data.filter((res)=>{return res.selected==true}).length})</p>
						<ul className="userlist_ul_del">
							{data.filter((res)=>{return res.selected==true}).length>0&&data.filter((res)=>{return res.selected==true}).map((item, index) => {
								return <li
									key={index}
									className="pr"
									onClick={this.handClickDel.bind(this, item)}>
									<Avatar
										style={{ display:'inlineBlock',backgroundColor: colorList[index%10], verticalAlign: 'middle' }} size="small">
										{item.realname.slice(0,1)}
									</Avatar>  {/*颜色随机取值*/}
									<span className="ul_name">{item.realname}</span>
									<Icon
										style={{
											position: 'absolute',
											top: '13px',
											right: '15px',
											fontSize: '16px',
											color: 'rgba(184,80,74,1)',
											cursor: 'pointer'
										}}
										className="userlist_ul_icon"
										type="close" />
								</li>
							})
							}
						</ul>
					</Col>
				</Row>
			</Modal>
		)
	}
}


export default UserList;





