import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Button, Radio, message, DatePicker, Select, Input, Switch} from 'antd';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';
import EditableTableUpload from "@/components/handover/EditableTableUpload";
const { Option } = Select;


@inject('DeliveryStore')
@withRouter
@observer
class TwoItem extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
            fields: {},
			dataSource: [],
		}
	}

    componentDidMount() {
        this.getConstructionScheduleList();
        this.getEnclosureList();
    }

    getConstructionScheduleList = () => {
        const {taskDetail} = this.store;
        this.store.getConfirmEnterOneMain({"taskId": mobx.toJS(taskDetail).id}).then((res) => {
            this.setState({
                fields: res
            });
        });
    }

    handleDate = (type, date, dateString) => {
        let value = dateString == null || dateString == "" ? 0 : moment(dateString, 'YYYY-MM-DD').valueOf();
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = value;
        this.store.saveConfirmEnterOneMain({mian: data}).then((res)=>{
            if (res) {
                message.success("保存成功");
                this.setState({
                    fields: Object.assign({}, res)
                });
            }
        });
    }

    getEnclosureList=()=>{
        const { taskDetail } = this.store;
        this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, "indexs":0}).then((res)=>{
            this.setState({dataSource: res})
        })
    }

	overCurrentStage = () => {
		this.props.overCurrentStageTask();
	}

    onChange = (type, e) => {
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = e.target.value;
        this.setState({
            fields: Object.assign({}, data)
        }, ()=>{
            this.onSave();
        });
    }

    onSave = () => {
        this.store.saveConfirmEnterOneMain({mian: this.state.fields}).then((res)=>{
            if (res) {
                message.success("保存成功");
                /*this.setState({
                   fields: Object.assign({}, res)
                });*/
            }
        });
    }

    handleChange2 = (type, e) => {
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data[type] = e.target.value;
        this.setState({
            fields: Object.assign({}, data)
        });
    }

    handleBChange2 = (type, bfData) => {
        let value = BraftEditor.createEditorState(bfData).toHTML();
        let fields = JSON.parse(JSON.stringify(this.state.fields));
        fields[type] = value;
        this.setState({
            fields: Object.assign({}, fields)
        }, ()=>{
            this.onSave();
        });
    }

    onSwitch = (checked) => {
        let data = JSON.parse(JSON.stringify(this.state.fields));
        data["firstPartySignature"] = checked?1:0;
        this.setState({
            fields: Object.assign({}, data)
        }, ()=>{
            this.onSave();
        });
    }

    saveContents=()=>{
        this.getEnclosureList();
    }

    saveItem=(row)=>{
        const { dataSource } = this.state;
        const { taskDetail } = this.store;
        let em = {taskId:taskDetail.id, name:'',indexs:1,orders:dataSource.length+1, canDel:1, enclosureItems:[],templates: [], cases:[]};
        if(row.id!==undefined&& row.id[0]!=="!"){
            em['id'] = row.id
            em['orders']= row.orders
            em['enclosureItems'] = row.enclosureItems
            em['templates'] = row.templates
            em['cases'] = row.cases
            em['name']= row.name
        }
        const parmas = {"em":em, taskId:taskDetail.id, indexs:1}
        this.store.saveEnclosureMain(parmas).then((res)=>{
            this.getEnclosureList()
        })
    }

    getEnclosureList=()=>{
        const { taskDetail } = this.store;
        this.store.getEnclosureList({"taskId":mobx.toJS(taskDetail).id, "indexs":0}).then((res)=>{
            this.store.enclosureList = res
            this.setState({dataSource: res, enclosureList: res})
        })
    }

	render() {
		const {confirmEnterState} = this.store;
		const {currentStep, switchSteps} = this.props;
		const {dataSource, fields} = this.state;
        const controls = ['bold',  'text-color']

        return (
			<div className={confirmEnterState.state2 === 2?"":"mt30"}>
				<div
					ref={node => {
						this.container = node;
					}}
                >
					{/*编辑态*/}
					<div className={confirmEnterState.state2 === 2 ? "hidden" : ""}>
						<div className="daily_tips">
							<p><span>小交温馨提醒：请在正式进场前，确认基础条件要求，在正式进场前现场确认，争取甲方的签字验收哦~</span></p>
						</div>
                        <div className="mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>实际踏勘结果</span></div>
                        <table className="makeplotTable">
                            <tr>
                                <th>序号</th>
                                <th>基础条件项目</th>
                                <th>提供方</th>
                                <th>验收项</th>
                                <th>验收标准</th>
                                <th><span style={{
                                    fontWeight: "400",
                                    marginRight: "4px",
                                    color: "#f5222d",
                                    fontSize: "14px",
                                    fontFamily: "SimSun, sans-serif"
                                }}>*</span>验收结果</th>
                                <th>验收时间</th>
                            </tr>
                            <tr>
                                <td rowSpan={5} className="vm">1</td>
                                <td rowSpan={5} className="vm">
                                    基础
                                </td>
                                <td rowSpan={5} className="vm">
                                    {fields.basicProvider}
                                </td>
                                <td>
                                    结构形式
                                </td>
                                <td>
                                    {fields.basicStructureForm}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "basicStructureFormResult")} value={fields.basicStructureFormResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.basicStructureFormResult==0?<Input
                                        value={fields.basicStructureFormActual}
                                        onChange={this.handleChange2.bind(this, "basicStructureFormActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td rowSpan={5} className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.basicActualOverTime==null||fields.basicActualOverTime==0?"":moment(moment(fields.basicActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "basicActualOverTime")} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    设计强度要求
                                </td>
                                <td>
                                    {fields.basicStrengthRequirement}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "basicStrengthRequirementResult")} value={fields.basicStrengthRequirementResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.basicStrengthRequirementResult==0?<Input
                                        value={fields.basicStrengthRequirementActual}
                                        onChange={this.handleChange2.bind(this, "basicStrengthRequirementActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    坐标纵、横轴线偏差
                                </td>
                                <td>
                                    {fields.basicLongitudinal}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "basicLongitudinalResult")} value={fields.basicLongitudinalResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.basicLongitudinalResult==0?<Input
                                        value={fields.basicLongitudinalActual}
                                        onChange={this.handleChange2.bind(this, "basicLongitudinalActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    平面标高偏差
                                </td>
                                <td>
                                    {fields.basicElevation}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "basicElevationResult")} value={fields.basicElevationResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.basicElevationResult==0?<Input
                                        value={fields.basicElevationActual}
                                        onChange={this.handleChange2.bind(this, "basicElevationActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    外形尺寸偏差
                                </td>
                                <td>
                                    {fields.basicSize}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "basicSizeResult")} value={fields.basicSizeResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.basicSizeResult==0?<Input
                                        value={fields.basicSizeActual}
                                        onChange={this.handleChange2.bind(this, "basicSizeActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                            </tr>
                            <tr>
                                <td>2</td>
                                <td>临水</td>
                                <td>
                                    {fields.waterProvider}
                                </td>
                                <td>施工用水</td>
                                <td>
                                    {fields.waterStandard}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "waterStandardResult")} value={fields.waterStandardResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.waterStandardResult==0?<Input
                                        value={fields.waterStandardActual}
                                        onChange={this.handleChange2.bind(this, "waterStandardActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.waterActualOverTime==null||fields.waterActualOverTime==0?"":moment(moment(fields.waterActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "waterActualOverTime")} />
                                </td>
                            </tr>
                            <tr>
                                <td>3</td>
                                <td>临电</td>
                                <td>
                                    {fields.powerProvider}
                                </td>
                                <td>电压</td>
                                <td>
                                    {fields.powerStandard}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "powerStandardResult")} value={fields.powerStandardResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.powerStandardResult==0?<Input
                                        value={fields.powerStandardActual}
                                        onChange={this.handleChange2.bind(this, "powerStandardActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.powerActualOverTime==null||fields.powerActualOverTime==0?"":moment(moment(fields.powerActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "powerActualOverTime")} />
                                </td>
                            </tr>
                            <tr>
                                <td>4</td>
                                <td>道路</td>
                                <td>
                                    {fields.roadProvider}
                                </td>
                                <td>车辆进场</td>
                                <td>
                                    {fields.roadStandard}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "roadStandardResult")} value={fields.roadStandardResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.roadStandardResult==0?<Input
                                        value={fields.roadStandardActual}
                                        onChange={this.handleChange2.bind(this, "roadStandardActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.roadActualOverTime==null||fields.roadActualOverTime==0?"":moment(moment(fields.roadActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "roadActualOverTime")} />
                                </td>
                            </tr>
                            <tr>
                                <td rowSpan={2} className="vm">5</td>
                                <td rowSpan={2} className="vm">场地</td>
                                <td>
                                    {fields.materialFieldProvider}
                                </td>
                                <td>材料堆场</td>
                                <td>
                                    {fields.materialFieldStandard}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "materialFieldStandardResult")} value={fields.materialFieldStandardResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.materialFieldStandardResult==0?<Input
                                        value={fields.materialFieldStandardActual}
                                        onChange={this.handleChange2.bind(this, "materialFieldStandardActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.materialFieldActualOverTime==null||fields.materialFieldActualOverTime==0?"":moment(moment(fields.materialFieldActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "materialFieldActualOverTime")} />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {fields.craneSitesProvider}
                                </td>
                                <td>吊车站位</td>
                                <td>
                                    {fields.craneSitesStandard}
                                </td>
                                <td>
                                    <Radio.Group onChange={this.onChange.bind(this, "craneSitesStandardResult")} value={fields.craneSitesStandardResult}>
                                        <Radio value={1}>满足</Radio>
                                        <Radio value={0}>不满足</Radio>
                                    </Radio.Group>
                                    {fields.craneSitesStandardResult==0?<Input
                                        value={fields.craneSitesStandardActual}
                                        onChange={this.handleChange2.bind(this, "craneSitesStandardActual")}
                                        onBlur={this.onSave}
                                        style={{ width: "140px" }}
                                        placeholder="请输入实际结果" />:<div style={{ width: "140px",height: "34px", display: "inline-block" }}></div>}
                                </td>
                                <td className="vm">
                                    <DatePicker
                                        format="YYYY-MM-DD"
                                        style={{ width: 120 }}
                                        value={fields.craneSitesActualOverTime==null||fields.craneSitesActualOverTime==0?"":moment(moment(fields.craneSitesActualOverTime).format('YYYY-MM-DD'), "YYYY-MM-DD")}
                                        onChange={this.handleDate.bind(this, "craneSitesActualOverTime")} />
                                </td>
                            </tr>
                        </table>
                        <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>备注</span></div>
                        <BraftEditor
                            className="my-editor"
                            onBlur={(e)=>{this.handleBChange2('remarkTwo', e)}}
                            controls={controls}
                            contentStyle={{
                                minHeight: 100,
                                maxHeight: 100,
                                overflowY: 'scroll',
                                height: 'auto',
                                width:'100%'
                            }}
                            value={fields.remarkTwo==null||fields.remarkTwo==""?BraftEditor.createEditorState(null):BraftEditor.createEditorState(fields.remarkTwo)}
                            placeholder="请输入"
                        />
                        <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>甲方是否签字</span>（若甲方签字困难，可通过小程序拍摄现场基础照片，上传至系统中~）</div>
                        <Switch
                            checked={fields.firstPartySignature==1?true:false}
                            onChange={this.onSwitch}
                        />
                        <div className="mt20 mb20" style={{fontFamily: 'PingFang-SC-Regular', fontWeight: 400, fontSize: '14px'}}><span style={{fontWeight: '700', fontSize: '18px'}}>附件</span>（请按照要求上传附件内容，标星为必须上传项，可自由添加附件项目中不含的相关内容）</div>
                        <EditableTableUpload
                            id={'id'}
                            store={this.store}
                            dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
                            saveData={this.saveContents}
                            saveItem={this.saveItem}
                            disabled={false}
                            pid={''}
                        />
					</div>

                    {/*详情态*/}
                    <div style={{padding: "0 10px"}} className={confirmEnterState.state2 === 2?"":"hidden"}>
                        <Button
                            ghost
                            type="primary"
                            icon="poweroff"
                            onClick={this.props.reStartHandover}
                            style={confirmEnterState.state2 === 2 ? {display: ''} : {display: 'none'}}
                        >
                            重新启动
                        </Button>
                        <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>
                            实际踏勘结果
                        </h1>
                        <table className="makeplotTable">
                            <tr>
                                <th>序号</th>
                                <th>基础条件项目</th>
                                <th>提供方</th>
                                <th>验收项</th>
                                <th>验收标准</th>
                                <th>验收结果</th>
                                <th>验收时间</th>
                            </tr>
                            <tr>
                                <td rowSpan={5} className="vm">1</td>
                                <td rowSpan={5} className="vm">
                                    基础
                                </td>
                                <td rowSpan={5} className="vm">
                                    {fields.basicProvider}
                                </td>
                                <td>
                                    结构形式
                                </td>
                                <td>
                                    {fields.basicStructureForm}
                                </td>
                                <td className="tl">
                                    {fields.basicStructureFormResult==1?
                                        <div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>
                                        }
                                    {fields.basicStructureFormResult==0&&fields.basicStructureFormActual!=null?": "+fields.basicStructureFormActual:null}
                                </td>
                                <td rowSpan={5} className="vm">
                                    {fields.basicActualOverTime==null||fields.basicActualOverTime==0?"":moment(fields.basicActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    设计强度要求
                                </td>
                                <td>
                                    {fields.basicStrengthRequirement}
                                </td>
                                <td className="tl">
                                    {fields.basicStrengthRequirementResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.basicStrengthRequirementResult==0&&fields.basicStrengthRequirementActual!=null?": "+fields.basicStrengthRequirementActual:null}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    坐标纵、横轴线偏差
                                </td>
                                <td>
                                    {fields.basicLongitudinal}
                                </td>
                                <td className="tl">
                                    {fields.basicLongitudinalResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.basicLongitudinalResult==0&&fields.basicLongitudinalActual!=null?": "+fields.basicLongitudinalActual:null}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    平面标高偏差
                                </td>
                                <td>
                                    {fields.basicElevation}
                                </td>
                                <td className="tl">
                                    {fields.basicElevationResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.basicElevationResult==0&&fields.basicElevationActual!=null?": "+fields.basicElevationActual:null}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    外形尺寸偏差
                                </td>
                                <td>
                                    {fields.basicSize}
                                </td>
                                <td className="tl">
                                    {fields.basicSizeResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.basicSizeResult==0&&fields.basicSizeActual!=null?": "+fields.basicSizeActual:null}
                                </td>
                            </tr>
                            <tr>
                                <td>2</td>
                                <td>临水</td>
                                <td>
                                    {fields.waterProvider}
                                </td>
                                <td>施工用水</td>
                                <td>
                                    {fields.waterStandard}
                                </td>
                                <td className="tl">
                                    {fields.waterStandardResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</div>}
                                    {fields.waterStandardResult==0&&fields.waterStandardActual!=null?": "+fields.waterStandardActual:null}
                                </td>
                                <td className="vm">
                                    {fields.waterActualOverTime==null||fields.waterActualOverTime==0?"":moment(fields.waterActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                            <tr>
                                <td>3</td>
                                <td>临电</td>
                                <td>
                                    {fields.powerProvider}
                                </td>
                                <td>电压</td>
                                <td>
                                    {fields.powerStandard}
                                </td>
                                <td className="tl">
                                    {fields.powerStandardResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.powerStandardResult==0&&fields.powerStandardActual!=null?": "+fields.powerStandardActual:null}
                                </td>
                                <td className="vm">
                                    {fields.powerActualOverTime==null||fields.powerActualOverTime==0?"":moment(fields.powerActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                            <tr>
                                <td>4</td>
                                <td>道路</td>
                                <td>
                                    {fields.roadProvider}
                                </td>
                                <td>车辆进场</td>
                                <td>
                                    {fields.roadStandard}
                                </td>
                                <td className="tl">
                                    {fields.roadStandardResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.roadStandardResult==0&&fields.roadStandardActual!=null?": "+fields.roadStandardActual:null}
                                </td>
                                <td className="vm">
                                    {fields.roadActualOverTime==null||fields.roadActualOverTime==0?"":moment(fields.roadActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                            <tr>
                                <td rowSpan={2} className="vm">5</td>
                                <td rowSpan={2} className="vm">场地</td>
                                <td>
                                    {fields.materialFieldProvider}
                                </td>
                                <td>材料堆场</td>
                                <td>
                                    {fields.materialFieldStandard}
                                </td>
                                <td className="tl">
                                    {fields.materialFieldStandardResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.materialFieldStandardResult==0&&fields.materialFieldStandardActual!=null?": "+fields.materialFieldStandardActual:null}
                                </td>
                                <td className="vm">
                                    {fields.materialFieldActualOverTime==null||fields.materialFieldActualOverTime==0?"":moment(fields.materialFieldActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    {fields.craneSitesProvider}
                                </td>
                                <td>吊车站位</td>
                                <td>
                                    {fields.craneSitesStandard}
                                </td>
                                <td className="tl">
                                    {fields.craneSitesStandardResult==1?<div><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/success.png'} alt=""/>满足条件</div>:
                                        <span><img
                                            width="18"
                                            height="18"
                                            style={{"vertical-align":'middle', marginRight: '6px'}}
                                            src={'./images/delivery/error.png'} alt=""/>不满足</span>}
                                    {fields.craneSitesStandardResult==0&&fields.craneSitesStandardActual!=null?": "+fields.craneSitesStandardActual:null}
                                </td>
                                <td className="vm">
                                    {fields.craneSitesActualOverTime==null||fields.craneSitesActualOverTime==0?"":moment(fields.craneSitesActualOverTime).format('YYYY-MM-DD')}
                                </td>
                            </tr>
                        </table>
                        <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>备注</h1>
                        <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}
                             dangerouslySetInnerHTML={{__html: fields.remarkTwo}}></div>
                        <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>甲方是否签字</h1>
                        <div className="deaft_content_detail mt20" style={{paddingLeft: '14px'}}>
                            {fields.firstPartySignature==1?<div>
                                <img
                                    width="18"
                                    height="18"
                                    style={{"vertical-align":'middle', marginRight: '6px'}}
                                    src={'./images/delivery/success.png'} alt=""/>
                                已签字
                            </div>:"未签字"}
                        </div>
                        <h1 style={{padding: "10px 0", fontWeight: 700, fontSize: "14px"}}>附件</h1>
                        <EditableTableUpload
                            id={'id'}
                            store={this.store}
                            dataSource={dataSource.map((item, index)=>{return Object.assign(item, {"keyIndex":index+1})})}
                            saveData={()=>{}}
                            saveItem={()=>{}}
                            disabled={true}
                            pid={''}
                        />
                    </div>
					<div className="mt30 mb30" style={confirmEnterState.state2 !== 2 ? {
						display: 'flex',
						justifyContent: 'center'
					} : {display: 'none'}}>
						<Button onClick={this.overCurrentStage}
                                ghost
                                type="primary"
                                > 完成该阶段任务</Button>
					</div>
					<div className="mt30 mb30" style={confirmEnterState.state2 === 2 ? {
						display: 'flex',
						justifyContent: 'center'
					} : {display: 'none'}}>
						<Button onClick={() => {
							switchSteps(currentStep - 1)
						}} type='primary'>上一步</Button>
					</div>
				</div>
			</div>
		)
	}
}


export default TwoItem;



