/*
*交付管理-干系人分析
*qiufh@bocspace.cn
*/
import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Scrollbars} from 'react-custom-scrollbars';
import {
	Button,
	Icon,
	Menu,
	Dropdown,
	message
} from 'antd';
import StakeholdeEdit from "@/components/delivery/StakeholdeEdit";
import StakeholdeView from "@/components/stakeholder/StakeholdeView";
import StakeholdeFile from "@/components/stakeholder/StakeholdeFile";
import {v4 as uuidv4} from 'uuid';
import emitter from "@/utils/events";

@inject('DeliveryStore')
@withRouter
@observer
export default class Stakeholder extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			fileVis: false, // 附件页
		}
	}

	componentDidMount() {
		this.changeVis("2");
	}

	// 新增
	addNewLog = () => {
		let stakeholderList = JSON.parse(JSON.stringify(this.store.stakeholderList));

		// 更新第一个的状态
		if (stakeholderList.length==0) {
			let taskDetail = JSON.parse(JSON.stringify(this.store.taskDetail));
			taskDetail.state = 1;
			this.store.updateTask({task: taskDetail}).then((res)=>{
				if(res) {
					this.store.taskDetail = Object.assign({}, res);
				}
			});
		}

		const createUserName = localStorage.getItem('username');
		const createTime = moment().valueOf();
		let param = {
			id: "!" + uuidv4(),
			name: "",
			taskId: "",
			copingStrategy: "",
			phone: "",
			position: "",
			sex: "",
			age: "",
			appellation: "",
			email: "",
			maritalStatus: "",
			monthlyIncome: "",
			hobby: "",
			profile: "",
			photo: "",
            wechat: "",
			communicatorId: "",
			communicatorName: "",
			communicationMode: [],
			communicator: [],
			remark: "",
			createTime: createTime,
			createUserName: createUserName
		}

		stakeholderList = [param, ...stakeholderList];
		let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		});
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.store.stakeholderList = Object.assign([], dataArray);
		this.store.stakeObj = Object.assign({}, param);
		this.store.stakeState = true;
	}

	handleSelectedDaily = (item, index) => {
		if (this.store.stakeState == true) {
			message.warn("请先完成当前新增或编辑操作");
			return false;
		}
		let param = item;
		let stakeholderList = JSON.parse(JSON.stringify(this.store.stakeholderList));
		let dataArray = stakeholderList.map((item) => Object.assign(item, {selected: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		});
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.store.stakeholderList = Object.assign([], dataArray);
		this.store.stakeObj = Object.assign({}, param);
		this.store.stakeState = false;
	}

	onClick = (e) => {
		const {key} = e;
		let taskDetail = JSON.parse(JSON.stringify(this.store.taskDetail));
		if (key == "1") {
			taskDetail.state = 1;
		} else if (key == "2") {
			taskDetail.state = 10;
		}
		this.store.updateTask({task: taskDetail}).then((res)=>{
			if(res) {
               message.success("操作成功");
               this.store.taskDetail = Object.assign({}, res);
				emitter.emit("getProjectState",{});
			}
		});
	}

	changeVis = (type) => {
		this.setState({
			fileVis: type==1?true:false
		});
		if (type == "1") {
			this.store.getSts();
		}
	}


	render() {
		const {stakeholderList, stakeState, taskDetail} = this.store;
		const {fileVis} = this.state;
		const menu = (
			<Menu onClick={this.onClick}>
				<Menu.Item key="1">
					进行中
				</Menu.Item>
				<Menu.Item key="2">
					已完成
				</Menu.Item>
			</Menu>
		);
		const taskObj = {0: "待启动", 1: "进行中", 10: "已完成"};
		const taskD = mobx.toJS(taskDetail);

		return (
			<div className="dailyModal" style={{padding: 0}}>
				<div className={fileVis==true?"hidden":"fl"}
				     style={{paddingRight: '0', width: '260px', minHeight: '600px', borderRight: '1px solid #EBEBEB'}}>
					<div className="daily_list_op">
						<Button style={{padding: "5px 3px"}} onClick={this.addNewLog} className="title" type="link"><img
							src="./images/delivery/u3772.png" style={{width: '21px', height: '21px'}}/>新建</Button>
						<Button style={{padding: "5px 3px"}} className="title" type="link" onClick={this.changeVis.bind(this, '1')}><img src="./images/delivery/u3771.png"
						                                           style={{width: '23px', height: '23px'}}/>附件</Button>
						<Dropdown overlay={menu}>
							<Button style={{padding: "5px 3px"}} className="title" type="link"><img src="./images/delivery/u3773.png"
							                                           style={{width: '21px', height: '21px'}}/>
								{taskObj[taskD.state]}<Icon type="down"/>
							</Button>
						</Dropdown>
					</div>
					<Scrollbars
						style={{
							height: "calc(100vh - 200px)"
						}}
						autoHide={true}>
						{mobx.toJS(stakeholderList).length === 0 ? <div className="empty_daily_list">
								<div><img src={'./images/delivery/u2030.png'} style={{width: '191px', height: '187px'}}/>
								</div>
								<div>
									<Button onClick={this.addNewLog} type="primary">新建第一个干系人</Button>
								</div>
							</div> :
							mobx.toJS(stakeholderList).map((item, index) => {
								return (
									<div onClick={() => {
										this.handleSelectedDaily(item, index)
									}}
									     className={item.selected == true ? "daily_item daily_active_item" : "daily_item"}>
										<div className="label_info">
											<div>
												{item.photo !== null && item.photo !== "" ?
													<img src={item.photo} style={{width: '86px', height: '82px'}}/> :
													<div style={{
														width: '86px',
														height: '82px',
														lineHeight: "82px",
														textAlign: "center",
														background: "#4AA9F8",
														color: "#FFF",
														fontSize: "14px"
													}}>
														{item.name==null||item.name==""?"新增":item.name.slice(-2)}
													</div>}
											</div>
											<div>
												<div className="title">{item.name==null||item.name==""?"新增":item.name}</div>
												<div className="desc_info"><p>{item.appellation} {item.phone==null||item.phone==""?null:"- "+item.phone}</p>
												</div>
												<div className="worker_num">{item.position}</div>
											</div>
										</div>
										<div className="author_progress">
											<div
												className="author_date">{item.createUserName === null ? '' : item.createUserName} {item.createTime !== 0 && item.createTime !== null ? moment(item.createTime).format('YYYY.MM.DD') : ''}</div>
										</div>
									</div>
								)
							})
						}
					</Scrollbars>
				</div>
				<div className={fileVis==true?"hidden":"fl"}
				     style={{
					width: 'calc(100% - 270px)',
					height: "calc(100vh - 135px)",
					overflowX: "hidden",
					overflowY: "scroll",
					padding: '0 0 30px 0'
				}}>
					{stakeState ? <StakeholdeEdit store={this.store} getTable={this.props.getTable}/> :
						(mobx.toJS(stakeholderList).length === 0 ? <div className="empty_daily_content" style={{
								height: "calc(100vh - 168px)"
							}}><img src='./images/delivery/u2033.png ' style={{width: '336px', height: '336px'}}/></div>
							: <StakeholdeView store={this.store} getTable={this.props.getTable}/>)}
				</div>
				{fileVis==true?<StakeholdeFile
					changeVis={this.changeVis}
					store={this.store}
				/>:null}
			</div>
		)
	}
}






